import React from 'react'
import Paper from '@material-ui/core/Paper'
import { subscribe } from 'redux-subscriber'

// Components
import { CptTextFields, CptTextField } from '../CptTextField/CptTextField'
import TextField from 'SharedComponents/TextField/CustomTextField'
import TextFieldCurrency from 'SharedComponents/TextFieldCurrency/TextFieldCurrency'
import Button from '@material-ui/core/Button'
import Alert from 'SharedComponents/Alert/Alert'
import ToothNumber from '../ToothNumber/ToothNumber'
// import DentalProcedureFormValidator from './DentalProcedureFormValidator'
// import DiagnosisValidator from './DiagnosisValidator'
import DropDown from 'SharedComponents/DropDown/DropDown'

// Styles
import classes from './DentalProcedureForm.module.scss'

// Services
// import IcdCptStorageServices from 'Services/Storage/IcdCptStorageServices'
import ClaimsServices from 'Services/Api/Claims/ClaimsServices'

// Store
import { useSelector, useDispatch } from 'react-redux'
import { ClaimsEncoding as actions } from 'Pages/Protected/Claims/Store/actions'

const {
  setDentalProcedure,
  resetSuggestion,
  setSuggestion,
  addClaimProcedure,
  updateClaimProcedure,
  removeClaimProcedure,
  setError
} = actions

export default function DentalProcedureForm (props) {
  const {
    showAmountPaidByMember = true,
    showPhilhealthField = true,
    showPhilhealthRemarks = true,
    diagnosis,
    doValidate,
    errors
  } = props
  const dispatch = useDispatch()

  const [openDialog, setOpenDialog] = React.useState(false)

  const dentalProcedure = useSelector(({ ClaimsEncoding: state }) => state.dentalProcedure)
  const suggestions = useSelector(({ ClaimsEncoding: state }) => state.suggestions)
  const claim = useSelector(({ ClaimsEncoding: state }) => state.claim)
  const claim_type = useSelector(({ ClaimsEncoding: state }) => state.claimType)
  const [debounce, setDebounce] = React.useState(null)

  const [fetching, setFetching] = React.useState({
    description: false,
    code: false
  })

  React.useEffect(() => {
    const unsubscribe = subscribe('dentalProcedure.show', state => {
      setOpenDialog(false)
    })
    return () => unsubscribe()
  }, [])

  React.useEffect(() => {
    if (diagnosis) {
      const { diagnosis_code, diagnosis_description } = dentalProcedure.details
      if (!diagnosis.find(item => item.code === diagnosis_code && item.description === diagnosis_description)) {
        dispatch(setDentalProcedure({ details: { diagnosis_code: '', diagnosis_description: '' } }))
      }
    }
  }, [diagnosis])

  const handleOnChangeProcedureForm = ({ value, field }) => {
    dispatch(setDentalProcedure({ details: { [field]: value } }))
  }

  const handleOnChangeDiagnosis = ({ value, field }) => {
    const fieldArr = field.split('_')
    const _field = fieldArr.length === 2 ? fieldArr[1] : `${fieldArr[1]}_${fieldArr[2]}`
    const diagnosi = diagnosis.find(d => d[_field] === value)
    dispatch(
      setDentalProcedure({
        details: {
          diagnosis_code: diagnosi.code,
          diagnosis_description: diagnosi.description,
          diagnosis_given_diagnosis: diagnosi.given_diagnosis
        }
      })
    )
  }

  const handleOnChangeCpt = ({ value = {}, field, search }) => {
    if (search) {
      dispatch(setDentalProcedure({ details: { [field]: value } }))
      // const { 1: by } = field.split('_')
      dispatch(resetSuggestion({ field: 'cpt' }))
      getCptSuggestions({ value, by: field })
    } else {
      dispatch(resetSuggestion({ field: 'cpt' }))
      return dispatch(
        setDentalProcedure({
          details: { description: value.description, code: value.code, gross_amount: value.rate }
        })
      )
    }
  }

  const getCptSuggestions = ({ value, by }) => {
    if (value.trim().length < 3) return

    clearTimeout(debounce)
    setFetching(state => ({ ...state, [by]: true }))
    setDebounce(
      setTimeout(() => {
        if (value) {
          const field = by === 'code' ? 'cpt_code' : 'procedure_name'
          ClaimsServices.lookups({
            on: 'cpt',
            params: {
              search_field: field,
              search_value: value,
              claim_type: claim_type.split(' ').join(''),
              facility_id: claim.facility_id || '',
              room_type: claim.room_type || '',
              availment_date: claim_type === 'InPatient' ? claim.date_from : claim.date_to,
              limit: 10,
              sort_field: 'cpt_code',
              sort_direction: 'asc'
            }
          })
            .then(res => {
              const { data } = res.data
              dispatch(
                setSuggestion({
                  field: 'cpt',
                  suggestion: data.map(d => ({ code: d.cpt_code, description: d.procedure_name, rate: d.rate }))
                })
              )
            })
            .finally(() => {
              setFetching(state => ({ ...state, [by]: false }))
            })
        }
      }, 500)
    )
  }

  const handleOnSaveProcedure = () => {
    dispatch(setError({ procedure: { index: null, details: {} } }))
    const _errors = doValidate({ data: dentalProcedure.details })
    dispatch(
      setError({
        procedure: { ..._errors }
      })
    )
    if (Object.keys(_errors).length) return

    dispatch(addClaimProcedure(dentalProcedure.details))
    dispatch(setDentalProcedure({ show: false }))
  }

  const handleOnUpdateProcedure = index => {
    dispatch(setError({ procedure: { index: null, details: {} } }))
    const _errors = doValidate({ data: dentalProcedure.details })
    dispatch(
      setError({
        procedure: { index, ..._errors }
      })
    )
    if (Object.keys(_errors).length) return

    dispatch(updateClaimProcedure({ index, details: dentalProcedure.details }))
    dispatch(setDentalProcedure({ show: false }))
  }

  const handleOnRemoveProcedure = index => {
    dispatch(setError({ procedure: { index: null, details: {} } }))
    dispatch(removeClaimProcedure({ index }))
    dispatch(setDentalProcedure({ show: false }))
    setOpenDialog(false)
  }

  return (
    dentalProcedure.show && (
      <React.Fragment>
        <Paper className={classes.root}>
          {diagnosis && (
            <React.Fragment>
              <div className={classes.groups}>
                <div className={classes.input}>
                  <DropDown
                    id='given-diagnosis'
                    label='Given Diagnosis'
                    required
                    items={diagnosis.map(({ given_diagnosis }) => ({
                      text: given_diagnosis,
                      value: given_diagnosis
                    }))}
                    value={dentalProcedure.details.diagnosis_given_diagnosis}
                    onChange={event =>
                      handleOnChangeDiagnosis({
                        field: 'diagnosis_given_diagnosis',
                        value: event.target.value
                      })
                    }
                    error={errors.diagnosis_given_diagnosis}
                  />
                </div>
              </div>

              <div className={classes.groups}>
                <div className={classes.input}>
                  <DropDown
                    id='icd10-description'
                    required
                    label='ICD10 Description'
                    items={diagnosis.map(({ description }) => ({ text: description, value: description }))}
                    value={dentalProcedure.details.diagnosis_description}
                    onChange={event =>
                      handleOnChangeDiagnosis({
                        field: 'diagnosis_description',
                        value: event.target.value
                      })
                    }
                    error={errors.diagnosis_description}
                  />
                </div>
                <div className={classes.input}>
                  <DropDown
                    id='icd10-code'
                    required
                    label='ICD10 Code'
                    items={diagnosis.map(({ code }) => ({ text: code, value: code }))}
                    value={dentalProcedure.details.diagnosis_code}
                    onChange={event =>
                      handleOnChangeDiagnosis({
                        field: 'diagnosis_code',
                        value: event.target.value
                      })
                    }
                    error={errors.diagnosis_code}
                  />
                </div>
              </div>
            </React.Fragment>
          )}
          <div className={classes.groups}>
            <CptTextFields>
              <CptTextField
                id={`procedure-description-autocomplete`}
                codeId={`procedure-code-autocomplete`}
                description={dentalProcedure.details.description}
                suggestions={suggestions.cpt}
                code={dentalProcedure.details.code}
                onChangeDescription={(value, search) =>
                  handleOnChangeCpt({
                    field: 'description',
                    value,
                    search
                  })
                }
                onChangeCode={(value, search) => handleOnChangeCpt({ field: 'code', value, search })}
                fetching={fetching}
                errors={errors}
              />
            </CptTextFields>
          </div>

          <div className={classes.groups}>
            <div className={classes.input}>
              <ToothNumber
                id='tooth-number'
                label='Tooth Number'
                required
                value={dentalProcedure.details.tooth_number}
                onChange={event =>
                  handleOnChangeProcedureForm({
                    field: 'tooth_number',
                    value: event.target.value
                  })
                }
                error={errors.tooth_number}
              />
            </div>
          </div>

          <div className={classes.groups}>
            <div className={classes.input}>
              <TextFieldCurrency
                id='procedure-gross-amount'
                label='Gross Amount'
                value={dentalProcedure.details.gross_amount}
                onChange={event =>
                  handleOnChangeProcedureForm({
                    field: 'gross_amount',
                    value: event.target.value
                  })
                }
                error={errors.gross_amount}
              />
            </div>

            <div className={classes.input}>
              <TextFieldCurrency
                id='procedure-vat-amount'
                label='VAT Amount'
                value={dentalProcedure.details.vat_amount}
                onChange={event =>
                  handleOnChangeProcedureForm({
                    field: 'vat_amount',
                    value: event.target.value
                  })
                }
                error={errors.vat_amount}
              />
            </div>
            {showAmountPaidByMember && (
              <div className={classes.input}>
                <TextFieldCurrency
                  id='procedure-amount-paid-by-member'
                  label='Amount Paid by Member'
                  value={dentalProcedure.details.amount_paid_by_member}
                  onChange={event =>
                    handleOnChangeProcedureForm({
                      field: 'amount_paid_by_member',
                      value: event.target.value
                    })
                  }
                  error={errors.amount_paid_by_member}
                />
              </div>
            )}
          </div>
          {showPhilhealthField && (
            <div className={classes.groups}>
              <div className={classes.input}>
                <TextFieldCurrency
                  id='philhealth-amount-paid'
                  label='PhilHealth Amount Paid'
                  value={dentalProcedure.details.philhealth_amount_paid}
                  onChange={event =>
                    handleOnChangeProcedureForm({
                      field: 'philhealth_amount_paid',
                      value: event.target.value
                    })
                  }
                  error={errors.philhealth_amount_paid}
                />
              </div>
              {showPhilhealthRemarks && (
                <div className={classes.input}>
                  <TextField
                    multiline
                    rows={3}
                    id='philhealth-remarks'
                    label='PhilHealth Remarks'
                    value={dentalProcedure.details.philhealth_remarks}
                    onChange={event =>
                      handleOnChangeProcedureForm({
                        field: 'philhealth_remarks',
                        value: event.target.value
                      })
                    }
                  />
                </div>
              )}
            </div>
          )}

          <div className={`${classes.groups} ${classes.buttons}`}>
            {dentalProcedure.mode === 'add' && (
              <React.Fragment>
                <Button
                  id='procedure-save-button'
                  variant='contained'
                  color='primary'
                  className='button-positive'
                  onClick={handleOnSaveProcedure}
                >
                  Save
                </Button>
                <Button
                  id='procedure-cancel-button'
                  variant='contained'
                  color='secondary'
                  onClick={() => dispatch(setDentalProcedure({ show: false }))}
                >
                  Cancel
                </Button>
              </React.Fragment>
            )}
            {dentalProcedure.mode !== 'add' && (
              <React.Fragment>
                <Button
                  id='procedure-update-button'
                  variant='contained'
                  color='primary'
                  className='button-positive'
                  onClick={() => handleOnUpdateProcedure(dentalProcedure.index)}
                >
                  Update
                </Button>
                <Button
                  id='procedure-delete-button'
                  variant='contained'
                  color='secondary'
                  onClick={() => setOpenDialog(true)}
                >
                  Delete
                </Button>
              </React.Fragment>
            )}
          </div>
        </Paper>
        <Alert
          open={openDialog}
          message='Are you sure you want to delete this procedure?'
          cancelLabel='No'
          okLabel='Yes'
          onCancel={() => setOpenDialog(false)}
          onOk={() => handleOnRemoveProcedure(dentalProcedure.index)}
        />
      </React.Fragment>
    )
  )
}
