import React from 'react'
import Paper from '@material-ui/core/Paper'

// Components
import AutoComplete from 'SharedComponents/AutoComplete2/AutoComplete2'
import DropDown from 'SharedComponents/DropDown/DropDown'
import TextField from 'SharedComponents/TextField/CustomTextField'
import TextFieldCurrency from 'SharedComponents/TextFieldCurrency/TextFieldCurrency'
import Button from '@material-ui/core/Button'
import DiscrepancyBase from './../DiscrepancyBase/DiscrepancyBase'
import Alert from 'SharedComponents/Alert/Alert'
import Typography from '@material-ui/core/Typography'

// Styles
import classes from './ProcedureForm.module.scss'

// Services
// import IcdCptStorageServices from 'Services/Storage/IcdCptStorageServices'
import ClaimsServices from 'Services/Api/Claims/ClaimsServices'

// Store
import { useSelector, useDispatch } from 'react-redux'
import { ClaimsAnalysis as actions } from 'Pages/Protected/Claims/Store/actions'
const {
  setProcedure,
  resetSuggestion,
  setSuggestion,
  addClaimProcedure,
  updateClaimProcedure,
  removeClaimProcedure,
  setError
} = actions

export default function ProcedureForm (props) {
  const dispatch = useDispatch()
  const {
    index,
    discrepancy = {},
    error = {},
    doValidate,
    permissions,
    diagnosis,
    isDental,
    showAmountPaidByMember = true,
    showPhilhealthField = true,
    showAmountNotCovered = true,
    showAmountDueFromMember = true
  } = props

  const procedure = useSelector(({ ClaimsAnalysis: state }) => state.procedure)
  const suggestions = useSelector(({ ClaimsAnalysis: state }) => state.suggestions)
  const dropDown = useSelector(({ ClaimsAnalysis: state }) => state.dropDown)
  const claim = useSelector(({ ClaimsAnalysis: state }) => state.claim)
  const claim_type = useSelector(({ ClaimsAnalysis: state }) => state.claimType)

  const [openDeleteDialog, setOpenDeleteDialog] = React.useState({ show: false, index: null })
  const [_errors, setErrors] = React.useState({})
  const [debounce, setDebounce] = React.useState(null)

  const [fetching, setFetching] = React.useState({
    description: false,
    code: false
  })

  // ***** REACTIVE

  React.useEffect(() => {
    for (var prop in error) {
      if (error.hasOwnProperty(prop)) {
        setErrors(error)
        break
      }
    }
  }, [error, index])

  React.useEffect(() => {
    if (diagnosis) {
      const { diagnosis_code, diagnosis_description } = procedure.details
      if (!diagnosis.find(item => item.code === diagnosis_code && item.description === diagnosis_description)) {
        dispatch(setProcedure({ details: { diagnosis_code: '', diagnosis_description: '' } }))
      }
    }
  }, [diagnosis])

  // ***** EVENTS

  const handleOnChangeCpt = ({ value = {}, field, search }) => {
    if (search) {
      dispatch(setProcedure({ details: { [field]: value } }))
      // const { 1: by } = field.split('_')
      dispatch(resetSuggestion({ field: 'cpt' }))
      getCptSuggestions({ value, by: field })
    } else {
      dispatch(resetSuggestion({ field: 'cpt' }))
      return dispatch(
        setProcedure({
          details: { description: value.description, code: value.code, gross_amount: value.rate }
        })
      )
    }
  }

  const handleOnChangeDiagnosis = ({ value, field }) => {
    const fieldArr = field.split('_')
    const _field = fieldArr.length === 2 ? fieldArr[1] : `${fieldArr[1]}_${fieldArr[2]}`
    const { code, description, given_diagnosis } = diagnosis.find(d => d[_field] === value)
    dispatch(
      setProcedure({
        details: {
          diagnosis_code: code,
          diagnosis_description: description,
          diagnosis_given_diagnosis: given_diagnosis
        }
      })
    )
  }

  // ***** FETCHING

  const getCptSuggestions = ({ value, by }) => {
    if (value.trim().length < 3) return

    clearTimeout(debounce)
    setFetching(state => ({ ...state, [by]: true }))
    setDebounce(
      setTimeout(() => {
        if (value) {
          const field = by === 'code' ? 'cpt_code' : 'procedure_name'
          ClaimsServices.lookups({
            on: 'cpt',
            params: {
              search_field: field,
              search_value: value,
              claim_type: claim_type.split(' ').join(''),
              facility_id: claim.facility_id || '',
              room_type: claim.room_type || '',
              availment_date: claim_type === 'InPatient' ? claim.date_from : claim.date_to,
              limit: 10,
              sort_field: 'cpt_code',
              sort_direction: 'asc'
            }
          })
            .then(res => {
              const { data } = res.data
              dispatch(
                setSuggestion({
                  field: 'cpt',
                  suggestion: data.map(d => ({ code: d.cpt_code, description: d.procedure_name, rate: d.rate }))
                })
              )
            })
            .finally(() => {
              setFetching(state => ({ ...state, [by]: false }))
            })
        }
      }, 500)
    )
  }
  // ***** ACTIONS

  const handleOnChange = ({ value, field }) => {
    dispatch(setProcedure({ details: { [field]: value } }))
  }

  const handleOnSave = () => {
    dispatch(setError({ procedure: { index: null, details: {} } }))
    const _error = doValidate({ data: procedure.details, index })
    setErrors(_error)
    if (Object.keys(_error).length <= 0) {
      dispatch(addClaimProcedure(procedure.details))
      dispatch(setProcedure({ show: false, mode: '', index: '' }))
    }
  }

  const handleOnUpdate = index => {
    dispatch(setError({ procedure: { index: null, details: {} } }))
    const _error = doValidate({ data: procedure.details, index })
    setErrors(_error)
    if (Object.keys(_error).length <= 0) {
      dispatch(updateClaimProcedure({ index, details: procedure.details }))
      dispatch(setProcedure({ show: false, mode: '', index: '' }))
    }
  }

  const handleOnRemoveConfirm = index => {
    setOpenDeleteDialog({ show: true, index })
  }

  const handleOnRemove = index => {
    dispatch(setError({ procedure: { index: null, details: {} } }))
    dispatch(setProcedure({ show: false, mode: '', index: '' }))
    dispatch(removeClaimProcedure({ index }))
    setOpenDeleteDialog({ show: false, index: null })
  }

  const handleOnCancel = index => {
    dispatch(setError({ procedure: { index: null, details: {} } }))
    dispatch(setProcedure({ show: false, mode: '', index: '' }))
  }

  return (
    <React.Fragment>
      {/* {console.group('Procedure Form')} */}
      {/* {console.log('Data', procedure)} */}
      {/* {/* {console.log('Discrepancy', discrepancy)} */}
      {/* {console.log('Errors', error)} */}
      {/* {console.log('Suggestions ', suggestions)} */}
      {/* {console.log('Index ', procedure.index, index)} */}
      {/* {console.groupEnd()} */}
      {procedure.show && procedure.index === index && (
        <Paper className={classes.root}>
          {diagnosis && (
            <React.Fragment>
              <div className={classes.groups}>
                <div className={classes.input}>
                  <DropDown
                    id='given-diagnosis'
                    label='Given Diagnosis'
                    required
                    items={diagnosis.map(({ given_diagnosis }) => ({
                      text: given_diagnosis,
                      value: given_diagnosis
                    }))}
                    value={procedure.details.diagnosis_given_diagnosis}
                    onChange={event =>
                      handleOnChangeDiagnosis({
                        field: 'diagnosis_given_diagnosis',
                        value: event.target.value
                      })
                    }
                    error={_errors.diagnosis_given_diagnosis}
                    readOnly={!permissions.update}
                  />
                </div>
              </div>
              <div className={classes.groups}>
                <div className={classes.input}>
                  <DropDown
                    id='icd10-description'
                    required
                    label='ICD10 Description'
                    items={diagnosis.map(({ description }) => ({ text: description, value: description }))}
                    value={procedure.details.diagnosis_description}
                    onChange={event =>
                      handleOnChangeDiagnosis({
                        field: 'diagnosis_description',
                        value: event.target.value
                      })
                    }
                    error={_errors.diagnosis_description}
                    readOnly={!permissions.update}
                  />
                </div>
                <div className={classes.input}>
                  <DropDown
                    id='icd10-code'
                    required
                    label='ICD10 Code'
                    items={diagnosis.map(({ code }) => ({ text: code, value: code }))}
                    value={procedure.details.diagnosis_code}
                    onChange={event =>
                      handleOnChangeDiagnosis({
                        field: 'diagnosis_code',
                        value: event.target.value
                      })
                    }
                    error={_errors.diagnosis_code}
                    readOnly={!permissions.update}
                  />
                </div>
              </div>
            </React.Fragment>
          )}
          <div className={classes.groups}>
            <div className={classes.input}>
              <DiscrepancyBase
                id='dp-procedure-description'
                discrepancy={{
                  message: 'Failed',
                  result: { code: discrepancy.code, description: discrepancy.description }
                }}
              >
                <div className={classes.description}>
                  <AutoComplete
                    id={`procedure-description-autocomplete`}
                    required
                    label={'Procedure Details'}
                    value={procedure.details.description}
                    suggestions={suggestions.cpt}
                    field={'description'}
                    onChange={(value, search) =>
                      handleOnChangeCpt({
                        field: 'description',
                        value,
                        search
                      })
                    }
                    error={_errors.description}
                    readOnly={!permissions.update}
                  />
                </div>

                <div className={classes.code}>
                  <AutoComplete
                    id={`procedure-code-autocomplete`}
                    required
                    label={'CPT Code'}
                    value={procedure.details.code}
                    suggestions={suggestions.cpt}
                    field={'code'}
                    onChange={(value, search) => handleOnChangeCpt({ field: 'code', value, search })}
                    error={_errors.code}
                    readOnly={!permissions.update}
                  />
                </div>
              </DiscrepancyBase>
            </div>
          </div>

          {isDental && (
            <div className={classes.groups}>
              <div className={classes.input}>
                <DiscrepancyBase
                  id='dp-procedure-tooth-number'
                  discrepancy={{
                    message: 'Failed',
                    result: discrepancy.tooth_number
                  }}
                  autoWidth
                >
                  <Typography variant='subtitle2' className={classes['label']}>
                    {`Tooth Number *`}
                  </Typography>
                  <TextField
                    id='tooth-number'
                    value={procedure.details.tooth_number}
                    onChange={event => {
                      handleOnChange({
                        field: 'tooth_number',
                        value: event.target.value
                      })
                    }}
                    error={_errors.tooth_number}
                    readOnly={!permissions.update}
                  />
                </DiscrepancyBase>
              </div>
            </div>
          )}

          <div className={classes.groups}>
            <div className={classes.input}>
              <DiscrepancyBase
                id='dp-procedure-gross-amount'
                discrepancy={{
                  message: 'Failed',
                  result: discrepancy.gross_amount
                }}
              >
                <TextFieldCurrency
                  id='procedure-gross-amount'
                  required
                  label='Gross Amount'
                  value={procedure.details.gross_amount}
                  onChange={event =>
                    handleOnChange({
                      field: 'gross_amount',
                      value: event.target.value
                    })
                  }
                  error={_errors.gross_amount}
                  readOnly={!permissions.update}
                />
              </DiscrepancyBase>
            </div>

            <div className={classes.input}>
              <DiscrepancyBase
                id='dp-procedure-vat-amount'
                discrepancy={{
                  message: 'Failed',
                  result: discrepancy.vat_amount
                }}
              >
                <TextFieldCurrency
                  id='procedure-vat-amount'
                  label='VAT Amount'
                  value={procedure.details.vat_amount}
                  onChange={event =>
                    handleOnChange({
                      field: 'vat_amount',
                      value: event.target.value
                    })
                  }
                  error={_errors.vat_amount}
                  readOnly={!permissions.update}
                />
              </DiscrepancyBase>
            </div>
          </div>

          {showAmountPaidByMember && (
            <div className={classes.groups}>
              <div className={classes.input}>
                <DiscrepancyBase
                  id='dp-procedure-amount-paid-by-member'
                  discrepancy={{
                    message: 'Failed',
                    result: discrepancy.amount_paid_by_member
                  }}
                >
                  <TextFieldCurrency
                    id='procedure-amount-paid-by-member'
                    label='Amount Paid by Member'
                    value={procedure.details.amount_paid_by_member}
                    onChange={event =>
                      handleOnChange({
                        field: 'amount_paid_by_member',
                        value: event.target.value
                      })
                    }
                    error={_errors.amount_paid_by_member}
                    readOnly={!permissions.update}
                  />
                </DiscrepancyBase>
              </div>
            </div>
          )}

          {showPhilhealthField && (
            <div className={classes.groups}>
              <div className={classes.input}>
                <DiscrepancyBase
                  id='dp-philhealth-amount-paid'
                  discrepancy={{
                    message: 'Failed',
                    result: discrepancy.philhealth_amount_paid
                  }}
                >
                  <TextFieldCurrency
                    id='philhealth-amount-paid'
                    label='PhilHealth Amount Paid'
                    value={procedure.details.philhealth_amount_paid}
                    onChange={event =>
                      handleOnChange({
                        field: 'philhealth_amount_paid',
                        value: event.target.value
                      })
                    }
                    error={_errors.philhealth_amount_paid}
                    readOnly={!permissions.update}
                  />
                </DiscrepancyBase>
              </div>

              <div className={classes.input}>
                <DiscrepancyBase
                  id='dp-philhealth-remarks'
                  discrepancy={{
                    message: 'Failed',
                    result: discrepancy.philhealth_remarks
                  }}
                >
                  <TextField
                    multiline
                    rows={3}
                    id='philhealth-remarks'
                    label='PhilHealth Remarks'
                    value={procedure.details.philhealth_remarks}
                    onChange={event =>
                      handleOnChange({
                        field: 'philhealth_remarks',
                        value: event.target.value
                      })
                    }
                    error={_errors.philhealth_remarks}
                    readOnly={!permissions.update}
                  />
                </DiscrepancyBase>
              </div>
            </div>
          )}

          {showAmountNotCovered && (
            <React.Fragment>
              <div className={classes.groups}>
                <div className={classes.input}>
                  <DiscrepancyBase
                    id='dp-amount-not-covered'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.amount_not_covered
                    }}
                  >
                    <TextFieldCurrency
                      id='amount-not-covered'
                      label='Amount Not Covered'
                      value={procedure.details.amount_not_covered}
                      onChange={event => {
                        if (event.target.value === 0) {
                          procedure.details.reason = ''
                          setErrors(state => ({ ...state, reason: '' }))
                        }

                        handleOnChange({
                          field: 'amount_not_covered',
                          value: event.target.value
                        })
                      }}
                      error={_errors.amount_not_covered}
                      readOnly={!permissions.update}
                    />
                  </DiscrepancyBase>
                </div>
              </div>
              <div className={classes.groups}>
                <div className={classes.input}>
                  <DiscrepancyBase
                    id='dp-amount-not-covered-reason'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.reason
                    }}
                  >
                    <DropDown
                      id='amount-not-covered-reason'
                      label='Reason'
                      required={procedure.details.amount_not_covered > 0}
                      disableComponents={procedure.details.amount_not_covered === 0}
                      value={procedure.details.reason}
                      items={dropDown.reason}
                      onChange={event =>
                        handleOnChange({
                          field: 'reason',
                          value: event.target.value
                        })
                      }
                      error={_errors.reason}
                      readOnly={!permissions.update}
                    />
                  </DiscrepancyBase>
                </div>
              </div>
            </React.Fragment>
          )}

          {showAmountDueFromMember && (
            <React.Fragment>
              <div className={classes.groups}>
                <div className={classes.input}>
                  <DiscrepancyBase
                    id='dp-amount-due-from-member'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.amount_due_from_member
                    }}
                  >
                    <TextFieldCurrency
                      id='amount-due-from-member'
                      label='Amount Due from Member'
                      value={procedure.details.amount_due_from_member}
                      onChange={event => {
                        if (event.target.value === 0) {
                          procedure.details.reason_for_charging_member = ''
                          setErrors(state => ({ ...state, amount_due_from_member: '' }))
                        }

                        handleOnChange({
                          field: 'amount_due_from_member',
                          value: event.target.value
                        })
                      }}
                      error={_errors.amount_due_from_member}
                      readOnly={!permissions.update}
                    />
                  </DiscrepancyBase>
                </div>
              </div>
              <div className={classes.groups}>
                <div className={classes.input}>
                  <DiscrepancyBase
                    id='dp-hospital-item'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.reason_for_charging_member
                    }}
                  >
                    <DropDown
                      id='reason-for-charging-member'
                      label='Reason for Charging Member'
                      required={procedure.details.amount_due_from_member > 0}
                      disableComponents={procedure.details.amount_due_from_member === 0}
                      value={procedure.details.reason_for_charging_member}
                      items={dropDown.reason_charging}
                      onChange={event =>
                        handleOnChange({
                          field: 'reason_for_charging_member',
                          value: event.target.value
                        })
                      }
                      error={_errors.reason_for_charging_member}
                      readOnly={!permissions.update}
                    />
                  </DiscrepancyBase>
                </div>
              </div>
            </React.Fragment>
          )}

          {showAmountDueFromMember && (
            <React.Fragment>
              <div className={classes.groups}>
                <div className={classes.input}>
                  <DiscrepancyBase
                    id='dp-amount-due-from-client'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.amount_due_from_client
                    }}
                  >
                    <TextFieldCurrency
                      id='amount-due-from-client'
                      label='Amount Due from Client'
                      value={procedure.details.amount_due_from_client}
                      onChange={event => {
                        if (event.target.value === 0) {
                          procedure.details.reason_for_charging_client = ''
                          setErrors(state => ({ ...state, amount_due_from_client: '' }))
                        }

                        handleOnChange({
                          field: 'amount_due_from_client',
                          value: event.target.value
                        })
                      }}
                      error={_errors.amount_due_from_client}
                      readOnly={!permissions.update}
                    />
                  </DiscrepancyBase>
                </div>
              </div>
              <div className={classes.groups}>
                <div className={classes.input}>
                  <DiscrepancyBase
                    id='dp-amount-due-from-client'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.reason_for_charging_client
                    }}
                  >
                    <DropDown
                      id='reason-for-charging-client'
                      label='Reason for Charging Client'
                      required={procedure.details.amount_due_from_client > 0}
                      disableComponents={procedure.details.amount_due_from_client === 0}
                      value={procedure.details.reason_for_charging_client}
                      items={dropDown.reason_charging}
                      onChange={event =>
                        handleOnChange({
                          field: 'reason_for_charging_client',
                          value: event.target.value
                        })
                      }
                      error={_errors.reason_for_charging_client}
                      readOnly={!permissions.update}
                    />
                  </DiscrepancyBase>
                </div>
              </div>
            </React.Fragment>
          )}

          <div className={`${classes.groups} ${classes.buttons}`}>
            {procedure.mode === 'add' && permissions.create && (
              <React.Fragment>
                <Button
                  id='procedure-save-button'
                  variant='contained'
                  color='primary'
                  className='button-positive'
                  onClick={handleOnSave}
                >
                  Save
                </Button>
              </React.Fragment>
            )}
            {procedure.mode !== 'add' && permissions.update && (
              <React.Fragment>
                <Button
                  id='procedure-update-button'
                  variant='contained'
                  color='primary'
                  className='button-positive'
                  onClick={() => handleOnUpdate(procedure.index)}
                >
                  Update
                </Button>
                {permissions.delete && (
                  <Button
                    id='procedure-delete-button'
                    variant='contained'
                    color='secondary'
                    className='button-negative'
                    onClick={() => handleOnRemoveConfirm(procedure.index)}
                  >
                    Delete
                  </Button>
                )}
              </React.Fragment>
            )}
            <Button
              id={`procedure-${permissions.create && permissions.update ? 'cancel' : 'close'}-button`}
              variant='contained'
              color='secondary'
              className='button-negative'
              onClick={() => handleOnCancel(procedure.index)}
            >
              {permissions.create && permissions.update ? 'Cancel' : 'Close'}
            </Button>
          </div>
        </Paper>
      )}
      <Alert
        open={openDeleteDialog.show}
        message='Are you sure you want to delete this procedure?'
        cancelLabel='No'
        okLabel='Yes'
        onCancel={() => setOpenDeleteDialog({ show: false, index: null })}
        onOk={() => handleOnRemove(procedure.index)}
      />
    </React.Fragment>
  )
}
