const initialState = {
  pro: [],
  facility: [],
  doctor: [],
  anesthesiologist: [],
  pfDoctor: [],
  specialization: [],
  hospitalItem: [],
  icd10: [],
  cpt: [],
  contract: [],
  given_diagnosis: [],
  description: [],
  code: []
}

const suggestionsReducer = (state = {}, action) => {
  if (action.for !== constants.for.CLAIMS_ANALYSIS) return state

  switch (action.type) {
    case 'INIT_SUGGESTIONS':
      return initialState

    case 'SET_SUGGESTION':
      return (() => {
        let { field, suggestion } = action.payload
        return {
          ...state,
          [field]: suggestion
        }
      })()

    case 'RESET_SUGGESTION':
      return (() => {
        let { field } = action.payload
        return {
          ...state,
          [field]: [...initialState[field]]
        }
      })()

    // case 'SET_SUGGESTION_OBJECT':
    //   return (() => {
    //     let { field, suggestion } = action.payload
    //     return {
    //       ...state,
    //       [field]: { ...initialState[field], ...suggestion }
    //     }
    //   })()

    // case 'SET_SUGGESTION_ON_ARRAY':
    //   return (() => {
    //     let { field, index, by, suggestion, data } = action.payload

    //     return {
    //       ...state,
    //       [field]: state[field].map((i, _index) => {
    //         if (index === _index) {
    //           i = { ...i, [by]: suggestion, data }
    //         }
    //         return i
    //       })
    //     }
    //   })()

    // case 'ADD_SUGGESTION_TO_ARRAY':
    //   return (() => {
    //     const { field } = action.payload
    //     return {
    //       ...state,
    //       [field]: [...state[field], { ...icd10 }]
    //     }
    //   })()

    // case 'RESET_SUGGESTION_OBJECT':
    //   return (() => {
    //     const { field } = action.payload
    //     return { ...state, [field]: initialState[field] }
    //   })()

    // case 'RESET_SUGGESTION_ARRAY':
    //   return (() => {
    //     const { field, index } = action.payload
    //     return {
    //       ...state,
    //       [field]: state[field].map((i, _index) => {
    //         if (index === _index) {
    //           for (let f in icd10) i[f] = icd10[f]
    //         }
    //         return i
    //       })
    //     }
    //   })()

    default:
      return state
  }
}

export default suggestionsReducer
