import React from 'react'

// Material Components
import Button from '@material-ui/core/Button'
import Paper from '@material-ui/core/Paper'
import Table from 'SharedComponents/Table/Table'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TableSortLabel from '@material-ui/core/TableSortLabel'
import Typography from '@material-ui/core/Typography'
import GetAppIcon from '@material-ui/icons/GetApp'
import CircularProgress from '@material-ui/core/CircularProgress'

// Shared Components
import DatePicker from 'SharedComponents/DatePicker2/DatePicker2'
import AutoComplete from 'SharedComponents/AutoComplete/AutoComplete'
import TextField from 'SharedComponents/TextField/CustomTextField'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import ClaimsServices from 'Services/Api/Claims/ClaimsServices'
import ArrayToCSV from 'Services/ArrayToCSV'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'

// Utility
import { DateFormatter, CurrencyFormatter } from 'Utilities/TextFormatter'
import { staticHeaderFooter } from './helper/staticHeaderFooter'

// Components
import Footer from './Footer/Footer'

// Validator
import DateRangeValidator from './../TransmittalReport/validator/DateRangeValidator'

// Styles
import classes from './PaymentRequestReport.module.scss'

const columns = [
  { label: 'No.', sortField: 'number', sort: false, search: false, align: 'left' },
  { label: 'RCS/LOA No.', sortField: 'loa_number', sort: true, search: true, align: 'left', match: 'start' },
  {
    label: 'Member Account No.',
    sortField: 'member_account_number',
    sort: true,
    search: true,
    align: 'left',
    match: 'any'
  },
  { label: 'Member Name', sortField: 'member_name', sort: true, search: true, align: 'left', match: 'any' },
  { label: 'Approved Amount', sortField: 'approved_amount', sort: false, search: false, align: 'right' },
  { label: 'Tax Amount', sortField: 'tax_amount', sort: false, search: false, align: 'right' }
]

const initialDetails = {
  total_pr_amount: 0,
  discount_amount: 0,
  due_date: '',
  total_approved_amount: 0,
  total_tax_amount: 0
}

export default function PaymentRequestReport (props) {
  // added
  const [searchValue, setSearchValue] = React.useState({
    medical_partner_id: '',
    medical_partner_name: '',
    bundle: ''
  })

  const [claims, setClaims] = React.useState({
    bundles: [],
    raw: [],
    data: [],
    range: { from: new Date().toISOString(), to: new Date().toISOString(), field: 'status_date' },
    orderBy: 'loa_number',
    order: true,
    page: 1,
    limit: 1,
    count: 0,
    filter: {},
    rowsPerPageOptions: [0],
    // on filter_value
    search: {
      medical_partner_id: '',
      medical_partner_name: '',
      bundle: ''
    }
  })

  const [fetching, setFetching] = React.useState(false)

  const [details, setDetails] = React.useState(initialDetails)

  const [suggestions, setSuggestions] = React.useState({
    medicalPartner: {
      id: [],
      name: [],
      data: []
    },

    bundle: {
      id: [],
      data: []
    }
  })

  const [dateRangeError, setDateRangeError] = React.useState({
    date_from: '',
    date_to: ''
  })

  React.useEffect(() => {
    PageTitleService('Payment Request Report')
    BreadcrumbsService.setSelectedItems(['paymentRequestReport'])
  }, [])

  const [debounce, setDebounce] = React.useState({
    facility: null,
    bundle: null,
    column: null
  })

  //* Facility
  const handleOnChangeFacility = ({ value, field, isSuggestionClick }) => {
    setClaims(state => ({ ...state, search: { ...state.search, [field]: value } }))
    if (isSuggestionClick) {
      for (let i of suggestions.medicalPartner.data) {
        if (value === i.id || value === i.name) {
          setClaims(state => ({
            ...state,
            search: { ...state.search, medical_partner_id: i.id, medical_partner_name: i.name }
          }))

          setSuggestions(state => ({
            ...state,
            medicalPartner: {
              id: [],
              name: [],
              data: []
            }
          }))
          return
        }
      }
    } else {
      setSuggestions(state => ({
        ...state,
        medicalPartner: {
          id: [],
          name: [],
          data: []
        }
      }))
      const { 2: by } = field.split('_')
      getFacilitySuggestions({ value, by })
    }
  }

  const getFacilitySuggestions = ({ value = '', by }) => {
    if (value.trim().length < 3) return

    clearTimeout(debounce.facility)
    setDebounce(state => ({
      ...state,
      facility: setTimeout(() => {
        if (value) {
          ClaimsServices.lookups({
            on: 'medical-partner',
            params: { query: value, by }
          }).then(res => {
            const { data } = res.data
            let _items = []
            for (let d of data) _items.push(d[by])
            setSuggestions(state => ({ ...state, medicalPartner: { ...state.medicalPartner, [by]: _items, data } }))
          })
        }
      }, 500)
    }))
  }

  // Bundle/SOA*
  const handleOnChangeBundleSoa = ({ value, field, isSuggestionClick }) => {
    setClaims(state => ({ ...state, search: { ...state.search, [field]: value } }))
    if (isSuggestionClick) {
      for (let i of suggestions.bundle.data) {
        if (value === i.bundle_number || value === i.soa) {
          setClaims(state => ({
            ...state,
            search: {
              ...state.search,
              bundle: value
            }
          }))
          setSuggestions(state => ({
            ...state,
            bundle: {
              id: [],
              data: []
            }
          }))
          return
        }
      }
    } else {
      setSuggestions(state => ({
        ...state,
        bundle: {
          id: [],
          data: []
        }
      }))
      getBundleSoaSuggestions({ value })
    }
  }

  const getBundleSoaSuggestions = ({ value = '' }) => {
    if (value.trim().length < 3) return

    clearTimeout(debounce.bundle)
    setDebounce(state => ({
      ...state,
      bundle: setTimeout(() => {
        if (value) {
          ClaimsServices.lookups({
            on: 'bundle-soa',
            params: { query: value }
          }).then(res => {
            const { data } = res.data
            let _items = []

            // TODO Clarify if its OR (Bundle/SOA)
            // store soa_number first
            data.forEach(d => {
              if (d.soa) _items.push(d.soa)
              _items.push(d.bundle_number)
            })

            setSuggestions(state => ({
              ...state,
              bundle: { ...state.bundle, id: _items.filter(i => i.startsWith(value)), data }
            }))
          })
        }
      }, 500)
    }))
  }

  const handleOnFilter = params => {
    // added
    setSearchValue(state => ({ ...state, ...params.search }))
    const _claims = { ...claims, ...params.search }

    if (claims.search.medical_partner_id.trim().length < 3 || claims.search.medical_partner_name.trim().length < 3) {
      LoadingService.loading(false)
      setFetching(false)
      return setClaims(state => ({ ...state, data: [] }))
    }
    LoadingService.loading(true)

    // prev 'claims'
    ClaimsServices.paymentRequestReport({
      range: _claims.range,
      sort: `${_claims.orderBy}|${_claims.order ? 'asc' : 'desc'}`,
      page: _claims.page,
      limit: _claims.limit,
      filter: _claims.filter,
      searchValue: _claims.search
    }).then(res => {
      const { results = [], raw = [], count, limit, page } = res.data.data
      setClaims(state => ({ ...state, bundles: results, raw, count, page, limit }))

      setFetching(true)

      const bundle_number = results[_claims.page - 1]
      if (bundle_number) {
        ClaimsServices.paymentRequestClaims(
          { sort: `${_claims.orderBy}|${_claims.order ? 'asc' : 'desc'}`, searchValue: _claims.search },
          bundle_number
        ).then(res => {
          const { results = [], details = {} } = res.data.data

          // details.due_date = DateFormatter(details.due_date)

          setClaims(state => ({ ...state, data: results }))
          setDetails(state => ({ ...state, ...details }))
          setFetching(false)
          LoadingService.loading(false)
        })
      } else {
        setClaims(state => ({ ...state, data: [] }))
        setDetails(state => ({ ...state, ...initialDetails }))
        setFetching(false)
        LoadingService.loading(false)
      }
    })
  }

  const handlePagination = params => {
    const bundle_number = params.bundles[params.page - 1]

    ClaimsServices.paymentRequestClaims(
      {
        sort: `${params.orderBy}|${params.order ? 'asc' : 'desc'}`,
        filter: params.filter,
        // added
        searchValue: searchValue
      },
      bundle_number
    ).then(res => {
      const { results = [], details = {} } = res.data.data

      // details.due_date = DateFormatter(details.due_date)

      setClaims(state => ({ ...state, data: results }))
      setDetails(state => ({ ...state, ...details }))
      setFetching(false)
    })
  }

  const validate = () => {
    const { error } = DateRangeValidator({
      date_from: claims.range.from,
      date_to: claims.range.to
    })

    setDateRangeError({ ...error })
    return !!error
  }

  const handleColumnFiltering = params => {
    const _params = {
      ...claims,
      ...params
    }
    // setFetching(true)
    setClaims(state => ({ ...state, ...params }))
    handlePagination(_params)
  }

  const handleOnPaginate = params => {
    const _params = {
      ...claims,
      ...params
    }
    setFetching(true)
    setClaims(state => ({ ...state, ...params }))
    handlePagination(_params)
  }

  const handleGenerateReport = () => {
    if (!claims.raw.length) return

    const header = columns.map(i => i.label)

    const data = staticHeaderFooter({ ...claims.range, raw: claims.raw, ...claims.search, columns: header })

    ArrayToCSV({ fileName: `${claims.search.medical_partner_name} Payment Request Report`, data })
  }

  return (
    <Paper className={classes.root}>
      <div className={classes.header}>
        <div className={classes['search-header']}>
          {/* row-1 */}
          <div className={classes['search-header-child']}>
            <div className={classes['column-label']}>
              <Typography variant='h6'>Medical Partner ID:</Typography>
            </div>
            <div className={classes['column-input']}>
              <div className={classes['input']}>
                <AutoComplete
                  placeholder='Search by medical partner id'
                  id='medical-partner-id'
                  value={claims.search.medical_partner_id}
                  suggestions={suggestions.medicalPartner.id}
                  onChange={(value, isSuggestionClick) =>
                    handleOnChangeFacility({
                      field: 'medical_partner_id',
                      value,
                      isSuggestionClick
                    })
                  }
                />
              </div>
            </div>
            <div className={classes['column-label2']}>
              <Typography variant='h6'>{`Total PR Amount: ${CurrencyFormatter(details.total_pr_amount)}`}</Typography>
            </div>
          </div>
          {/* row-2 */}
          <div className={classes['search-header-child']}>
            <div className={classes['column-label']}>
              <Typography variant='h6'>Medical Partner Name:</Typography>
            </div>
            <div className={classes['column-input']}>
              <div className={classes['input']}>
                <AutoComplete
                  placeholder='Search by medical partner name'
                  id='medical-partner-name'
                  value={claims.search.medical_partner_name}
                  suggestions={suggestions.medicalPartner.name}
                  onChange={(value, isSuggestionClick) =>
                    handleOnChangeFacility({
                      field: 'medical_partner_name',
                      value,
                      isSuggestionClick
                    })
                  }
                />
              </div>
            </div>
            <div className={classes['column-label2']}>
              <Typography variant='h6'>{`Discount Amount: ${CurrencyFormatter(details.discount_amount)}`}</Typography>
            </div>
          </div>
          {/* row-3 */}
          <div className={classes['search-header-child']}>
            <div className={classes['column-label']}>
              <Typography variant='h6'>Bundle/SOA Number:</Typography>
            </div>
            <div className={classes['column-input']}>
              <div className={classes['input']}>
                <AutoComplete
                  placeholder='Search by bundle/soa number'
                  id='account-number'
                  value={claims.search.bundle}
                  suggestions={suggestions.bundle.id}
                  onChange={(value, isSuggestionClick) =>
                    handleOnChangeBundleSoa({
                      field: 'bundle',
                      value,
                      isSuggestionClick
                    })
                  }
                />
              </div>
            </div>
            <div className={classes['column-label2']}>
              <Typography variant='h6'>{`Due Date: ${
                details.due_date ? DateFormatter(details.due_date) : ''
              }`}</Typography>
            </div>
          </div>
        </div>
        <Typography variant='h6'>Date Range: </Typography>
        <div className={classes.content}>
          <div className={classes.filter}>
            <div className={classes.input}>
              <p>From: </p>
              <DatePicker
                id='filter-date-from'
                value={claims.range.from}
                onChange={date =>
                  setClaims(state => ({
                    ...state,
                    range: {
                      ...state.range,
                      from: date.toISOString()
                    }
                  }))
                }
                disableFuture
                error={dateRangeError.date_from}
              />
            </div>
            <div className={classes.input}>
              <p>To: </p>
              <DatePicker
                id='filter-date-to'
                value={claims.range.to}
                onChange={date =>
                  setClaims(state => ({
                    ...state,
                    range: {
                      ...state.range,
                      to: date.toISOString()
                    }
                  }))
                }
                disableFuture
                error={dateRangeError.date_to}
              />
            </div>
            <Button
              className={`button-positive ${classes.submit}`}
              onClick={() => {
                if (validate()) return
                LoadingService.loading(true)
                // handleOnFilter({ ...claims, page: 1 })
                handleOnFilter({ range: claims.range, search: claims.search })
              }}
              variant='contained'
              color='primary'
            >
              Filter
            </Button>
          </div>
          <div className={classes.action}>
            <Button id='generate-report' onClick={() => handleGenerateReport()} disabled={!claims.data.length}>
              <GetAppIcon fontSize='small' />
              <Typography variant='caption' className={`bold ${!claims.data.length ? classes.disabled : classes.typo}`}>
                Generate Report
              </Typography>
            </Button>
          </div>
        </div>
      </div>
      <div className={classes.body}>
        <div className={classes.table}>
          <Table
            TableContainerProps={{ style: { maxHeight: '60vh', overflow: 'auto', position: 'relative' } }}
            head={
              <TableRow>
                {columns.map((headCell, index) => (
                  <TableCell className={classes[headCell.class]} align={headCell.align} key={`table-header-${index}`}>
                    {headCell.sort ? (
                      <div>
                        <TableSortLabel
                          active={claims.orderBy === headCell.sortField}
                          direction={claims.order ? 'asc' : 'desc'}
                          // onClick={event => {}}
                          onClick={event =>
                            handleColumnFiltering({ orderBy: headCell.sortField, order: !claims.order })
                          }
                        >
                          {headCell.label}
                        </TableSortLabel>
                      </div>
                    ) : (
                      headCell.label
                    )}
                    <TextField
                      id={`text-field-${headCell.sortField}`}
                      readOnly={!headCell.search}
                      TextFieldStyle={{ margin: 0 }}
                      fullWidth
                      onChange={event => {
                        const search = event.target.value
                        clearTimeout(debounce.column)
                        setDebounce(state => ({
                          ...state,
                          column: setTimeout(() => {
                            handleColumnFiltering({
                              filter: {
                                ...claims.filter,
                                [headCell.sortField]: { value: search, match: headCell.match }
                              }
                            })
                            // setClaims(state => ({
                            //   ...state,
                            //   filter: { ...state.filter, [headCell.sortField]: { value: search, match: headCell.match } } }))
                          }, 500)
                        }))
                      }}
                    />
                  </TableCell>
                ))}
              </TableRow>
            }
            body={
              fetching ? (
                <TableRow className={classes['table-row']}>
                  <TableCell align='center' colSpan={6}>
                    <CircularProgress size={30} />
                  </TableCell>
                </TableRow>
              ) : claims.data.length ? (
                claims.data.map((item, index) => (
                  <TableRow className={classes['table-row']} key={index}>
                    <TableCell align='left'>{item.no}</TableCell>
                    <TableCell align='left'>{item.loa_number || 'N/A'}</TableCell>
                    <TableCell align='left'>{item.member_account_number || 'N/A'}</TableCell>
                    <TableCell align='left'>{`${item.member_name || 'N/A'}`}</TableCell>
                    <TableCell align='right'>
                      {CurrencyFormatter(!item.approved_amount <= 0 ? item.approved_amount : 0)}
                    </TableCell>
                    <TableCell align='right'>
                      {CurrencyFormatter(!item.tax_amount <= 0 ? item.tax_amount : 0)}
                    </TableCell>
                  </TableRow>
                ))
              ) : (
                <TableRow>
                  <TableCell className={classes.handling} colSpan={columns.length}>
                    No data found
                  </TableCell>
                </TableRow>
              )
            }
            pagination
            count={claims.count}
            page={claims.page}
            limit={claims.limit}
            rowsPerPageOptions={claims.rowsPerPageOptions}
            onChange={args => handleOnPaginate(args)}
          />
          <Footer
            total={{ total_approved_amount: details.total_approved_amount, total_tax_amount: details.total_tax_amount }}
            bundles={claims.bundles}
            page={claims.page}
          />
        </div>
      </div>
    </Paper>
  )
}
