import React from 'react'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'

import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'

// Components
import DashboardTile from 'SharedComponents/DashboardTileComponent/DashboardTileComponent'
import ViewList from './../ViewList2/ViewList'
import LoadingIndicator from 'SharedComponents/LoadingIndicator/LoadingIndicator'

// Utilities
import { DateFormatter } from 'Utilities/TextFormatter'

const columns = [
  { name: 'Claim Number', sortField: 'claim_number', match: 'start' },
  { name: 'Due Date', sortField: 'date_due', match: 'date' },
  { name: 'Medical Partner Name (Facility)', sortField: 'facility.name', match: 'any' }
]

const EscalatedClaimsTile = props => {
  const { services, sizes = { sm: 12 } } = props
  const [claims, setClaims] = React.useState({
    for_review: { count: 0, data: [] }
  })

  const [fetching, setFetching] = React.useState(false)
  const [fetchingViewList, setFetchingViewList] = React.useState(false)

  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    data: [],
    orderBy: 'date_due',
    order: true
  }

  const [viewList, setViewList] = React.useState(initialValue)

  React.useEffect(() => {
    setFetching(true)
    Promise.all([
      services.getClaims({
        category: 'for-review',
        limit: 10,
        sort: 'date_due|desc',
        range: 'this-week'
      }),
      services.getClaimCount({ category: 'for-review' })
    ])
      .then(values => {
        const [claimResponse, claimCountResponse] = values
        const allClaims = claimResponse.data.data.results
        const allClaimsCount = claimCountResponse.data.data
        setClaims(prevState => ({ ...prevState, for_review: { data: allClaims, count: allClaimsCount } }))
      })
      .finally(values => {
        setFetching(false)
      })
  }, [])

  const handleFilterChange = category => value => {
    setFetching(true)

    services
      .getClaims({ category, range: value, limit: 10, sort: 'date_due|desc' })
      .then(({ data: res }) => {
        const { data } = res

        setClaims(prevState => ({ ...prevState, for_review: { ...prevState.for_review, data: data.results } }))
      })
      .finally(values => {
        setFetching(false)
      })
  }

  const handleOnShowViewList = category => () => {
    let title = 'Escalated Claims'

    setViewList({ ...initialValue, show: true, title, category })
    setFetchingViewList(true)
    services
      .getClaims({ category, limit: 10, sort: 'date_due|desc' })
      .then(({ data: res }) => {
        const { results, count, limit, page } = res.data
        setViewList(state => ({ ...state, data: results, count, limit, page }))
      })
      .finally(values => {
        setFetchingViewList(false)
      })
  }

  const handleOnViewListChange = params => {
    const _params = {
      category: viewList.category,
      limit: viewList.limit,
      page: viewList.page,
      search: viewList.search,
      sort: `${viewList.orderBy}|${viewList.order ? 'desc' : 'asc'}`,
      ...params
    }

    setFetchingViewList(true)
    services
      .getClaims(_params)
      .then(({ data: res }) => {
        const { results, count, limit, page } = res.data
        setViewList(state => ({ ...state, data: results, count, limit, page }))
      })
      .finally(values => {
        setFetchingViewList(false)
      })
  }

  return (
    <React.Fragment>
      <DashboardTile
        id='escalated-claims'
        count={{ color: 3, value: claims.for_review.count, id: 'escalated-count' }}
        sizes={sizes}
        title='Escalated Claims'
        viewList={
          <Typography
            id='ec-view-list'
            variant='caption'
            className='bold text-link'
            onClick={handleOnShowViewList('for-review')}
          >
            View List
          </Typography>
        }
        onFilterChange={handleFilterChange('for-review')}
      >
        <LoadingIndicator loading={fetching}>
          <Table stickyHeader size='small'>
            <TableHead>
              <TableRow>
                <TableCell id='ec-claim-no'>Claim No.</TableCell>
                <TableCell id='ec-due-date'>Due Date</TableCell>
                <TableCell id='ec-facility-name'>Facility Name</TableCell>
                <TableCell />
              </TableRow>
            </TableHead>
            <TableBody>
              {claims.for_review.data.map((item, index) => (
                <TableRow key={item.claim_number}>
                  <TableCell id={`ec-claim-no-${index}`}>{item.claim_number}</TableCell>
                  <TableCell id={`ec-due-date-${index}`}>{DateFormatter(item.date_due)}</TableCell>
                  <TableCell id={`ec-facility-name-${index}`}>{item.facility.name}</TableCell>
                  <TableCell id={`ec-update-claim-${index}`}>
                    <Link to={`/claims/review/${item.claim_number}`}>
                      <Typography id={`review-claim-${index}`} variant='caption' className={`text-link underline`}>
                        Review
                      </Typography>
                    </Link>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </LoadingIndicator>
      </DashboardTile>

      <ViewList
        id='escalated-claims'
        columns={columns}
        searchTextPlaceHolder='Search for Claim Number, Due Date or Facility Name'
        viewList={viewList}
        setViewList={setViewList}
        onViewListChange={handleOnViewListChange}
        fetching={fetchingViewList}
        body={_data =>
          _data.map((item, index) => (
            <TableRow key={item.claim_number}>
              <TableCell id={`claim-number-${index}`}>{item.claim_number}</TableCell>
              <TableCell id={`due-date-${index}`}>{DateFormatter(item.date_due)}</TableCell>
              <TableCell id={`facility-name=${index}`}>{item.facility.name}</TableCell>
              <TableCell id={`review-cell-${index}`}>
                <Link to={`/claims/review/${item.claim_number}`}>
                  <Typography id={`review-claim-${index}`} variant='caption' className={`text-link underline`}>
                    Review
                  </Typography>
                </Link>
              </TableCell>
            </TableRow>
          ))
        }
      />
    </React.Fragment>
  )
}

EscalatedClaimsTile.displayName = 'DashboardTileComponent'
export default EscalatedClaimsTile
