import React from 'react'
import SearchIcon from '@material-ui/icons/Search'
import Button from '@material-ui/core/Button'

// Components
import TextField from 'SharedComponents/TextField/CustomTextField'
import DropDown from 'SharedComponents/DropDown/DropDown'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import ClientMemberModal from 'SharedComponents/ClientMemberLookup/ClientMemberModal'
import LoadingIndicator from 'SharedComponents/LoadingIndicator/LoadingIndicator'

// Services
import CustomerCareService from 'Services/Api/Claims/CustomerCareService'
import { AutoFill, AutoFillInitializer } from '../AutoFill'
import CustomerCareServices from 'Services/Api/Claims/CustomerCareServices'

// Store
import { useSelector, useDispatch } from 'react-redux'
import { ClaimsEncoding as actions } from 'Pages/Protected/Claims/Store/actions'

// Styles
import classes from './BaseDetails.module.scss'

// Constants
import ClaimTypes from 'Pages/Protected/Claims/Constant/ClaimTypes'

// Libraries

const { setClaimBaseDetails, resetSuggestion, setSuggestion } = actions

export default function BaseDetails (props) {
  const defaultFunction = () => {}
  // Events
  const {
    onChange = defaultFunction,
    onChangeReimbursementType = defaultFunction,
    handleMemberAccountNumber = defaultFunction,
    onLoaPull = defaultFunction
  } = props
  // Services
  const { getRoomsList = defaultFunction } = props
  // Varibles
  const {
    claim,
    errors,
    disableControls,
    showReimbursement = false,
    showCompany = false,
    // showContractRange = false,
    showClaimSource = false,
    showApprovalCode = true,
    reqApprovalCode = false,
    showLoaNumber = true,
    reqLoaNumber = false,
    isDental = false,
    showMemberAccountNumber = true,
    isCM = false,
    contractRange = false
  } = props

  const dispatch = useDispatch()

  const claim_type = useSelector(({ ClaimsEncoding: state }) => state.claimType)
  const [memberAccountNumber, setMemberAccountNumber] = React.useState()

  const [contractPlanTypeLoading, setContractPlanTypeLoading] = React.useState(false)

  const [memberModal, setMemberModal] = React.useState(false)
  const [clientModal, setClientModal] = React.useState(false)

  const handleOnClickMemberLookup = () => {
    setMemberModal(true)
  }

  const handleOnClickClientLookup = () => {
    setClientModal(true)
  }

  const handleLoaDetails = event => {
    const value = event.target.value.trim()
    // if has "/" skip else proceed
    if (value.includes('/')) return

    if (value) {
      LoadingService.loading(true)
      CustomerCareService.getLoaDetails({ approval_code: encodeURIComponent(value) })
        .then(res => {
          const loa = res.data

          if (loa) {
            try {
              AutoFill({
                loa,
                claim_type,
                claim,
                services: { getRoomsList }
              })
              onLoaPull()
            } catch (error) {
              console.log(error)
              LoadingService.loading(false)
            }
          }

          LoadingService.loading(false)
        })
        .finally(() => {
          LoadingService.loading(false)
        })
    }
  }

  const handleSetMember = member => {
    const { member_id, first_name, middle_name, last_name } = member
    onChange({
      field: 'member_account_number',
      value: member_id
    })
    onChange({
      field: 'member_first_name',
      value: first_name
    })
    onChange({ field: 'member_last_name', value: last_name })
    onChange({ field: 'member_middle_name', value: middle_name })

    if (contractRange) handleFetchContracts(member_id)

    setContractPlanTypeLoading(true)
    CustomerCareServices.getMemberContractPlanType({ account_number: member_id })
      .then(res => {
        const { data } = res.data
        dispatch(setClaimBaseDetails({ field: 'contract_plan_type', value: data.contract_plan_type }))
      })
      .catch(err => {
        console.log(err.response)
        dispatch(setClaimBaseDetails({ field: 'contract_plan_type', value: '' }))
      })
      .finally(res => {
        setContractPlanTypeLoading(false)
      })
  }

  const handleSetClient = client => {
    const { corporate_account_no, registered_name } = client
    onChange({ field: 'client_account_number', value: corporate_account_no })
    onChange({ field: 'client_account_name', value: registered_name })

    setContractPlanTypeLoading(true)
    CustomerCareServices.getClientrContractPlanType({ account_number: corporate_account_no })
      .then(res => {
        const { data } = res.data
        dispatch(setClaimBaseDetails({ field: 'contract_plan_type', value: data.contract_plan_type }))
      })
      .catch(err => {
        console.log(err.response)
        dispatch(setClaimBaseDetails({ field: 'contract_plan_type', value: '' }))
      })
      .finally(res => {
        setContractPlanTypeLoading(false)
      })
  }

  const handleFetchContracts = accountNumber => {
    if (!handleMemberAccountNumber) return

    if (accountNumber === memberAccountNumber) return

    dispatch(resetSuggestion({ field: 'contract' }))
    setMemberAccountNumber(accountNumber)
    dispatch(setClaimBaseDetails({ field: 'contract_year_from', value: null }))
    dispatch(setClaimBaseDetails({ field: 'contract_year_to', value: null }))

    if (!accountNumber) return

    LoadingService.loading(true)
    CustomerCareService.getContractList({ member_account_number: accountNumber }).then(({ data: res }) => {
      LoadingService.loading(false)

      if (!res) return

      const { latest_contract } = res
      const { coverage_start_date: from, coverage_end_date: to } = latest_contract.custom_metadata

      dispatch(
        setSuggestion({
          field: 'contract',
          suggestion: [{ from, to }]
          // suggestion: { years: [{ from, to }] }
        })
      )
    })
  }

  const handleQuickFetchMember = event => {
    const value = event.target.value.trim()

    //* escape "/"(slash) to "%2F" #8384
    const escape = string => {
      if (string.includes('/')) return string.replace(/\//g, '%2F')
      return string
    }
    if (!value) return

    LoadingService.loading(true)
    CustomerCareServices.getMembers({
      where: { member_id: { value: escape(value), match: 'exact' } },
      // order: 'member_id asc',
      fields: { member_id: true, last_name: true, first_name: true, middle_name: true },
      limit: 1
    })
      .then(res => {
        const { data } = res.data
        const member = data['members'].pop() || {}
        handleSetMember(member)
      })
      .finally(() => {
        LoadingService.loading(false)
      })
  }

  const handleQuickFetchClient = event => {
    const value = event.target.value.trim()

    //* escape "/"(slash) to "%2F" #8384
    const escape = string => {
      if (string.includes('/')) return string.replace(/\//g, '%2F')
      return string
    }
    if (!value) return

    LoadingService.loading(true)
    CustomerCareServices.getClients({
      where: { corporate_account_no: { value: escape(value), match: 'exact' } },
      // order: 'member_id asc',
      fields: { corporate_account_no: true, registered_name: true },
      limit: 1
    })
      .then(res => {
        const { data } = res.data
        const client = data['clients'].pop() || {}
        handleSetClient(client)
      })
      .finally(() => {
        LoadingService.loading(false)
      })
  }

  return (
    <div id={'claim-base-details'} className={classes.root}>
      {!isCM && (
        <div className={classes.wrapper}>
          <div className={classes.groups}>
            {showApprovalCode && (
              <div className={classes.input}>
                <TextField
                  id='approval-code'
                  label='Approval Code'
                  required={reqApprovalCode}
                  value={claim.approval_code}
                  disabled={disableControls}
                  onChange={event => onChange({ field: 'approval_code', value: event.target.value })}
                  onBlur={handleLoaDetails}
                  error={errors.approval_code}
                />
              </div>
            )}

            {showLoaNumber && (
              <div className={classes.input}>
                <TextField
                  id='loa-number'
                  label='LOA No.'
                  required={reqLoaNumber}
                  value={claim.loa_number}
                  disabled={disableControls}
                  onChange={event => onChange({ field: 'loa_number', value: event.target.value })}
                  error={errors.loa_number}
                />
              </div>
            )}

            <div className={classes.input}>
              <LoadingIndicator loading={contractPlanTypeLoading}>
                <TextField
                  id='contract-plan-type'
                  label='Contract Plan Type'
                  value={claim.contract_plan_type}
                  readOnly
                />
              </LoadingIndicator>
            </div>

            {showClaimSource && (
              <div className={classes.input}>
                <TextField
                  id='claim-source'
                  label='Claim Source'
                  required
                  value={claim.claim_source}
                  disabled={disableControls}
                  onChange={event => onChange({ field: 'claim_source', value: event.target.value })}
                  error={errors.claim_source}
                />
              </div>
            )}

            {showReimbursement && (
              <div className={classes.input}>
                <DropDown
                  id='reimbursement-type'
                  label='Reimbursement Type'
                  items={ClaimTypes.Reimbursement.subType}
                  value={claim.reimbursement_type}
                  disabled={disableControls}
                  required
                  onChange={event =>
                    onChangeReimbursementType({ field: 'reimbursement_type', value: event.target.value })
                  }
                  error={errors.reimbursement_type}
                />
              </div>
            )}
          </div>

          {showCompany && (
            <div className={classes.groups}>
              <div className={classes.input}>
                <TextField
                  id='company-account-number'
                  label='Customer Account Number'
                  value={claim.client_account_number}
                  disabled={disableControls}
                  required
                  onChange={event => onChange({ field: 'client_account_number', value: event.target.value })}
                  onBlur={handleQuickFetchClient}
                  error={errors.client_account_number}
                />
                <div className={classes.search}>
                  <Button
                    id='client-account-number-search'
                    variant='outlined'
                    className={`${classes['box-search-button']} ${classes['no-pad']}`}
                    onClick={handleOnClickClientLookup}
                  >
                    <SearchIcon />
                  </Button>
                </div>
              </div>
              <div className={classes.input}>
                <TextField
                  id='company-name'
                  label='Company Name'
                  required
                  value={claim.client_account_name}
                  disabled={disableControls}
                  onChange={event => onChange({ field: 'client_account_name', value: event.target.value })}
                  error={errors.client_account_name}
                />
              </div>
            </div>
          )}

          <div className={classes.groups}>
            {showMemberAccountNumber && (
              <div className={classes.input}>
                <TextField
                  id='member-account-number'
                  required
                  label='Member Account No.'
                  value={claim.member_account_number}
                  disabled={disableControls}
                  onChange={event =>
                    onChange({
                      field: 'member_account_number',
                      value: event.target.value
                    })
                  }
                  onBlur={handleQuickFetchMember}
                  error={errors.member_account_number}
                />
                <div className={classes.search}>
                  <Button
                    id='member-account-number-search'
                    variant='outlined'
                    className={`${classes['box-search-button']} ${classes['no-pad']}`}
                    onClick={handleOnClickMemberLookup}
                  >
                    <SearchIcon />
                  </Button>
                </div>
              </div>
            )}

            <div className={classes.input}>
              <TextField
                id='member-last-name'
                required
                label='Member Last Name'
                value={claim.member_last_name}
                disabled={disableControls}
                onChange={event => onChange({ field: 'member_last_name', value: event.target.value })}
                error={errors.member_last_name}
              />
            </div>

            <div className={classes.input}>
              <TextField
                id='member-first-name'
                required
                label='Member First Name'
                value={claim.member_first_name}
                disabled={disableControls}
                onChange={event =>
                  onChange({
                    field: 'member_first_name',
                    value: event.target.value
                  })
                }
                error={errors.member_first_name}
              />
            </div>

            <div className={classes.input}>
              <TextField
                id='member-middle-name'
                label='Member Middle Name'
                required={!!isDental}
                value={claim.member_middle_name}
                disabled={disableControls}
                onChange={event =>
                  onChange({
                    field: 'member_middle_name',
                    value: event.target.value
                  })
                }
                error={errors.member_middle_name}
              />
            </div>
          </div>
        </div>
      )}

      {isCM && (
        <div className={classes.wrapper}>
          <div className={classes.groups}>
            <div className={classes.input}>
              <TextField
                id='client-account-number'
                label='Client Account Number'
                value={claim.client_account_number}
                disabled={disableControls}
                required
                onChange={event =>
                  dispatch(
                    setClaimBaseDetails({
                      value: event.target.value,
                      field: 'client_account_number'
                    })
                  )
                }
                onBlur={handleQuickFetchClient}
                error={errors.client_account_number}
              />
              <div className={classes.search}>
                <Button
                  id='client-account-number-search'
                  variant='outlined'
                  className={`${classes['box-search-button']} ${classes['no-pad']}`}
                  onClick={handleOnClickClientLookup}
                >
                  <SearchIcon />
                </Button>
              </div>
            </div>
            <div className={classes.input}>
              <TextField
                id='client-account-name'
                label='Client Account Name'
                value={claim.client_account_name}
                disabled={disableControls}
                required
                onChange={event =>
                  dispatch(
                    setClaimBaseDetails({
                      value: event.target.value,
                      field: 'client_account_name'
                    })
                  )
                }
                error={errors.client_account_name}
              />
            </div>
            <div className={classes.input}>
              <LoadingIndicator loading={contractPlanTypeLoading}>
                <TextField
                  id='contract-plan-type'
                  label='Contract Plan Type'
                  value={claim.contract_plan_type}
                  disabled
                />
              </LoadingIndicator>
            </div>
          </div>
        </div>
      )}

      <AutoFillInitializer />

      <ClientMemberModal
        show={memberModal}
        mode={'members'}
        title={'Member Search'}
        allSearchText={'Member Account No., Account Name, Member Name'}
        allSearch={claim.member_account_number}
        columns={[
          { id: 'member_id', label: 'Account Number', value: data => data.member_id },
          { id: 'last_name', label: 'Last Name', value: data => data.last_name },
          { id: 'first_name', label: 'First Name', value: data => data.first_name },
          { id: 'middle_name', label: 'Middle Name', value: data => data.middle_name },
          { id: 'registered_name', label: 'Account Name', value: data => data.registered_name },
          {
            id: 'contract',
            label: 'Contract Period',
            value: data => data.contract,
            match: 'like'
          }
        ]}
        allSearchFields={[
          { id: 'member_id', match: 'like' },
          { id: 'last_name', match: 'like' },
          { id: 'first_name', match: 'like' },
          { id: 'middle_name', match: 'like' },
          { id: 'registered_name', match: 'like' }
        ]}
        service={CustomerCareServices.getMembers}
        onRecordSet={member => {
          if (!member) return
          setMemberModal(false)
          handleSetMember(member)
        }}
        onClose={() => setMemberModal(false)}
      />

      <ClientMemberModal
        show={clientModal}
        mode={'clients'}
        title={'Account Search'}
        allSearchText={'Client Account No., Account Name'}
        allSearch={claim.client_account_number}
        columns={[
          { id: 'corporate_account_no', label: 'Client Account Number', value: data => data.corporate_account_no },
          { id: 'registered_name', label: 'Client Account Name', value: data => data.registered_name },
          {
            id: 'contract',
            label: 'Contract Period',
            value: data => data.contract,
            match: 'like'
          }
        ]}
        allSearchFields={[
          { id: 'corporate_account_no', match: 'like' },
          { id: 'registered_name', match: 'like' }
        ]}
        service={CustomerCareServices.getClients}
        onRecordSet={client => {
          if (!client) return
          setClientModal(false)
          handleSetClient(client)
        }}
        onClose={() => setClientModal(false)}
      />
    </div>
  )
}
