import React from 'react'

// Material Components
import Button from '@material-ui/core/Button'
import Paper from '@material-ui/core/Paper'
import Table from 'SharedComponents/Table/Table'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TableSortLabel from '@material-ui/core/TableSortLabel'
import Typography from '@material-ui/core/Typography'
import GetAppIcon from '@material-ui/icons/GetApp'

// Shared Components
import DatePicker from 'SharedComponents/DatePicker2/DatePicker2'
import TextField from 'SharedComponents/TextField/CustomTextField'
import AutoComplete from 'SharedComponents/AutoComplete/AutoComplete'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import ClaimsServices from 'Services/Api/Claims/ClaimsServices'
import ArrayToCSV from 'Services/ArrayToCSV'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'

// Utility
import { DateFormatter, CurrencyFormatter } from 'Utilities/TextFormatter'
// import { SafeAccess } from 'Utilities/SafeAccess'

// Styles
import classes from './ClaimsBeyond3MonthsReport.module.scss'

// Validator
import DateRangeValidator from '../TransmittalReport/validator/DateRangeValidator'

const completeColumns = [
  { label: 'No', data: 'no' },
  { label: 'Claim Number', data: 'claim_number', search: true, sort: true, match: 'start' },
  { label: 'Date Received', data: 'received_date', type: 'date', search: true, match: 'date', sort: true },
  { label: 'Medical Partner ID', data: 'facility_id', search: true, sort: true, match: 'any' },
  { label: 'Medical Partner Name', data: 'facility_name', search: true, sort: true, match: 'any' },
  { label: 'Date of Service', data: 'date_of_service', type: 'date', search: true, sort: true, match: 'date' },
  { label: 'Claim Amount', data: 'claim_amount', sort: false, type: 'currency' },
  { label: 'Claim Type', data: 'claim_type', search: true, sort: true, match: 'any' },
  { label: 'Claim Source', data: 'claim_source', search: true, sort: true, match: 'any' },
  { label: 'Claim Status', data: 'claim_status', search: true, sort: true, match: 'any' }
]

/**
 * Claims Utilization Report
 *
 * @export
 *
 * @param {*} props
 *   dataColumns - default []; if defined, will be the allowable data columns that
 *     will be shown on the claims utilization report
 *   hideReport - default false; if defined, will be used to hide the entire report
 *   hideheader - default false; if defined, will be used to hide the header section
 *   dateRange - default current date; if defined, will be used to define the date range in-effect
 *
 * @returns
 */
export default props => {
  // const { dataColumns = [], hideReport, hideHeader, dateRange } = props
  // separated date range to avoid fetch for non-filtering func
  const [dateRange, setDateRange] = React.useState({
    from: new Date().toISOString(),
    to: new Date().toISOString(),
    field: 'latest.date.received'
  })

  const [claims, setClaims] = React.useState({
    data: [],
    range: { from: new Date().toISOString(), to: new Date().toISOString(), field: 'latest.date.received' },
    // range: {
    //   from: SafeAccess(dateRange, 'from', new Date().toISOString()),
    //   to: SafeAccess(dateRange, 'to', new Date().toISOString()),
    //   field: 'status_date'
    // },
    filter: {
      claim_number: '',
      date_received: '',
      facility_id: '',
      facility_name: '',
      date_of_service: '',
      claim_type: '',
      claim_source: '',
      claim_status: ''
    },
    grandTotal: 0,
    orderBy: 'claim_number',
    order: true,
    page: 1,
    limit: 10,
    count: 0,
    rowsPerPageOptions: [10, 15, 20]
  })

  const [dateRangeError, setDateRangeError] = React.useState({
    date_from: '',
    date_to: ''
  })
  const [debounce, setDebounce] = React.useState(null)

  const [columns, setColumns] = React.useState(completeColumns)

  React.useEffect(() => {
    PageTitleService('Claims Beyond 3 Months Report')
    BreadcrumbsService.setSelectedItems(['claimsBeyond3Months'])

    // autofilter today
    handleOnClickFilter({ range: claims.range })
  }, [])

  const handleOnClickFilter = ({ range }) => {
    if (validate()) return
    LoadingService.loading(true)

    // setRange to use in pagination, filtering
    setDateRange(state => ({ ...state, ...range }))
    fetchRecords({ ...claims, ...range })
  }

  // const handleOnFilter = args => {
  //   const { field, value, match } = args
  //   setClaims(state => ({ ...state, filter: { ...state.filter, [field]: { value, match } } }))
  // }

  const handleOnPaginate = params => {
    const _params = {
      ...claims,
      ...params
    }

    setClaims(state => ({ ...state, ...params }))
    fetchRecords({ ..._params, range: dateRange })
  }

  const fetchRecords = params => {
    LoadingService.loading(true)
    ClaimsServices.claimsBeyond3Months({
      range: params.range,
      filter: params.filter,
      sort: `${params.orderBy}|${params.order ? 'asc' : 'desc'}`,
      page: params.page,
      limit: params.limit
    }).then(res => {
      const { data } = res.data
      const { results = [], grand_total = 0, count, limit, page } = data
      setClaims(state => ({
        ...state,
        data: results,
        count,
        limit,
        page,
        grandTotal: grand_total
      }))
      LoadingService.loading(false)
    })
  }

  const validate = () => {
    const { error } = DateRangeValidator({
      date_from: claims.range.from,
      date_to: claims.range.to
    })

    setDateRangeError({ ...error })
    return !!error
  }

  const fetchCSV = () => {
    LoadingService.loading(true)
    ClaimsServices.claimsBeyond3Months({
      range: claims.range,
      sort: `${claims.orderBy}|${claims.order ? 'asc' : 'desc'}`
    }).then(res => {
      const { data } = res.data
      const { results = [], grand_total = 0 } = data
      const rows = results.map(claim => {
        return columns.map(col => formatData({ type: col.type, source: claim, field: col.data }))
      })

      const header = columns.map(c => c.label)

      ArrayToCSV({
        statics: [
          [
            'Claims Beyond 3 Months',
            '\r\n',
            '\r\n',
            `Date Range: ${DateFormatter(claims.range.from)} - ${DateFormatter(claims.range.to)}`,
            '\r\n',
            '\r\n'
          ],
          ['', '', '', '', '', `Grand Total`, `${grand_total}`].join(',')
        ],
        fileName: 'Claims Beyond 3 Months',
        header,
        rows
      })
      LoadingService.loading(false)
    })
  }

  // React.useEffect(() => {
  //   fetchRecords()
  //   // claim.range is triggered manually via Filter button
  // }, [props.refresh])

  React.useEffect(() => {
    setClaims(state => ({
      ...state,
      range: {
        ...state.range,
        to: dateRange ? dateRange.to : new Date().toISOString(),
        from: dateRange ? dateRange.from : new Date().toISOString()
      }
    }))
  }, [dateRange])

  // React.useEffect(() => {
  //   // Goal: Adjust columns to show based on data columns passed as props

  //   let filtered = null
  //   if (!dataColumns || dataColumns.length === 0) {
  //     filtered = completeColumns
  //   } else {
  //     filtered = completeColumns.filter(entry => dataColumns.indexOf(entry.data) > -1)
  //   }
  //   setColumns(filtered)
  // }, [dataColumns])

  const formatData = ({ type = null, source, field }) => {
    let output = ''
    if (type === 'date') {
      output = DateFormatter(source[field])
    } else if (type === 'array') {
      const [root, property] = field.split('.')
      output = source[root] ? source[root].map(f => f[property]).join(`${'\r\n'}${'\r\n'}`) : ''
    } else if (type === 'currency') {
      output = CurrencyFormatter(source[field])
    } else if (type === 'number') {
      output = source[field]
    } else {
      output = source[field] ? source[field] : ''
    }

    return output !== '' && output !== 'undefined' && output !== `${'\r\n'}${'\r\n'}` ? output : 'N/A'
  }

  return (
    // <Paper className={classes.root} style={{ display: hideReport === 'true' ? 'none' : 'block' }}>
    //   <div className={classes.header} style={{ display: hideHeader === 'true' ? 'none' : 'block' }}>
    <Paper className={classes.root}>
      <div className={classes.header}>
        <Typography variant='h6'>Date Range</Typography>
        <div className={classes.content}>
          <div className={classes.filter}>
            <div className={classes.input}>
              <p>From: </p>
              <DatePicker
                id='filter-date-from'
                value={claims.range.from}
                onChange={date =>
                  setClaims(state => ({
                    ...state,
                    range: {
                      ...state.range,
                      from: date.toISOString()
                    }
                  }))
                }
                disableFuture
                error={dateRangeError.date_from}
              />
            </div>
            <div className={classes.input}>
              <p>To: </p>
              <DatePicker
                id='filter-date-to'
                value={claims.range.to}
                onChange={date =>
                  setClaims(state => ({
                    ...state,
                    range: {
                      ...state.range,
                      to: date.toISOString()
                    }
                  }))
                }
                disableFuture
                error={dateRangeError.date_to}
              />
            </div>
            <Button
              className={`button-positive ${classes.submit}`}
              onClick={() => handleOnClickFilter({ range: claims.range })}
              variant='contained'
              color='primary'
            >
              Filter
            </Button>
          </div>
          <div className={classes.action}>
            <Button id='generate-report' onClick={() => fetchCSV()} disabled={!claims.data.length}>
              <GetAppIcon fontSize='small' />
              <Typography variant='caption' className={`bold ${!claims.data.length ? classes.disabled : classes.typo}`}>
                Generate Report
              </Typography>
            </Button>
          </div>
        </div>
      </div>
      <div className={classes.body}>
        <div className={classes.table}>
          <Table
            TableContainerProps={{ style: { maxHeight: '60vh', overflow: 'auto', position: 'relative' } }}
            TableProps={{ stickyHeader: true }}
            head={
              <TableRow>
                {columns.map((headCell, index) => (
                  <TableCell key={`table-header-${index}`}>
                    {headCell.sort ? (
                      <div>
                        <TableSortLabel
                          active={claims.orderBy === headCell.sortField}
                          direction={claims.order ? 'asc' : 'desc'}
                          onClick={event => handleOnPaginate({ orderBy: headCell.data, order: !claims.order })}
                        >
                          {headCell.label}
                        </TableSortLabel>
                      </div>
                    ) : (
                      headCell.label
                    )}
                    {/*  */}
                    <div className={classes.input}>
                      {headCell.type && headCell.type === 'date' ? (
                        <DatePicker
                          noMargin
                          id={`${headCell.data}`}
                          value={null}
                          onChange={date => {
                            clearTimeout(debounce)
                            setDebounce(
                              setTimeout(() => {
                                handleOnPaginate({
                                  filter: {
                                    ...claims.filter,
                                    [headCell.data]: { value: date.toISOString(), match: headCell.match }
                                  },
                                  page: 1
                                })
                              }, 500)
                            )
                          }}
                        />
                      ) : (
                        <TextField
                          readOnly={!headCell.search}
                          noMargin
                          onChange={event => {
                            const search = event.target.value
                            clearTimeout(debounce)
                            setDebounce(
                              setTimeout(() => {
                                handleOnPaginate({
                                  filter: {
                                    ...claims.filter,
                                    [headCell.data]: { value: search, match: headCell.match }
                                  },
                                  page: 1
                                })
                              }, 500)
                            )
                          }}
                        />
                      )}
                    </div>
                  </TableCell>
                ))}
              </TableRow>
            }
            body={
              claims.data.length ? (
                claims.data.map((claim, claimIndex) => (
                  <TableRow className={classes['table-row']} key={`table-item-${claimIndex}`}>
                    {columns.map((col, colIndex) => (
                      <TableCell
                        className={col.remarks ? classes.remarks : classes['table-cell']}
                        key={`table-cell-${claimIndex}-${colIndex}`}
                      >
                        {formatData({ type: col.type, source: claim, field: col.data })}
                      </TableCell>
                    ))}
                  </TableRow>
                ))
              ) : (
                <TableRow>
                  <TableCell className={classes.handling} colSpan={columns.length}>
                    No data found
                  </TableCell>
                </TableRow>
              )
            }
            pagination
            count={claims.count}
            page={claims.page}
            limit={claims.limit}
            rowsPerPageOptions={claims.rowsPerPageOptions}
            onChange={args => handleOnPaginate(args)}
            footerComponent={
              <div className={classes.footer}>
                <Typography variant='caption' className='bold'>
                  <span>Grand Total: </span>
                  <span>{CurrencyFormatter(claims.grandTotal)}</span>
                </Typography>
              </div>
            }
          />
        </div>
      </div>
    </Paper>
  )
}
