import instance from './instance'
const axios = instance('/p/f')

export default class FacilitiesServices {
  static getFacilities () {
    return axios({
      url: '/partners/facilities',
      method: 'GET'
    })
  }

  static getFacilityByName ({ name }) {
    return axios({
      url: `/partners/facilities/${name}`,
      method: 'GET'
    })
  }

  static getFacilityById ({ id }) {
    return axios({
      url: `/partners/facilities/id/${id}`,
      method: 'GET'
    })
  }
}
