import React from 'react'

import CustomerCareService from 'Services/Api/Claims/CustomerCareServices'

// Component
import { MozillaPdfURL } from 'SharedComponents/PdfViewer2/PdfViewer2'

// Utilities
import { NameFormatter } from 'Utilities/TextFormatter'

// Styles
import classes from './Contract.module.scss'

export default function Contract (props) {
  // Service
  // const { getMemberContract, getClientContract } = props
  // const { getMemberContractUrl, getClientContractUrl } = props
  // Variables
  const { isMember, member, client } = props

  const [fileName, setFileName] = React.useState('')
  // const [title, setTitle] = React.useState('')
  const [service, setService] = React.useState(null)

  // React.useEffect(() => {
  //   if (isMember) {
  //     if (!member) return
  //     const mct_id = member.benefit_tree.MCT.details._id
  //     setService(getMemberContract({ mct_id }))
  //     setFileName(`${NameFormatter(member.info).format2} Contract`)
  //     setTitle('Member Contract')
  //   } else {
  //     if (!client) return
  //     const mct_id = client.benefit_tree.MCT.details._id
  //     setService(getClientContract({ mct_id }))
  //     setFileName(`${client.info.brand_name} Contract`)
  //     setTitle('Client Contract')
  //   }
  // }, [isMember, member, client])

  React.useEffect(() => {
    if (isMember) {
      if (!member) return
      const mct_id = member.latest_contract._id
      setService(CustomerCareService.getMemberContractUrl({ mct_id }))
      setFileName(`${NameFormatter(member.info).format2} Contract`)
    } else {
      if (!client) return
      const mct_id = client.latest_contract._id
      setService(CustomerCareService.getClientContractUrl({ mct_id }))
      setFileName(`${client.info.brand_name} Contract`)
    }
  }, [isMember, member, client])

  return (
    <div className={classes.root}>
      <div className={classes.wrapper}>
        <div className={classes.pdf}>
          <MozillaPdfURL name={fileName} service={service} enabled />
        </div>
        {/* <div className={classes.pdf}>
          <PdfView2Component
            name={title}
            fileName={fileName}
            initialPageNumber={1}
            initialScale={1}
            service={service}
            enabled
          />
        </div> */}
      </div>
    </div>
  )
}
