import React from 'react'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import startCase from 'lodash/startCase'

// Components
import DashboardTile from 'SharedComponents/DashboardTileComponent/DashboardTileComponent'
import ViewList from '../ViewList2/ViewList'
import LoadingIndicator from 'SharedComponents/LoadingIndicator/LoadingIndicator'

const columns = [
  { id: 's-encoder-name', name: ' Encoder Name', sortField: 'username', match: 'any' },
  { id: 's-total-batches', name: ' Total Batches', sortField: 'total_batches', match: 'any' },
  { id: 's-total-rcs', name: ' Total No. of RCS', sortField: 'total_rcs', match: 'any' }
]
const ClaimsEncoderTile = props => {
  const { services, sizes = { sm: 12 } } = props

  const [batches, setBatches] = React.useState({
    count: 0,
    data: []
  })
  const [fetching, setFetching] = React.useState(false)
  const [fetchingViewList, setFetchingViewList] = React.useState(false)

  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    filter: {},
    data: [],
    orderBy: 'name',
    order: true
  }

  const [viewList, setViewList] = React.useState(initialValue)

  React.useEffect(() => {
    getEncoders()
  }, [])

  const getEncoders = () => {
    setFetching(true)
    services
      .getEncodersBatches({ limit: 10, sort: 'name|asc' })
      .then(res => {
        const { results, total_count } = res.data.data
        setBatches(state => ({ ...state, count: total_count, data: results }))
      })
      .catch(err => {
        console.error(err)
        setBatches(state => ({ ...state, count: 0, data: [] }))
      })
      .finally(values => {
        setFetching(false)
      })
  }

  const handleOnShowViewList = category => () => {
    setViewList({ ...initialValue, show: true, title: startCase(`${category}`), category })
    setFetchingViewList(true)
    services
      .getEncodersBatches({ page: 1, limit: 10, sort: 'name|asc' })
      .then(({ data: res }) => {
        const { results, count, limit, page } = res.data
        setViewList(state => ({ ...state, data: results, count, limit, page }))
      })
      .finally(values => {
        setFetchingViewList(false)
      })
  }

  const handleOnViewListChange = params => {
    const _params = {
      limit: viewList.limit,
      page: viewList.page,
      search: viewList.search,
      sort: `${viewList.orderBy}|${viewList.order ? 'desc' : 'asc'}`,
      filter: viewList.filter,
      ...params
    }

    setFetchingViewList(true)
    services
      .getEncodersBatches(_params)
      .then(({ data: res }) => {
        const { results, count, limit, page } = res.data

        setViewList(state => ({ ...state, data: results, count, limit, page }))
      })
      .finally(values => {
        setFetchingViewList(false)
      })
  }

  return (
    <React.Fragment>
      <DashboardTile
        id='claims-encoder'
        count={{ color: 3, value: batches.count, id: 'claims-encoder-count' }}
        isFilterShown={false}
        title='Claims Encoder'
        sizes={sizes}
        viewList={
          <Typography
            id='ob-view-list'
            variant='caption'
            className='bold text-link'
            onClick={handleOnShowViewList('claim-encoder')}
          >
            View List
          </Typography>
        }
        onFilterChange={() => {}}
      >
        <LoadingIndicator loading={fetching}>
          <Table stickyHeader size='small'>
            <TableHead>
              <TableRow>
                {columns.map(item => (
                  <TableCell key={`ces-${item.id}`} id={`ces-${item.id}`}>
                    {item.name}
                  </TableCell>
                ))}

                <TableCell />
              </TableRow>
            </TableHead>
            <TableBody>
              {batches.data.map((item, index) => (
                <TableRow key={index}>
                  <TableCell id={`ces-encoder-name-${index}`}>{item.username}</TableCell>
                  <TableCell id={`ces-total-batches-${index}`}>{item.total_batches}</TableCell>
                  <TableCell id={`ces-total-rcs-${index}`}>{item.total_rcs}</TableCell>
                  <TableCell>
                    <Link to={`/batches/assigned/${item.id}`}>
                      <Typography variant='caption' className='text-link underline' id={`view-encoder-${index}`}>
                        View
                      </Typography>
                    </Link>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </LoadingIndicator>
      </DashboardTile>

      <ViewList
        id='claims-encoder'
        columns={columns}
        searchTextPlaceHolder='Search for Encoder Name, Total Bundles or Total Batches'
        viewList={viewList}
        setViewList={setViewList}
        onViewListChange={handleOnViewListChange}
        fetching={fetchingViewList}
        body={_data =>
          _data.map((item, index) => (
            <TableRow key={item.id}>
              <TableCell id={`batch-number-${index}`}>{item.username}</TableCell>
              <TableCell id={`date-received-${index}`}>{item.total_batches}</TableCell>
              <TableCell id={`due-date-${index}`}>{item.total_rcs}</TableCell>
              <TableCell id={`view-encoder-${index}`}>
                <Link to={`/batches/assigned/${item.id}`}>
                  <Typography variant='caption' className='text-link underline' id={`view-encoder-${index}`}>
                    View
                  </Typography>
                </Link>
              </TableCell>
            </TableRow>
          ))
        }
      />
    </React.Fragment>
  )
}

ClaimsEncoderTile.displayName = 'DashboardTileComponent'

export default ClaimsEncoderTile
