import React, { Fragment } from 'react'
// import Grid from '@material-ui/core/Grid'
// import Paper from '@material-ui/core/Paper'
// import moment from 'moment'
import Button from '@material-ui/core/Button'
// import Typography from '@material-ui/core/Typography'

// Controller
import SoaEtlController from 'Controllers/SoaEtlController'

// Components
import Alert from 'SharedComponents/Alert/Alert'
import UploadSpace from './Components/UploadSpace/UploadSpace'
import DynamicTable from './Components/DynamicTable/DynamicTable'
import FieldMapper from '../../FieldMapper/FieldMapper'
// import CustomTextField from 'SharedComponents/TextField/CustomTextField'
// import DatePicker from 'SharedComponents/DatePicker2/DatePicker2'
// import AutoComplete from 'SharedComponents/AutoComplete/AutoComplete'
// import TextFieldCurrency from 'SharedComponents/TextFieldCurrency/TextFieldCurrency'
// import TextFieldNumber from 'SharedComponents/TextFieldNumber/TextFieldNumber'
// import Alert from 'SharedComponents/Alert/Alert'

// // Services
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import BatchEncodingServices from 'Services/Api/Claims/BatchEncodingServices'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
// Style
import classes from './UploadSoaPage.module.scss'
import claimTypes from 'Constant/claimTypes'

export default function UploadSoaPage (props) {
  const { ApiService } = props
  const [data, setData] = React.useState()
  const [headerInputs, setHeaderInputs] = React.useState({})
  const [claimTypeValues, setClaimTypeValues] = React.useState([])
  const [openAlert, setOpenAlert] = React.useState({})
  const [dataRows, setDataRows] = React.useState([])

  React.useEffect(() => {
    PageTitleService(`Upload SOA File`)
    BreadcrumbsService.setSelectedItems(['dashboard', 'uploadSoa'])
  }, [])

  const handleOnSelectFile = data => setData(data)

  const handleOnRemoveFile = () => setData()

  const handleOnCancelUpload = () => setData()

  const handleProceedUpload = () => {
    if (!claimTypeValues || claimTypeValues.length === 0) {
      setOpenAlert(state => ({
        ...state,
        show: true,
        message:
          'SOA details and mapping of data is required. Please complete first the SOA details and mapping of data to continue.'
      }))
      return
    }

    if (!headerInputs || !headerInputs.facility_id) {
      setOpenAlert(state => ({
        ...state,
        show: true,
        message: 'Please provide Medical Partner ID'
      }))
      return
    }

    if (!headerInputs || !headerInputs.facility_name) {
      setOpenAlert(state => ({
        ...state,
        show: true,
        message: 'Please provide Medical Partner Name'
      }))
      return
    }

    if (!headerInputs || !headerInputs.claim_source) {
      setOpenAlert(state => ({
        ...state,
        show: true,
        message: 'Please provide Claim Source'
      }))
      return
    }

    // Construct data mapping for Transformation
    const claimTypeMap = {}
    claimTypeValues.forEach(element => {
      claimTypeMap[element.source] = element.target
    })

    const dataMapping = {
      columns: SoaEtlController.COLUMN_TEMPLATES.DEFAULT,
      fields: {
        [SoaEtlController.MINIMUM_KEYS.BATCH_CLAIM_TYPE]: claimTypeMap
      }
    }

    // Construct defaults for Transformation
    const defaults = {
      [SoaEtlController.MINIMUM_KEYS.BATCH_CLAIMS_SOURCE]: headerInputs.claim_source,
      [SoaEtlController.MINIMUM_KEYS.BUNDLE_FACILITY_CODE]: headerInputs.facility_id,
      [SoaEtlController.MINIMUM_KEYS.BUNDLE_FACILITY_NAME]: headerInputs.facility_name
    }

    const fileHeaders = Object.keys(dataRows[0])
    const fileMapping = Object.keys(dataMapping.columns)
    const err = fileMapping.filter(head => !fileHeaders.includes(head))

    if (err.length > 0) {
      setOpenAlert(state => ({
        ...state,
        show: true,
        message: 'Please make sure that the uploaded SOA file has the correct column headers.'
      }))
      return
    }

    LoadingService.loading(true)

    let bundle = null
    // Invoke transformation
    try {
      bundle = SoaEtlController.transform(dataRows, dataMapping, defaults)
    } catch (ex) {
      LoadingService.loading(false)
      // Unable to upload SOA file. Please ensure there is one SOA no. only."
      console.log('Transform Error', ex)
      setOpenAlert(state => ({
        ...state,
        show: true,
        message: ex.message || ex
      }))
      return
    }

    const onSuccess = function (bundle_number) {
      // alert('SOA Upload successful')
      // setAlertCloseHandlerAlert(handleOnCancel)

      setOpenAlert(state => ({
        ...state,
        show: true,
        message: 'SOA Uploaded Successfully: Bundle No. ' + bundle_number,
        onOk: () => {
          handleOnCancel()
          setOpenAlert(state => ({
            ...state,
            show: false
          }))
        }
      }))
    }

    // Invoke loading unto Claims API
    SoaEtlController.loadToApi(bundle, onSuccess)
      .then(bundle_number => {
        onSuccess(bundle_number)
        LoadingService.loading(false)
      })
      .catch((message) => {
        LoadingService.loading(false)
        setOpenAlert(state => ({
          ...state,
          show: true,
          message: 'SOA Upload failed.\n' + message
        }))
      })
  }

  // close for validation
  const handleOnClose = () => {
    setOpenAlert(state => ({
      ...state,
      show: false
    }))
  }

  const handleOnCancel = () => {
    LoadingService.loading(true)
    BatchEncodingServices.createBundle().then(res => {
      const { data } = res.data
      props.history.push(`/bundles/${data}/create`)
      handleOnCancelUpload()
      LoadingService.loading(false)
    })
  }

  return (
    <div className={classes.root}>
      {SoaEtlController.DEBUG && (
        <Button
          variant='contained'
          color='primary'
          className='button-negative'
          id='test-etl'
          onClick={SoaEtlController.test}
        >
          Test ETL
        </Button>
      )}

      {!data && <UploadSpace onSelectFile={handleOnSelectFile} />}

      {data && (
        <div className={classes.table}>
          <DynamicTable
            data={data}
            onRemoveFile={handleOnRemoveFile}
            onDetectDataRows={dataRows => {
              setDataRows(dataRows)
            }}
          />
        </div>
      )}

      {data && (
        <Fragment>
          <FieldMapper
            ApiService={ApiService}
            onSaveSuccess={({ template, facility }) => {
              console.log('Field Mapper Saved!', { template, facility })
            }}
            onChangeHeaderInputs={headerInputs => {
              setHeaderInputs(headerInputs)
            }}
            onChangeClaimTypeDataMapping={fields => {
              setClaimTypeValues(fields)
            }}
          />

          {/* TODO: replace evil quick-fix */}
          <br />

          <div align='right'>
            <Button
              variant='contained'
              color='primary'
              className='button-negative'
              id='cancel-upload'
              onClick={handleOnCancel}
            >
              Cancel
            </Button>
            {/* TODO: replace evil quick-fix */}
            &nbsp; &nbsp;
            <Button
              id='proceed-upload'
              variant='contained'
              color='primary'
              className='button-positive'
              onClick={handleProceedUpload}
            >
              Upload
            </Button>
          </div>
        </Fragment>
      )}

      <Alert open={openAlert.show} message={openAlert.message} okLabel='Close' onOk={openAlert.onOk || handleOnClose} />
    </div>
  )
}
