import legend from '../data/legend'
import theme from '../style/theme'

const getDefault = ({ month = [] }) => {
  const count = month.length
  const _legend = legend.map(i => ({
    label: i.description,
    hoverBorderWidth: 1,
    hoverBorderColor: '#000',
    // hidden: type === 'all' ? false : type !== i.description.replace(/ /g, ''),
    backgroundColor: theme[i.description.replace(/ /g, '')] }))

  return _legend.map(i => ({ ...i, data: Array.from(Array(count), () => 0) }))
}

export default getDefault
