import React from 'react'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'
import Alert from 'SharedComponents/Alert/Alert'

// Components

import { ICDTextFields, ICDTextField, GivenDiagnosisField } from '../ICDTextField/ICDTextField'
import DiscrepancyBase from './../DiscrepancyBase/DiscrepancyBase'

// Styles
import classes from './DiagnosisList.module.scss'

// Services
import IcdCptStorageServices from 'Services/Storage/IcdCptStorageServices'
import ClaimsAdjudicationServices from 'Services/Api/Claims/ClaimsAdjudicationServices'

// Store
import { useSelector, useDispatch } from 'react-redux'
import { ClaimsAnalysis as actions } from 'Pages/Protected/Claims/Store/actions'

const {
  addClaimDiagnosis,
  removeClaimDiagnosis,
  addSuggestionToArray,
  removeSuggestionToArray,
  setClaimPrimaryDiagnosis,
  setClaimDiagnosis,
  resetSuggestion,
  setSuggestion,
  setError
} = actions

export default function DiagnosisList (props) {
  const { items, error, permissions, id } = props

  const dispatch = useDispatch()
  const suggestions = useSelector(({ ClaimsAnalysis: state }) => state.suggestions)
  const discrepancy = useSelector(({ ClaimsAnalysis: state }) => state.discrepancy)
  const [openDeleteDialog, setOpenDeleteDialog] = React.useState({ show: false, index: null })

  const [debounce, setDebounce] = React.useState(null)
  const [fetching, setFetching] = React.useState([
    {
      given_diagnosis: false,
      description: false,
      code: false
    }
  ])

  const handleOnClickAddDiagnosis = (defaultBehaviour = true) => {
    const lastEntry = items.length !== 0 ? items[items.length - 1] : null
    if (defaultBehaviour && lastEntry && (!lastEntry.code.length || !lastEntry.description.length)) {
      return false
    }
    dispatch(addClaimDiagnosis())
    dispatch(addSuggestionToArray({ field: 'icd10' }))
  }

  const handleOnConfirmDelete = index => {
    setOpenDeleteDialog({ show: true, index })
  }

  const handleOnRemoveDiagnosis = index => {
    setOpenDeleteDialog({ show: false, index: null })
    dispatch(removeClaimDiagnosis({ index }))
    dispatch(setError({ diagnosis: {} }))
    dispatch(removeSuggestionToArray({ index, field: 'icd10' }))
    if (items.length === 1 && index !== null) {
      handleOnClickAddDiagnosis(false)
    }
  }

  const handleOnChangeIcd10 = args => {
    const { field, value, search, index } = args
    if (search) {
      dispatch(setClaimDiagnosis({ index, data: { [field]: value } }))
      dispatch(resetSuggestion({ field: 'icd10', index }))
      getIcd10Suggestions({ value, by: field, index, synonym: field === 'given_diagnosis' })
    } else {
      const _value = value || {}
      dispatch(
        setClaimDiagnosis({
          index,
          data: { description: _value.description, code: _value.code }
        })
      )
      // if (field === 'given_diagnosis') {
      //   getIcd10Suggestions({ value, by: field, index, synonym: true })
      // }
    }
  }

  const getIcd10Suggestions = ({ value = '', by, index, synonym = false }) => {
    if (value.trim().length < 3) return

    clearTimeout(debounce)
    setDebounce(
      setTimeout(() => {
        setFetching(state => ({ ...state, [index]: { ...state[index], [by]: true } }))
        if (value) {
          if (by === 'given_diagnosis') {
            ClaimsAdjudicationServices.getICDSynonyms({ query: value })
              .then(res => {
                const { data } = res.data
                console.log('fetched', data)
                dispatch(
                  setSuggestion({
                    field: by,
                    suggestion: data
                  })
                )
              })
              .finally(() => {
                setFetching(state => ({ ...state, [index]: { ...state[index], [by]: false } }))
              })
          } else {
            IcdCptStorageServices.lookups({
              on: 'icd-10',
              params: { query: value, by }
            })
              .then(res => {
                const { data } = res.data
                dispatch(
                  setSuggestion({
                    field: by,
                    suggestion: data
                  })
                )
              })
              .finally(() => {
                setFetching(state => ({ ...state, [index]: { ...state[index], [by]: false } }))
              })
          }
        }
      }, 500)
    )
  }

  return (
    <React.Fragment>
      {permissions.read && (
        <div className={classes.groups} id={id}>
          <div
            id={'claim-diagnosis-items'}
            className={`${classes['diagnosis-list']} ${error['list'] && classes['has-error']}`}
          >
            <Typography variant='h6' className={classes.title}>
              Diagnosis List
            </Typography>
            <ICDTextFields>
              {items.map((item, index) => (
                <React.Fragment key={`diagnosis-${index}`}>
                  {/* // Given Diagnosis */}
                  <DiscrepancyBase
                    id={`dp-icd-description-${index}`}
                    key={`icd-given-${index}`}
                    discrepancy={{
                      result: discrepancy.diagnosis[index]
                    }}
                    showIcon={false}
                    retainSpace
                    noMargin
                    className={classes.discrepancy}
                  >
                    <GivenDiagnosisField
                      required
                      id={`given-diagnosis-${index}`}
                      givenDiag={item.given_diagnosis}
                      onChangeGivenDiag={(value, search) =>
                        handleOnChangeIcd10({ field: 'given_diagnosis', index, value, search })
                      }
                      fetching={fetching[index]}
                      permissions={permissions}
                      suggestions={suggestions}
                      error={error && error.items && error.items[index]}
                    />
                  </DiscrepancyBase>
                  <DiscrepancyBase
                    id={`dp-icd-description-${index}`}
                    key={`icd-${index}`}
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.diagnosis[index]
                    }}
                    retainSpace
                    noMargin
                    className={classes.discrepancy}
                  >
                    <ICDTextField
                      required
                      key={`icd-${index} ${error[index]}`}
                      id={`icd-description-${index}`}
                      codeId={`icd-code-${index}`}
                      error={error && error.items && error.items[index]}
                      description={item.description}
                      suggestions={suggestions}
                      code={item.code}
                      primary={item.primary}
                      onChangeDescription={(value, search) =>
                        handleOnChangeIcd10({ field: 'description', index, value, search })
                      }
                      onChangeCode={(value, search) => handleOnChangeIcd10({ field: 'code', index, value, search })}
                      onChangePrimary={() => dispatch(setClaimPrimaryDiagnosis({ index }))}
                      onDeleteDiagnosis={() => handleOnConfirmDelete(index)}
                      fetching={fetching[index]}
                      permissions={permissions}
                    />
                  </DiscrepancyBase>
                </React.Fragment>
              ))}
            </ICDTextFields>
            {permissions.create && (
              <Button
                id='add-diagnosis-button'
                className={classes['add-diagnosis-button']}
                onClick={handleOnClickAddDiagnosis}
              >
                + Add a diagnosis
              </Button>
            )}
          </div>
          {error.error && <span className={classes.error}>{error.list}</span>}
          <Alert
            open={openDeleteDialog.show}
            message='Are you sure you want to delete this diagnosis?'
            cancelLabel='No'
            okLabel='Yes'
            onCancel={() => setOpenDeleteDialog({ show: false, index: null })}
            onOk={() => handleOnRemoveDiagnosis(openDeleteDialog.index)}
          />
        </div>
      )}
    </React.Fragment>
  )
}
