import React from 'react'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'

import DatePicker from '../../Components/DatePicker2/DatePicker2'
import DatePickerCurrent from 'SharedComponents/DatePicker2/DatePicker2'

// Utility
import { DateFormatter } from 'Utilities/TextFormatter'

// Validators
import DateValidator from '../../Validations/DateValidator'

import classes from './DatePickers.module.scss'

export default function DatePickers (props) {
  const { storage, errors } = props
  const { setErrors, onChange } = props

  const dateValidate = () => {
    const { error } = DateValidator({ date_from: storage.date_from })
    setErrors(error || {})
  }

  return (
    <div className={classes.root}>
      <div className={classes.row}>
        <div className={classes.input}>
          <DatePickerCurrent
            label='Current'
            value={storage.date_from}
            onChange={date => {
              onChange({ value: date, field: 'date_from' })
            }}
            error={errors.date_from}
          />
        </div>
        <div className={classes.input}>
          <DatePicker
            label='Beta'
            value={storage.date_from}
            onChange={date => {
              onChange({ value: date, field: 'date_from' })
            }}
            error={errors.date_from}
          />
        </div>
        <div className={classes.input}>
          <Typography>{DateFormatter(storage.date_from)}</Typography>
        </div>
        <div className={classes.input}>
          <Button
            variant='contained'
            color='primary'
            className={`${classes.save} button-positive`}
            onClick={dateValidate}
            id='save'
          >
            Validate
          </Button>
        </div>
      </div>
    </div>
  )
}
