import React from 'react'
import Paper from '@material-ui/core/Paper'

// Components
import DropDown from 'SharedComponents/DropDown/DropDown'
import TextField from 'SharedComponents/TextField/CustomTextField'
import TextFieldCurrency from 'SharedComponents/TextFieldCurrency/TextFieldCurrency'
import Button from '@material-ui/core/Button'
import DiscrepancyBase from './../DiscrepancyBase/DiscrepancyBase'
import AutoComplete from 'SharedComponents/AutoComplete2/AutoComplete2'
import Alert from 'SharedComponents/Alert/Alert'

// Styles
import classes from './ProfessionalFeeForm.module.scss'

// Services
import ClaimsAdjudicationServices from 'Services/Api/Claims/ClaimsAdjudicationServices'
import CustomerCareServices from 'Services/Api/Claims/CustomerCareServices'

// Utilities
import { SafeAccess } from 'Utilities/SafeAccess'

// Store
import { useSelector, useDispatch } from 'react-redux'
import { ClaimsAnalysis as actions } from 'Pages/Protected/Claims/Store/actions'
const {
  setProfessionalFee,
  resetSuggestion,
  setSuggestion,
  addClaimProfessionalFee,
  updateClaimProfessionalFee,
  removeClaimProfessionalFee,
  setError
} = actions

export default function ProfessionalFeeForm (props) {
  const dispatch = useDispatch()
  const {
    index,
    discrepancy = {},
    error = {},
    doValidate,
    permissions,
    diagnosis,
    showAmountPaidByMember = true,
    showPhilhealthField = true,
    showAmountNotCovered = true,
    showAmountDueFromMember = true
  } = props
  const professionalFee = useSelector(({ ClaimsAnalysis: state }) => state.professionalFee)
  const pfSuggestions = useSelector(({ ClaimsAnalysis: state }) => state.suggestions)
  const dropDown = useSelector(({ ClaimsAnalysis: state }) => state.dropDown)

  const [debounce, setDebounce] = React.useState({
    pfDoctor: null
  })

  const [fetching, setFetching] = React.useState({
    description: false,
    code: false
  })
  const [openDeleteDialog, setOpenDeleteDialog] = React.useState({ show: false, index: null })
  const [_errors, setErrors] = React.useState({})

  // ***** REACTIVE

  React.useEffect(() => {
    for (var prop in error) {
      if (error.hasOwnProperty(prop)) {
        setErrors(error)
        break
      }
    }
  }, [error, index])

  React.useEffect(() => {
    if (diagnosis) {
      const { diagnosis_code, diagnosis_description } = professionalFee.details
      if (!diagnosis.find(item => item.code === diagnosis_code && item.description === diagnosis_description)) {
        dispatch(setProfessionalFee({ details: { diagnosis_code: '', diagnosis_description: '' } }))
      }
    }
  }, [diagnosis])

  // ***** EVENTS

  const handleOnChangeDoctor = ({ value, field, search }) => {
    if (search) {
      dispatch(setProfessionalFee({ details: { [field]: value } }))
      dispatch(resetSuggestion({ field: 'pfDoctor' }))
      const { 1: by } = field.split('_')
      let _by = by === 'id' ? 'id' : 'full_name'
      getDoctorSuggestions({ value, by: _by })
    } else {
      const _value = value || {}
      dispatch(
        setProfessionalFee({
          details: {
            doctor_id: SafeAccess(_value, 'id', ''),
            doctor_name: SafeAccess(_value, 'full_name', ''),
            specialization: _value.specialization ? SafeAccess(_value, 'specialization.main', '') + ', ' + SafeAccess(_value, 'specialization.major', '') : ''
          }
        })
      )
      dispatch(resetSuggestion({ field: 'pfDoctor' }))
    }
  }

  const handleOnChangeDiagnosis = ({ value, field }) => {
    const fieldArr = field.split('_')
    const _field = fieldArr.length === 2 ? fieldArr[1] : `${fieldArr[1]}_${fieldArr[2]}`
    const { code, description, given_diagnosis } = diagnosis.find(d => d[_field] === value)
    dispatch(
      setProfessionalFee({
        details: {
          diagnosis_code: code,
          diagnosis_description: description,
          diagnosis_given_diagnosis: given_diagnosis
        }
      })
    )
  }

  const handleOnChangeSpecialization = ({ value, field, search }) => {
    if (search) {
      dispatch(setProfessionalFee({ details: { [field]: value } }))
      dispatch(resetSuggestion({ field: 'specialization' }))
      getSpecializationSuggestions({ value, by: 'description' })
    } else {
      const _value = value || {}
      dispatch(
        setProfessionalFee({
          details: {
            specialization: _value.description
          }
        })
      )
      dispatch(resetSuggestion({ field: 'specialization' }))
    }
  }

  // ***** FETCHING

  const getDoctorSuggestions = ({ value = '', by }) => {
    if (value.trim().length < 3) return

    clearTimeout(debounce.pfDoctor)
    setFetching(state => ({ ...state, [by]: true }))
    setDebounce(state => ({
      ...state,
      pfDoctor: setTimeout(() => {
        if (value) {
          ClaimsAdjudicationServices.lookups({
            on: 'doctor-partner',
            params: { query: value, by }
          })
            .then(res => {
              const { data } = res.data
              dispatch(
                setSuggestion({
                  field: 'pfDoctor',
                  suggestion: data
                })
              )
            })
            .finally(() => {
              setFetching(state => ({ ...state, [by]: false }))
            })
        }
      }, 500)
    }))
  }

  const getSpecializationSuggestions = ({ value = '', by }) => {
    if (value.trim().length < 3) return

    clearTimeout(debounce.pfDoctor)
    setFetching(state => ({ ...state, [by]: true }))
    setDebounce(state => ({
      ...state,
      pfDoctor: setTimeout(() => {
        if (value) {
          CustomerCareServices.getDoctorSpecilizations({ data: value })
            .then(res => {
              const { data } = res.data
              dispatch(
                setSuggestion({
                  field: 'specialization',
                  suggestion: data
                })
              )
            })
            .finally(() => {
              setFetching(state => ({ ...state, [by]: false }))
            })
        }
      }, 500)
    }))
  }

  // ***** ACTIONS

  const handleOnChange = ({ value, field }) => {
    dispatch(setProfessionalFee({ details: { [field]: value } }))
  }

  const handleOnSave = () => {
    dispatch(setError({ professional_fee: { index: null, details: {} } }))
    const _error = doValidate({ data: professionalFee.details, index })
    setErrors(_error)
    if (Object.keys(_error).length <= 0) {
      dispatch(addClaimProfessionalFee(professionalFee.details))
      dispatch(setProfessionalFee({ show: false, mode: '', index: '' }))
    }
  }

  const handleOnUpdate = index => {
    dispatch(setError({ professional_fee: { index: null, details: {} } }))
    const _error = doValidate({ data: professionalFee.details, index })
    setErrors(_error)
    if (Object.keys(_error).length <= 0) {
      dispatch(updateClaimProfessionalFee({ index, details: professionalFee.details }))
      dispatch(setProfessionalFee({ show: false, mode: '', index: '' }))
    }
  }

  const handleOnRemoveConfirm = index => {
    setOpenDeleteDialog({ show: true, index })
  }

  const handleOnRemove = index => {
    dispatch(setError({ professional_fee: { index: null, details: {} } }))
    dispatch(setProfessionalFee({ show: false, mode: '', index: '' }))
    dispatch(removeClaimProfessionalFee({ index }))
    setOpenDeleteDialog({ show: false, index: null })
  }

  const handleOnCancel = index => {
    dispatch(setError({ professional_fee: { index: null, details: {} } }))
    dispatch(setProfessionalFee({ show: false, mode: '', index: '' }))
  }

  return (
    <React.Fragment>
      {/* {console.group('ProfessionalFee Form')} */}
      {/* {console.log('Data', professionalFee)} */}
      {/* {console.log('Discrepancy', discrepancy)} */}
      {/* {console.log('Errors', error)} */}
      {/* {console.log('Suggestions ', suggestions)} */}
      {/* {console.log('Index ', professionalFee.index, index)} */}
      {/* {console.groupEnd()} */}
      {professionalFee.show && professionalFee.index === index && (
        <Paper className={classes.root}>
          {diagnosis && (
            <React.Fragment>
              <div className={classes.groups}>
                <div className={classes.input}>
                  <DropDown
                    id='given-diagnosis'
                    label='Given Diagnosis'
                    required
                    items={diagnosis.map(({ given_diagnosis }) => ({
                      text: given_diagnosis,
                      value: given_diagnosis
                    }))}
                    value={professionalFee.details.diagnosis_given_diagnosis}
                    onChange={event =>
                      handleOnChangeDiagnosis({
                        field: 'diagnosis_given_diagnosis',
                        value: event.target.value
                      })
                    }
                    error={_errors.diagnosis_given_diagnosis}
                    readOnly={!permissions.update}
                  />
                </div>
              </div>
              <div className={classes.groups}>
                <div className={classes.input}>
                  <DropDown
                    id='icd10-description'
                    required
                    label='ICD10 Description'
                    items={diagnosis.map(({ description }) => ({ text: description, value: description }))}
                    value={professionalFee.details.diagnosis_description}
                    onChange={event =>
                      handleOnChangeDiagnosis({
                        field: 'diagnosis_description',
                        value: event.target.value
                      })
                    }
                    error={_errors.diagnosis_description}
                    readOnly={!permissions.update}
                  />
                </div>
                <div className={classes.input}>
                  <DropDown
                    id='icd10-code'
                    required
                    label='ICD10 Code'
                    items={diagnosis.map(({ code }) => ({ text: code, value: code }))}
                    value={professionalFee.details.diagnosis_code}
                    onChange={event =>
                      handleOnChangeDiagnosis({
                        field: 'diagnosis_code',
                        value: event.target.value
                      })
                    }
                    error={_errors.diagnosis_code}
                    readOnly={!permissions.update}
                  />
                </div>
              </div>
            </React.Fragment>
          )}
          <div className={classes.groups}>
            <div className={classes.input}>
              <DiscrepancyBase
                id='dp-professional-fee-medical-partner-id'
                discrepancy={{
                  message: 'Failed',
                  result: discrepancy.doctor_id
                }}
              >
                <AutoComplete
                  id='professional-fee-medical-partner-id'
                  required
                  label='Medical Partner ID'
                  value={professionalFee.details.doctor_id}
                  suggestions={pfSuggestions.pfDoctor}
                  field={'id'}
                  loading={fetching['doctor_id']}
                  onChange={(value, search) => handleOnChangeDoctor({ field: 'doctor_id', value, search })}
                  error={_errors.doctor_id}
                  readOnly={!permissions.update}
                />
              </DiscrepancyBase>
            </div>

            <div className={classes.input}>
              <DiscrepancyBase
                id='dp-professional-fee-doctor-name'
                discrepancy={{
                  message: 'Failed',
                  result: discrepancy.doctor_name
                }}
              >
                <AutoComplete
                  id='professional-fee-doctor-name'
                  required
                  label='Doctor Name'
                  value={professionalFee.details.doctor_name}
                  suggestions={pfSuggestions.pfDoctor}
                  field={'full_name'}
                  loading={fetching['doctor_name']}
                  onChange={(value, search) => handleOnChangeDoctor({ field: 'doctor_name', value, search })}
                  error={_errors.doctor_name}
                  readOnly={!permissions.update}
                />
              </DiscrepancyBase>
            </div>
          </div>
          <div className={classes.groups}>
            <div className={classes.input}>
              <DiscrepancyBase
                id='dp-professional-fee-specialization'
                discrepancy={{
                  message: 'Failed',
                  result: discrepancy.specialization
                }}
              >
                <AutoComplete
                  id='professional-fee-specialization'
                  required
                  label='Specialization'
                  value={professionalFee.details.specialization}
                  suggestions={pfSuggestions.specialization}
                  field={'description'}
                  loading={fetching['description']}
                  onChange={(value, search) => handleOnChangeSpecialization({ field: 'specialization', value, search })}
                  error={_errors.specialization}
                  readOnly={!permissions.update}
                />
              </DiscrepancyBase>
            </div>
          </div>

          <div className={classes.groups}>
            <div className={classes.input}>
              <DiscrepancyBase
                id='dp-professional-fee-gross-amount'
                discrepancy={{
                  message: 'Failed',
                  result: discrepancy.gross_amount
                }}
              >
                <TextFieldCurrency
                  id='professional-fee-gross-amount'
                  required
                  label='Gross Amount'
                  value={professionalFee.details.gross_amount}
                  onChange={event =>
                    handleOnChange({
                      field: 'gross_amount',
                      value: event.target.value
                    })
                  }
                  error={_errors.gross_amount}
                  readOnly={!permissions.update}
                />
              </DiscrepancyBase>
            </div>

            <div className={classes.input}>
              <DiscrepancyBase
                id='dp-professional-fee-vat-amount'
                discrepancy={{
                  message: 'Failed',
                  result: discrepancy.vat_amount
                }}
              >
                <TextFieldCurrency
                  id='professional-fee-vat-amount'
                  label='VAT Amount'
                  value={professionalFee.details.vat_amount}
                  onChange={event =>
                    handleOnChange({
                      field: 'vat_amount',
                      value: event.target.value
                    })
                  }
                  error={_errors.vat_amount}
                  readOnly={!permissions.update}
                />
              </DiscrepancyBase>
            </div>
          </div>

          {showAmountPaidByMember && (
            <div className={classes.groups}>
              <div className={classes.input}>
                <DiscrepancyBase
                  id='dp-professional-fee-amount-paid-by-member'
                  discrepancy={{
                    message: 'Failed',
                    result: discrepancy.amount_paid_by_member
                  }}
                >
                  <TextFieldCurrency
                    id='professional-fee-amount-paid-by-member'
                    label='Amount Paid by Member'
                    value={professionalFee.details.amount_paid_by_member}
                    onChange={event =>
                      handleOnChange({
                        field: 'amount_paid_by_member',
                        value: event.target.value
                      })
                    }
                    error={_errors.amount_paid_by_member}
                    readOnly={!permissions.update}
                  />
                </DiscrepancyBase>
              </div>
            </div>
          )}

          {showPhilhealthField && (
            <div className={classes.groups}>
              <div className={classes.input}>
                <DiscrepancyBase
                  id='dp-philhealth-amount-paid'
                  discrepancy={{
                    message: 'Failed',
                    result: discrepancy.philhealth_amount_paid
                  }}
                >
                  <TextFieldCurrency
                    id='philhealth-amount-paid'
                    label='PhilHealth Amount Paid'
                    value={professionalFee.details.philhealth_amount_paid}
                    onChange={event =>
                      handleOnChange({
                        field: 'philhealth_amount_paid',
                        value: event.target.value
                      })
                    }
                    error={_errors.philhealth_amount_paid}
                    readOnly={!permissions.update}
                  />
                </DiscrepancyBase>
              </div>

              <div className={classes.input}>
                <DiscrepancyBase
                  id='dp-philhealth-remarks'
                  discrepancy={{
                    message: 'Failed',
                    result: discrepancy.philhealth_remarks
                  }}
                >
                  <TextField
                    multiline
                    rows={3}
                    id='philhealth-remarks'
                    label='PhilHealth Remarks'
                    value={professionalFee.details.philhealth_remarks}
                    onChange={event =>
                      handleOnChange({
                        field: 'philhealth_remarks',
                        value: event.target.value
                      })
                    }
                    error={_errors.philhealth_remarks}
                    readOnly={!permissions.update}
                  />
                </DiscrepancyBase>
              </div>
            </div>
          )}

          {showAmountNotCovered && (
            <React.Fragment>
              <div className={classes.groups}>
                <div className={classes.input}>
                  <DiscrepancyBase
                    id='dp-amount-not-covered'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.amount_not_covered
                    }}
                  >
                    <TextFieldCurrency
                      id='amount-not-covered'
                      label='Amount Not Covered'
                      value={professionalFee.details.amount_not_covered}
                      onChange={event => {
                        if (event.target.value === 0) {
                          professionalFee.details.reason = ''
                          setErrors(state => ({ ...state, reason: '' }))
                        }

                        handleOnChange({
                          field: 'amount_not_covered',
                          value: event.target.value
                        })
                      }}
                      error={_errors.amount_not_covered}
                      readOnly={!permissions.update}
                    />
                  </DiscrepancyBase>
                </div>
              </div>
              <div className={classes.groups}>
                <div className={classes.input}>
                  <DiscrepancyBase
                    id='dp-amount-not-covered-reason'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.reason
                    }}
                  >
                    <DropDown
                      id='amount-not-covered-reason'
                      label='Reason'
                      required={professionalFee.details.amount_not_covered > 0}
                      disableComponents={professionalFee.details.amount_not_covered === 0}
                      value={professionalFee.details.reason}
                      items={dropDown.reason}
                      onChange={event =>
                        handleOnChange({
                          field: 'reason',
                          value: event.target.value
                        })
                      }
                      error={_errors.reason}
                      readOnly={!permissions.update}
                    />
                  </DiscrepancyBase>
                </div>
              </div>
            </React.Fragment>
          )}

          {showAmountDueFromMember && (
            <React.Fragment>
              <div className={classes.groups}>
                <div className={classes.input}>
                  <DiscrepancyBase
                    id='dp-amount-due-from-member'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.amount_due_from_member
                    }}
                  >
                    <TextFieldCurrency
                      id='amount-due-from-member'
                      label='Amount Due from Member'
                      value={professionalFee.details.amount_due_from_member}
                      onChange={event => {
                        if (event.target.value === 0) {
                          professionalFee.details.reason_for_charging_member = ''
                          setErrors(state => ({ ...state, amount_due_from_member: '' }))
                        }

                        handleOnChange({
                          field: 'amount_due_from_member',
                          value: event.target.value
                        })
                      }}
                      error={_errors.amount_due_from_member}
                      readOnly={!permissions.update}
                    />
                  </DiscrepancyBase>
                </div>
              </div>
              <div className={classes.groups}>
                <div className={classes.input}>
                  <DiscrepancyBase
                    id='dp-hospital-item'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.reason_for_charging_member
                    }}
                  >
                    <DropDown
                      id='reason-for-charging-member'
                      label='Reason for Charging Member'
                      required={professionalFee.details.amount_due_from_member > 0}
                      disableComponents={professionalFee.details.amount_due_from_member === 0}
                      value={professionalFee.details.reason_for_charging_member}
                      items={dropDown.reason_charging}
                      onChange={event =>
                        handleOnChange({
                          field: 'reason_for_charging_member',
                          value: event.target.value
                        })
                      }
                      error={_errors.reason_for_charging_member}
                      readOnly={!permissions.update}
                    />
                  </DiscrepancyBase>
                </div>
              </div>
            </React.Fragment>
          )}

          {showAmountDueFromMember && (
            <React.Fragment>
              <div className={classes.groups}>
                <div className={classes.input}>
                  <DiscrepancyBase
                    id='dp-amount-due-from-client'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.amount_due_from_client
                    }}
                  >
                    <TextFieldCurrency
                      id='amount-due-from-client'
                      label='Amount Due from Client'
                      value={professionalFee.details.amount_due_from_client}
                      onChange={event => {
                        if (event.target.value === 0) {
                          professionalFee.details.reason_for_charging_client = ''
                          setErrors(state => ({ ...state, amount_due_from_client: '' }))
                        }

                        handleOnChange({
                          field: 'amount_due_from_client',
                          value: event.target.value
                        })
                      }}
                      error={_errors.amount_due_from_client}
                      readOnly={!permissions.update}
                    />
                  </DiscrepancyBase>
                </div>
              </div>
              <div className={classes.groups}>
                <div className={classes.input}>
                  <DiscrepancyBase
                    id='dp-amount-due-from-client'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.reason_for_charging_client
                    }}
                  >
                    <DropDown
                      id='reason-for-charging-client'
                      label='Reason for Charging Client'
                      required={professionalFee.details.amount_due_from_client > 0}
                      disableComponents={professionalFee.details.amount_due_from_client === 0}
                      value={professionalFee.details.reason_for_charging_client}
                      items={dropDown.reason_charging}
                      onChange={event =>
                        handleOnChange({
                          field: 'reason_for_charging_client',
                          value: event.target.value
                        })
                      }
                      error={_errors.reason_for_charging_client}
                      readOnly={!permissions.update}
                    />
                  </DiscrepancyBase>
                </div>
              </div>
            </React.Fragment>
          )}

          <div className={`${classes.groups} ${classes.buttons}`}>
            {professionalFee.mode === 'add' && permissions.create && (
              <React.Fragment>
                <Button
                  id='professional-fee-save-button'
                  variant='contained'
                  color='primary'
                  className='button-positive'
                  onClick={handleOnSave}
                >
                  Save
                </Button>
              </React.Fragment>
            )}
            {professionalFee.mode !== 'add' && permissions.update && (
              <React.Fragment>
                <Button
                  id='professional-fee-update-button'
                  variant='contained'
                  color='primary'
                  className='button-positive'
                  onClick={() => handleOnUpdate(professionalFee.index)}
                >
                  Update
                </Button>
                {permissions.delete && (
                  <Button
                    id='professional-fee-delete-button'
                    variant='contained'
                    color='secondary'
                    className='button-negative'
                    onClick={() => handleOnRemoveConfirm(professionalFee.index)}
                  >
                    Delete
                  </Button>
                )}
              </React.Fragment>
            )}
            <Button
              id={`professional-fee-${permissions.create && permissions.update ? 'cancel' : 'close'}-button`}
              variant='contained'
              color='secondary'
              className='button-negative'
              onClick={() => handleOnCancel(professionalFee.index)}
            >
              {permissions.create && permissions.update ? 'Cancel' : 'Close'}
            </Button>
          </div>
        </Paper>
      )}
      <Alert
        open={openDeleteDialog.show}
        message='Are you sure you want to delete this professional fee?'
        cancelLabel='No'
        okLabel='Yes'
        onCancel={() => setOpenDeleteDialog({ show: false, index: null })}
        onOk={() => handleOnRemove(professionalFee.index)}
      />
    </React.Fragment>
  )
}
