import React from 'react'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import IconButton from '@material-ui/core/IconButton'
import CloudDownloadIcon from '@material-ui/icons/CloudDownload'
import CloseIcon from '@material-ui/icons/Close'
import Typography from '@material-ui/core/Typography'

// https://github.com/plangrid/react-file-viewer
import ReactFileViewer from 'react-file-viewer'

import { fromUrl } from 'Services/Download'

// Styles
import classes from './FileViewer.module.scss'

export default function FileViewer (props) {
  // Variables
  const { show = false, title = '', fileName = '', fileType = '', url = '' } = props
  // Services
  // Events
  // Callbacks
  const { onClose = () => {} } = props
  // Local Variables

  const handleDownload = () => {
    fromUrl({ fileName: fileName, content: url, extension: fileType })
  }

  return (
    <Dialog
      className={classes.root}
      fullWidth
      maxWidth='lg'
      onClose={onClose}
      aria-labelledby='customized-dialog-title'
      open={show}
      PaperProps={{ style: { minHeight: '65vh' } }}
    >
      <DialogTitle className={classes['header-container']} disableTypography>
        <div className={classes.header}>
          <Typography variant='h6' className={classes.title}>
            {title}
          </Typography>
          <IconButton aria-label='close' className={classes.button} onClick={handleDownload}>
            <CloudDownloadIcon />
          </IconButton>

          <IconButton aria-label='close' className={classes.button} onClick={onClose}>
            <CloseIcon />
          </IconButton>
        </div>
      </DialogTitle>

      <DialogContent dividers className={classes.content}>
        <ReactFileViewer
          fileName={fileName}
          fileType={fileType}
          filePath={url}
          errorComponent={props => <Typography> Unsupported File Type</Typography>}
        />
      </DialogContent>
    </Dialog>
  )
}
