import React from 'react'

// Material Components
import Button from '@material-ui/core/Button'
import Paper from '@material-ui/core/Paper'
import Table from 'SharedComponents/Table/Table'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TableSortLabel from '@material-ui/core/TableSortLabel'
import Typography from '@material-ui/core/Typography'
import GetAppIcon from '@material-ui/icons/GetApp'

// Shared Components
import DatePicker from 'SharedComponents/DatePicker2/DatePicker2'
import TextField from 'SharedComponents/TextField/CustomTextField'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import ClaimsServices from 'Services/Api/Claims/ClaimsServices'
import ArrayToCSV from 'Services/ArrayToCSV'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'

// Utility
import { CurrencyFormatter } from 'Utilities/TextFormatter'
import { DateFormatter } from 'Utilities/TextFormatter'

// Styles
import classes from './ReturnedClaimsReport.module.scss'

// Validator
import DateRangeValidator from '../TransmittalReport/validator/DateRangeValidator'

const columns = [
  { label: 'No.', sortField: 'no', search: false, sort: false, type: 'number' },
  { label: 'Claim Number', sortField: 'claim_number', search: true, sort: true, match: 'start' },
  { label: 'Claim Amount', sortField: 'claim_amount', search: false, sort: false, type: 'number' },
  { label: 'Reason for Return', sortField: 'return_reason', search: true, sort: true, match: 'any' },
  { label: 'Medical Partner ID', sortField: 'facility_id', search: true, sort: true, match: 'start' },
  { label: 'Medical Partner Name', sortField: 'facility_name', search: true, sort: true, match: 'start' },
  { label: 'Claim Type', sortField: 'claim_type', search: true, sort: true, match: 'any' },
  { label: 'Claim Source', sortField: 'claim_source', search: true, sort: true, match: 'any' },
  { label: 'Geography', sortField: 'geography', search: true, sort: true, match: 'any' }
]

export default function ReturnedClaimsReport (props) {
  // separated date range to avoid fetch for non-filtering func
  const [dateRange, setDateRange] = React.useState({
    from: new Date().toISOString(),
    to: new Date().toISOString(),
    field: 'status_date'
  })

  const [claims, setClaims] = React.useState({
    data: [],
    search: '',
    range: { from: new Date().toISOString(), to: new Date().toISOString(), field: 'status_date' },
    filter: {},
    orderBy: 'claim_number',
    order: true,
    page: 1,
    limit: 10,
    count: 0,
    total_amount: 0,
    rowsPerPageOptions: [10, 15, 20]
  })

  const [dateRangeError, setDateRangeError] = React.useState({
    date_from: '',
    date_to: ''
  })

  const [debounce, setDebounce] = React.useState(null)

  React.useEffect(() => {
    PageTitleService('Returned Claims Report')
    BreadcrumbsService.setSelectedItems(['returnedClaimsReport'])

    // autofilter today
    handleOnClickFilter({ range: claims.range })
  }, [])

  const fetchReport = params => {
    LoadingService.loading(true)
    ClaimsServices.returnedClaimsReport({
      range: params.range,
      sort: `${params.orderBy}|${params.order ? 'asc' : 'desc'}`,
      page: params.page,
      limit: params.limit,
      filter: params.filter
    }).then(res => {
      const { results = [], total = [], count, limit, page } = res.data.data
      const [_total] = total

      setClaims(state => ({
        ...state,
        data: results,
        count,
        page,
        limit,
        total_amount: total.length ? _total.total_amount : 0
      }))

      LoadingService.loading(false)
    })
  }

  const handleOnClickFilter = ({ range }) => {
    if (validate()) return
    LoadingService.loading(true)

    // setRange to use in pagination, filtering
    setDateRange(state => ({ ...state, ...range }))
    fetchReport({ ...claims, ...range })
  }

  const handleOnPaginate = params => {
    const _params = {
      ...claims,
      ...params
    }

    setClaims(state => ({ ...state, ...params }))
    fetchReport({ ..._params, range: dateRange })
  }

  const validate = () => {
    const { error } = DateRangeValidator({
      date_from: claims.range.from,
      date_to: claims.range.to
    })

    setDateRangeError({ ...error })
    return !!error
  }

  const fetchCSV = params => {
    LoadingService.loading(true)
    ClaimsServices.returnedClaimsReport({
      range: dateRange,
      sort: `${params.orderBy}|${params.order ? 'asc' : 'desc'}`
    }).then(res => {
      const { results } = res.data.data
      const header = columns.map(i => i.label)

      const rows = results.map(i => {
        return columns.map(col => formatData({ type: col.type, source: i, field: col.sortField }))
      })

      // array.from or of
      ArrayToCSV({
        statics: [
          [
            'Returned Claims Report',
            '\r\n',
            '\r\n',
            ['Date Range:', `${DateFormatter(dateRange.from)} - ${DateFormatter(dateRange.to)}`],
            '\r\n',
            '\r\n'
          ],
          ['', 'Total Amount:', `${claims.total_amount}`].join(',')
        ],
        fileName: 'Returned Claims Report',
        header,
        rows
      })
      LoadingService.loading(false)
    })
  }

  const formatData = ({ type = null, source, field }) => {
    let output = ''
    if (type === 'date') {
      output = DateFormatter(source[field])
    } else if (type === 'array') {
      const [root, property] = field.split('.')
      output = source[root] ? source[root].map(f => f[property]).join(`${'\r\n'}${'\r\n'}`) : ''
    } else if (type === 'number') {
      output = source[field]
    } else {
      output = source[field] ? source[field] : ''
    }

    return output !== '' && output !== 'undefined' && output !== `${'\r\n'}${'\r\n'}` ? output : 'N/A'
  }

  return (
    <Paper className={classes.root}>
      <div className={classes.header}>
        <Typography variant='h6'>Date Range:</Typography>
        <div className={classes.content}>
          <div className={classes.filter}>
            <div className={classes.input}>
              <p>From: </p>
              <DatePicker
                id='filter-date-from'
                value={claims.range.from}
                onChange={date =>
                  setClaims(state => ({
                    ...state,
                    range: {
                      ...state.range,
                      from: date.toISOString()
                    }
                  }))
                }
                disableFuture
                error={dateRangeError.date_from}
              />
            </div>
            <div className={classes.input}>
              <p>To: </p>
              <DatePicker
                id='filter-date-to'
                value={claims.range.to}
                onChange={date =>
                  setClaims(state => ({
                    ...state,
                    range: {
                      ...state.range,
                      to: date.toISOString()
                    }
                  }))
                }
                disableFuture
                error={dateRangeError.date_to}
              />
            </div>
            <Button
              className={`button-positive ${classes.submit}`}
              onClick={() => {
                handleOnClickFilter({ range: claims.range })
              }}
              variant='contained'
              color='primary'
            >
              Filter
            </Button>
          </div>
          <div className={classes.action}>
            <Button id='generate-report' onClick={() => fetchCSV(claims)} disabled={!claims.data.length}>
              <GetAppIcon fontSize='small' />
              <Typography variant='caption' className={`bold ${!claims.data.length ? classes.disabled : classes.typo}`}>
                Generate Report
              </Typography>
            </Button>
          </div>
        </div>
      </div>
      <div className={classes.body}>
        <div className={classes.table}>
          <Table
            TableContainerProps={{ style: { maxHeight: '60vh', overflow: 'auto', position: 'relative' } }}
            TableProps={{ stickyHeader: true }}
            head={
              <TableRow>
                {columns.map((headCell, index) => (
                  <TableCell key={`table-header-${index}`}>
                    {headCell.sort ? (
                      <div>
                        <TableSortLabel
                          active={claims.orderBy === headCell.sortField}
                          direction={claims.order ? 'asc' : 'desc'}
                          onClick={event => handleOnPaginate({ orderBy: headCell.sortField, order: !claims.order })}
                        >
                          {headCell.label}
                        </TableSortLabel>
                      </div>
                    ) : (
                      headCell.label
                    )}
                    <div className={classes.input}>
                      <TextField
                        noMargin
                        id={`text-field-${headCell.sortField}`}
                        readOnly={!headCell.search}
                        TextFieldStyle={{ margin: 0 }}
                        fullWidth
                        onChange={event => {
                          const search = event.target.value
                          clearTimeout(debounce)
                          setDebounce(
                            setTimeout(() => {
                              handleOnPaginate({
                                filter: {
                                  ...claims.filter,
                                  [headCell.sortField]: { value: search, match: headCell.match }
                                },
                                page: 1
                              })
                            }, 500)
                          )
                        }}
                      />
                    </div>
                  </TableCell>
                ))}
              </TableRow>
            }
            body={
              claims.data.length ? (
                claims.data.map((item, index) => (
                  <TableRow className={classes['table-row']} key={index}>
                    <TableCell align='left'>{item.no}</TableCell>
                    <TableCell align='left'> {item.claim_number}</TableCell>
                    <TableCell align='right'>{CurrencyFormatter(item.claim_amount)}</TableCell>
                    <TableCell align='left'>{item.return_reason}</TableCell>
                    <TableCell align='left'> {item.facility_id}</TableCell>
                    <TableCell align='left'>{item.facility_name}</TableCell>
                    <TableCell align='left'>{item.claim_type}</TableCell>
                    <TableCell align='left'>{item.claim_source}</TableCell>
                    <TableCell align='left'>{item.geography}</TableCell>
                  </TableRow>
                ))
              ) : (
                <TableRow>
                  <TableCell className={classes.handling} colSpan={columns.length}>
                    No data found
                  </TableCell>
                </TableRow>
              )
            }
            pagination
            count={claims.count}
            page={claims.page}
            limit={claims.limit}
            rowsPerPageOptions={claims.rowsPerPageOptions}
            onChange={args => handleOnPaginate(args)}
            footerComponent={
              <Typography variant='caption' className='bold'>
                <span>Total Amount: </span>
                <span>{CurrencyFormatter(claims.total_amount)}</span>
              </Typography>
            }
          />
        </div>
      </div>
    </Paper>
  )
}
