import React from 'react'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import Typography from '@material-ui/core/Typography'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
// import TableSortLabel from '@material-ui/core/TableSortLabel'
// import DatePicker from 'SharedComponents/DatePicker2/DatePicker2'

// Components
import Table from 'SharedComponents/Table/Table'
import SearchTextComponent from 'SharedComponents/SearchTextComponent/SearchTextComponent'
import TextField from 'SharedComponents/TextField/CustomTextField'
import LoadingIndicator from 'SharedComponents/LoadingIndicator/LoadingIndicator'
import TableSortLabel from 'SharedComponents/TableSortLabel/TableSortLabel'

// Styles
import classes from './ViewList.module.scss'

export default function CustomizedDialogs ({
  id,
  columns,
  searchTextPlaceHolder,
  viewList,
  setViewList,
  onViewListChange,
  body,
  fetching = false
}) {
  const handleOnClose = () => {
    setViewList(p => ({
      ...p,
      show: false,
      count: 0,
      search: '',
      limit: 10,
      page: 1,
      filter: {}
    }))
  }

  const [debounce, setDebounce] = React.useState(null)

  const handleSearchChange = e => {
    const search = e.target.value
    clearTimeout(debounce)
    setDebounce(
      setTimeout(() => {
        setViewList(p => ({ ...p, search }))
        onViewListChange({ search, page: 1 })
      }, 500)
    )
  }

  const handleOnSort = orderBy => e => {
    setViewList(p => ({ ...p, orderBy, order: !viewList.order }))
    onViewListChange({ sort: `${orderBy}|${viewList.order ? 'asc' : 'desc'}` })
  }

  const handleColumnFiltering = args => {
    const { field, value, match } = args
    // console.log(args)
    setViewList(p => ({ ...p, filter: { ...p.filter, [field]: { value, match } } }))
    onViewListChange({ filter: { ...viewList.filter, [field]: { value, match } }, page: 1 })
  }

  return (
    <Dialog
      id={id}
      fullWidth
      maxWidth='lg'
      onClose={handleOnClose}
      aria-labelledby='customized-dialog-title'
      open={viewList.show}
      className={classes.root}
      PaperProps={{ style: { height: '100%', display: 'flex' } }}
    >
      <DialogTitle disableTypography className={classes['header-container']}>
        <div className={classes.header}>
          <Typography variant='h6' className={classes.title}>
            {viewList.title}
          </Typography>
          <IconButton aria-label='close' className={classes.button} onClick={handleOnClose} id='close'>
            <CloseIcon />
          </IconButton>
        </div>
      </DialogTitle>

      <DialogContent id={`${id}-body`} dividers className={classes.content}>
        <div className={classes.search}>
          <SearchTextComponent id='search' onChange={handleSearchChange} placeholder={searchTextPlaceHolder} />
        </div>

        <Table
          id={`${id}-table`}
          TableContainerProps={{ style: { height: '100%', overflow: 'auto', position: 'relative' } }}
          TableProps={{ stickyHeader: true }}
          head={
            <TableRow id={`${id}-header`}>
              {columns.map(headCell => (
                <TableCell key={headCell.sortField} id={headCell.id}>
                  {/* <TableSortLabel
                    active={viewList.orderBy === headCell.sortField}
                    direction={viewList.order ? 'asc' : 'desc'}
                    onClick={handleOnSort(headCell.sortField)}
                  >
                    {headCell.name}
                  </TableSortLabel> */}
                  <TableSortLabel
                    field={headCell.sortField}
                    label={headCell.name}
                    direction={viewList.order ? 'asc' : 'desc'}
                    selected={viewList.orderBy}
                    onClick={handleOnSort(headCell.sortField)}
                  />
                  <TextField
                    onChange={event => {
                      const search = event.target.value
                      clearTimeout(debounce)
                      setDebounce(
                        setTimeout(() => {
                          handleColumnFiltering({
                            field: headCell.sortField,
                            value: search,
                            match: headCell.match
                          })
                        }, 500)
                      )
                    }}
                  />
                  {/* {(headCell.type && headCell.type === 'date') || headCell.match === 'date' ? (
                    <DatePicker
                      noMargin
                      id={`${headCell.field}`}
                      onChange={date => {
                        clearTimeout(debounce)
                        setDebounce(
                          setTimeout(() => {
                            handleColumnFiltering({
                              field: headCell.sortField,
                              value: date.toISOString(),
                              match: headCell.match
                            })
                          }, 500)
                        )
                      }}
                      value={null}
                    />
                  ) : (
                    <TextField
                      noMargin
                      onChange={event => {
                        const search = event.target.value
                        clearTimeout(debounce)
                        setDebounce(
                          setTimeout(() => {
                            handleColumnFiltering({
                              field: headCell.sortField,
                              value: search,
                              match: headCell.match
                            })
                          }, 500)
                        )
                      }}
                    />
                  )} */}
                </TableCell>
              ))}

              <TableCell align='center' />
            </TableRow>
          }
          body={
            fetching ? (
              <TableRow>
                <TableCell colSpan={columns.length}>
                  <LoadingIndicator loading={fetching} />
                </TableCell>
              </TableRow>
            ) : (
              body(viewList.data)
            )
          }
          count={viewList.count}
          page={viewList.page}
          limit={viewList.limit}
          rowsPerPageOptions={[10, 15, 20]}
          onChange={onViewListChange}
        />
      </DialogContent>
    </Dialog>
  )
}
