import React from 'react'
import TableSortLabel from '@material-ui/core/TableSortLabel'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

// Style
// import classes from './TableSortLabel.module.module.scss'

export default function _TableSortLabel (props) {
  // Variables
  const { field = '', label = '', selected = '', direction = 'asc', ...others } = props
  // Services
  // Events
  const { onClick = () => {} } = props
  // Callbacks
  // Local Variables

  return (
    // <TableSortLabel active={showArrow} direction={direction} onClick={onClick} {...others}>

    <React.Fragment>
      <TableSortLabel hideSortIcon onClick={onClick} {...others}>
        {label}
      </TableSortLabel>
      {selected === field ? (
        <FontAwesomeIcon style={{ marginLeft: '10px' }} icon={direction === 'asc' ? 'sort-up' : 'sort-down'} />
      ) : (
        <FontAwesomeIcon style={{ marginLeft: '10px' }} icon={'sort'} />
      )}
    </React.Fragment>
  )
}
