import React from 'react'
import Paper from '@material-ui/core/Paper'
import Typography from '@material-ui/core/Typography'

// Components
import BaseDetails from '../BaseDetails/BaseDetails'
import ClaimFooter from '../ClaimFooter/ClaimFooter'
import DatePicker from 'SharedComponents/DatePicker2/DatePicker2'
import DisapprovedLabel from '../DisapprovedLabel/DisapprovedLabel'
import DiscrepancyBase from '../DiscrepancyBase/DiscrepancyBase'
import HospitalItemList from '../HospitalItemList/HospitalItemList'
import ProcedureList from '../ProcedureList/ProcedureList'
import ProfessionalFeeList from '../ProfessionalFeeList/ProfessionalFeeList'
import RightPanelTabs from '../RightPanelTabs/RightPanelTabs'
import ClaimsRemarks from '../ClaimsRemarks/ClaimsRemarks'

// Services

// Utilities
import { CurrencyFormatter } from 'Utilities/TextFormatter'

// Styles
import classes from './ClaimClinicManagement.module.scss'

// Validator
import MainValidator from './../../Validators/Claim'

// Libraries
import moment from 'moment'

// Store
import { useSelector, useDispatch } from 'react-redux'
import { ClaimsAnalysis as actions } from 'Pages/Protected/Claims/Store/actions'
const { setClaimFields, setClaimBaseDetails, initSuggestions } = actions

export default function ClaimClinicManagement (props) {
  const dispatch = useDispatch()
  // Events
  const { onChangeFacility } = props
  // Variables
  const { claim, permissions, _type, grandTotal } = props
  // Services
  const { getClaimDetails, getReason } = props
  // Validation
  const { doValidateAll, doValidateProcedure, doValidateHospitalItem, doValidateProfessionalFee } = props

  const discrepancy = useSelector(({ ClaimsAnalysis: state }) => state.discrepancy)
  const error = useSelector(({ ClaimsAnalysis: state }) => state.error)
  const escalations = useSelector(({ ClaimsAnalysis: state }) => state.escalations)
  const inquiries = useSelector(({ ClaimsAnalysis: state }) => state.inquiries)
  const recon = useSelector(({ ClaimsAnalysis: state }) => state.recon)

  React.useEffect(() => {
    dispatch(initSuggestions())
    getReason()

    const _allowedFields = {
      contract_plan_type: '',
      client_account_name: '',
      client_account_number: '',
      date_from: new Date().toISOString(),
      date_to: new Date().toISOString(),
      claim_remarks: ''
    }

    const _lineItemFields = {
      procedures: [],
      hospital_items: [],
      professional_fees: []
    }

    setTimeout(() => {
      dispatch(setClaimFields({ ..._allowedFields, ..._lineItemFields }))
    }, 10)

    getClaimDetails(data => {
      dispatch(
        setClaimFields({
          contract_plan_type: data.contract_plan_type,
          client_account_name: data.client.account_name,
          client_account_number: data.client.account_number,
          date_from: new Date(data.date.from).toISOString(),
          date_to: new Date(data.date.to).toISOString(),
          procedures: data.procedures || [],
          hospital_items: data.hospital_items || [],
          professional_fees: data.professional_fees || [],
          claim_remarks: data.claim_remarks
        })
      )
    })
  }, [])

  return (
    <div className={classes.root}>
      {claim && (
        <React.Fragment>
          <DisapprovedLabel
            id='dp-failed-adjudication'
            value='Failed Adjudication'
            reasons={discrepancy.disapproved ? discrepancy.disapproved : []}
          />
          {permissions.base.read && (
            <Paper className={classes['first-container']}>
              <BaseDetails
                claim={claim}
                onChange={({ value, field }) => dispatch(setClaimBaseDetails({ value, field }))}
                permissions={permissions.base}
                isCM
              />
              <div className={classes.groups}>
                <div className={classes['input']}>
                  <DiscrepancyBase
                    id='dp-admission-date-from'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.date_from
                    }}
                  >
                    <DatePicker
                      id='admission-date-from'
                      required
                      label='Admission Date (From)'
                      value={claim.date_from}
                      onChange={date =>
                        dispatch(
                          setClaimBaseDetails({
                            value: date.toISOString(),
                            field: 'date_from'
                          })
                        )
                      }
                      error={error.date_from}
                      readOnly={!permissions.base.update}
                    />
                  </DiscrepancyBase>
                </div>
                <div className={classes['input']}>
                  <DiscrepancyBase
                    id='dp-admission-date-to'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.date_to
                    }}
                  >
                    <DatePicker
                      id='admission-date-to'
                      required
                      label='Admission Date (To)'
                      value={claim.date_to}
                      onChange={date =>
                        dispatch(
                          setClaimBaseDetails({
                            value: date.toISOString(),
                            field: 'date_to'
                          })
                        )
                      }
                      error={error.date_to}
                      readOnly={!permissions.base.update}
                    />
                  </DiscrepancyBase>
                </div>
              </div>
            </Paper>
          )}

          <div className={classes['tables-container']}>
            <div className={classes['left-side']} id='left-side-panel'>
              {permissions.procedures.read && (
                <ProcedureList
                  id='procedure'
                  data={claim.procedures}
                  doValidate={doValidateProcedure}
                  permissions={permissions.procedures}
                  // showAmountPaidByMember={false}
                  // showAmountNotCovered={false}
                  // showAmountDueFromMember={false}
                  // showPhilhealthField={false}
                  diagnosis={claim.diagnosis}
                />
              )}
              {permissions.hospital_items.read && (
                <HospitalItemList
                  id='hospital-item'
                  data={claim.hospital_items}
                  doValidate={doValidateHospitalItem}
                  permissions={permissions.hospital_items}
                  // showAmountPaidByMember={false}
                  // showAmountNotCovered={false}
                  // showAmountDueFromMember={false}
                  diagnosis={claim.diagnosis}
                />
              )}
              {permissions.professional_fees.read && (
                <ProfessionalFeeList
                  id='professional-fee'
                  data={claim.professional_fees}
                  doValidate={doValidateProfessionalFee}
                  permissions={permissions.professional_fees}
                  // showAmountNotCovered={false}
                  // showAmountDueFromMember={false}
                  diagnosis={claim.diagnosis}
                />
              )}
              <ClaimsRemarks
                data={claim.claim_remarks}
                permissions={!permissions.base.update}
                onChange={event =>
                  dispatch(
                    setClaimBaseDetails({
                      value: event.target.value,
                      field: 'claim_remarks'
                    })
                  )
                }
              />
            </div>
            <div className={classes['right-side']} id='right-side-panel'>
              <div className={classes['wrapper']}>
                <div className={classes['sticky']}>
                  <RightPanelTabs inquiries={inquiries} escalations={escalations} recon={recon} {...props} />
                </div>
              </div>
            </div>
          </div>
          <div className={classes['grand-total']}>
            <Typography variant='subtitle2'>TOTAL PHP </Typography>
            <Typography id={'grand-total'} variant='subtitle2'>
              {CurrencyFormatter(grandTotal)}
            </Typography>
          </div>
          <ClaimFooter
            {...props}
            // userManagementService={userManagementService}
            // getUsers={getUsers}
            // doEscalate={onEscalate}
            // doReassign={onReassign}
            // doViewAnalysisSheet={onViewAnalysisSheet}
            // doViewLetterOfAdvice={onViewLetterOfAdvice}
            // doReject={onReject}
            // doApprove={onApprove}
            // doSave={onSave}
            // doCancel={onCancel}
            // doRemove={onRemove}
            // doReturn={onReturn}
            doValidate={() => {
              const valid = doValidateAll({ validator: MainValidator })
              return valid
            }}
            permissions={permissions.footer}
            grandTotal={grandTotal}
            claimType={_type}
          />
        </React.Fragment>
      )}
    </div>
  )
}
