const reportsItems = {
  BATCH_ENCODER: ['utilizationLedger', 'transmittal', 'confinementReport'],
  BATCH_ENCODER_SUPERVISOR: [
    'utilizationLedger',
    'confinementReport',
    'transmittal',
    'paymentRequestReport',
    'returnedClaimsReport',
    'detailedClaimsReport'
  ],
  CLAIMS_DETAILS_ENCODER: ['utilizationLedger', 'confinementReport'],
  CLAIMS_DETAILS_ENCODER_SUPERVISOR: [
    'utilizationLedger',
    'paymentRequestReport',
    'returnedClaimsReport',
    'detailedClaimsReport'
  ],
  CLAIMS_ANALYST: ['utilizationLedger', 'confinementReport'],
  PR_OFFICER: ['utilizationLedger', 'confinementReport'],
  PR_OFFICER_SUPERVISOR: [
    'utilizationLedger',
    'loadingHimsData',
    'confinementReport',
    'reimbursementReport',
    'paymentRequestReport',
    'returnedClaimsReport',
    'detailedClaimsReport',
    'detailedClaimsReport'
  ],
  MEDICAL_CONSULTANT: ['utilizationLedger', 'confinementReport'],
  CLAIMS_RECON: [
    'utilizationLedger',
    'transmittal',
    'prReports',
    'treasuryTransmittalReport',
    'utilizationReport',
    'reimbursementReport',
    'confinementReport'
  ],
  CLAIMS_VERIFIER_SUPERVISOR: [
    'utilizationLedger',
    'confinementReport',
    'reimbursementReport',
    'paymentRequestReport',
    'productivityPerformaceReport',
    'claimsSummaryReport',
    'returnedClaimsReport',
    'performanceReport',
    'agingPartialApprovedReport',
    'agingReturnedClaims',
    'claimsBeyond3Months',
    'agingReimbursement',
    'detailedClaimsReport',
    'detailedPerformanceReport',
    'detailedMonitoringReport'
  ],
  CLAIMS_MANAGER: [
    'utilizationLedger',
    'confinementReport',
    'reimbursementReport',
    'paymentRequestReport',
    'productivityPerformaceReport',
    'claimsSummaryReport',
    'returnedClaimsReport',
    'performanceReport',
    'agingPartialApprovedReport',
    'agingReturnedClaims',
    'claimsBeyond3Months',
    'agingReimbursement',
    'detailedClaimsReport',
    'detailedPerformanceReport',
    'detailedMonitoringReport'
  ]
}
const generateBreadCrumbs = userRole => {
  let breads = {}
  for (const r of reportsItems[userRole]) {
    breads[r] = { text: reportPages[r].label, to: reportPages[r].to }
  }
  return breads
}

const generateSideBar = userRole => {
  return {
    label: 'Reports',
    icon: 'chart-bar',
    type: 'popover',
    child: (reportsItems[userRole] && reportsItems[userRole].map(r => reportPages[r])) || []
  }
}

const generateRoutes = userRole => {
  return reportsItems[userRole].map(r => (
    <Route key={reportPages[r].name} exact path={reportPages[r].to} component={reportPages[r].component} />
  ))
}

export { generateSideBar, generateBreadCrumbs, generateRoutes }
