import React from 'react'
import Dialog from '@material-ui/core/Dialog'
import DialogContent from '@material-ui/core/DialogContent'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import Slide from '@material-ui/core/Slide'
import Tooltip from '@material-ui/core/Tooltip'
import Typography from '@material-ui/core/Typography'

import { Document, Page, pdfjs } from 'react-pdf'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import printJS from 'print-js'

import SearchTextComponent from 'SharedComponents/SearchTextComponent/SearchTextComponent'
import BaseUrlFetcher from 'Services/BaseUrlFetcher'
import { fromBlob } from 'Services/Download'

// Store
import { useSelector, useDispatch } from 'react-redux'
import { setPdf } from 'Pages/Protected/Store/actions'

// Styles
import classes from './PdfViewer2.module.scss'

pdfjs.GlobalWorkerOptions.workerSrc = `https://cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`

const getMozillaPdfViewerUrl = () =>
  BaseUrlFetcher.getClaimsBaseUrl() + '/thirdparty/mozilla-pdfjs-2.4.456-es5/web/viewer.html'
// const mozillaPdfViewer = 'https://mozilla.github.io/pdf.js/web/viewer.html'

