import React from 'react'
import Paper from '@material-ui/core/Paper'
import { subscribe } from 'redux-subscriber'

// Components
import { HospitalItemTextFields, HospitalItemTextField } from '../HospitalItemTextField/HospitalItemTextField'
import TextField from 'SharedComponents/TextField/CustomTextField'
import TextFieldCurrency from 'SharedComponents/TextFieldCurrency/TextFieldCurrency'
import Button from '@material-ui/core/Button'
import Alert from 'SharedComponents/Alert/Alert'
import HospitalItemFormValidator from './HospitalItemFormValidator'
import DiagnosisValidator from './DiagnosisValidator'
import DropDown from 'SharedComponents/DropDown/DropDown'

// Styles
import classes from './HospitalItemForm.module.scss'

// Services
import ClaimsEncodingServices from 'Services/Api/Claims/ClaimsEncodingServices'

/// Store
import { useSelector, useDispatch } from 'react-redux'
import { ClaimsEncoding as actions } from 'Pages/Protected/Claims/Store/actions'

const {
  setHospitalItem,
  addClaimHospitalItem,
  updateClaimHospitalItem,
  removeClaimHospitalItem,
  resetSuggestion,
  setSuggestion,
  setError
} = actions

export default function HospitalItemForm (props) {
  const { showAmountPaidByMember = true, showPhilhealthRemarks = true, diagnosis, errors, doValidate } = props
  const dispatch = useDispatch()

  const [openDialog, setOpenDialog] = React.useState(false)

  const hospitalItem = useSelector(({ ClaimsEncoding: state }) => state.hospitalItem)
  const suggestions = useSelector(({ ClaimsEncoding: state }) => state.suggestions)

  const [debounce, setDebounce] = React.useState({
    hospitalItem: null
  })

  const [fetching, setFetching] = React.useState({
    description: false,
    code: false,
    name: false,
    item_code: false
  })

  React.useEffect(() => {
    const unsubscribe = subscribe('hospitalItem.show', state => {
      setOpenDialog(false)
    })
    return () => unsubscribe()
  }, [])

  React.useEffect(() => {
    if (diagnosis) {
      const { diagnosis_code, diagnosis_description } = hospitalItem.details
      if (!diagnosis.find(item => item.code === diagnosis_code && item.description === diagnosis_description)) {
        dispatch(setHospitalItem({ details: { diagnosis_code: '', diagnosis_description: '' } }))
      }
    }
  }, [diagnosis])

  const handleOnChangeHospitalItem = ({ value, field, search }) => {
    if (search) {
      dispatch(setHospitalItem({ details: { [field]: value } }))
      dispatch(resetSuggestion({ field: 'hospitalItem' }))
      getHospitalItemSuggestions({ value, by: field })
    } else {
      const _value = value || {}
      dispatch(
        setHospitalItem({
          details: {
            id: _value.id,
            name: _value.name,
            item_code: _value.item_code
          }
        })
      )
      dispatch(resetSuggestion({ field: 'hospitalItem' }))
    }
  }

  const handleOnChangeDiagnosis = ({ value, field }) => {
    const fieldArr = field.split('_')
    const _field = fieldArr.length === 2 ? fieldArr[1] : `${fieldArr[1]}_${fieldArr[2]}`
    const diagnosi = diagnosis.find(d => d[_field] === value)
    dispatch(
      setHospitalItem({
        details: {
          diagnosis_code: diagnosi.code,
          diagnosis_description: diagnosi.description,
          diagnosis_given_diagnosis: diagnosi.given_diagnosis
        }
      })
    )
  }

  const getHospitalItemSuggestions = ({ value = '', by }) => {
    if (value.trim().length < 3) return

    clearTimeout(debounce.hospitalItem)
    setFetching(state => ({ ...state, [by]: true }))
    setDebounce(state => ({
      ...state,
      hospitalItem: setTimeout(() => {
        if (value) {
          ClaimsEncodingServices.lookups({
            on: 'hospital-item',
            params: { query: value, by }
          })
            .then(res => {
              const { data } = res.data
              dispatch(
                setSuggestion({
                  field: 'hospitalItem',
                  suggestion: data
                })
              )
            })
            .finally(() => {
              setFetching(state => ({ ...state, [by]: false }))
            })
        }
      }, 500)
    }))
  }

  const handleOnChangeHospitalForm = args => {
    const { value, field } = args
    dispatch(setHospitalItem({ details: { [field]: value } }))
  }

  const handleOnSaveHospitalItem = () => {
    dispatch(setError({ hospital_item: { index: null, details: {} } }))
    const _errors = doValidate({ data: hospitalItem.details })
    dispatch(
      setError({
        hospital_item: { ..._errors }
      })
    )
    if (Object.keys(_errors).length) return

    dispatch(addClaimHospitalItem(hospitalItem.details))
    dispatch(setHospitalItem({ show: false }))
  }

  const handleOnUpdateHospitalItem = index => {
    dispatch(setError({ hospital_item: { index: null, details: {} } }))
    const _errors = doValidate({ data: hospitalItem.details })
    dispatch(
      setError({
        hospital_item: { ..._errors }
      })
    )
    if (Object.keys(_errors).length) return

    dispatch(updateClaimHospitalItem({ index, details: hospitalItem.details }))
    dispatch(setHospitalItem({ show: false }))
  }

  const handleOnRemoveHospitalItem = index => {
    dispatch(setError({ hospital_item: { index: null, details: {} } }))
    dispatch(removeClaimHospitalItem({ index }))
    dispatch(setHospitalItem({ show: false }))
    setOpenDialog(false)
  }

  return (
    hospitalItem.show && (
      <React.Fragment>
        <Paper className={classes.root}>
          {diagnosis && (
            <React.Fragment>
              <div className={classes.groups}>
                <div className={classes.input}>
                  <DropDown
                    id='given-diagnosis'
                    label='Given Diagnosis'
                    required
                    items={diagnosis.map(({ given_diagnosis }) => ({
                      text: given_diagnosis,
                      value: given_diagnosis
                    }))}
                    value={hospitalItem.details.diagnosis_given_diagnosis}
                    onChange={event =>
                      handleOnChangeDiagnosis({
                        field: 'diagnosis_given_diagnosis',
                        value: event.target.value
                      })
                    }
                    error={errors.diagnosis_given_diagnosis}
                  />
                </div>
              </div>

              <div className={classes.groups}>
                <div className={classes.input}>
                  <DropDown
                    id='icd10-description'
                    required
                    label='ICD10 Description'
                    items={diagnosis.map(({ description }) => ({ text: description, value: description }))}
                    value={hospitalItem.details.diagnosis_description}
                    onChange={event =>
                      handleOnChangeDiagnosis({
                        field: 'diagnosis_description',
                        value: event.target.value
                      })
                    }
                    error={errors.diagnosis_description}
                  />
                </div>
                <div className={classes.input}>
                  <DropDown
                    id='icd10-code'
                    required
                    label='ICD10 Code'
                    items={diagnosis.map(({ code }) => ({ text: code, value: code }))}
                    value={hospitalItem.details.diagnosis_code}
                    onChange={event =>
                      handleOnChangeDiagnosis({
                        field: 'diagnosis_code',
                        value: event.target.value
                      })
                    }
                    error={errors.diagnosis_code}
                  />
                </div>
              </div>
            </React.Fragment>
          )}
          <HospitalItemTextFields>
            <HospitalItemTextField
              id='hospital-item-name'
              codeId={`hospital-item-code`}
              name={hospitalItem.details.name || ''}
              item_code={hospitalItem.details.item_code || ''}
              suggestions={suggestions.hospitalItem}
              onChangeName={(value, search) => handleOnChangeHospitalItem({ field: 'name', value, search })}
              onChangeCode={(value, search) => handleOnChangeHospitalItem({ field: 'item_code', value, search })}
              fetching={fetching}
              errors={errors}
            />
          </HospitalItemTextFields>

          <div className={classes.groups}>
            <div className={classes.input}>
              <TextFieldCurrency
                id='hospital-item-gross-amount'
                label='Gross Amount'
                value={hospitalItem.details.gross_amount}
                onChange={event =>
                  handleOnChangeHospitalForm({
                    field: 'gross_amount',
                    value: event.target.value
                  })
                }
                error={errors.gross_amount}
              />
            </div>

            <div className={classes.input}>
              <TextFieldCurrency
                id='hospital-item-vat-amount'
                label='VAT Amount'
                value={hospitalItem.details.vat_amount}
                onChange={event =>
                  handleOnChangeHospitalForm({
                    field: 'vat_amount',
                    value: event.target.value
                  })
                }
                error={errors.vat_amount}
              />
            </div>
            {showAmountPaidByMember && (
              <div className={classes.input}>
                <TextFieldCurrency
                  id='hospital-item-amount-paid-by-member'
                  label='Amount Paid by Member'
                  value={hospitalItem.details.amount_paid_by_member}
                  onChange={event =>
                    handleOnChangeHospitalForm({
                      field: 'amount_paid_by_member',
                      value: event.target.value
                    })
                  }
                  error={errors.amount_paid_by_member}
                />
              </div>
            )}
          </div>

          <div className={classes.groups}>
            <div className={classes.input}>
              <TextFieldCurrency
                id='philhealth-amount-paid'
                label='PhilHealth Amount Paid'
                value={hospitalItem.details.philhealth_amount_paid}
                onChange={event =>
                  handleOnChangeHospitalForm({
                    field: 'philhealth_amount_paid',
                    value: event.target.value
                  })
                }
                error={errors.philhealth_amount_paid}
              />
            </div>
            {showPhilhealthRemarks && (
              <div className={classes.input}>
                <TextField
                  multiline
                  rows={3}
                  id='philhealth-remarks'
                  label='PhilHealth Remarks'
                  value={hospitalItem.details.philhealth_remarks}
                  onChange={event =>
                    handleOnChangeHospitalForm({
                      field: 'philhealth_remarks',
                      value: event.target.value
                    })
                  }
                />
              </div>
            )}
          </div>

          <div className={`${classes.groups} ${classes.buttons}`}>
            {hospitalItem.mode === 'add' && (
              <React.Fragment>
                <Button
                  id='hospital-save-button'
                  variant='contained'
                  color='primary'
                  className='button-positive'
                  onClick={handleOnSaveHospitalItem}
                >
                  Save
                </Button>
                <Button
                  id='hospital-cancel-button'
                  variant='contained'
                  color='secondary'
                  onClick={() => dispatch(setHospitalItem({ show: false }))}
                >
                  Cancel
                </Button>
              </React.Fragment>
            )}

            {hospitalItem.mode !== 'add' && (
              <React.Fragment>
                <Button
                  id='hospital-update-button'
                  variant='contained'
                  color='primary'
                  className='button-positive'
                  onClick={() => handleOnUpdateHospitalItem(hospitalItem.index)}
                >
                  Update
                </Button>
                <Button
                  id='hospital-delete-button'
                  variant='contained'
                  color='secondary'
                  onClick={() => setOpenDialog(true)}
                >
                  Delete
                </Button>
              </React.Fragment>
            )}
          </div>
        </Paper>
        <Alert
          open={openDialog}
          message='Are you sure you want to delete this hospital item?'
          cancelLabel='No'
          okLabel='Yes'
          onCancel={() => setOpenDialog(false)}
          onOk={() => handleOnRemoveHospitalItem(hospitalItem.index)}
        />
      </React.Fragment>
    )
  )
}
