import React from 'react'
import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography'

// Styles
import classes from './ButtonFooter.module.scss'

// Services
import UserManagementServices from 'Services/Api/UserManagementServices'

// lodash
import { sortBy } from 'lodash'

// Component
import AssignDialog from './../AssignDialog/AssignDialog'

export default function ClaimFooter (props) {
  const {
    handleOnSubmit = () => {},
    handleOnCancel = () => {},
    handleOnClose = () => {},
    selectStatus,
    claimsCount = 0,
    _id = ''
  } = props

  const [dialog, setDialog] = React.useState({
    assign: false,
    alert: false
  })

  const [dropDownUsers, setDropDownUsers] = React.useState([])

  const initDropdownUsers = async ({ roles }) => {
    UserManagementServices.getUsersByRole(roles).then(({ data = [] }) => {
      let users = data
        .map(user => ({ text: `${user.first_name} ${user.last_name}`, value: user._id }))
        .filter(i => i.value !== _id)

      setDropDownUsers(sortBy(users, ['text']))
    })
  }

  return (
    <div className={classes.root}>
      <div className={classes['counter']}>
        {selectStatus ? <Typography>Total Claims Selected: {claimsCount} </Typography> : ''}
      </div>
      <div className={classes['buttons']}>
        {!selectStatus ? (
          <React.Fragment>
            <Button
              variant='contained'
              color='primary'
              id='btn-close'
              className='button-negative'
              onClick={() => handleOnClose()}
            >
              Close
            </Button>
          </React.Fragment>
        ) : (
          <React.Fragment>
            <div>
              <Button
                variant='contained'
                color='primary'
                id='save-for-now'
                className='button-positive'
                disabled={claimsCount < 1}
                onClick={() => {
                  initDropdownUsers({ roles: 'CLAIMS_ANALYST' })
                  setDialog(state => ({ ...state, assign: true }))
                }}
              >
                Assign
              </Button>
              <Button
                variant='contained'
                color='primary'
                id='save-for-now'
                className='button-negative'
                onClick={() => handleOnCancel()}
              >
                Cancel
              </Button>
            </div>
          </React.Fragment>
        )}
      </div>
      <AssignDialog dialog={dialog} setDialog={setDialog} handleOnSubmit={handleOnSubmit} dropDownUsers={dropDownUsers} />
    </div>
  )
}
