import React from 'react'
import CircularProgress from '@material-ui/core/CircularProgress'

// Style
import classes from './LoadingIndicator.module.scss'

export default function LoadingIndicator (props) {
  // Variables
  const { type = 'replace', loading = false, children, style } = props
  // Services
  // Events
  // Callbacks
  // Local Variables

  return (
    <React.Fragment>
      {type === 'replace' ? (
        loading ? (
          <div className={classes.loading} id='loading-overlay'>
            <CircularProgress id='fetching-animation' className={classes.animation} size={50} />
          </div>
        ) : (
          children
        )
      ) : (
        type === 'overlay' && (
          <React.Fragment>
            {loading && (
              <div className={classes.overlay} id='loading-overlay'>
                <CircularProgress id='fetching-animation' className={classes.animation} size={50} />
              </div>
            )}
            {children}
          </React.Fragment>
        )
      )}
    </React.Fragment>
  )
}
