import React from 'react'
import Paper from '@material-ui/core/Paper'
import Typography from '@material-ui/core/Typography'

// Components
import BaseDetails from '../BaseDetails/BaseDetails'
import DiagnosisList from '../DiagnosisList/DiagnosisList'
import TextField from 'SharedComponents/TextField/CustomTextField'
import DatePicker from 'SharedComponents/DatePicker2/DatePicker2'
import DropDown from 'SharedComponents/DropDown/DropDown'
import AutoComplete from 'SharedComponents/AutoComplete2/AutoComplete2'
import ProcedureList from '../ProcedureList/ProcedureList'
import ProfessionalFeeList from '../ProfessionalFeeList/ProfessionalFeeList'
import ClaimFooter from '../ClaimFooter/ClaimFooter'
import DiscrepancyBase from '../DiscrepancyBase/DiscrepancyBase'
import DisapprovedLabel from '../DisapprovedLabel/DisapprovedLabel'
import RightPanelTabs from '../RightPanelTabs/RightPanelTabs'
import ClaimsRemarks from '../ClaimsRemarks/ClaimsRemarks'

// Services
import ClaimsAdjudicationServices from 'Services/Api/Claims/ClaimsAdjudicationServices'

// Utilities
import { CurrencyFormatter } from 'Utilities/TextFormatter'

// Styles
import classes from './ClaimOutPatientProcedure.module.scss'

// Validator
import MainValidator from './../../Validators/Claim'

// Libraries
import moment from 'moment'

// Store
import { useSelector, useDispatch } from 'react-redux'
import { ClaimsAnalysis as actions } from 'Pages/Protected/Claims/Store/actions'
const { setClaimFields, setClaimBaseDetails, initSuggestions } = actions

export default function ClaimOutPatientProcedure (props) {
  const dispatch = useDispatch()
  // Events
  const { onChangeFacility, onChangeDoctor, onChangeAnaesthesiologist } = props
  // Variables
  const { claim, permissions, _type, grandTotal } = props
  const { fetching } = props
  // Services
  const { getClaimDetails, getReason, getSubTypes } = props
  // Validation
  const { doValidateAll, doValidateProcedure, doValidateHospitalItem, doValidateProfessionalFee } = props

  const suggestions = useSelector(({ ClaimsAnalysis: state }) => state.suggestions)
  const discrepancy = useSelector(({ ClaimsAnalysis: state }) => state.discrepancy)
  const error = useSelector(({ ClaimsAnalysis: state }) => state.error)
  const escalations = useSelector(({ ClaimsAnalysis: state }) => state.escalations)
  const inquiries = useSelector(({ ClaimsAnalysis: state }) => state.inquiries)
  const recon = useSelector(({ ClaimsAnalysis: state }) => state.recon)
  const dropDown = useSelector(({ ClaimsAnalysis: state }) => state.dropDown)

  React.useEffect(() => {
    dispatch(initSuggestions())
    getReason()
    getSubTypes()

    const _allowedFields = {
      approval_code: '',
      contract_plan_type: '',
      loa_number: '',
      member_account_number: '',
      member_first_name: '',
      member_middle_name: '',
      member_last_name: '',
      facility_id: '',
      facility_name: '',
      doctor_id: '',
      doctor_name: '',
      date_from: new Date().toISOString(),
      date_to: new Date().toISOString(),
      department: '',
      anesthesiologist_id: '',
      anesthesiologist_name: '',
      claim_sub_type: '',
      claim_sub_sub_type: '',
      with_corporate_guarantee: '',
      diagnosis: [],
      loa_notes: '',
      claim_remarks: ''
    }
    const _lineItemFields = {
      procedures: [],
      professional_fees: []
    }

    setTimeout(() => {
      dispatch(setClaimFields({ ..._allowedFields, ..._lineItemFields }))
    }, 10)

    getClaimDetails(data => {
      dispatch(
        setClaimFields({
          approval_code: data.approval_code,
          contract_plan_type: data.contract_plan_type,
          loa_number: data.loa_number,
          member_account_number: data.member.account_number,
          member_first_name: data.member.first_name,
          member_middle_name: data.member.middle_name,
          member_last_name: data.member.last_name,
          facility_id: data.facility.id,
          facility_name: data.facility.name,
          doctor_id: data.doctor.id,
          doctor_name: data.doctor.name,
          date_from: data.date.from,
          date_to: data.date.to,
          department: data.department,
          anesthesiologist_id: data.anesthesiologist.id,
          anesthesiologist_name: data.anesthesiologist.name,
          claim_sub_type: data.claim_sub.type,
          claim_sub_sub_type: data.claim_sub.sub_type,
          with_corporate_guarantee: data.with_corporate_guarantee,
          loa_notes: data.loa_notes,
          diagnosis: data.diagnosis || [],
          procedures: data.procedures || [],
          professional_fees: data.professional_fees || [],
          claim_remarks: data.claim_remarks
        })
      )
    })
  }, [])

  const handleOnChangeFacility = args => {
    onChangeFacility(args)
  }

  return (
    <div className={classes.root}>
      {claim && (
        <React.Fragment>
          <DisapprovedLabel
            id='dp-failed-adjudication'
            value='Failed Adjudication'
            reasons={discrepancy.disapproved ? discrepancy.disapproved : []}
          />
          {permissions.base.read && (
            <Paper className={classes['first-container']}>
              <BaseDetails
                claim={claim}
                onChange={({ value, field }) => dispatch(setClaimBaseDetails({ value, field }))}
                permissions={permissions.base}
              />

              <div className={classes.groups}>
                <div className={classes['input']}>
                  <DiscrepancyBase
                    id='dp-medical-partner-id'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.facility_id
                    }}
                  >
                    <AutoComplete
                      id='medical-partner-id'
                      required
                      label='Medical Partner ID'
                      value={claim.facility_id}
                      suggestions={suggestions.facility}
                      field={'id'}
                      loading={fetching['facility_id']}
                      onChange={(value, search) =>
                        handleOnChangeFacility({
                          field: 'facility_id',
                          value,
                          search
                        })
                      }
                      error={error.facility_id}
                      readOnly={!permissions.base.update}
                    />
                  </DiscrepancyBase>
                </div>

                <div className={classes['input']}>
                  <DiscrepancyBase
                    id='dp-medical-partner-facility-name'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.facility_name
                    }}
                  >
                    <AutoComplete
                      id='medical-partner-facility-name'
                      required
                      label='Medical Partner (Facility) Name'
                      value={claim.facility_name}
                      suggestions={suggestions.facility}
                      field={'name'}
                      loading={fetching['facility_name']}
                      onChange={(value, search) =>
                        handleOnChangeFacility({
                          field: 'facility_name',
                          value,
                          search
                        })
                      }
                      error={error.facility_name}
                      readOnly={!permissions.base.update}
                    />
                  </DiscrepancyBase>
                </div>

                <div className={classes['input']}>
                  <DiscrepancyBase
                    id='dp-doctor-id'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.doctor_id
                    }}
                  >
                    <AutoComplete
                      id='doctor-id'
                      required
                      label='Medical Partner ID'
                      value={claim.doctor_id}
                      suggestions={suggestions.doctor}
                      field={'id'}
                      loading={fetching['doctor_id']}
                      onChange={(value, search) =>
                        onChangeDoctor({
                          field: 'doctor_id',
                          value,
                          search
                        })
                      }
                      error={error.doctor_id}
                      readOnly={!permissions.base.update}
                    />
                  </DiscrepancyBase>
                </div>

                <div className={classes['input']}>
                  <DiscrepancyBase
                    id='dp-doctor-name'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.doctor_name
                    }}
                  >
                    <AutoComplete
                      id='doctor-name'
                      required
                      label='Medical Partner (Doctor) Name'
                      value={claim.doctor_name}
                      suggestions={suggestions.doctor}
                      field={'full_name'}
                      loading={fetching['doctor_name']}
                      onChange={(value, search) =>
                        onChangeDoctor({
                          field: 'doctor_name',
                          value,
                          search
                        })
                      }
                      error={error.doctor_name}
                      readOnly={!permissions.base.update}
                    />
                  </DiscrepancyBase>
                </div>
              </div>

              <div className={classes.groups}>
                <div className={classes['input']}>
                  <DiscrepancyBase
                    id='dp-date-of-service'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.date_to
                    }}
                  >
                    <DatePicker
                      id='date-of-service'
                      required
                      label='Date of Service'
                      value={claim.date_to}
                      onChange={date => {
                        dispatch(
                          setClaimBaseDetails({
                            value: date.toISOString(),
                            field: 'date_to'
                          })
                        )
                        dispatch(
                          setClaimBaseDetails({
                            value: date.toISOString(),
                            field: 'date_from'
                          })
                        )
                      }}
                      error={error.date_to}
                      readOnly={!permissions.base.update}
                    />
                  </DiscrepancyBase>
                </div>

                <div className={classes['input']}>
                  <DiscrepancyBase
                    id='dp-department'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.department
                    }}
                  >
                    <TextField
                      id='department'
                      label='Department'
                      value={claim.department}
                      onChange={event =>
                        dispatch(
                          setClaimBaseDetails({
                            value: event.target.value,
                            field: 'department'
                          })
                        )
                      }
                      error={error.department}
                      readOnly={!permissions.base.update}
                    />
                  </DiscrepancyBase>
                </div>

                <div className={classes['input']}>
                  <DiscrepancyBase
                    id='dp-anesthesiologist-id'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.anesthesiologist_id
                    }}
                  >
                    <AutoComplete
                      id='anesthesiologist-id'
                      label='Medical Partner ID (Anesthesiologist)'
                      value={claim.anesthesiologist_id}
                      suggestions={suggestions.anesthesiologist}
                      field={'id'}
                      loading={fetching['anesthesiologist_id']}
                      onChange={(value, search) =>
                        onChangeAnaesthesiologist({
                          value,
                          field: 'anesthesiologist_id',
                          search
                        })
                      }
                      error={error.anesthesiologist_id}
                      readOnly={!permissions.base.update}
                    />
                  </DiscrepancyBase>
                </div>

                <div className={classes['input']}>
                  <DiscrepancyBase
                    id='dp-anesthesiologist-name'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.anesthesiologist_name
                    }}
                  >
                    <AutoComplete
                      id='anesthesiologist-name'
                      label='Medical Partner (Anesthesiologist) Name'
                      value={claim.anesthesiologist_name}
                      suggestions={suggestions.anesthesiologist}
                      field={'full_name'}
                      loading={fetching['anesthesiologist_name']}
                      onChange={(value, search) =>
                        onChangeAnaesthesiologist({
                          value,
                          field: 'anesthesiologist_name',
                          search
                        })
                      }
                      error={error.anesthesiologist_name}
                      readOnly={!permissions.base.update}
                    />
                  </DiscrepancyBase>
                </div>
              </div>

              <div className={classes.groups}>
                <div className={classes['input']}>
                  <DiscrepancyBase
                    id='dp-claims-sub-type'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.claim_sub_type
                    }}
                  >
                    <DropDown
                      id='claims-sub-type'
                      label='Claim Sub Type'
                      items={dropDown.sub_types}
                      value={claim.claim_sub_type}
                      onChange={event =>
                        dispatch(
                          setClaimBaseDetails({
                            value: event.target.value,
                            field: 'claim_sub_type'
                          })
                        )
                      }
                      error={error.claim_sub_type}
                      readOnly={!permissions.base.update}
                    />
                  </DiscrepancyBase>
                </div>

                <div className={classes['input']}>
                  <DiscrepancyBase
                    id='dp-claims-sub-sub-type'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.claim_sub_sub_type
                    }}
                  >
                    <DropDown
                      id='claims-sub-sub-type'
                      label='Claim Sub-subType'
                      items={dropDown.sub_sub_types}
                      value={claim.claim_sub_sub_type}
                      onChange={event =>
                        dispatch(
                          setClaimBaseDetails({
                            value: event.target.value,
                            field: 'claim_sub_sub_type'
                          })
                        )
                      }
                      error={error.claim_sub_sub_type}
                      readOnly={!permissions.base.update}
                    />
                  </DiscrepancyBase>
                </div>
                <div className={classes['input']}>
                  <DiscrepancyBase
                    id='dp-with-corporate-guaranty'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.with_corporate_guarantee
                    }}
                  >
                    <TextField
                      id='with-corporate-guaranty'
                      label='With Corporate Guaranty?'
                      value={claim.with_corporate_guarantee}
                      onChange={event =>
                        dispatch(
                          setClaimBaseDetails({
                            value: event.target.value,
                            field: 'with_corporate_guarantee'
                          })
                        )
                      }
                      error={error.with_corporate_guarantee}
                      readOnly
                    />
                  </DiscrepancyBase>
                </div>
              </div>
              <div className={classes['child-container']}>
                <div className={classes['left-side']}>
                  {permissions.diagnosis.read && (
                    <DiagnosisList
                      id='diagnosis-list'
                      permissions={permissions.diagnosis}
                      items={claim.diagnosis}
                      error={error.diagnosis}
                    />
                  )}
                </div>
                <div className={classes['right-side']}>
                  <DiscrepancyBase
                    id='dp-loa-notes'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.loa_notes
                    }}
                  >
                    <TextField
                      constainerStyle={{ width: 'unset', flex: 1, marginTop: '20px' }}
                      multiline
                      rows={3}
                      id='loa-notes'
                      label='LOA Notes'
                      value={claim.loa_notes}
                      onChange={event =>
                        dispatch(
                          setClaimBaseDetails({
                            value: event.target.value,
                            field: 'loa_notes'
                          })
                        )
                      }
                      error={error.loa_notes}
                      readOnly
                    />
                  </DiscrepancyBase>
                </div>
              </div>
            </Paper>
          )}

          <div className={classes['tables-container']}>
            <div className={classes['left-side']} id='left-side-panel'>
              {permissions.procedures.read && (
                <ProcedureList
                  id='procedure'
                  data={claim.procedures}
                  doValidate={doValidateProcedure}
                  permissions={permissions.procedures}
                  diagnosis={claim.diagnosis}
                />
              )}
              {permissions.professional_fees.read && (
                <ProfessionalFeeList
                  id='professional-fee'
                  data={claim.professional_fees}
                  doValidate={doValidateProfessionalFee}
                  permissions={permissions.professional_fees}
                  diagnosis={claim.diagnosis}
                />
              )}
              <ClaimsRemarks
                data={claim.claim_remarks}
                permissions={!permissions.base.update}
                onChange={event =>
                  dispatch(
                    setClaimBaseDetails({
                      value: event.target.value,
                      field: 'claim_remarks'
                    })
                  )
                }
              />
            </div>
            <div className={classes['right-side']} id='right-side-panel'>
              <div className={classes['wrapper']}>
                <div className={classes['sticky']}>
                  <RightPanelTabs inquiries={inquiries} escalations={escalations} recon={recon} {...props} />
                </div>
              </div>
            </div>
          </div>
          <div className={classes['grand-total']}>
            <Typography variant='subtitle2'>TOTAL PHP </Typography>
            <Typography id={'grand-total'} variant='subtitle2'>
              {CurrencyFormatter(grandTotal)}
            </Typography>
          </div>
          <ClaimFooter
            {...props}
            // userManagementService={userManagementService}
            // getUsers={getUsers}
            // doEscalate={onEscalate}
            // doReassign={onReassign}
            // doViewAnalysisSheet={onViewAnalysisSheet}
            // doViewLetterOfAdvice={onViewLetterOfAdvice}
            // doReject={onReject}
            // doApprove={onApprove}
            // doSave={onSave}
            // doCancel={onCancel}
            // doRemove={onRemove}
            // doReturn={onReturn}
            doValidate={() => {
              const valid = doValidateAll({ validator: MainValidator })
              return valid
            }}
            permissions={permissions.footer}
            grandTotal={grandTotal}
            claimType={_type}
          />
        </React.Fragment>
      )}
    </div>
  )
}
