import constants from './../../contants'

const initialValue = {
  note: ''
}

const returnReducer = (state = initialValue, action) => {
  if (action.for !== constants.for.CLAIMS_ANALYSIS) return state

  switch (action.type) {
    case 'RESET_RETURN_NOTES':
      return initialValue

    case 'SET_RETURN_NOTES':
      return (() => {
        return {
          ...state,
          note: action.payload
        }
      })()

    default:
      return state
  }
}

export default returnReducer
