import React from 'react'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import startCase from 'lodash/startCase'

// Components
import DashboardTile from 'SharedComponents/DashboardTileComponent/DashboardTileComponent'
import ViewList2 from './../ViewList2/ViewList'
import LoadingIndicator from 'SharedComponents/LoadingIndicator/LoadingIndicator'

// Utilities
import { DateFormatter } from 'Utilities/TextFormatter'

const columns = [
  { id: 'claim-number', name: ' Claim Number', sortField: 'claim_number', match: 'start' },
  { id: 'due-date', name: ' Due Date', sortField: 'date_due', match: 'date' },
  { id: 'facility-name', name: ' Medical Partner Name (Facility)', sortField: 'facility.name', match: 'any' }
]

const MyClaimsTile = props => {
  const { services, sizes = { sm: 12 } } = props
  const [claims, setClaims] = React.useState({
    my: { count: 0, data: [] }
  })

  const [fetching, setFetching] = React.useState(false)
  const [fetchingViewList, setFetchingViewList] = React.useState(false)

  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    data: [],
    filter: {},
    orderBy: 'date_due',
    order: true
  }

  const [viewList, setViewList] = React.useState(initialValue)

  React.useEffect(() => {
    setFetching(true)
    Promise.all([
      services.getClaims({
        category: 'my',
        limit: 10,
        sort: 'date_due|desc',
        range: 'this-week'
      }),
      services.getClaimCount({ category: 'my' })
    ])
      .then(values => {
        const [batchResponse, batchCountResponse] = values
        const openBatches = batchResponse.data.data.results
        const openBatchCount = batchCountResponse.data.data
        setClaims(prevState => ({ ...prevState, my: { data: openBatches, count: openBatchCount } }))
      })
      .finally(values => {
        setFetching(false)
      })
  }, [])

  const handleFilterChange = category => value => {
    // Not yet working, but change of filter will not show error.

    setFetching(true)
    services
      .getClaims({ category, range: value, limit: 10, sort: 'date_due|desc' })
      .then(({ data: res }) => {
        const { data } = res
        setClaims(prevState => ({ ...prevState, [category]: { ...prevState[category], data: data.results } }))
      })
      .finally(values => {
        setFetching(false)
      })
  }

  const handleOnShowViewList = category => () => {
    let title = startCase(`${category} Claims`)
    if (category === 'open') title = 'For Adjudication'

    setViewList({ ...initialValue, show: true, title, category })
    setFetchingViewList(true)
    services
      .getClaims({ category, limit: 10, sort: 'date_due|desc' })
      .then(({ data: res }) => {
        const { results, count, limit, page } = res.data
        setViewList(state => ({ ...state, data: results, count, limit, page }))
      })
      .finally(values => {
        setFetchingViewList(false)
      })
  }

  const handleOnViewListChange = params => {
    const _params = {
      category: viewList.category,
      limit: viewList.limit,
      page: viewList.page,
      search: viewList.search,
      sort: `${viewList.orderBy}|${viewList.order ? 'desc' : 'asc'}`,
      filter: viewList.filter,
      ...params
    }

    setFetchingViewList(true)
    services
      .getClaims(_params)
      .then(({ data: res }) => {
        const { results, count, limit, page } = res.data
        setViewList(state => ({ ...state, data: results, count, limit, page }))
      })
      .finally(values => {
        setFetchingViewList(false)
      })
  }

  return (
    <React.Fragment>
      <DashboardTile
        id='my-claims'
        count={{ color: 3, value: claims.my.count, id: 'my-claims-count' }}
        sizes={sizes}
        title='My Claims'
        viewList={
          <Typography
            id='mc-view-list'
            variant='caption'
            className='bold text-link'
            onClick={handleOnShowViewList('my')}
          >
            View List
          </Typography>
        }
        onFilterChange={handleFilterChange('my')}
      >
        <LoadingIndicator loading={fetching}>
          <Table stickyHeader size='small'>
            <TableHead>
              <TableRow>
                <TableCell id='mc-claim-no'>Claim No.</TableCell>
                <TableCell id='mc-due-date'>Due Date</TableCell>
                <TableCell id='mc-facility-name'>Facility Name</TableCell>
                <TableCell />
              </TableRow>
            </TableHead>
            <TableBody>
              {claims.my.data.map((item, index) => (
                <TableRow key={index}>
                  <TableCell id={`mc-claim-no-${index}`}>{item.claim_number}</TableCell>
                  <TableCell id={`mc-due-date-${index}`}>{DateFormatter(item.date_due)}</TableCell>
                  <TableCell id={`mc-facility-name${index}`}>{item.facility.name}</TableCell>
                  <TableCell>
                    {/* { item.claim_number.startsWith('IP') && */}
                    <Link to={`/claims/${item.claim_number}/analyze`}>
                      <Typography id={`update-claim-${index}`} variant='caption' className='text-link underline'>
                        Update
                      </Typography>
                    </Link>
                    {/* } */}
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </LoadingIndicator>
      </DashboardTile>

      <ViewList2
        id='my-claims'
        columns={columns}
        searchTextPlaceHolder='Search for Claim Number, Due Date or Facility Name'
        viewList={viewList}
        setViewList={setViewList}
        onViewListChange={handleOnViewListChange}
        fetching={fetchingViewList}
        body={_data =>
          _data.map((item, index) => (
            <TableRow key={item.claim_number}>
              <TableCell id={`claim-number-${index}`}>{item.claim_number}</TableCell>
              <TableCell id={`due-date-${index}`}>{DateFormatter(item.date_due)}</TableCell>
              <TableCell id={`facility-name=${index}`}>{item.facility.name}</TableCell>
              <TableCell id={`update-cell-${index}`}>
                <Link to={`/claims/${item.claim_number}/analyze`}>
                  <Typography variant='caption' className='text-link underline' id={`update-claim-${index}`}>
                    Update
                  </Typography>
                </Link>
              </TableCell>
            </TableRow>
          ))
        }
      />
    </React.Fragment>
  )
}

MyClaimsTile.displayName = 'DashboardTileComponent'

export default MyClaimsTile
