import React from 'react'
import Button from '@material-ui/core/Button'

// Components
import BatchDetails from '../../ClaimsDetailsEncoder/BatchPage/Components/BatchDetails/BatchDetails'
import ClaimListHeader from '../../ClaimsDetailsEncoder/BatchPage/Components/ClaimListHeader/ClaimListHeader'
import ClaimList from '../../ClaimsDetailsEncoder/BatchPage/Components/ClaimList/ClaimList'
// import AssignDialiog from './../'
import AssignDialog from './Components/AssignDialog/AssignDialog'
// import ProviderSchedule from './Components/ProviderSchedule/ProviderSchedule'

// import Alert from 'SharedComponents/Alert/Alert'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'

// Utilities
import { SafeAccess } from 'Utilities/SafeAccess'

// Services
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'

// Styles
import classes from './BatchDetailsPage.module.scss'

export default function BatchDetailsPage (props) {
  const { ApiService, UserManagementApi } = props
  const [details, setDetails] = React.useState()
  const [batch_number, setBatchNumber] = React.useState(null)
  const [bundle_number, setBundleNumber] = React.useState(null)
  const [claimCount, setClaimCount] = React.useState(0)
  const [claims, setClaim] = React.useState([])
  const [forOverride, setForOverride] = React.useState({ claim_numbers: [] })
  const [selected, setSelected] = React.useState([])
  const [selectStatus, setSelectStatus] = React.useState(false)
  const [dialog, setDialog] = React.useState({
    assign: false,
    alert: false
  })
  const [dropDownUsers, setDropDownUsers] = React.useState([])
  const [debounce, setDebounce] = React.useState(null)
  const [pagination, setPagination] = React.useState({
    search: '',
    count: 0,
    rowsPerPage: 5,
    page: 0,
    rowsPerPageOptions: [5, 10, 15],
    onChangePage: (event, newPage) => {
      setPagination(state => ({ ...state, page: newPage }))
    },
    onChangeRowsPerPage: event => {
      setPagination(state => ({ ...state, rowsPerPage: parseInt(event.target.value, 10), page: 0 }))
    }
  })

  React.useEffect(() => {
    LoadingService.loading(true)
    const { batch_number, bundle_number } = props.match.params
    setBatchNumber(batch_number)
    setBundleNumber(bundle_number)

    PageTitleService(`Batch No. ${batch_number}`)
    BreadcrumbsService.setSelectedItems([
      'dashboard',
      'submittedPaymentRequest',
      `submittedSpecificPaymentRequest:${bundle_number}`,
      `specificBatch:${batch_number}`
    ])

    ApiService.getBatch({ bundle_number, batch_number }).then(res => {
      const { data } = res.data
      setDetails(data)
    })
    getClaimsAndCount({ batch_number, bundle_number })
  }, [props.match.params.batch_number, props.match.params.bundle_number])

  const handleClickOverride = () => {
    initDropdownUsers()
    setDialog(state => ({ ...state, assign: true }))
  }

  const initDropdownUsers = () => {
    Promise.all([UserManagementApi.getClaimsAnalyst(), UserManagementApi.getClaimsVerifierSupervisors()]).then(res => {
      const [analystRes, supervisorRes] = res
      const data = [...analystRes.data, ...supervisorRes.data]
      let users = data.map(user => ({ text: `${user.first_name} ${user.last_name}`, value: user._id }))

      setDropDownUsers(users)
    })
  }

  const handleOnSubmitOverride = args => {
    LoadingService.loading(true)
    const { user_id, note } = args
    ApiService.overrideClaims({
      bundle_number,
      batch_number,
      claims: forOverride.claim_numbers,
      _id: user_id,
      note
    }).then(res => {
      getClaimsAndCount({ batch_number, bundle_number })
    })
  }

  const handleSearchChange = search => {
    clearTimeout(debounce)
    setDebounce(
      setTimeout(() => {
        setPagination(state => ({ ...state, search }))
      }, 500)
    )
  }

  const getClaimsAndCount = ({ batch_number, bundle_number }) => {
    ApiService.getApprovedClaims({
      bundle_number,
      batch_number,
      params: { search: pagination.search, limit: pagination.rowsPerPage, page: pagination.page + 1 }
    })
      .then(res => {
        const { results, count, page, limit } = res.data.data
        setClaim(results)
        setClaimCount(count)
        setPagination(state => ({ ...state, count, limit, page: page - 1 }))
        LoadingService.loading(false)
      })
  }

  React.useEffect(() => {
    const { batch_number, bundle_number } = props.match.params
    getClaimsAndCount({ batch_number, bundle_number })
  }, [pagination.search, pagination.rowsPerPage, pagination.page])

  const processor_id = SafeAccess(props.match, 'params.pr_officer', '')
  return (
    <div className={classes.root}>
      {details && (
        <React.Fragment>
          <BatchDetails details={details} reassign_notes={false} />
          <ClaimListHeader isManager count={claimCount} inputChange={handleSearchChange} />
          <ClaimList
            pagination={pagination}
            setPagination={setPagination}
            isManager
            items={claims}
            onItemClick={args => {}}
            actionItems={forOverride}
            setActionItems={setForOverride}
            setSelected={setSelected}
            selectStatus={selectStatus}
            processor_id={processor_id}
          />
          <div className={classes.footer}>
            <div className={classes.actions}>
              {!selectStatus ? (
                <React.Fragment>
                  <Button
                    id='override-claim'
                    variant='contained'
                    color='primary'
                    className='button-neutral'
                    onClick={() => setSelectStatus(true)}
                  >
                    Override Claim
                  </Button>
                  <Button
                    id='close'
                    variant='contained'
                    color='primary'
                    className='button-negative'
                    onClick={() => props.history.push(`/payment-request/${processor_id}/${bundle_number}/submitted`)}
                  >
                    Close
                  </Button>
                </React.Fragment>
              ) : (
                <React.Fragment>
                  <Button
                    id='override-claim'
                    variant='contained'
                    color='primary'
                    className='button-neutral'
                    onClick={() => handleClickOverride()}
                    disabled={forOverride.claim_numbers.length < 1}
                  >
                    Override
                  </Button>
                  <Button
                    id='cancel'
                    variant='contained'
                    color='primary'
                    className='button-negative'
                    onClick={() => {
                      setSelectStatus(false)
                    }}
                  >
                    Cancel
                  </Button>
                </React.Fragment>
              )}
            </div>
          </div>
        </React.Fragment>
      )}
      <AssignDialog
        handleOnSubmitReassign={handleOnSubmitOverride}
        dropDownUsers={dropDownUsers}
        dialog={dialog}
        setDialog={setDialog}
      />
    </div>
  )
}
