import { ClaimsAnalysis as actions } from 'Pages/Protected/Claims/Store/actions'

import { renameProp } from 'Utilities/RenameProperty'

const { setClaimBaseDetails, setClaimFacility, addSuggestionToArray } = actions

export default function Emergency ({ common, loa, dispatch }) {
  let procedures = loa.procedures || []
  let professional_fees = loa.professional_fees || []
  let hospital_items = loa.hospital_items || []
  let diagnosis = loa.diagnoses || []
  const primary_diagnosis = loa.primary_diagnosis
  const primary_icd10_code = loa.primary_icd10_code

  // Rename Object Keys to match used names in React
  procedures.map((proc, i) => {
    procedures[i] = renameProp('procedure_name', 'description', proc)
    procedures[i] = renameProp('amount', 'gross_amount', procedures[i])
    procedures[i] = renameProp('cpt_code', 'code', procedures[i])
    procedures[i].vat_amount = 0
    procedures[i].amount_paid_by_member = 0
    procedures[i].philhealth_amount_paid = 0
    procedures[i].philhealth_remarks = ''
    procedures[i].diagnosis_given_diagnosis = ''
    procedures[i].diagnosis_description = ''
    procedures[i].diagnosis_code = ''
  })

  // Rename Object Keys to match used names in React
  hospital_items.map((item, i) => {
    hospital_items[i] = renameProp('item_name', 'name', hospital_items[i])
    hospital_items[i] = renameProp('amount', 'gross_amount', hospital_items[i])
    hospital_items[i].doctor_id = ''
    hospital_items[i].vat_amount = 0
    hospital_items[i].amount_paid_by_member = 0
    hospital_items[i].philhealth_amount_paid = 0
    hospital_items[i].philhealth_remarks = ''
    hospital_items[i].diagnosis_given_diagnosis = ''
    hospital_items[i].diagnosis_description = ''
    hospital_items[i].diagnosis_code = ''
  })

  // Rename Object Keys to match used names in React
  professional_fees.map((prof, i) => {
    professional_fees[i] = renameProp('amount', 'gross_amount', professional_fees[i])
    professional_fees[i].vat_amount = 0
    professional_fees[i].amount_paid_by_member = 0
    professional_fees[i].philhealth_amount_paid = 0
    professional_fees[i].philhealth_remarks = ''
    professional_fees[i].diagnosis_given_diagnosis = ''
    professional_fees[i].diagnosis_description = ''
    professional_fees[i].diagnosis_code = ''
  })

  // Rename Object Keys to match used names in React
  diagnosis.map((diag, i) => {
    let primary = false
    if (diag.icd10_code === primary_icd10_code) primary = true
    diagnosis[i].primary = primary
    diagnosis[i] = renameProp('diagnosis', 'description', diagnosis[i])
    diagnosis[i] = renameProp('icd10_code', 'code', diagnosis[i])
  })

  let details = {
    ...common,
    date_from: loa.date_admitted_date ? loa.date_admitted_date : Date.now(),
    date_to: loa.date_discharged_date ? loa.date_discharged_date : Date.now(),
    pro_id: '',
    pro_name: '',
    with_corporate_guarantee: loa.corp_guarantee ? 'Yes' : 'No',
    diagnosis: diagnosis || [],
    loa_notes: '',
    procedures: procedures || '',
    hospital_items: hospital_items,
    professional_fees: professional_fees || []
  }

  // Fill Claim Details Based on LOA
  for (let i in details) dispatch(setClaimBaseDetails({ field: i, value: details[i] }))

  // Fill Medical Partner ID and Medical Partner Name
  const facility_id = loa.partner_id || ''
  const facility_name = loa.partner_name || ''
  dispatch(setClaimFacility({ facility_id: facility_id }))
  dispatch(setClaimFacility({ facility_name: facility_name }))

  // prep for https://gitlab.com/hims_partner/modules/hims_partner_app/-/merge_requests/4207
  // Set Rooms List
  // if (facility_id || facility_name) {
  //   const value = facility_id || facility_name
  //   const by = facility_id ? 'id' : 'name'
  //   ClaimsEncodingServices.lookups({
  //     on: 'medical-partner',
  //     params: { query: value, by }
  //   }).then(res => {
  //     const { data } = res.data
  //     const rooms = (data[0].rooms || []).map(item => ({
  //       text: item.desc,
  //       value: item.value,
  //       rate: item.rate
  //     }))
  //     dispatch(setDropDown({ rooms: rooms }))
  //   })
  // }

  // Set Diagnosis Suggestions
  details.diagnosis.map(i => dispatch(addSuggestionToArray({ field: 'icd10' })))
}
