import constants from './constants'
import { DateFormatter } from 'Utilities/TextFormatter'

// TODO Need refactor
// UGLY CSV Generation
export const staticHeaderFooter = args => {
  const { columns = [], to, from, raw = [], medical_partner_id, medical_partner_name } = args
  columns.push('\r\n')
  const data = []

  for (const item of raw) {
    const total_approved_amount = item.details.reduce((a, c) => a + c.approved_amount, 0)
    const total_tax_amount = item.details.reduce((a, c) => a + c.tax_amount, 0)

    // HEADER
    const static_header = constants.header.map(i => {
      switch (true) {
        case i.hasOwnProperty('id_row'):

          return [i.id_row.label_left, medical_partner_id, '', '', i.id_row.label_right, total_approved_amount, '\r\n']
        case i.hasOwnProperty('name_row'):
          // hardcoded discount value
          return [i.name_row.label_left, medical_partner_name, '', '', i.name_row.label_right, 0, '\r\n']
        case i.hasOwnProperty('bundle_row'):
          return [i.bundle_row.label_left, item._id, '', '', i.bundle_row.label_right, DateFormatter(item.due_date), '\r\n']
        case i.hasOwnProperty('date_row'):
          return [i.date_row.label_left, `${DateFormatter(from)} - ${DateFormatter(to)}`, '\r\n']
      }
    })

    // COLUMN HEADER
    const _columns = columns.reduce((joined, currentValue) => [joined, `"${currentValue}"`].join(','), '').slice(1)

    // BODY
    const claims = item.details.map((i, index) => ({ no: index + 1, ...i }))
    const rows = []
    claims.forEach(item => {
      delete item.bundle_number
      rows.push(Object.values(item))
    })
    const body = rows.map(row =>
      [
        row.reduce((joined, currentValue) => [joined, `"${currentValue}"`].join(','), '').slice(1),
        '\r\n'
      ].join('')
    )

    // FOOTER
    const static_footer = constants.footer.map(i => {
      switch (i) {
        case 'Total':
          return ['', '', '', i, total_approved_amount, total_tax_amount, '\r\n']
      }
    })
    const _data = [].concat(static_header, '\r\n', _columns, '\r\n', body, '\r\n', static_footer, '\r\n', '\r\n', '\r\n')

    data.push(..._data)
  }

  return data
}
