import React from 'react'

// Material Components
import Button from '@material-ui/core/Button'
import Paper from '@material-ui/core/Paper'
import Table from 'SharedComponents/Table/Table'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TableSortLabel from '@material-ui/core/TableSortLabel'
import Typography from '@material-ui/core/Typography'
import GetAppIcon from '@material-ui/icons/GetApp'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import ClaimsServices from 'Services/Api/Claims/ClaimsServices'
import ArrayToCSV from 'Services/ArrayToCSV'

// Shared Components
import DatePicker from 'SharedComponents/DatePicker2/DatePicker2'
import TextField from 'SharedComponents/TextField/CustomTextField'

// Utilities
import { DateFormatter } from 'Utilities/TextFormatter'
import { CurrencyFormatter } from 'Utilities/TextFormatter'

// Validator
import DateRangeValidator from '../TransmittalReport/validator/DateRangeValidator'

// Styles
import classes from './LoadingHimsData.module.scss'

const columns = [
  { label: `Member's Account No.`, field: 'member_account_number', match: 'start' },
  { label: `Member's Plan Contract`, field: 'contract', match: 'exact' },
  { label: 'Claim No.', field: 'claim_number', search: true, match: 'start' },
  { label: 'Date of Availment', type: 'date', field: 'availment_date', match: 'date' },
  { label: 'Approved Date', type: 'date', field: 'approved_date', match: 'date' },
  { label: 'Approved by', field: 'approved_by', match: 'any' },
  { label: 'Posted Date', type: 'date', field: 'posted_date', match: 'date' },
  { label: 'Posted by', field: 'posted_by', match: 'any' },
  { label: 'TPA', field: 'TPA', match: 'any' },
  { label: 'SOA Number', field: 'soa_number', match: 'start' },
  { label: 'Office Branch', field: 'office_branch', match: 'any' },
  { label: 'Reimbursement', field: 'reimbursement', match: 'exact' },
  { label: 'Type of Availment', field: 'availment_type', match: 'any' },
  { label: 'Amount of Availment', type: 'currency', field: 'availment_amount', match: 'number' },
  { label: 'Date Admitted', type: 'date', field: 'date_admitted', match: 'date' },
  { label: 'Date Discharge', type: 'date', field: 'date_discharged', match: 'date' },
  { label: 'Hospital/Clinic Code', field: 'facility_code', match: 'any' },
  { label: 'Procedure/s', field: 'procedures', match: 'any' },
  { label: 'Diagnosis', field: 'diagnosis', match: 'any' },
  { label: 'In House', field: 'in_house', match: 'any' },
  { label: 'Payment Due Date', type: 'date', field: 'payment_due_date', match: 'date' },
  { label: 'Breakdown Amount', type: 'currency', field: 'breakdown_amount', match: 'number' },
  { label: 'Breakdown AR Amount', type: 'currency', field: 'breakdown_ar_amount', match: 'number' },
  { label: 'AR Addresee', field: 'ar_addresee', match: 'any' },
  { label: `Member's Name`, field: 'member_name', match: 'any' },
  { label: 'Payee Code', field: 'payee_code', match: 'any' },
  { label: 'Payee Name', field: 'payee_name', match: 'any' },
  { label: 'Disease Code', field: 'disease_code', match: 'any' },
  { label: 'Disease Name', field: 'disease_name', match: 'any' },
  { label: 'Layer', field: 'layer', match: 'any' },
  { label: 'Remarks', field: 'remarks', match: 'any' },
  { label: 'AR Reason', field: 'ar_reason', match: 'any' },
  { label: 'Code of AR Reason', field: 'ar_reason_code', match: 'any' },
  { label: 'Type of Service', field: 'type_of_service', match: 'any' },
  { label: 'Hospital Services Details', field: 'hospital_service_details', match: 'any' },
  { label: 'Hospital Services Amount', type: 'currency', field: 'hospital_service_amount', match: 'number' },
  { label: 'Hospital Services AR Amount', type: 'currency', field: 'hospital_service_ar_amount', match: 'number' }
]

export default function LoadingHimsData (props) {
  const [claims, setClaims] = React.useState({
    raw: [],
    data: [],
    filter: {},
    range: { from: new Date().toISOString(), to: new Date().toISOString(), field: 'status_date' },
    orderBy: 'member_account_number',
    order: true,
    page: 1,
    limit: 10,
    count: 0,
    rowsPerPageOptions: [10, 15, 20]
  })

  const [debounce, setDebounce] = React.useState(null)

  const [dateRangeError, setDateRangeError] = React.useState({
    date_from: '',
    date_to: ''
  })

  React.useEffect(() => {
    PageTitleService('Template for Loading HIMS Data')
    BreadcrumbsService.setSelectedItems(['loadingHimsData'])
  }, [])

  const fetchHIMSData = params => {
    LoadingService.loading(true)
    ClaimsServices.loadingHIMSData({
      range: params.range,
      sort: `${params.orderBy}|${params.order ? 'asc' : 'desc'}`,
      page: params.page,
      limit: params.limit,
      filter: params.filter
    }).then(res => {
      const { results = [], raw = [], count, limit, page } = res.data.data
      console.log(res)
      setClaims(state => ({ ...state, data: results, raw, count, page, limit }))
      LoadingService.loading(false)
    })
  }

  const validate = () => {
    const { error } = DateRangeValidator({
      date_from: claims.range.from,
      date_to: claims.range.to
    })

    setDateRangeError({ ...error })
    return !!error
  }

  const formatData = ({ type = null, source, field }) => {
    let output = ''
    if (type === 'date') {
      output = DateFormatter(source[field])
    } else if (type === 'currency') {
      output = CurrencyFormatter(source[field])
    } else {
      output = source[field] ? source[field] : ''
    }

    return output && output !== '' && output !== `${'\r\n'}${'\r\n'}` ? output : 'N/A'
  }

  const handleOnPaginate = params => {
    const _params = {
      ...claims,
      ...params
    }

    setClaims(state => ({ ...state, ...params }))
    fetchHIMSData(_params)
  }

  const handleOnColumnFilter = params => {
    const _params = {
      ...claims,
      ...params
    }

    fetchHIMSData(_params)
  }

  const handleGenerateReport = () => {
    LoadingService.loading(true)
    ClaimsServices.loadingHIMSData({
      range: claims.range,
      sort: `${claims.orderBy}|${claims.order ? 'asc' : 'desc'}`,
      limit: claims.count > 0 ? claims.count : claims.rowsPerPageOptions[0]
    }).then(res => {
      const { results } = res.data.data

      const claims = results.map(i => ({
        ...i,
        availment_date: DateFormatter(i.availment_date),
        approved_date: DateFormatter(i.approved_date),
        posted_date: DateFormatter(i.posted_date),
        date_admitted: DateFormatter(i.date_admitted),
        date_discharged: DateFormatter(i.date_discharged),
        payment_due_date: DateFormatter(i.payment_due_date)
      }))

      const header = columns.map(c => c.label)
      const rows = claims.map(claim =>
        columns.map(col => formatData({ type: col.type, source: claim, field: col.field }))
      )

      ArrayToCSV({ fileName: 'Loading HIMS Data Report', header, rows })
      LoadingService.loading(false)
    })
  }

  return (
    <Paper className={classes.root}>
      <div className={classes.header}>
        <Typography variant='h6'>Date Range: </Typography>
        <div className={classes.content}>
          <div className={classes.filter}>
            <div className={classes.input}>
              <p>From: </p>
              <DatePicker
                id='filter-date-from'
                value={claims.range.from}
                onChange={date =>
                  setClaims(state => ({
                    ...state,
                    range: {
                      ...state.range,
                      from: date.toISOString()
                    }
                  }))
                }
                disableFuture
                error={dateRangeError.date_from}
              />
            </div>
            <div className={classes.input}>
              <p>To: </p>
              <DatePicker
                id='filter-date-to'
                value={claims.range.to}
                onChange={date =>
                  setClaims(state => ({
                    ...state,
                    range: {
                      ...state.range,
                      to: date.toISOString()
                    }
                  }))
                }
                disableFuture
                error={dateRangeError.date_to}
              />
            </div>
            <Button
              className={`button-positive ${classes.submit}`}
              onClick={() => {
                if (validate()) return
                LoadingService.loading(true)
                fetchHIMSData(claims)
              }}
              variant='contained'
              color='primary'
            >
              Filter
            </Button>
          </div>
          <div className={classes.action}>
            <Button id='generate-report' onClick={() => handleGenerateReport()} disabled={!claims.data.length}>
              <GetAppIcon fontSize='small' />
              <Typography variant='caption' className={`bold ${!claims.data.length ? classes.disabled : classes.typo}`}>
                Generate Report
              </Typography>
            </Button>
          </div>
        </div>
      </div>
      <div className={classes.body}>
        <div className={classes.table}>
          <Table
            TableContainerProps={{ style: { maxHeight: '60vh', overflow: 'auto', position: 'relative' } }}
            TableProps={{ stickyHeader: true }}
            head={
              <TableRow>
                {columns.map((headCell, index) => (
                  <TableCell className={classes[headCell.class]} align={headCell.align} key={`table-header-${index}`}>
                    <TableSortLabel
                      active={claims.orderBy === headCell.field}
                      direction={claims.order ? 'asc' : 'desc'}
                      onClick={event => handleOnPaginate({ orderBy: headCell.field, order: !claims.order })}
                    >
                      {headCell.label}
                    </TableSortLabel>
                    <div className={classes.input}>
                      <TextField
                        noMargin
                        onChange={event => {
                          const search = event.target.value
                          clearTimeout(debounce)
                          setDebounce(
                            setTimeout(() => {
                              handleOnColumnFilter({
                                filter: {
                                  ...claims.filter,
                                  [headCell.field]: { value: search, match: headCell.match }
                                },
                                page: 1
                              })
                              setClaims(state => ({
                                ...state,
                                filter: { ...state.filter, [headCell.field]: { value: search, match: headCell.match } }
                              }))
                            }, 500)
                          )
                        }}
                      />
                    </div>
                  </TableCell>
                ))}
              </TableRow>
            }
            body={
              claims.data &&
              claims.data.map((claim, claimIndex) => (
                <TableRow className={classes['table-row']} key={`table-item-${claimIndex}`}>
                  {columns.map((col, colIndex) => (
                    <TableCell key={`table-cell-${claimIndex}-${colIndex}`}>
                      {formatData({ type: col.type, source: claim, field: col.field })}
                    </TableCell>
                  ))}
                </TableRow>
              ))
            }
            pagination
            count={claims.count}
            page={claims.page}
            limit={claims.limit}
            rowsPerPageOptions={claims.rowsPerPageOptions}
            onChange={args => handleOnPaginate(args)}
          />
        </div>
      </div>
    </Paper>
  )
}
