import React from 'react'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'

// Components
import DashboardTile from 'SharedComponents/DashboardTileComponent/DashboardTileComponent'
import ViewList from '../ViewList2/ViewList'
import LoadingIndicator from 'SharedComponents/LoadingIndicator/LoadingIndicator'

// Utilities
import { DateFormatter, NameFormatter } from 'Utilities/TextFormatter'

const columns = [
  { id: 'claim-number', name: 'Claim No.', sortField: 'latest.claim_number', match: 'start' },
  { id: 'account-number', name: 'Account No.', sortField: 'latest.client.account_number', match: 'any' },
  { id: 'account-name', name: 'Account Name', sortField: 'latest.client.account_name', match: 'any' },
  { id: 'member-name', name: 'Member Name', sortField: 'member_name', match: 'any' }
]

const MedicalCollectiblesTile = props => {
  const { services, sizes = { sm: 12 } } = props
  const [claims, setClaims] = React.useState({
    count: 0,
    data: []
  })

  const [fetching, setFetching] = React.useState(false)
  const [fetchingViewList, setFetchingViewList] = React.useState(false)

  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    filter: {},
    page: 1,
    data: [],
    orderBy: 'latest.date.due',
    order: true
  }

  const [viewList, setViewList] = React.useState(initialValue)

  React.useEffect(() => {
    setFetching(true)
    Promise.all([
      services.getItems({
        limit: 10,
        sort: 'latest.date.due|desc',
        range: 'all'
      }),
      services.getItemCount()
    ])
      .then(values => {
        const [claimResponse, claimCountResponse] = values
        const allClaims = claimResponse.data.data.results
        const allClaimsCount = claimCountResponse.data.data
        setClaims(prevState => ({ ...prevState, data: allClaims, count: allClaimsCount }))
      })
      .finally(values => {
        setFetching(false)
      })
  }, [])

  const handleOnShowViewList = () => {
    let title = 'Medical Collectibles'

    setViewList({ ...initialValue, show: true, title })
    setFetchingViewList(true)
    services
      .getItems({
        limit: 10,
        sort: 'latest.date.due|desc',
        range: 'all'
      })
      .then(({ data: res }) => {
        const { results, count, limit, page } = res.data
        setViewList(state => ({ ...state, data: results, count, limit, page }))
      })
      .finally(values => {
        setFetchingViewList(false)
      })
  }

  const handleOnViewListChange = params => {
    const _params = {
      category: viewList.category,
      limit: viewList.limit,
      page: viewList.page,
      search: viewList.search,
      sort: `${viewList.orderBy}|${viewList.order ? 'desc' : 'asc'}`,
      filter: viewList.filter,
      ...params
    }

    setFetchingViewList(true)
    services
      .getItems(_params)
      .then(({ data: res }) => {
        const { results, count, limit, page } = res.data
        setViewList(state => ({ ...state, data: results, count, limit, page }))
      })
      .finally(values => {
        setFetchingViewList(false)
      })
  }

  return (
    <React.Fragment>
      <DashboardTile
        id='medical-collectibles'
        count={{ color: 3, value: claims.count, id: 'medical-collectibles-count' }}
        sizes={sizes}
        title='Medical Collectibles'
        isFilterShown={false}
        viewList={
          <Typography id='mc-view-list' variant='caption' className='bold text-link' onClick={handleOnShowViewList}>
            View List
          </Typography>
        }
      >
        <LoadingIndicator loading={fetching}>
          <Table stickyHeader size='small'>
            <TableHead>
              <TableRow>
                {columns.map(item => (
                  <TableCell key={item.id} id={`mc-${item.id}`}>
                    {item.name}
                  </TableCell>
                ))}
                <TableCell />
              </TableRow>
            </TableHead>
            <TableBody>
              {claims.data.map((item, index) => (
                <TableRow key={item.claim_number}>
                  <TableCell id={`mc-claim-no-${index}`}>{item.claim_number}</TableCell>
                  {/* added cond on client to support old data */}
                  <TableCell id={`mc-account-no-${index}`}>{item.client ? item.client.account_number : ''}</TableCell>
                  <TableCell id={`mc-account-name-${index}`}>{item.client ? item.client.account_name : ''}</TableCell>
                  <TableCell id={`mc-member-name-${index}`}>{item.member_name}</TableCell>
                  <TableCell id={`mc-view-claim-${index}`}>
                    <Link to={`/claims/review/${item.claim_number}`}>
                      <Typography variant='caption' className='text-link underline' id={`view-claim-${index}`}>
                        View
                      </Typography>
                    </Link>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </LoadingIndicator>
      </DashboardTile>

      <ViewList
        id='medical-collectibles'
        columns={columns}
        searchTextPlaceHolder='Search for claim number, date or facility name'
        viewList={viewList}
        setViewList={setViewList}
        onViewListChange={handleOnViewListChange}
        fetching={fetchingViewList}
        body={_data =>
          _data.map((item, index) => (
            <TableRow key={item.claim_number}>
              <TableCell id={`mc-claim-no-${index}`}>{item.claim_number}</TableCell>
              {/* added cond on client to support old data */}
              <TableCell id={`mc-account-no-${index}`}>{item.client ? item.client.account_number : ''}</TableCell>
              <TableCell id={`mc-account-name-${index}`}>{item.client ? item.client.account_name : ''}</TableCell>
              <TableCell id={`mc-member-name-${index}`}>{item.member_name}</TableCell>
              <TableCell id={`mc-view-claim-${index}`}>
                <Link to={`/claims/review/${item.claim_number}`}>
                  <Typography variant='caption' className='text-link underline' id={`view-claim-${index}`}>
                    View
                  </Typography>
                </Link>
              </TableCell>
            </TableRow>
          ))
        }
      />
    </React.Fragment>
  )
}

MedicalCollectiblesTile.displayName = 'DashboardTileComponent'

export default MedicalCollectiblesTile
