import React from 'react'

// Components
import GetClaimComponent from './GetClaimComponent'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import { PdfViewer2Service } from 'SharedComponents/PdfViewer2/PdfViewer2'
import UserManagementServices from 'Services/Api/UserManagementServices'
import CustomerCareServices from 'Services/Api/Claims/CustomerCareServices'
import ClaimsGenericService from 'Services/Api/Claims/ClaimsServices'

// Utilities
import { SafeAccess } from 'Utilities/SafeAccess'
import Total from 'Utilities/Total'

// Libraries
import moment from 'moment'

// Validators
import DiagnosisListValidator from './Components/DiagnosisList/DiagnosisListValidator'
import HospitalItemValidator from './Components/HospitalItemForm/HospitalItemValidator'
import ProfessionalFeeValidator from './Components/ProfessionalFeeForm/ProfessionalFeeValidator'
import ProcedureValidator from './Components/ProcedureForm/ProcedureValidator'
import ProcedureDiagnosisValidator from './Components/ProcedureForm/DiagnosisValidator'
import HospitalItemDiagnosisValidator from './Components/HospitalItemForm/DiagnosisValidator'
import ProfessionalFeeDiagnosisValidator from './Components/ProfessionalFeeForm/DiagnosisValidator'

// Store
import { useSelector, useDispatch } from 'react-redux'
import { ClaimsAnalysis as actions } from 'Pages/Protected/Claims/Store/actions'

const {
  resetClaim,
  setClaimType,
  setDropDown,
  initDropDowns,
  setSuggestion,
  resetSuggestion,
  setClaimFacility,
  setClaimDoctor,
  // addSuggestionToArray,
  initDiscrepancy,
  setDiscrepancy,
  setProcedure,
  setHospitalItem,
  setProfessionalFee,
  setError,
  resetError,
  setEscalationMessages,
  setInquiryMessages,
  setClaimPro,
  resetRecon,
  setReconNotes,
  setRejectStatus,
  setClaimRoom
} = actions

export default function ClaimAnalysis (props) {
  const dispatch = useDispatch()
  const claim = useSelector(({ ClaimsAnalysis: state }) => state.claim)
  const claim_type = useSelector(({ ClaimsAnalysis: state }) => state.claimType)
  // const suggestions = useSelector(({ ClaimsAnalysis: state }) => state.suggestions)
  const escalations = useSelector(({ ClaimsAnalysis: state }) => state.escalations)
  const inquiries = useSelector(({ ClaimsAnalysis: state }) => state.inquiries)
  const reassign = useSelector(({ ClaimsAnalysis: state }) => state.reassign)
  const c_return = useSelector(({ ClaimsAnalysis: state }) => state.return)
  const error = useSelector(({ ClaimsAnalysis: state }) => state.error)

  const { ApiService, breadCrumbsList, isManager = false } = props

  const [debounce, setDebounce] = React.useState({
    facility: null,
    doctor: null,
    icd10: null,
    cpt: null,
    hospital_items: null,
    pro: null
  })

  const [fetching, setFetching] = React.useState({
    facility_id: false,
    facility_name: false,
    doctor_id: false,
    doctor_name: false,
    pro_id: false,
    pro_name: false,
    anesthesiologist_id: false,
    anesthesiologist_name: false,
    room_type: false,
    room_rate: false
  })

  const [isMember, setIsMember] = React.useState(true)
  const [member, setMember] = React.useState(null)
  const [memberFetchStatus, setMemberFetchStatus] = React.useState('')
  const [memberFetchMessage, setMemberFetchMessage] = React.useState('')
  const [client, setClient] = React.useState(null)
  const [clientFetchStatus, setClientFetchStatus] = React.useState('')
  const [grandTotal, setGrandTotal] = React.useState(0)
  const [utilizationLoa, setUtilizationLoa] = React.useState({})
  const [claimNumber, setClaimNumber] = React.useState('')

  const REPORT_LETTER_OF_ADVICE = 1
  const REPORT_ANALYSIS_SHEET = 2
  const REPORT_MERGED_ANALYSIS_SHEET_AND_LETTER_OF_ADVICE = 3
  const NOTICE = 4
  const REPORT_RETURN_ADVICE = 5

  const getClaimType = ({ claim_number }) => {
    setFetching(true)
    BreadcrumbsService.setSelectedItems(breadCrumbsList)

    ApiService.getClaim({ claim_number }).then(res => {
      const { data } = res.data
      const _claim_type = data.__type
      dispatch(setClaimType(_claim_type))
      dispatch(setDiscrepancy(getDiscrepancy(data.analysis_results.result)))
      setFetching(false)
    })
  }

  React.useEffect(() => {
    LoadingService.loading(true)
    dispatch(resetClaim())
    dispatch(initDiscrepancy())
    dispatch(resetError())
    dispatch(resetRecon())
    const { claim_number } = props.match.params

    getClaimType({ claim_number })
    getEscalationLogs({ claim_number })
    getInquiryLogs({ claim_number })
    getReconNotes({ claim_number })

    return () => {
      dispatch(setClaimType(''))
    }
  }, [props.match.params.claim_number])

  React.useEffect(() => {
    const grand = Total.Grand(claim)
    setGrandTotal(grand)
    dispatch(setRejectStatus(!(grand === 0)))
  }, [claim])

  React.useEffect(() => {
    if (error) {
      console.group('Validation Failed')
      for (const value in error) {
        console.log(value, error[value])
      }
      console.groupEnd()
    }
  }, [error])

  // #region Fetching

  // #region Claims

  const getClaimDetails = callback => {
    const { claim_number } = props.match.params
    setClaimNumber(claim_number)
    dispatch(
      setProcedure({
        show: false,
        mode: '',
        index: null,
        details: {}
      })
    )
    dispatch(
      setHospitalItem({
        show: false,
        mode: '',
        index: null,
        details: {}
      })
    )
    dispatch(
      setProfessionalFee({
        show: false,
        mode: '',
        index: null,
        details: {}
      })
    )

    ApiService.getClaim({
      claim_number
    }).then(res => {
      const { data } = res.data
      // Set Diagnosis Suggestions
      // const diagnosis = data.diagnosis || []
      // diagnosis.map(i => dispatch(addSuggestionToArray({ field: 'icd10' })))
      callback(data)

      // FETCH Member from Customer Care
      if (data.member && data.member.account_number) {
        const member_account_no = data.member.account_number
        const client_account_number = data.member.customer_account_number
        if (isMember) {
          getMember({ member_account_no })
        } else {
          getClient({ client_account_number })
        }
      }

      LoadingService.loading(false)
    })
  }

  // #endregion

  // #region Facility Doctors PRO Anaesthesiologist

  const handleOnChangeFacility = ({ value, field, search, rooms = false }) => {
    console.log({ value, field, search })
    if (search) {
      dispatch(resetSuggestion({ field: 'facility' }))
      dispatch(setClaimFacility({ [field]: value }))
      const { 1: by } = field.split('_')
      getFacilitySuggestions({ value, by, field })
    } else {
      const _value = value || {}
      dispatch(resetSuggestion({ field: 'facility' }))
      dispatch(setClaimFacility({ facility_id: _value.id, facility_name: _value.name }))

      if (rooms) {
        dispatch(setClaimRoom({ room_type: '', room_rate: 0 }))
        getRoomsList({ field: 'facility_id', value: _value.id })
      }
    }
  }

  const getFacilitySuggestions = ({ value = '', by, field }) => {
    if (value.trim().length < 3) return

    setFetching(state => ({ ...state, [field]: true }))
    clearTimeout(debounce.facility)
    setDebounce(state => ({
      ...state,
      facility: setTimeout(() => {
        if (value) {
          ApiService.lookups({
            on: 'medical-partner',
            params: { query: value, by }
          })
            .then(res => {
              const { data } = res.data
              dispatch(
                setSuggestion({
                  field: 'facility',
                  suggestion: data
                })
              )
            })
            .finally(() => {
              setFetching(state => ({ ...state, [field]: false }))
            })
        }
      }, 500)
    }))
  }

  // Room Event
  const handleOnChangeRoom = ({ room_type, availment_date, facility_id }) => {
    getRoomRate({ room_type, availment_date, facility_id })
  }

  // Rooms Fetch
  const getRoomsList = ({ field = 'facility_id', value = '' }) => {
    setFetching(state => ({ ...state, room_type: true }))
    ClaimsGenericService.lookups({
      on: 'rooms',
      params: { search_field: field, search_value: value, limit: 100 }
    })
      .then(res => {
        const { data } = res.data
        const rooms = data.map(room => ({
          text: room.room_type,
          value: room.room_type
        }))
        dispatch(setDropDown({ rooms }))
      })
      .catch(() => {
        dispatch(setDropDown({ rooms: [] }))
      })
      .finally(() => setFetching(state => ({ ...state, room_type: false })))
  }

  // Room Rate Fetch
  const getRoomRate = ({ facility_id, room_type, availment_date }) => {
    setFetching(state => ({ ...state, room_rate: true }))
    dispatch(setClaimRoom({ room_type, room_rate: 0 }))
    ClaimsGenericService.lookups({
      on: 'room',
      params: { facility_id, availment_date: new Date(availment_date), room_type }
    })
      .then(res => {
        const { data } = res.data
        dispatch(setClaimRoom({ room_type, room_rate: data.rate }))
      })
      .catch(() => {
        // dispatch(setClaimRoom({ room_type, room_rate: 0 }))
      })
      .finally(() => setFetching(state => ({ ...state, room_rate: false })))
  }

  //* Doctor
  const handleOnChangeDoctor = ({ value, field, search }) => {
    if (search) {
      dispatch(setClaimDoctor({ [field]: value }))
      dispatch(resetSuggestion({ field: 'doctor' }))
      const { 1: by } = field.split('_')
      let _by = by === 'id' ? 'id' : 'full_name'
      getDoctorSuggestions({ value, by: _by, field })
    } else {
      const _value = value || {}
      dispatch(setClaimDoctor({ doctor_id: _value.id, doctor_name: _value.full_name }))
      dispatch(resetSuggestion({ field: 'doctor' }))
    }
  }

  const getDoctorSuggestions = ({ value = '', by, field }) => {
    if (value.trim().length < 3) return

    setFetching(state => ({ ...state, [field]: true }))
    clearTimeout(debounce.doctor)
    setDebounce(state => ({
      ...state,
      doctor: setTimeout(() => {
        if (value) {
          ApiService.lookups({
            on: 'doctor-partner',
            params: { query: value, by }
          })
            .then(res => {
              const { data } = res.data
              dispatch(
                setSuggestion({
                  field: 'doctor',
                  suggestion: data
                })
              )
            })
            .finally(() => {
              setFetching(state => ({ ...state, [field]: false }))
            })
        }
      }, 500)
    }))
  }
  // PRO
  const handleOnChangePro = ({ value, field, search }) => {
    if (search) {
      dispatch(setClaimPro({ [field]: value }))
      dispatch(resetSuggestion({ field: 'pro' }))
      getProSuggestions({ value, field })
    } else {
      const _value = value || {}
      dispatch(setClaimPro({ pro_name: _value.name, pro_id: _value.employee_id }))
      dispatch(resetSuggestion({ field: 'pro' }))
    }
  }

  const getProSuggestions = ({ value, field }) => {
    if (value.trim().length < 3) return

    setFetching(state => ({ ...state, [field]: true }))
    clearTimeout(debounce.pro)
    setDebounce(state => ({
      ...state,
      pro: setTimeout(() => {
        if (value) {
          // PRO User lookup
          CustomerCareServices.proUsersLookup({ value, field })
            .then(res => {
              // limit temporary - no limit in api seer
              const limit = []
              for (let i = 0; i < 10; i++) {
                if (!res.data.data[i]) break
                limit.push(res.data.data[i])
              }

              const data = limit.map(elem => {
                let full_name = `${elem.first_name} ${elem.last_name}`
                delete elem.first_name
                delete elem.last_name
                return { ...elem, name: full_name }
              })

              dispatch(
                setSuggestion({
                  field: 'pro',
                  suggestion: data
                })
              )
            })
            .finally(() => {
              setFetching(state => ({ ...state, [field]: false }))
            })
        }
      }, 500)
    }))
  }

  // Anaesthesiologist
  const handleOnChangeAnaesthesiologist = ({ value, field, search }) => {
    if (search) {
      dispatch(setClaimDoctor({ [field]: value }))
      dispatch(resetSuggestion({ field: 'anesthesiologist' }))
      const { 1: by } = field.split('_')
      let _by = by === 'id' ? 'id' : 'full_name'
      getAnaesthesiologistSuggestions({ value, by: _by, field })
    } else {
      const _value = value || {}
      dispatch(setClaimDoctor({ anesthesiologist_id: _value.id, anesthesiologist_name: _value.full_name }))
      dispatch(resetSuggestion({ field: 'anesthesiologist' }))
    }
  }

  const getAnaesthesiologistSuggestions = ({ value = '', by, field }) => {
    if (value.trim().length < 3) return

    setFetching(state => ({ ...state, [field]: true }))
    clearTimeout(debounce.anaesthesiologist)
    setDebounce(state => ({
      ...state,
      doctor: setTimeout(() => {
        if (value) {
          ApiService.lookups({
            on: 'doctor-partner',
            params: { query: value, by }
          })
            .then(res => {
              const { data } = res.data
              dispatch(
                setSuggestion({
                  field: 'anesthesiologist',
                  suggestion: data
                })
              )
            })
            .finally(() => {
              setFetching(state => ({ ...state, [field]: false }))
            })
        }
      }, 500)
    }))
  }

  // #endregion

  // #region Integrations

  const getUtilizations = args => ApiService.getUtilizations(args)

  const getPartners = args => ApiService.getPartners(args)

  const getDoctors = args => ApiService.getDoctors(args)

  const getUsers = args => ApiService.getUsers(args)

  const getMember = ({ member_account_no }) => {
    CustomerCareServices.getMemberUtilizationLoa({ account_number: member_account_no }).then(res => {
      const { data } = res.data
      setUtilizationLoa(data)
    })
    setMemberFetchStatus('fetching')
    CustomerCareServices.getMember({ account_number: member_account_no, version: 2 })
      .then(res => {
        const { data } = res.data
        setMemberFetchStatus('success')
        setMember(data)
      })
      .catch(err => {
        const { data } = err.response
        // status === 404
        setMemberFetchMessage(typeof data.message === 'string' ? data.message : data.message.error.message || '')
        setMemberFetchStatus(`${data.status}`)
      })
  }

  const getClient = ({ client_account_number }) => {
    setClientFetchStatus('fetching')
    CustomerCareServices.getClient({ account_number: client_account_number })
      .then(res => {
        const { data } = res.data
        setClientFetchStatus('success')
        setClient(data)
      })
      .catch(err => {
        const { data } = err.response
        // status === 404
        setClientFetchStatus(`${data.status}`)
      })
  }

  const getMemberContract = args => CustomerCareServices.getMemberContract(args)

  const getClientContract = args => CustomerCareServices.getClientContract(args)

  const getMemberContractUrl = args => CustomerCareServices.getMemberContractUrl(args)

  const getClientContractUrl = args => CustomerCareServices.getClientContractUrl(args)

  // #endregion

  const getDiscrepancy = adjudicationResults => {
    const debug = false
    if (debug) console.group('Discrepancy Check')
    let discrepancy = {}
    let diagnosis = {}
    let procedures = {}
    let hospital_items = {}
    let professional_fees = {}
    if (debug) console.log(adjudicationResults)

    if (adjudicationResults) {
      adjudicationResults.map((result, index) => {
        result.fields.map((field, fieldIndex) => {
          if (field.item_type === 'procedures') {
            if (!procedures[field.item_id]) procedures[field.item_id] = {}
            if (!procedures[field.item_id][field.id]) procedures[field.item_id][field.id] = []
            procedures = {
              ...procedures,
              [field.item_id]: {
                ...procedures[field.item_id],
                [field.id]: [...procedures[field.item_id][field.id], field.message]
              }
            }
          } else if (field.item_type === 'icd') {
            if (!diagnosis[field.item_id]) diagnosis[field.item_id] = {}
            if (!diagnosis[field.item_id][field.id]) diagnosis[field.item_id][field.id] = []
            diagnosis = {
              ...diagnosis,
              [field.item_id]: {
                ...diagnosis[field.item_id],
                [field.id]: [...diagnosis[field.item_id][field.id], field.message]
              }
            }
          } else if (field.item_type === 'hospital_items') {
            if (!hospital_items[field.item_id]) hospital_items[field.item_id] = {}
            if (!hospital_items[field.item_id][field.id]) hospital_items[field.item_id][field.id] = []
            hospital_items = {
              ...hospital_items,
              [field.item_id]: {
                ...hospital_items[field.item_id],
                [field.id]: [...hospital_items[field.item_id][field.id], field.message]
              }
            }
          } else if (field.item_type === 'professional_fees') {
            if (!professional_fees[field.item_id]) professional_fees[field.item_id] = {}
            if (!professional_fees[field.item_id][field.id]) professional_fees[field.item_id][field.id] = []
            professional_fees = {
              ...professional_fees,
              [field.item_id]: {
                ...professional_fees[field.item_id],
                [field.id]: [...professional_fees[field.item_id][field.id], field.message]
              }
            }
          } else if (field.item_type === 'icd') {
            if (!diagnosis[field.item_id]) diagnosis[field.item_id] = {}
            if (!diagnosis[field.item_id][field.id]) diagnosis[field.item_id][field.id] = []
            diagnosis = {
              ...diagnosis,
              [field.item_id]: {
                ...diagnosis[field.item_id],
                [field.id]: [...diagnosis[field.item_id][field.id], field.message]
              }
            }
          } else {
            if (!discrepancy[field.id]) discrepancy[field.id] = []
            discrepancy = { ...discrepancy, [field.id]: [...discrepancy[field.id], field.message] }
          }
        })
      })
    }
    discrepancy = {
      ...discrepancy,
      diagnosis: diagnosis,
      procedure: procedures,
      hospital_item: hospital_items,
      professional_fee: professional_fees
    }
    if (debug) console.log(discrepancy)
    if (debug) console.groupEnd()
    return discrepancy
  }

  // #region Messages

  const getEscalationLogs = ({ claim_number }) => {
    ApiService.getComments({ claim_number }).then(res => {
      const { data } = res.data
      dispatch(setEscalationMessages({ messages: data }))
    })
  }

  const getInquiryLogs = ({ claim_number }) => {
    ApiService.getInquiries({ claim_number }).then(res => {
      const { data } = res.data
      dispatch(setInquiryMessages({ messages: data }))
    })
  }

  const getReconNotes = ({ claim_number }) => {
    const { permissions } = props
    if (SafeAccess(permissions.tabs.notes, ['read'])) {
      ApiService.getNotes({ claim_number }).then(res => {
        const { data } = res.data
        dispatch(setReconNotes({ messages: data }))
      })
    }
  }

  // #endregion

  // #region Reports

  const getReport = (mode, params) => {
    LoadingService.loading(true)
    // ApiService.getClaim({ claim_number: claimNumber }).then(value => {
    // const hasAmount = value.data.data.has_amount_concerns
    let pdf = {}
    switch (mode) {
      case REPORT_ANALYSIS_SHEET:
        pdf = {
          name: 'Analysis Sheet',
          fileName: 'analysis_sheet',
          service: ApiService.getAnalysisSheet({ claimNo: claimNumber })
        }
        break

      case REPORT_MERGED_ANALYSIS_SHEET_AND_LETTER_OF_ADVICE:
        pdf = {
          name: 'Reports',
          fileName: 'reports',
          service: ApiService.getReports({ claimNo: claimNumber }),
          onClose: () => props.history.push('/')
        }
        break

      case REPORT_LETTER_OF_ADVICE:
        pdf = {
          name: 'Letter of Advice',
          fileName: 'letter_of_advice',
          service: ApiService.getLetterOfAdvice({ claimNo: claimNumber })
        }
        break

      case NOTICE:
        pdf = {
          name: 'Claims Notice',
          fileName: 'claim_notice',
          service: ApiService.getLetterNotice({ claimNo: claimNumber }),
          onClose: () => props.history.push('/')
        }
        break

      case REPORT_RETURN_ADVICE:
        pdf = {
          name: 'Return Advice',
          fileName: 'return_advice',
          service: ApiService.getReturnAdvice({ claim_number: claimNumber, sub_document_id: params.document_id })
        }
    }

    PdfViewer2Service({ ...pdf, open: true })
    LoadingService.loading(false)
    // })
  }

  // #endregion

  // #region Lookups

  const getReason = () => {
    dispatch(initDropDowns(['reason']))
    dispatch(initDropDowns(['reason_charging']))
    ApiService.lookups({ on: 'dropdown', params: { field: 'reason' } }).then(res => {
      const { data } = res.data
      const reason = data.map(item => ({
        text: item.reason,
        value: item.reason,
        code: item.code
      }))
      dispatch(setDropDown({ reason }))
    })
    ApiService.lookups({ on: 'dropdown', params: { field: 'reason-charging' } }).then(res => {
      const { data } = res.data
      const reason_charging = data.map(item => ({
        text: item.reason_charging,
        value: item.reason_charging,
        code: item.code
      }))
      dispatch(setDropDown({ reason_charging }))
    })
  }

  const getRooms = ({ value, by = 'id' }) => {
    ApiService.lookups({
      on: 'medical-partner',
      params: { query: value, by }
    }).then(res => {
      const { data } = res.data
      const rooms = data[0]
        ? data[0].rooms.map(item => ({
            text: item.type,
            value: item.type,
            rate: item.rate
          }))
        : []
      dispatch(setDropDown({ rooms }))
    })
  }

  const getSubTypes = () => {
    dispatch(initDropDowns(['sub_types']))
    dispatch(initDropDowns(['sub_sub_types']))
    Promise.all([
      ApiService.lookups({
        on: 'maintenance',
        params: { field: 'claims-sub-type', filter: { status: { value: 'Active', match: 'start' } } }
      }),
      ApiService.lookups({
        on: 'maintenance',
        params: { field: 'claims-sub-sub-type', filter: { status: { value: 'Active', match: 'start' } } }
      })
    ]).then(values => {
      const [subValues, subSubValues] = values
      const subTypes = subValues.data.data.results
      const subSubTypes = subSubValues.data.data.results
      const sub_types =
        subTypes.map(i => ({
          value: i.description,
          text: i.description
        })) || []
      const sub_sub_types =
        subSubTypes.map(i => ({
          value: i.description,
          text: i.description
        })) || []
      dispatch(setDropDown({ sub_types }))
      dispatch(setDropDown({ sub_sub_types }))
    })
  }

  // #endregion

  // #endregion

  // #region Events

  const handleOnEscalate = ({ goBack = true }) => {
    LoadingService.loading(true)
    if (!claim.date_to) claim.date_to = moment().toISOString()
    ApiService.update({ claim_number: claimNumber, body: claim }).then(res => {
      const { id } = escalations.assignee
      ApiService.escalate({
        claim_number: claimNumber,
        data: { receiver_id: id, message: escalations.message }
      }).then(res => {
        LoadingService.loading(false)
        if (goBack) props.history.push('/')
      })
    })
  }

  const handleOnReturn = ({ goBack = true }) => {
    LoadingService.loading(true)
    ApiService.returnClaim({
      claim_number: claimNumber,
      data: { note: c_return.note }
    }).then(res => {
      LoadingService.loading(false)
      if (goBack) props.history.push('/')
    })
  }

  const handleOnInquire = ({ goBack = true }) => {
    LoadingService.loading(true)
    if (!claim.date_to) claim.date_to = moment().toISOString()
    ApiService.update({ claim_number: claimNumber, body: claim }).then(res => {
      const { id } = inquiries.assignee
      ApiService.inquire({
        claim_number: claimNumber,
        data: { receiver_id: id, message: inquiries.message }
      }).then(res => {
        LoadingService.loading(false)
        if (goBack) props.history.push('/')
      })
    })
  }

  const handleOnReject = ({ goBack = false }) => {
    LoadingService.loading(true)
    if (!claim.date_to) claim.date_to = moment().toISOString()
    ApiService.update({ claim_number: claimNumber, body: claim }).then(res => {
      ApiService.reject({ claim_number: claimNumber }).then(res => {
        LoadingService.loading(false)
        if (goBack) {
          props.history.push('/')
        } else {
          if (claim_type === 'Reimbursement') getReport(NOTICE)
          else getReport(REPORT_MERGED_ANALYSIS_SHEET_AND_LETTER_OF_ADVICE)
        }
      })
    })
  }

  const handleOnReassign = ({ goBack = true }) => {
    LoadingService.loading(true)

    const { id } = reassign.assignee

    ApiService.reassign({
      claim_number: claimNumber,
      data: { receiver_id: id, message: reassign.message }
    }).then(res => {
      if (goBack) props.history.push('/')
      LoadingService.loading(false)
    })
  }

  const handleOnApprove = ({ goBack = false }) => {
    LoadingService.loading(true)
    if (!claim.date_to) claim.date_to = moment().toISOString()
    ApiService.update({ claim_number: claimNumber, body: claim }).then(res => {
      ApiService.approve({ claim_number: claimNumber }).then(res => {
        LoadingService.loading(false)
        if (goBack) {
          props.history.push('/')
        } else {
          getReport(REPORT_MERGED_ANALYSIS_SHEET_AND_LETTER_OF_ADVICE)
        }
      })
    })
  }

  const handleOnViewAnalysisSheet = () => {
    getReport(REPORT_ANALYSIS_SHEET)
  }

  const handleOnViewLetterOfAdvice = () => {
    getReport(REPORT_LETTER_OF_ADVICE)
  }

  const handleOnViewReturnAdvice = ({ document_id }) => {
    getReport(REPORT_RETURN_ADVICE, { document_id })
  }

  const handleOnSave = async ({ goBack = 'dashboard' }) => {
    // goBack
    // dashboard
    // list
    // stay

    LoadingService.loading(true)

    if (!claim.date_to) claim.date_to = moment().toISOString()

    const res = await ApiService.update({ claim_number: claimNumber, body: claim }).then(res => {
      LoadingService.loading(false)
      switch (goBack) {
        case 'dashboard':
          props.history.push(`/`)
          return true
        case 'stay':
        default:
          return true
      }
    })
    return res
  }

  const handleOnCancel = ({ goBack = true }) => {
    LoadingService.loading(true)
    if (goBack && !isManager) props.history.push('/')
    else {
      const { bundle_number = '', batch_number = '', pr_officer = '' } = props.match.params
      props.history.push(`/payment-request/${pr_officer}/${bundle_number}/submitted/${batch_number}`)
    }
    LoadingService.loading(false)
  }

  const handleOnReturnEscalate = ({ comment, goBack }) => {
    LoadingService.loading(true)
    ApiService.comment({ claim_number: claimNumber, data: { comment } }).then(res => {
      LoadingService.loading(false)
      if (goBack) props.history.push('/')
    })
  }

  const handleOnReturnInquire = ({ comment, goBack }) => {
    LoadingService.loading(true)
    ApiService.inquire({ claim_number: claimNumber, data: { comment } }).then(res => {
      LoadingService.loading(false)
      if (goBack) props.history.push('/')
    })
  }

  const handleOnRemove = ({ goBack = true }) => {
    LoadingService.loading(true)
    ApiService.removeClaim({ claim_number: claimNumber }).then(res => {
      LoadingService.loading(false)
      if (goBack) props.history.push('/')
    })
  }

  // #endregion

  // #region  Validation
  const doValidateAll = ({ validator, field = undefined, type = false }) => {
    dispatch(resetError())
    const base_errors = doValidateBase({ claim, validator: validator, field, type })
    let diagnosis = {}
    let procedure = {}
    let hospital_item = {}
    let professional_fee = {}

    if (claim.diagnosis) {
      diagnosis = doValidateDiagnosis({ diagnosis: claim.diagnosis })
    }

    if (claim.procedures) {
      for (const [index, value] of claim.procedures.entries()) {
        const error = doValidateProcedure({ data: value, index })
        if (Object.keys(error).length > 0) {
          procedure = { index, ...error }
          // AUTO OPEN CLAIM
          dispatch(
            setProcedure({
              show: true,
              mode: 'edit',
              index,
              details: value
            })
          )
          break
        }
      }
    }
    if (claim.hospital_items) {
      for (const [index, value] of claim.hospital_items.entries()) {
        const error = doValidateHospitalItem({ data: value, index })
        if (Object.keys(error).length > 0) {
          hospital_item = { index, ...error }
          // AUTO OPEN CLAIM
          dispatch(
            setHospitalItem({
              show: true,
              mode: 'edit',
              index,
              details: value
            })
          )
          break
        }
      }
    }
    if (claim.professional_fees) {
      for (const [index, value] of claim.professional_fees.entries()) {
        const error = doValidateProfessionalFee({ data: value, index })
        if (Object.keys(error).length > 0) {
          professional_fee = { index, ...error }
          // AUTO OPEN CLAIM
          dispatch(
            setProfessionalFee({
              show: true,
              mode: 'edit',
              index,
              details: value
            })
          )
          break
        }
      }
    }

    if (Object.keys(base_errors).length > 0) {
      return 'claim-base-details'
    } else if (diagnosis.error) {
      return 'claim-diagnosis-items'
    } else if (Object.keys(procedure).length > 0) {
      return 'claim-procedure-items'
    } else if (Object.keys(hospital_item).length > 0) {
      return 'claim-hospital-items'
    } else if (Object.keys(professional_fee).length > 0) {
      return 'claim-professional-items'
    } else {
      // pass false since usage expect false for valid
      return false
    }
  }

  const doValidateBase = ({ claim, validator, field, type }) => {
    const data = {}
    const {
      diagnosis,
      procedures,
      hospital_items,
      professional_fees,
      reimbursement_info,
      reimbursement_info_analysts,
      ppe_packages,
      ...rest
    } = claim
    for (let f in rest) data[f] = claim[f]
    let validate
    if (type) validate = validator(Object.keys(field || claim))({ ...data }, true)
    else validate = validator(Object.keys(field || claim))({ ...data })

    const { error = {} } = validate

    dispatch(
      setError({
        ...error
      })
    )
    return error
  }

  const doValidateDiagnosis = ({ diagnosis }) => {
    const error = DiagnosisListValidator(diagnosis)
    dispatch(
      setError({
        diagnosis: error
      })
    )
    return error
  }

  const doValidateProcedure = ({ data, index }) => {
    const { error } = ProcedureValidator({
      data
    })
    let diagnosisError = {}
    if (claim.diagnosis) {
      let exists = true
      if (
        !claim.diagnosis.find(
          item => item.code === data.diagnosis_code && item.description === data.diagnosis_description
        )
      ) {
        exists = false
        dispatch(setProcedure({ details: { diagnosis_code: '', diagnosis_description: '' } }))
      }
      diagnosisError = ProcedureDiagnosisValidator({
        diagnosis_description: exists ? data.diagnosis_description : '',
        diagnosis_code: exists ? data.diagnosis_code : '',
        diagnosis_given_diagnosis: exists ? data.diagnosis_given_diagnosis : ''
      })
    }

    dispatch(
      setError({
        procedure: { index, ...error, ...diagnosisError.error }
      })
    )
    return { ...error, ...diagnosisError.error }
  }

  const doValidateHospitalItem = ({ data, index }) => {
    const { error } = HospitalItemValidator({
      data
    })
    let diagnosisError = {}
    if (claim.diagnosis) {
      let exists = true
      if (
        !claim.diagnosis.find(
          item => item.code === data.diagnosis_code && item.description === data.diagnosis_description
        )
      ) {
        exists = false
        dispatch(setHospitalItem({ details: { diagnosis_code: '', diagnosis_description: '' } }))
      }
      diagnosisError = HospitalItemDiagnosisValidator({
        diagnosis_description: exists ? data.diagnosis_description : '',
        diagnosis_code: exists ? data.diagnosis_code : '',
        diagnosis_given_diagnosis: exists ? data.diagnosis_given_diagnosis : ''
      })
    }

    dispatch(
      setError({
        hospital_item: { index, ...error, ...diagnosisError.error }
      })
    )
    return { ...error, ...diagnosisError.error }
  }

  const doValidateProfessionalFee = ({ data, index }) => {
    const { error } = ProfessionalFeeValidator({
      data
    })
    let diagnosisError = {}
    if (claim.diagnosis) {
      let exists = true
      if (
        !claim.diagnosis.find(
          item => item.code === data.diagnosis_code && item.description === data.diagnosis_description
        )
      ) {
        exists = false
        dispatch(setProfessionalFee({ details: { diagnosis_code: '', diagnosis_description: '' } }))
      }
      diagnosisError = ProfessionalFeeDiagnosisValidator({
        diagnosis_description: exists ? data.diagnosis_description : '',
        diagnosis_code: exists ? data.diagnosis_code : '',
        diagnosis_given_diagnosis: exists ? data.diagnosis_given_diagnosis : ''
      })
    }
    dispatch(
      setError({
        professional_fee: { index, ...error, ...diagnosisError.error }
      })
    )
    return { ...error, ...diagnosisError.error }
  }
  // #endregion

  return (
    <React.Fragment>
      {claim_type && (
        <GetClaimComponent
          // #region VARIABLES
          _type={claim_type}
          {...props}
          claim={claim}
          fetching={fetching}
          isMember={isMember}
          member={member}
          memberFetchStatus={memberFetchStatus}
          memberFetchMessage={memberFetchMessage}
          client={client}
          clientFetchStatus={clientFetchStatus}
          utilizationLoa={utilizationLoa}
          grandTotal={grandTotal}
          //  #endregion
          // #region FETCHING
          getClaimDetails={getClaimDetails}
          getUtilizations={getUtilizations}
          getUsers={getUsers}
          getPartners={getPartners}
          getDoctors={getDoctors}
          getMember={getMember}
          getClient={getClient}
          getMemberContract={getMemberContract}
          getClientContract={getClientContract}
          getMemberContractUrl={getMemberContractUrl}
          getClientContractUrl={getClientContractUrl}
          getReason={getReason}
          getRooms={getRooms}
          getSubTypes={getSubTypes}
          getRoomsList={getRoomsList}
          //  #endregion
          // #region EVENTS
          onChangeFacility={handleOnChangeFacility}
          onChangeRoom={handleOnChangeRoom}
          onChangeDoctor={handleOnChangeDoctor}
          onChangeAnaesthesiologist={handleOnChangeAnaesthesiologist}
          onReturnEscalate={handleOnReturnEscalate}
          onReturnInquire={handleOnReturnInquire}
          onEscalate={handleOnEscalate}
          onInquire={handleOnInquire}
          onViewAnalysisSheet={handleOnViewAnalysisSheet}
          onViewLetterOfAdvice={handleOnViewLetterOfAdvice}
          onViewClaimNotice={handleOnViewReturnAdvice}
          onReject={handleOnReject}
          onReassign={handleOnReassign}
          onApprove={handleOnApprove}
          onSave={handleOnSave}
          onCancel={handleOnCancel}
          onChangePro={handleOnChangePro}
          onRemove={handleOnRemove}
          onReturn={handleOnReturn}
          //  #endregion
          // #region SERVICES
          userManagementService={UserManagementServices}
          // #endregion
          // #region VALIDATION
          doValidateAll={doValidateAll}
          doValidateDiagnosis={doValidateDiagnosis}
          doValidateProcedure={doValidateProcedure}
          doValidateHospitalItem={doValidateHospitalItem}
          doValidateProfessionalFee={doValidateProfessionalFee}
          // #endregion
        />
      )}
    </React.Fragment>
  )
}
