import InPatient from './InPatient'
import OutPatientConsultation from './OutPatientConsultation'
import OutPatientProcedure from './OutPatientProcedure'
import Emergency from './Emergency'
import Reimbursement from './Reimbursement'
import MedicineAllowance from './MedicineAllowance'
import MedicalSupplies from './MedicalSupplies'
import Pharmacy from './Pharmacy'
import Dental from './Dental'

// Store
import { useDispatch } from 'react-redux'

import ClaimTypes from 'Pages/Protected/Claims/Constant/ClaimTypes'

let dispatch = null
const AutoFillInitializer = () => {
  dispatch = useDispatch()
  return ''
}
const AutoFill = ({ loa, claim_type, claim, ...others }) => {
  const type = ClaimTypes[claim_type].other_name
  // Work Around for Member Names
  const [last_name, res_name] = loa.member_info.member_name.split(',')
  const first_middle = res_name.trim().split(' ')
  let given_name = ''
  let middle_name = ''
  switch (first_middle.length) {
    case 0:
      break
    case 1:
      given_name = first_middle.join(' ')
      break
    case 2:
      given_name = first_middle[0]
      middle_name = first_middle[1]
      break
    case 3:
      middle_name = first_middle.pop()
      given_name = first_middle.join(' ')
      break
  }

  let common = {
    loa_number: loa.loa_no,
    member_account_number: loa.member_id,
    member_last_name: last_name,
    member_first_name: given_name,
    member_middle_name: middle_name
  }

  //* check if array
  loa.procedures = loa.procedures ? (Array.isArray(loa.procedures) ? loa.procedures : []) : []
  loa.professional_fees = loa.professional_fees
    ? Array.isArray(loa.professional_fees)
      ? loa.professional_fees
      : []
    : []
  loa.hospital_items = loa.hospital_items ? (Array.isArray(loa.hospital_items) ? loa.hospital_items : []) : []

  switch (type) {
    case 'InPatient':
      InPatient({ common, loa, dispatch, ...others })
      break

    case 'OutPatientProcedure':
      OutPatientProcedure({ common, loa, dispatch, ...others })
      break

    case 'OutPatientConsultation':
      OutPatientConsultation({ common, loa, dispatch, ...others })
      break

    case 'Emergency':
      Emergency({ common, loa, dispatch, ...others })
      break

    case 'Reimbursement':
      Reimbursement({ common, loa, dispatch, claim, ...others })
      break
    case 'MedicineAllowance':
      MedicineAllowance({ common, loa, dispatch, ...others })
      break
    case 'MedicalSupplies':
      MedicalSupplies({ common, loa, dispatch, ...others })
      break
    case 'Pharmacy':
      Pharmacy({ common, loa, dispatch, ...others })
      break
    case 'Dental':
      Dental({ common, loa, dispatch, ...others })
      break
  }
}

export { AutoFill, AutoFillInitializer }
