import React from 'react'
// import Typography from '@material-ui/core/Typography'
import Grid from '@material-ui/core/Grid'
import moment from 'moment'

// import AutoComplete from 'SharedComponents/AutoComplete/AutoComplete'
import AutoComplete from 'SharedComponents/AutoComplete2/AutoComplete2'

import TextField from 'SharedComponents/TextField/CustomTextField'
import DatePicker from 'SharedComponents/DatePicker2/DatePicker2'
import TextFieldCurrency from 'SharedComponents/TextFieldCurrency/TextFieldCurrency'
import TextFieldNumber from 'SharedComponents/TextFieldNumber/TextFieldNumber'

// Services
import BatchEncodingServices from 'Services/Api/Claims/BatchEncodingServices'

import classes from './BundleDetails.module.scss'

export default function BundleDetails ({ details, errors, isReadOnly, onChange, disableControls }) {
  // const [newBundle, setNewBundle] = React.useState({
  //   bundle_number: '',
  //   total_rcs: '',
  //   soa_number: '',
  //   facility_id: '',
  //   facility_name: '',
  //   doctor_id: '',
  //   doctor_name: '',
  //   date_received: moment(),
  //   due_date: moment()
  // })

  const [suggestions, setSuggestions] = React.useState({
    medicalPartner: [],
    doctorPartner: []
  })

  const [debounce, setDebounce] = React.useState({
    medicalPartner: null,
    doctorPartner: null
  })

  const [fetching, setFetching] = React.useState({
    facility_id: false,
    facility_name: false,
    doctor_id: false,
    doctor_name: false
  })

  // Medical Partner (Facility) ////////////////////////////////////
  const getMedicalPartnerSuggestions = ({ value = '', by, field }) => {
    if (value.trim().length < 3) return

    setSuggestions(state => ({
      ...state,
      medicalPartner: []
    }))
    setFetching(state => ({ ...state, [field]: true }))
    clearTimeout(debounce.medicalPartner)
    setDebounce(state => ({
      ...state,
      medicalPartner: setTimeout(() => {
        if (value) {
          BatchEncodingServices.lookups({ on: 'medical-partner', params: { query: value, by } })
            .then(res => {
              const { data } = res.data
              setSuggestions(state => ({ ...state, medicalPartner: data }))
            })
            .finally(() => {
              setFetching(state => ({ ...state, [field]: false }))
            })
        }
      }, 500)
    }))
  }

  // Medical Partner (Doctor) ////////////////////////////////////
  const getDoctorPartnerSuggestions = ({ value = '', by, field }) => {
    if (value.trim().length < 3) return

    setSuggestions(state => ({
      ...state,
      doctorPartner: []
    }))
    setFetching(state => ({ ...state, [field]: true }))
    clearTimeout(debounce.doctorPartner)
    setDebounce(state => ({
      ...state,
      doctorPartner: setTimeout(() => {
        if (value) {
          BatchEncodingServices.lookups({ on: 'doctor-partner', params: { query: value, by } })
            .then(res => {
              const { data } = res.data
              setSuggestions(state => ({
                ...state,
                doctorPartner: data.map(i => ({ ...i, name: i.full_name }))
              }))
            })
            .finally(() => {
              setFetching(state => ({ ...state, [field]: false }))
            })
        }
      }, 500)
    }))
  }

  const handleOnChangeAutosuggest = ({ value, field, search }) => {
    if (search) {
      onChange({ field, value })
      switch (field) {
        case 'facility_id':
          getMedicalPartnerSuggestions({ value, by: 'id', field })
          break
        case 'facility_name':
          getMedicalPartnerSuggestions({ value, by: 'name', field })
          break
        case 'doctor_id':
          getDoctorPartnerSuggestions({ value, by: 'id', field })
          break
        case 'doctor_name':
          getDoctorPartnerSuggestions({ value, by: 'full_name', field })
          break
        default:
          break
      }
    } else {
      if (field === 'facility_id' || field === 'facility_name') {
        handleOnChangeMedicalPartner({ value, field })
      } else if (field === 'doctor_id' || field === 'doctor_name') {
        handleOnChangeDoctorPartner({ value, field })
      }
    }
  }

  const handleOnChangeMedicalPartner = ({ value, field }) => {
    // Compute due date based on date received and earliest term found
    // Fallback term is 30 days
    let term = 30
    if (value.earliest_term) {
      term = parseInt(value.earliest_term)
      if (isNaN(term) || term < 0) {
        term = 30
      }
    }
    let newDate = moment(details.date_received)
      .add(term, 'days')
      .format('MM/DD/YYYY')
    onChange({ field: 'facility_id', value: value.id })
    onChange({ field: 'facility_name', value: value.name })
    onChange({ field: 'due_date', value: newDate })
  }

  const handleOnChangeDoctorPartner = ({ value, field }) => {
    onChange({ field: 'doctor_name', value: value.full_name })
    onChange({ field: 'doctor_id', value: value.id })
  }

  return (
    <div className={classes.root}>
      <Grid container spacing={3}>
        <Grid item lg={3} md={4} sm={6} xs={12}>
          <TextField required readOnly='true' label='Bundle No.' id='bundle-no' value={details.bundle_number} />
        </Grid>
        <Grid item lg={3} md={4} sm={6} xs={12}>
          <TextFieldNumber
            required
            readOnly={isReadOnly}
            id='total-rcs'
            label='Total No. of RCS'
            value={details.total_rcs}
            onChange={e => onChange({ field: 'total_rcs', value: e.target.value })}
            error={errors.total_rcs}
          />
        </Grid>
      </Grid>
      <Grid container spacing={3}>
        <Grid item lg={3} md={4} sm={6} xs={12}>
          <DatePicker
            required
            id='date-received'
            label='Date Received'
            readOnly={isReadOnly}
            value={details.date_received}
            onChange={date => onChange({ field: 'date_received', value: date.toISOString() })}
            error={errors.date_received}
          />
        </Grid>
        <Grid item lg={3} md={4} sm={6} xs={12}>
          <DatePicker
            required
            readOnly={isReadOnly}
            id='due-date'
            label='Due Date'
            value={details.due_date}
            onChange={date => onChange({ field: 'due_date', value: date.toISOString() })}
            error={errors.due_date}
          />
        </Grid>
        <Grid item lg={3} md={4} sm={6} xs={12}>
          <TextField
            readOnly={isReadOnly}
            id='soa-no'
            label='SOA No.'
            value={details.soa_number}
            onChange={e => onChange({ field: 'soa_number', value: e.target.value })}
            error={errors.soa_number}
          />
        </Grid>
        <Grid item lg={3} md={4} sm={6} xs={12}>
          <TextFieldCurrency
            required
            readOnly={isReadOnly}
            id='total-soa-amt'
            label='Total SOA Amount'
            value={details.total_soa_amt}
            onChange={e => onChange({ field: 'total_soa_amt', value: e.target.value })}
            error={errors.total_soa_amt}
          />
        </Grid>
      </Grid>
      <Grid container spacing={3}>
        <Grid item lg={3} md={4} sm={6} xs={12}>
          <AutoComplete
            required
            readOnly={isReadOnly}
            id='facility-partner-id'
            label='Medical Partner ID'
            value={details.facility_id}
            disabled={disableControls}
            suggestions={suggestions.medicalPartner}
            field={'id'}
            loading={fetching['facility_id']}
            onChange={(value, search) => handleOnChangeAutosuggest({ field: 'facility_id', value, search })}
            error={errors.facility_id}
          />
        </Grid>

        <Grid item lg={3} md={4} sm={6} xs={12}>
          <AutoComplete
            required
            readOnly={isReadOnly}
            id='facility-partner-name'
            label='Medical Partner Name (Facility)'
            value={details.facility_name}
            disabled={disableControls}
            suggestions={suggestions.medicalPartner}
            field={'name'}
            loading={fetching['facility_name']}
            onChange={(value, search) => handleOnChangeAutosuggest({ field: 'facility_name', value, search })}
            error={errors.facility_name}
          />
        </Grid>

        <Grid item lg={3} md={4} sm={6} xs={12}>
          <AutoComplete
            readOnly={isReadOnly}
            id='doctor-partner-id'
            label='Medical Partner ID'
            value={details.doctor_id}
            disabled={disableControls}
            suggestions={suggestions.doctorPartner}
            field={'id'}
            loading={fetching['doctor_id']}
            onChange={(value, search) => handleOnChangeAutosuggest({ field: 'doctor_id', value, search })}
            error={errors.doctor_id}
          />
        </Grid>

        <Grid item lg={3} md={4} sm={6} xs={12}>
          <AutoComplete
            readOnly={isReadOnly}
            id='doctor-name'
            label='Medical Partner Name (Doctor)'
            value={details.doctor_name}
            disabled={disableControls}
            suggestions={suggestions.doctorPartner}
            field={'name'}
            loading={fetching['doctor_name']}
            onChange={(value, search) => handleOnChangeAutosuggest({ field: 'doctor_name', value, search })}
            error={errors.doctor_name}
          />
        </Grid>
      </Grid>

      {details.reassign_notes && (
        <Grid container spacing={3}>
          <Grid item sm={6} xs={12}>
            <TextField
              multiline
              rows={3}
              id='reassign-notes'
              label='Reassign Notes'
              value={details.reassign_notes}
              readOnly
            />
          </Grid>
        </Grid>
      )}
    </div>
  )
}
