import 'date-fns'
import React from 'react'
import MomentUtils from '@date-io/moment'
import TextField from '@material-ui/core/TextField'
import Paper from '@material-ui/core/Paper'
import InputBase from '@material-ui/core/InputBase'
import Typography from '@material-ui/core/Typography'
import IconButton from '@material-ui/core/IconButton'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { MuiPickersUtilsProvider, KeyboardDatePicker } from '@material-ui/pickers'
import { DateFormatter } from 'Utilities/TextFormatter'

// Libraries
import moment from 'moment'

import classes from './DatePicker2.module.scss'

/**
 * Props
 * id
 * label
 * required
 * value - should be date type
 * format
 * onChange
 *
 * other props
 * https://material-ui-pickers.dev/api/KeyboardDatePicker
 */

export default function DatePicker (props) {
  const {
    id,
    label,
    required,
    value,
    format,
    readOnly,
    onChange,
    error,
    tabindex,
    noMargin,
    disableComponents,
    minDate,
    maxDate,
    ...other
  } = props

  const InputRef = React.useRef()
  const [focus, setFocus] = React.useState()
  const [selectedDate, setSelectedDate] = React.useState()

  React.useEffect(() => {
    setSelectedDate(value)
    // setSelectedDate(new Date(value))
  }, [value])

  const handleOnClose = () => {
    handleOnFocus()
  }

  const handleOnFocus = () => {
    setFocus(true)
    setTimeout(() => InputRef.current.focus(), 1)
  }

  const handleOnBlur = e => {
    if (e.currentTarget.contains(e.relatedTarget)) return
    setFocus(false)
  }

  const handleDateChange = (date, _value = null) => {
    if (typeof onChange === 'function') {
      setSelectedDate(date)
      onChange(date, _value)
    }
    handleOnFocus()
  }

  return (
    <div className={classes.root}>
      {label && (
        <Typography variant='subtitle2' className={classes.label}>
          {label}
          {!readOnly && required ? ' *' : ''}
        </Typography>
      )}
      <div className={classes.wrapper}>
        {readOnly ? (
          //  Read Only Value
          <Typography
            readOnly
            id={props.id}
            variant='subtitle2'
            className={`${classes.label} ${classes['read-only-value']}`}
          >
            {DateFormatter(props.value)}
          </Typography>
        ) : (
          <React.Fragment>
            <MuiPickersUtilsProvider utils={MomentUtils}>
              <Paper
                className={`${classes['text-field']} ${noMargin ? classes['no-margin'] : ''}  ${
                  error ? classes['has-error'] : focus && classes.focus
                } box-shadow-field ${disableComponents && classes.disabled}`}
                onBlur={handleOnBlur}
                onFocus={handleOnFocus}
              >
                <KeyboardDatePicker
                  InputProps={{
                    ref: InputRef,
                    disableUnderline: true
                  }}
                  className={classes.input}
                  value={selectedDate}
                  onClose={handleOnClose}
                  onPaste={e => console.log(e.target)}
                  onChange={(date, _value) => handleDateChange(moment(date), _value)}
                  allowKeyboardControl
                  format='MM/DD/YYYY'
                  placeholder='MM/DD/YYYY'
                  minDate={minDate}
                  maxDate={maxDate}
                  helperText={''}
                  clearable
                  {...other}
                />
              </Paper>
            </MuiPickersUtilsProvider>
            {error && <span className={classes.error}>{error}</span>}
          </React.Fragment>
        )}
      </div>
    </div>
  )
}
