/**
 * Claims Utilization Report
 *
 * @export
 *
 * @param {*} props
 *   dataColumns - default []; if defined, will be the allowable data columns that
 *     will be shown on the claims utilization report
 *   hideReport - default false; if defined, will be used to hide the entire report
 *   hideheader - default false; if defined, will be used to hide the header section
 *   dateRange - default current date; if defined, will be used to define the date range in-effect
 *
 * @returns
 */
export default function ClaimsUtilLedgerReport (props) {
  const { dataColumns = [], hideReport, hideHeader, dateRange, refresh } = props

  const [claims, setClaims] = React.useState({
    data: [],
    search: '',
    range: {
      from: SafeAccess(dateRange, 'from', new Date().toISOString()),
      to: SafeAccess(dateRange, 'to', new Date().toISOString()),
      field: 'status_date'
    },
    filter: {},
    orderBy: 'claim_number',
    order: true,
    page: 1,
    limit: 10,
    count: 0,
    rowsPerPageOptions: [10, 15, 20]
  })

  const [dateRangeError, setDateRangeError] = React.useState({
    date_from: '',
    date_to: ''
  })

  const [debounce, setDebounce] = React.useState(null)

  // const [columns, setColumns] = React.useState(completeColumns)

  const [btnDisabled, setBtnDisabled] = React.useState(true)

  React.useEffect(() => {
    PageTitleService('Utilization Ledger Report')
    BreadcrumbsService.setSelectedItems(['utilizationLedger'])
  }, [])

  React.useEffect(() => {
    fetchLedger()
    // claim.range is triggered manually via Filter button
  }, [props.refresh, claims.search, claims.filter, claims.orderBy, claims.order, claims.limit, claims.page])

  // React.useEffect(() => {
  //   setClaims(state => ({
  //     ...state,
  //     range: {
  //       ...state.range,
  //       to: dateRange ? dateRange.to : new Date().toISOString(),
  //       from: dateRange ? dateRange.from : new Date().toISOString()
  //     }
  //   }))
  // }, [dateRange])

  // React.useEffect(() => {
  //   // Goal: Adjust columns to show based on data columns passed as props

  //   let filtered = null
  //   if (!dataColumns || dataColumns.length === 0) {
  //     filtered = completeColumns
  //   } else {
  //     filtered = completeColumns.filter(entry => dataColumns.indexOf(entry.data) > -1)
  //   }
  //   setColumns(filtered)
  // }, [dataColumns])

  const handleOnPaginate = args => {
    setClaims(state => ({ ...state, ...args }))
  }

  const handleOnFilter = args => {
    const { field, value, match } = args
    setClaims(state => ({ ...state, filter: { ...state.filter, [field]: { value, match } } }))
  }

  const validate = () => {
    const { error } = DateRangeValidator({
      date_from: claims.range.from,
      date_to: claims.range.to
    })

    setDateRangeError({ ...error })
    return !!error
  }

  const fetchLedger = () => {
    LoadingService.loading(true)
    ClaimsServices.utilizationLegder({
      search: claims.search,
      range: claims.range,
      filter: claims.filter,
      sort: `${claims.orderBy}|${claims.order ? 'asc' : 'desc'}`,
      page: claims.page,
      limit: claims.limit
    }).then(res => {
      const { data } = res.data
      const { results = [], count = 0 } = data
      const _claims = results.map(claim => ({
        ...claim,
        date_admission_from: DateFormatter(claim.date_admission_from),
        date_admission_to: DateFormatter(claim.date_admission_to),
        date_service: DateFormatter(claim.date_service),
        date_service_from: DateFormatter(claim.date_service_from),
        date_service_to: DateFormatter(claim.date_service_to),
        contract_from: DateFormatter(claim.contract_from),
        contract_to: DateFormatter(claim.contract_to),
        date_encoded: DateFormatter(claim.date_encoded)
        // date_admission_from: DateFormatter(claim.date_admitted),
        // date_discharged: DateFormatter(claim.date_discharged),
        // date_issued: DateFormatter(claim.date_issued),
        // diagnosis: claim.diagnosis && claim.diagnosis.length > 0 ? claim.diagnosis.shift().description : null
      }))
      setBtnDisabled(!_claims.length)
      setClaims(state => ({ ...state, data: _claims, count }))
      LoadingService.loading(false)
    })
  }

  const fetchCSV = () => {
    LoadingService.loading(true)
    setBtnDisabled(true)
    ClaimsServices.utilizationLegder({
      search: claims.search,
      range: claims.range,
      filter: claims.filter,
      sort: `${claims.orderBy}|${claims.order ? 'asc' : 'desc'}`,
      page: claims.page,
      limit: claims.count > 0 ? claims.count : claims.rowsPerPageOptions[0]
    }).then(res => {
      const { data } = res.data
      const { results = [] } = data
      const _claims = results.map(claim => ({
        ...claim,
        date_admission_from: DateFormatter(claim.date_admission_from),
        date_admission_to: DateFormatter(claim.date_admission_to),
        date_service: DateFormatter(claim.date_service),
        date_service_from: DateFormatter(claim.date_service_from),
        date_service_to: DateFormatter(claim.date_service_to),
        contract_from: DateFormatter(claim.contract_from),
        contract_to: DateFormatter(claim.contract_to),
        date_encoded: DateFormatter(claim.date_encoded)
      }))

      const rows = _claims.map(claim => {
        return completeColumns.map(col => formatData({ type: col.type, source: claim, field: col.data }))
      })
      // console.log(rows)

      const header = completeColumns.map(c => c.label)

      ArrayToCSV({ fileName: 'Utilization Ledger Report', header, rows })
      LoadingService.loading(false)
      setBtnDisabled(false)
    })
  }

  const formatData = ({ type = null, source, field, sub_type = 'any' }) => {
    let output = ''
    if (source[field]) {
      if (type === 'date') {
        output = DateFormatter(source[field])
      } else if (type === 'array') {
        output = source[field].map(c => formatData({ type: sub_type, source, field })).join('\r\n\r\n')
      } else if (type === 'boolean') {
        output = source[field] ? 'Yes' : 'No'
      } else {
        output = source[field]
      }
    }

    return output && output !== '' && output !== `${'\r\n'}${'\r\n'}` ? output : 'N/A'
  }

  return (
    <Paper className={classes.root} style={{ display: hideReport === 'true' ? 'none' : 'block' }}>
      <div className={classes.header} style={{ display: hideHeader === 'true' ? 'none' : 'block' }}>
        <Typography variant='h6'>Utilization Date Range</Typography>
        <div className={classes.content}>
          <div className={classes.filter}>
            <div className={classes.input}>
              <p>From: </p>
              <DatePicker
                id='filter-date-from'
                value={claims.range.from}
                onChange={date =>
                  setClaims(state => ({
                    ...state,
                    range: {
                      ...state.range,
                      from: date.toISOString()
                    }
                  }))
                }
                disableFuture
                error={dateRangeError.date_from}
              />
            </div>
            <div className={classes.input}>
              <p>To: </p>
              <DatePicker
                id='filter-date-to'
                value={claims.range.to}
                onChange={date =>
                  setClaims(state => ({
                    ...state,
                    range: {
                      ...state.range,
                      to: date.toISOString()
                    }
                  }))
                }
                disableFuture
                error={dateRangeError.date_to}
              />
            </div>
            <Button
              className={`button-positive ${classes.submit}`}
              onClick={() => {
                if (validate()) return
                fetchLedger()
              }}
              variant='contained'
              color='primary'
            >
              Filter
            </Button>
          </div>
          <div className={classes.action}>
            <Button id='generate-report' onClick={() => fetchCSV()} disabled={btnDisabled}>
              <GetAppIcon fontSize='small' />
              <Typography variant='caption' className={`bold ${btnDisabled ? classes.disabled : classes.typo}`}>
                Generate Report
              </Typography>
            </Button>
          </div>
        </div>
      </div>
      <div className={classes.body}>
        <div className={classes.table}>
          <Table
            TableContainerProps={{ style: { maxHeight: '60vh', overflow: 'auto', position: 'relative' } }}
            TableProps={{ stickyHeader: true }}
            head={
              <TableRow>
                {completeColumns.map((headCell, index) => (
                  <TableCell key={`table-header-${index}`}>
                    <TableSortLabel
                      onClick={event => handleOnPaginate({ orderBy: headCell.data, order: !claims.order })}
                    >
                      {headCell.label}
                    </TableSortLabel>
                    {headCell.search && (
                      <div className={classes.input}>
                        {headCell.type && headCell.type === 'date' ? (
                          <DatePicker
                            noMargin
                            id={`${headCell.data}`}
                            value={null}
                            onChange={date =>
                              handleOnFilter({
                                field: headCell.data,
                                value: date.toISOString(),
                                match: headCell.match
                              })
                            }
                          />
                        ) : (
                          <TextField
                            noMargin
                            // onChange={event =>
                            //   handleOnFilter({
                            //     field: headCell.data,
                            //     value: event.target.value,
                            //     match: headCell.match
                            //   })
                            // }
                            onChange={event => {
                              const search = event.target.value
                              clearTimeout(debounce)
                              setDebounce(
                                setTimeout(() => {
                                  handleOnFilter({
                                    field: headCell.data,
                                    value: search,
                                    match: headCell.match
                                  })
                                }, 500)
                              )
                            }}
                          />
                        )}
                      </div>
                    )}
                  </TableCell>
                ))}
              </TableRow>
            }
            body={
              claims.data &&
              claims.data.map((claim, claimIndex) => (
                <TableRow className={classes['table-row']} key={`table-item-${claimIndex}`}>
                  {completeColumns.map((col, colIndex) => (
                    <TableCell
                      className={col.remarks ? classes.remarks : classes['table-cell']}
                      key={`table-cell-${claimIndex}-${colIndex}`}
                    >
                      {formatData({ type: col.type, source: claim, field: col.data, sub_type: col.match })}
                    </TableCell>
                  ))}
                </TableRow>
              ))
            }
            pagination
            count={claims.count}
            page={claims.page}
            limit={claims.limit}
            rowsPerPageOptions={claims.rowsPerPageOptions}
            onChange={args => handleOnPaginate(args)}
          />
        </div>
      </div>
    </Paper>
  )
}
