import React from 'react'
// import Autocomplete from '@material-ui/lab/Autocomplete'

import classes from './AutoCompletes.module.scss'

import AutoCompleteCurrent from 'SharedComponents/AutoComplete/AutoComplete'
import AutoComplete2 from '../../Components/AutoComplete2/AutoComplete2'

export default function AutoCompletes (props) {
  const { storage, errors, debounce, suggestion } = props
  const { setErrors, onChange, setDebounce, setSuggestion } = props
  const { service } = props

  const [fetching, setFetching] = React.useState(false)

  const getFacilitySuggestions = ({ value = '', by }) => {
    // if (value.trim().length < 3) return

    setSuggestion(state => ({ ...state, facilities: [] }))
    clearTimeout(debounce.facility)
    setFetching(true)
    setDebounce(state => ({
      ...state,
      facility: setTimeout(() => {
        if (value) {
          service
            .lookups({
              on: 'medical-partner',
              params: { query: value, by }
            })
            .then(res => {
              const { data } = res.data
              setSuggestion(state => ({ ...state, facilities: data }))
            })
            .finally(() => {
              setFetching(false)
            })
        }
      }, 500)
    }))
  }

  // const handleOnChangeAutosuggest = ({ value, field, search }) => {
  //   setSuggestion(state => ({ ...state, facilities: [] }))
  //   onChange({ value: value, field })

  //   if (isSuggestionClick) {
  //     for (let i of suggestion.facilities) {
  //       if (value === i.id || value === i.name) {
  //         onChange({ value: i.id, field: 'facility_id' })
  //         onChange({ value: i.name, field: 'facility_name' })
  //         return i.rooms
  //       }
  //     }
  //   } else {
  //     const { 1: by } = field.split('_')
  //     getFacilitySuggestions({ value, by })
  //   }
  // }

  const handleOnChangeAutosuggest2 = ({ value, field, search }) => {
    if (search) {
      const { 1: by } = field.split('_')
      getFacilitySuggestions({ value, by })
      onChange({ value, field })
    } else {
      const _value = value || {}
      console.log('AutoSuggest selected value', _value)
      onChange({ value: _value.id, field: 'facility_id' })
      onChange({ value: _value.name, field: 'facility_name' })
    }
  }

  return (
    <div className={classes.root}>
      <div className={classes.row}>
        {/* <div className={classes.input}>
          <AutoCompleteCurrent
            onChange={(value, isSuggestionClick) =>
              handleOnChangeAutosuggest({ field: 'facility_id', value, isSuggestionClick })
            }
            label={'Medical Partner ID using Current'}
            suggestions={suggestion.facilities}
            value={storage.facility_id}
          />
        </div> */}
        <div className={classes.input}>
          <AutoComplete2
            onChange={(value, search) => handleOnChangeAutosuggest2({ field: 'facility_id', value, search })}
            label={'Medical Partner ID using AutoComplete2'}
            suggestions={suggestion.facilities}
            field={'id'}
            loading={fetching}
            value={storage.facility_id}
          />
        </div>
      </div>
    </div>
  )
}
