import React from 'react'
import Grid from '@material-ui/core/Grid'
import Paper from '@material-ui/core/Paper'
import moment from 'moment'
import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'

// Components
import CustomTextField from 'SharedComponents/TextField/CustomTextField'
import DatePicker from 'SharedComponents/DatePicker2/DatePicker2'
// import AutoComplete from 'SharedComponents/AutoComplete/AutoComplete'
import AutoComplete from 'SharedComponents/AutoComplete2/AutoComplete2'
import TextFieldCurrency from 'SharedComponents/TextFieldCurrency/TextFieldCurrency'
import TextFieldNumber from 'SharedComponents/TextFieldNumber/TextFieldNumber'
import Alert from 'SharedComponents/Alert/Alert'

// Controller
import SoaEtlController from 'Controllers/SoaEtlController'

// Services
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import BatchEncodingServices from 'Services/Api/Claims/BatchEncodingServices'

// Style
import classes from './NewBundlePage.module.scss'

// Validator
import NewBundleValidator from './NewBundleValidator'
export default function NewBundlePage (props) {
  const [disableControls, setDisableControls] = React.useState(false)
  const [openAlert, setOpenAlert] = React.useState({})
  const [newBundle, setNewBundle] = React.useState({
    bundle_number: '',
    total_rcs: 0,
    soa_number: '',
    facility_id: '',
    facility_name: '',
    total_soa_amt: 0,
    doctor_id: '',
    doctor_name: '',
    date_received: moment(),
    due_date_adjustment: 0,
    due_date: moment().add(30, 'days')
  })

  const [errors, setErrors] = React.useState({})

  const [suggestions, setSuggestions] = React.useState({
    medicalPartner: [],
    doctorPartner: []
  })

  const [debounce, setDebounce] = React.useState({
    medicalPartner: null,
    doctorPartner: null
  })

  const [fetching, setFetching] = React.useState({
    facility_id: false,
    facility_name: false,
    doctor_id: false,
    doctor_full_name: false
  })

  // Medical Partner (Facility) ////////////////////////////////////
  const getMedicalPartnerSuggestions = ({ value = '', by }) => {
    if (value.trim().length < 3) return

    setSuggestions(state => ({
      ...state,
      medicalPartner: []
    }))

    setFetching(state => ({ ...state, [`facility_${by}`]: true }))
    clearTimeout(debounce.medicalPartner)
    setDebounce(state => ({
      ...state,
      medicalPartner: setTimeout(() => {
        if (value) {
          BatchEncodingServices.lookups({ on: 'medical-partner', params: { query: value, by } })
            .then(res => {
              const { data } = res.data
              // let _items = []
              // for (let d of data) {
              //   _items.push(d[by])
              // }
              setSuggestions(state => ({ ...state, medicalPartner: data }))
            })
            .finally(() => {
              setFetching(state => ({ ...state, [`facility_${by}`]: false }))
            })
        }
      }, 500)
    }))
  }

  // Medical Partner (Doctor) ////////////////////////////////////
  const getDoctorPartnerSuggestions = ({ value = '', by }) => {
    if (value.trim().length < 3) return
    setSuggestions(state => ({
      ...state,
      doctorPartner: []
    }))

    setFetching(state => ({ ...state, [`doctor_${by}`]: true }))
    clearTimeout(debounce.doctorPartner)
    setDebounce(state => ({
      ...state,
      doctorPartner: setTimeout(() => {
        if (value) {
          BatchEncodingServices.lookups({ on: 'doctor-partner', params: { query: value, by } })
            .then(res => {
              const { data } = res.data
              // let _items = []
              // for (let d of data) {
              //   _items.push(d[by])
              // }
              setSuggestions(state => ({ ...state, doctorPartner: data }))
            })
            .finally(() => {
              setFetching(state => ({ ...state, [`doctor_${by}`]: false }))
            })
        }
      }, 500)
    }))
  }

  const handleOnChangeAutosuggest = ({ value, field, search }) => {
    if (search) {
      setNewBundle(state => ({ ...state, [field]: value }))
      switch (field) {
        case 'facility_id':
          getMedicalPartnerSuggestions({ value, by: 'id' })
          break
        case 'facility_name':
          getMedicalPartnerSuggestions({ value, by: 'name' })
          break
        case 'doctor_id':
          getDoctorPartnerSuggestions({ value, by: 'id' })
          break
        case 'doctor_name':
          getDoctorPartnerSuggestions({ value, by: 'full_name' })
          break
        default:
          break
      }
    } else {
      const _value = value || {}
      if (field === 'facility_id' || field === 'facility_name') {
        handleOnChangeMedicalPartner({ value: _value, field })
      } else if (field === 'doctor_id' || field === 'doctor_name') {
        handleOnChangeDoctorPartner({ value: _value, field })
      }
    }
  }

  const handleOnChangeMedicalPartner = ({ value = {}, field }) => {
    // Compute due date based on date received and earliest term found
    // Fallback term is 30 days
    let term = 30
    if (value.earliest_term) {
      term = parseInt(value.earliest_term)
      if (isNaN(term) || term < 0) {
        term = 30
      }
    }
    let newDate = moment(newBundle.date_received).add(term, 'days')

    setNewBundle(state => ({
      ...state,
      facility_id: value.id,
      facility_name: value.name,
      due_date: newDate,
      due_date_adjustment: term
    }))
    // }
  }

  const handleOnChangeDoctorPartner = ({ value = {}, field }) => {
    setNewBundle(state => ({ ...state, doctor_id: value.id, doctor_name: value.full_name }))
  }

  React.useEffect(() => {
    PageTitleService(`Create New Bundle`)
    BreadcrumbsService.setSelectedItems(['dashboard', 'newBundle'])
    setNewBundle(state => ({ ...state, bundle_number: props.match.params.bundle_number }))
  }, [props.match.params.bundle_number])

  const validate = () => {
    const data = {
      ...newBundle,
      date_received: moment(newBundle.date_received).toISOString(),
      due_date: moment(newBundle.due_date).toISOString()
    }

    const { error } = NewBundleValidator({
      bundle_number: data.bundle_number,
      total_rcs: data.total_rcs,
      date_received: data.date_received,
      due_date: data.due_date,
      soa_number: data.soa_number,
      total_soa_amt: data.total_soa_amt,
      facility_id: data.facility_id,
      facility_name: data.facility_name,
      doctor_id: data.doctor_id,
      doctor_name: data.doctor_name
    })
    setErrors(error || {})
    return !!error
  }

  const handleOnSave = () => {
    setDisableControls(true)
    setOpenAlert(state => ({
      ...state,
      show: false
    }))

    const data = {
      ...newBundle,
      date_received: moment(newBundle.date_received).toISOString(),
      due_date: moment(newBundle.due_date).toISOString()
    }

    if (validate()) {
      setDisableControls(false)
      return
    }

    LoadingService.loading(true)

    // Remove date due adjustment; it will cause an error on server
    delete data.due_date_adjustment

    BatchEncodingServices.updateBundle({
      data,
      bundle_number: newBundle.bundle_number
    }).then(res => {
      props.history.push(`/bundles/${newBundle.bundle_number}`)
    })
  }

  const handleOnDownloadTemplate = () => {
    LoadingService.loading(true)
    SoaEtlController.downloadTemplate()
    LoadingService.loading(false)
  }

  const cancel = () => props.history.push(`/`)

  return (
    <div className={classes.root}>
      <div className={classes['upload-container']}>
        <Link to={`/bundles/upload-soa`}>
          <Typography
            id='upload-soa-button'
            variant='subtitle2'
            className={`bold text-link ${classes.label}`}
            display='inline'
          >
            + Upload SOA
          </Typography>
        </Link>
        <Typography
          onClick={handleOnDownloadTemplate}
          id='download-soa-template-button'
          variant='subtitle2'
          className={`bold text-link ${classes.label}`}
          display='inline'
          style={{ marginLeft: 10 }}
        >
          + Download SOA Template
        </Typography>
      </div>

      <Paper className={classes.form}>
        <Grid container spacing={3} className={classes.inputs}>
          <Grid item lg={3} md={4} sm={6} xs={12} className={classes.input}>
            <CustomTextField
              id='create-new-bundle-bundle-number'
              label='Bundle No.'
              value={newBundle.bundle_number}
              fullWidth
              disabled
              error={errors.bundle_number}
            />
          </Grid>

          <Grid item lg={3} md={4} sm={6} xs={12} className={classes.input}>
            <TextFieldNumber
              id='create-new-bundle-total-rcs'
              required
              label='Total No. of RCS'
              value={newBundle.total_rcs}
              fullWidth
              disabled={disableControls}
              onChange={e => setNewBundle(state => ({ ...state, total_rcs: e.target.value }))}
              error={errors.total_rcs}
            />
          </Grid>
        </Grid>

        <Grid container spacing={3} className={classes.inputs}>
          <Grid item lg={3} md={4} sm={6} xs={12} className={classes.input}>
            <DatePicker
              id='create-new-bundle-date-received'
              label='Date Received'
              value={newBundle.date_received}
              disabled={disableControls}
              onChange={date => {
                // Adjust due date accordingly to date received and term adjustment
                const due_date = date.add(newBundle.due_date_adjustment, 'days').toISOString()

                setNewBundle(state => ({ ...state, date_received: date.toISOString(), due_date: due_date }))
              }}
              error={errors.date_received}
            />
          </Grid>

          <Grid item lg={3} md={4} sm={6} xs={12} className={classes.input}>
            <DatePicker
              id='create-new-bundle-due-date'
              label='Due Date'
              disabled={disableControls}
              value={newBundle.due_date}
              onChange={date => setNewBundle(state => ({ ...state, due_date: date.toISOString() }))}
              error={errors.due_date}
            />
          </Grid>

          <Grid item lg={3} md={4} sm={6} xs={12} className={classes.input}>
            <CustomTextField
              id='create-new-bundle-soa-number'
              label='SOA No.'
              fullWidth
              disabled={disableControls}
              //* e.target is in an asynchronous context (setNewBundle)
              //* moved e.target.value outside asynchronous context
              onChange={e => {
                const { value } = e.target
                // setNewBundle(state => ({ ...state, soa_number: e.target.value }))
                setNewBundle(state => ({ ...state, soa_number: value }))
              }}
              error={errors.soa_number}
            />
          </Grid>

          <Grid item lg={3} md={4} sm={6} xs={12} className={classes.input}>
            <TextFieldCurrency
              id='create-new-bundle-total-soa'
              required
              label='Total SOA Amount'
              fullWidth
              value={newBundle.total_soa_amt}
              disabled={disableControls}
              onChange={e => setNewBundle(state => ({ ...state, total_soa_amt: e.target.value }))}
              error={errors.total_soa_amt}
            />
          </Grid>
        </Grid>

        <Grid container spacing={3} className={classes.inputs}>
          <Grid item lg={3} md={4} sm={6} xs={12} className={classes.input}>
            <AutoComplete
              id='create-new-bundle-facility-partner-id'
              required
              label='Medical Partner ID'
              value={newBundle.facility_id}
              disabled={disableControls}
              suggestions={suggestions.medicalPartner}
              field={'id'}
              loading={fetching['facility_id']}
              onChange={(value, search) => handleOnChangeAutosuggest({ field: 'facility_id', value, search })}
              error={errors.facility_id}
            />
          </Grid>

          <Grid item lg={3} md={4} sm={6} xs={12} className={classes.input}>
            <AutoComplete
              id='create-new-bundle-facility-partner-name'
              required
              label='Medical Partner Name (Facility)'
              value={newBundle.facility_name}
              disabled={disableControls}
              suggestions={suggestions.medicalPartner}
              field={'name'}
              loading={fetching['facility_name']}
              onChange={(value, search) => handleOnChangeAutosuggest({ field: 'facility_name', value, search })}
              error={errors.facility_name}
            />
          </Grid>

          <Grid item lg={3} md={4} sm={6} xs={12} className={classes.input}>
            <AutoComplete
              id='create-new-bundle-docotr-partner-id'
              label='Medical Partner ID'
              value={newBundle.doctor_id}
              disabled={disableControls}
              suggestions={suggestions.doctorPartner}
              field={'id'}
              loading={fetching['doctor_id']}
              onChange={(value, search) => handleOnChangeAutosuggest({ field: 'doctor_id', value, search })}
              error={errors.doctor_id}
            />
          </Grid>

          <Grid item lg={3} md={4} sm={6} xs={12} className={classes.input}>
            <AutoComplete
              id='create-new-bundle-doctor-name'
              label='Medical Partner Name (Doctor)'
              value={newBundle.doctor_name}
              disabled={disableControls}
              suggestions={suggestions.doctorPartner}
              field={'full_name'}
              loading={fetching['doctor_full_name']}
              onChange={(value, search) => handleOnChangeAutosuggest({ field: 'doctor_name', value, search })}
              error={errors.doctor_name}
            />
          </Grid>
        </Grid>

        <div className={classes.actions}>
          <Button variant='contained' color='primary' className='button-negative' onClick={cancel} id='cancel'>
            Cancel
          </Button>

          <Button
            variant='contained'
            color='primary'
            className={`${classes.save} button-positive`}
            onClick={() =>
              !validate() &&
              setOpenAlert(state => ({
                ...state,
                show: true,
                message: 'Are you sure you want to save this?'
              }))
            }
            disabled={disableControls}
            id='save'
          >
            Save
          </Button>

          <Alert
            open={openAlert.show}
            message={openAlert.message}
            cancelLabel='No'
            okLabel='Yes'
            onCancel={() => setOpenAlert(state => ({ ...state, show: false }))}
            onOk={handleOnSave}
          />
        </div>
      </Paper>
    </div>
  )
}
