import React from 'react'
import Typography from '@material-ui/core/Typography'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'

// Components
import DashboardTile from 'SharedComponents/DashboardTileComponent/DashboardTileComponent'
import ViewList from './../ViewList2/ViewList'
import LoadingIndicator from 'SharedComponents/LoadingIndicator/LoadingIndicator'

const columns = [
  { id: 'verifier-name', name: 'Verifier Name', sortField: 'username', match: 'any' },
  { id: 'total-batches', name: 'Total Batches', sortField: 'total_batches', match: 'any' },
  { id: 'total-claims', name: 'Total Claims', sortField: 'total_claims', match: 'any' }
]

const ClaimsVerifierTile = props => {
  const { services, sizes = { sm: 12 } } = props

  const [viewList, setViewList] = React.useState({
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    data: [],
    filter: {},
    orderBy: 'latest.date.due',
    order: true
  })
  const [verifiers, setVerifiers] = React.useState({ data: [], count: 0 })
  const [fetching, setFetching] = React.useState(false)
  const [fetchingViewList, setFetchingViewList] = React.useState(false)

  React.useEffect(() => {
    setFetching(true)
    Promise.all([
      services.totalSubordinatesClaims(),
      // services.getClaimsBySubbordinateCount(),
      services.getSubordinates({ page: 1, limit: 10, sort: 'name|asc' })
    ])
      .then(res => {
        const [usersCount, verifiers] = res
        const { results, count } = verifiers.data.data.data

        setViewList(prev => ({ ...prev, count: count }))
        setVerifiers(p => ({ ...p, data: results, count: usersCount.data.data }))
      })
      .catch(err => console.error(err.message))
      .finally(value => {
        setFetching(false)
      })
  }, [])

  const getSubordinates = params => {
    setFetching(true)
    setFetchingViewList(true)
    services
      .getSubordinates(params)
      .then(res => {
        const { results, count } = res.data.data.data
        const { limit, page } = res.data.data

        setViewList(state => ({ ...state, data: results, count, limit, page }))
      })
      .catch(err => console.error('Error fetching subordinates: ', err))
      .finally(value => {
        setFetching(false)
        setFetchingViewList(false)
      })
  }

  const handleOnReviewUser = (params = null) => () => {
    if (!params) return false
    props.history.push(`/list/${params}`)
  }

  const handleOnShowViewList = category => () => {
    let title = 'Claims Verifier List'

    setViewList(prev => ({ ...prev, show: true, title, data: [] }))
    getSubordinates({ page: 1, limit: 10, sort: 'name|asc' })
  }

  const handleOnViewListChange = params => {
    const _params = {
      limit: viewList.limit,
      page: viewList.page,
      search: viewList.search,
      sort: `${viewList.orderBy}|${viewList.order ? 'desc' : 'asc'}`,
      filter: viewList.filter,
      ...params
    }
    // console.log(_params)
    getSubordinates({ ..._params })
  }

  return (
    <React.Fragment>
      <DashboardTile
        id='claims-verifier'
        count={{ color: 3, value: verifiers.count, id: 'claims-verifier-count' }}
        sizes={sizes}
        title='Claims Verifier'
        isFilterShown={false}
        viewList={
          <Typography id='cv-view-list' variant='caption' className='bold text-link' onClick={handleOnShowViewList()}>
            View List
          </Typography>
        }
      >
        <LoadingIndicator loading={fetching}>
          <Table stickyHeader size='small'>
            <TableHead>
              <TableRow>
                {columns.map(item => (
                  <TableCell key={`cv-${item.id}`} id={`cv-${item.id}`}>
                    {item.name}
                  </TableCell>
                ))}

                <TableCell />
              </TableRow>
            </TableHead>
            <TableBody>
              {verifiers.data &&
                verifiers.data.map((item, index) => (
                  <TableRow key={`verifier-list-item-${index}`}>
                    <TableCell id={`cv-verifier-name-${index}`}>{item.username}</TableCell>
                    <TableCell id={`cv-total-batches-${index}`}>{item.total_batches}</TableCell>
                    <TableCell id={`cv-total-claims-${index}`}>{item.total_claims}</TableCell>
                    <TableCell id={`cv-view-user-${index}`}>
                      <Typography
                        id={`view-user-${index}`}
                        variant='caption'
                        className={`text-link underline`}
                        onClick={handleOnReviewUser(item.id)}
                      >
                        View
                      </Typography>
                    </TableCell>
                  </TableRow>
                ))}
            </TableBody>
          </Table>
        </LoadingIndicator>
      </DashboardTile>

      <ViewList
        id='claims-verifier'
        columns={columns}
        searchTextPlaceHolder='Search for Claims Analyst'
        viewList={viewList}
        setViewList={setViewList}
        onViewListChange={handleOnViewListChange}
        fetching={fetchingViewList}
        body={_data =>
          _data.map((item, index) => (
            <TableRow key={`verifier-list-item-${index}`}>
              <TableCell id={`cv-verifier-name-${index}`}>{item.username}</TableCell>
              <TableCell id={`cv-total-batches-${index}`}>{item.total_batches}</TableCell>
              <TableCell id={`cv-total-claims-${index}`}>{item.total_claims}</TableCell>
              <TableCell id={`cv-view-user-${index}`}>
                <Typography
                  id={`view-user-${index}`}
                  variant='caption'
                  className={`text-link underline`}
                  onClick={handleOnReviewUser(item.id)}
                >
                  View
                </Typography>
              </TableCell>
            </TableRow>
          ))
        }
      />
    </React.Fragment>
  )
}

ClaimsVerifierTile.displayName = 'DashboardTileComponent'

export default ClaimsVerifierTile
