import moment from 'moment'

import React from 'react'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import Typography from '@material-ui/core/Typography'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import DeleteIcon from '@material-ui/icons/Delete'
import Button from '@material-ui/core/Button'

import DropDown from 'SharedComponents/DropDown/DropDown'
import LoadingIndicator from 'SharedComponents/LoadingIndicator/LoadingIndicator'

// Components
import Table from 'SharedComponents/Table/Table'
import TextField from 'SharedComponents/TextField/CustomTextField'
import DatePicker from 'SharedComponents/DatePicker2/DatePicker2'
import Alert from 'SharedComponents/Alert/Alert'

// Styles
import classes from './ReimbursementInfo.module.scss'

// Utilities
import { DateFormatter } from 'Utilities/TextFormatter'

// Validators
import NoticeValidator from './NoticeValidator'

const headCells = [
  { id: 'date-created', name: 'Date Created' },
  { id: 'prepared-by', name: 'Prepared By' },
  { id: 'date-encoded', name: 'Date Encoded' },
  { id: 'released-to', name: 'Released To' }
]

export default function ReimbursementInfo (props) {
  // Variables
  const {
    show,
    canPrintNotice = true,
    setShowModal,
    handleOnSave,
    doReject,
    doViewClaimNotice = () => {},
    service,
    lookup
  } = props

  const { batch_number, claimNumber, member_last_name, member_first_name, member_middle_name } = props.claim

  const { data = [] } = props

  const container = React.useRef()

  const [showAdd, setShowAdd] = React.useState()
  const [rejectModal, setRejectModal] = React.useState(false)
  const [validateModal, setValidateModal] = React.useState(false)
  const [noticeModal, setNoticeModal] = React.useState(false)
  const [loading, setLoading] = React.useState(false)
  const [items, setItems] = React.useState([])
  const [reasonDropdown, setReasonDropdown] = React.useState([])

  React.useEffect(() => {
    setItems(data.map(d => ({ ...d, freeze: d.complied && d.date_return })))
  }, [data])

  React.useEffect(() => {
    if (show && reasonDropdown.length <= 0) {
      service
        .lookups({
          on: 'dropdown',
          params: { field: lookup }
        })
        .then(res => {
          const { data } = res.data
          setReasonDropdown(
            data.map(item => ({
              value: item.reason,
              text: item.reason
            }))
          )
        })
    }
  }, [show])

  // API Calls
  const insertNotice = async ({ item }) => {
    setLoading(true)
    const ff = await service.addReimburesmentInfo({ claim_number: claimNumber, data: item })
    setLoading(false)
    return ff
  }

  const updateNotice = async ({ item, index }) => {
    setLoading(true)
    const ff = await service.updateReimburesmentInfo({ claim_number: claimNumber, info_id: item._id, data: item })
    setLoading(false)
    return ff
  }

  // Callbacks

  const handleOnItemSave = items => {
    const _items = []

    for (let i of items) {
      _items.push({
        _id: i._id,
        complied: i.complied,
        date_created: i.date_created,
        prepared_by: i.prepared_by,
        date_encoded: i.date_encoded,
        released_to: i.released_to,
        date_released: i.date_released,
        date_return: i.date_return,
        remarks: i.remarks,
        reasons: i.reasons
      })
    }
    handleOnSave(_items)
  }

  const handleOnClickPrintNotice = index => {
    const item = items[index] || {}
    doViewClaimNotice({ document_id: item._id })
  }

  const handleOnItemClick = ({ index, value = true }) => {
    if (showAdd && value) return

    setItems(items => {
      return items.map((item, _index) => {
        if (_index === index) item.show = value
        return item
      })
    })

    setTimeout(() => {
      setItems(items => {
        return items.map((item, _index) => {
          if (_index === index) item.showCell = value
          return item
        })
      })
    }, 1)
  }

  const validateItems = ({ items = [] }) => {
    if (!items.length) {
      setValidateModal(true)
    } else setRejectModal(true)
  }

  // UI Events
  // Animations
  const handleOnClickAdd = () => {
    const openNotice = items.find(i => !i.complied)
    if (openNotice) return setNoticeModal(true)

    setShowAdd(true)
    setItems(items => items.map(i => ({ ...i, show: false, showCell: false })))
    setTimeout(() => {
      container.current.scrollTo({
        top: container.current.scrollHeight,
        behavior: 'smooth'
      })
    }, 1)
  }

  const handleOnClickCancel = index => {
    if (index === 'add') return setShowAdd()
    handleOnItemClick({ index, value: false })
  }

  // Data Events
  const handleOnClickSave = ({ item }) => {
    insertNotice({ item }).then(res => {
      const { data } = res.data
      setItems(items => {
        const _items = [...items, { ...item, _id: data._id }]
        handleOnItemSave(_items)
        return _items
      })
      setShowAdd()
    })
  }

  const handleOnClickUpdate = ({ index, item }) => {
    updateNotice({ item, index }).then(res => {
      const { data } = res.data
      items[index] = { ...data[index], freeze: data.complied && data.date_return, show: false, showCell: false }
      setItems(items)
      handleOnItemSave(items)
    })
  }

  const handleOnClose = () => {
    setShowModal(false)
    setShowAdd()
    setItems(items => {
      return items.map((item, _index) => {
        item.show = false
        return item
      })
    })

    setTimeout(() => {
      setItems(items => {
        return items.map((item, _index) => {
          item.showCell = false
          return item
        })
      })
    }, 1)
  }

  return (
    <Dialog
      fullWidth
      maxWidth='lg'
      onClose={handleOnClose}
      aria-labelledby='customized-dialog-title'
      open={show}
      className={classes.root}
      PaperProps={{ style: { height: '100%', display: 'flex' } }}
    >
      <DialogTitle disableTypography className={classes['header-container']}>
        <div className={classes.header}>
          <Typography variant='h6' className={classes.title}>
            Reimbursement Info
          </Typography>
          <IconButton aria-label='close' className={classes.button} onClick={handleOnClose}>
            <CloseIcon />
          </IconButton>
        </div>
      </DialogTitle>

      <DialogContent dividers className={classes.content}>
        <div className={classes['basic-info']}>
          <Typography variant='subtitle2' className={classes.label}>
            Batch No.: {batch_number}
          </Typography>

          <Typography variant='subtitle2' className={classes.label}>
            Member Name.: {member_last_name}, {member_first_name} {member_middle_name}
          </Typography>
        </div>
        <Table
          TableContainerProps={{
            style: { height: '100%', overflow: 'auto', position: 'relative' },
            ref: container
          }}
          TableProps={{ stickyHeader: true }}
          head={
            <TableRow>
              {headCells.map(headCell => (
                <TableCell key={headCell.id} id={`header-${headCell.id}`}>
                  {headCell.name}
                </TableCell>
              ))}
            </TableRow>
          }
          pagination={false}
          body={
            <React.Fragment>
              {items.map((item, index) => (
                <React.Fragment key={index}>
                  <TableRow onClick={() => handleOnItemClick({ index })}>
                    <TableCell id={`batch-number-${index}`}>{DateFormatter(item.date_created)}</TableCell>
                    <TableCell id={`prepared-by-${index}`}>{item.prepared_by}</TableCell>
                    <TableCell id={`date-encoded-${index}`}>{DateFormatter(item.date_encoded)}</TableCell>
                    <TableCell id={`released-to-${index}`}>{item.released_to}</TableCell>
                  </TableRow>

                  {item.show && (
                    <TableRow key={`collapsible-${index}`}>
                      <TableCell
                        colSpan={headCells.length}
                        id={`collabsible-cell-${index}`}
                        className={classes['collapsible-cell']}
                      >
                        <LoadingIndicator type={'overlay'} loading={loading}>
                          <ItemForm
                            reasonDropdown={reasonDropdown}
                            index={index}
                            item={item}
                            handleOnClickCancel={handleOnClickCancel}
                            handleOnClickUpdate={handleOnClickUpdate}
                            canPrintNotice={canPrintNotice}
                            service={service}
                            lookup={lookup}
                            freeze={Boolean(item.freeze)}
                            setReasonDropdown={setReasonDropdown}
                            handleOnClickPrintNotice={handleOnClickPrintNotice}
                          />
                        </LoadingIndicator>
                      </TableCell>
                    </TableRow>
                  )}
                </React.Fragment>
              ))}

              {/* Add Form */}
              {showAdd && (
                <TableRow key={`collapsible-add`}>
                  <TableCell
                    colSpan={headCells.length}
                    id={`collabsible-cell-add`}
                    className={classes['collapsible-cell']}
                  >
                    <LoadingIndicator type={'overlay'} loading={loading}>
                      <ItemForm
                        index='add'
                        reasonDropdown={reasonDropdown}
                        item={{}}
                        handleOnClickCancel={handleOnClickCancel}
                        handleOnClickSave={handleOnClickSave}
                        canPrintNotice={canPrintNotice}
                        service={service}
                        lookup={lookup}
                        setReasonDropdown={setReasonDropdown}
                      />
                    </LoadingIndicator>
                  </TableCell>
                </TableRow>
              )}
              {/* Add Form */}
            </React.Fragment>
          }
        />

        <div className={classes.footer}>
          <Button variant='contained' color='primary' id='cancel' className='button-negative' onClick={handleOnClose}>
            Close
          </Button>

          {doReject && (
            <Button
              variant='contained'
              color='primary'
              id='reject'
              className='button-neutral'
              onClick={() => validateItems({ items: items })}
            >
              Reject
            </Button>
          )}

          <Button
            variant='contained'
            color='primary'
            id='save-for-now'
            className='button-positive'
            onClick={handleOnClickAdd}
            disabled={showAdd}
          >
            Add
          </Button>
        </div>
      </DialogContent>

      <Alert
        open={noticeModal}
        message='Max of one notice that has not been marked as complied only.'
        okLabel='Close'
        onOk={() => {
          setNoticeModal(false)
        }}
      />

      <Alert
        open={rejectModal}
        message='Are you sure you want to reject this claim?'
        cancelLabel='No'
        okLabel='Yes'
        onCancel={() => setRejectModal(false)}
        onOk={() => {
          setRejectModal(false)
          setShowModal(false)
          doReject()
        }}
      />

      <Alert
        open={validateModal}
        message='Please add at least one entry/info first before rejecting this claim.'
        okLabel='Okay'
        onOk={() => {
          setValidateModal(false)
        }}
      />
    </Dialog>
  )
}

const ItemForm = props => {
  const defaultFunction = () => {}

  const { index, canPrintNotice = true, item: _item, readOnly, reasonDropdown, freeze = false } = props

  // Events
  const {
    handleOnClickCancel = defaultFunction,
    handleOnClickSave = defaultFunction,
    handleOnClickUpdate = defaultFunction,
    handleOnClickPrintNotice = defaultFunction
  } = props

  const [notice, setNotice] = React.useState({})

  const [error, setError] = React.useState({ released_to: '', date_released: '' })

  React.useEffect(() => {
    const today = new Date().toISOString()

    setNotice({
      complied: false,
      date_created: today,
      prepared_by: '',
      date_encoded: today,
      released_to: '',
      date_released: today,
      date_return: null,
      remarks: '',
      reasons: [],
      ..._item
    })
  }, [])

  const handleOnChange = ({ field, value }) => {
    setNotice(p => ({ ...p, [field]: value }))
  }

  const handleOnClickAddReason = () => {
    setNotice(item => ({
      ...item,
      reasons: [...item.reasons, { value: '', other_details: '' }]
    }))
  }
  const handleOnClickRemoveReason = __index => {
    setNotice(item => ({
      ...item,
      reasons: item.reasons.filter((item, _index) => __index !== _index)
    }))
  }

  const handleOnChangeReason = ({ field, value, index }) => {
    setNotice(item => ({
      ...item,
      reasons: item.reasons.map((item, _index) => {
        if (index === _index) {
          item[field] = value
          item.error = ''
        }
        return item
      })
    }))
  }

  const validate = item => {
    const { showCell, show, freeze, ...others } = item
    const { error } = NoticeValidator(others)
    if (error) {
      setError(error)
      return false
    } else {
      return true
    }
  }

  return (
    <div className={`${classes.container} ${!notice.showCell || index === 'add' ? classes['show-cell'] : ''}`}>
      <div className={classes.cell}>
        <Typography variant='h6' className={classes.label}>
          {index === 'add' ? 'Add Item' : 'Update Item'}
        </Typography>

        <div className={classes.groups}>
          <div className={classes['input']}>
            <DatePicker
              id={`date-created-${index}`}
              label='Date Created'
              value={notice.date_created}
              onChange={date => handleOnChange({ field: 'date_created', value: date.toISOString() })}
              readOnly={notice.complied}
              error={error.date_created}
              maxDate={new Date()}
            />
          </div>

          <div className={classes['input']}>
            <TextField
              id={`prepared-by-${index}`}
              label='Prepared By'
              value={notice.prepared_by || ''}
              onChange={event => handleOnChange({ field: 'prepared_by', value: event.target.value })}
              readOnly={notice.complied}
              error={error.prepared_by}
            />
          </div>

          <div className={classes['input']}>
            <DatePicker
              id={`date-encoded-${index}`}
              label='Date Encoded'
              value={notice.date_encoded}
              onChange={date => handleOnChange({ field: 'date_encoded', value: date.toISOString() })}
              readOnly
              error={error.date_encoded}
              maxDate={new Date()}
            />
          </div>

          <div className={classes['input']}>
            <TextField
              id={`released-to-${index}`}
              label='Released To'
              required
              value={notice.released_to || ''}
              onChange={event => handleOnChange({ field: 'released_to', value: event.target.value })}
              readOnly={notice.complied}
              error={error.released_to}
            />
          </div>
        </div>

        <div className={classes.groups}>
          <div className={classes['input']}>
            <DatePicker
              id={`date-released-${index}`}
              label='Date Released'
              required
              value={notice.date_released}
              onChange={date => handleOnChange({ field: 'date_released', value: date.toISOString() })}
              readOnly={notice.complied}
              error={error.date_released}
              disableFuture
            />
          </div>

          <div className={classes['input']}>
            <DatePicker
              id={`return-date-${index}`}
              label='Return Date'
              value={notice.date_return || null}
              onChange={date => handleOnChange({ field: 'date_return', value: date.toISOString() })}
              disabled={!notice.complied || freeze}
              readOnly={freeze}
              error={error.date_return}
              minDate={moment(notice.date_released)}
              disableFuture
            />
          </div>

          <div className={classes['input']} style={{ width: '50%' }}>
            <TextField
              constainerStyle={{ width: 'unset', flex: 1 }}
              multiline
              rows={3}
              id='remarks'
              label='Remarks'
              value={notice.remarks || ''}
              onChange={event => handleOnChange({ field: 'remarks', value: event.target.value })}
              readOnly={notice.complied}
              error={error.remarks}
            />
          </div>
        </div>

        <div className={classes['reasons-container']}>
          <div className={classes.header}>
            <Typography variant='subtitle2' className={classes.label}>
              Reasons
            </Typography>
            <Typography variant='subtitle2' className={classes.label}>
              Other Details
            </Typography>

            <div className={classes.delete} />
          </div>

          {/* Reasons */}
          {(notice.reasons || []).map((reason, _index) => (
            <div className={classes.reasons} key={`reason-${_index}`}>
              <div className={classes['input']}>
                <DropDown
                  // noMargin
                  items={reasonDropdown}
                  id={`reason-text-${_index}`}
                  value={reason.value || ''}
                  onChange={event => handleOnChangeReason({ field: 'value', value: event.target.value, index: _index })}
                  readOnly={notice.complied}
                />
              </div>

              <div className={classes['input']}>
                <TextField
                  noMargin
                  id={`reason-other-details-${_index}`}
                  value={reason.other_details || ''}
                  onChange={event =>
                    handleOnChangeReason({ field: 'other_details', value: event.target.value, index: _index })
                  }
                  readOnly={notice.complied}
                />
              </div>

              <div className={classes.delete}>
                {!notice.complied && (
                  <IconButton id='delete-reason' onClick={() => handleOnClickRemoveReason(_index)}>
                    <DeleteIcon fontSize='small' />
                  </IconButton>
                )}
              </div>
            </div>
          ))}

          {error.reasons && <span className={classes.error}>{error.reasons}</span>}

          {!notice.complied && (
            <Typography
              variant='body2'
              className={`${classes['add-link']} ${(notice.reasons || []).length ? '' : classes['empty-reasons']}`}
              onClick={() => handleOnClickAddReason()}
            >
              + Add a reason
            </Typography>
          )}

          {/* Reasons */}
        </div>

        <div className={classes.footer}>
          {!readOnly && (
            <React.Fragment>
              <Button
                variant='contained'
                color='primary'
                id='cancel'
                className='button-negative'
                onClick={() => handleOnClickCancel(index)}
              >
                Cancel
              </Button>
              {index === 'add' && (
                <React.Fragment>
                  <Button
                    variant='contained'
                    color='primary'
                    id='save-for-now'
                    className='button-positive'
                    onClick={() => validate(notice) && handleOnClickSave({ index, item: notice })}
                    disabled={notice.complied}
                  >
                    Save
                  </Button>
                </React.Fragment>
              )}
              {index !== 'add' && (
                <React.Fragment>
                  <Button
                    variant='contained'
                    color='primary'
                    id='save-for-now'
                    className='button-positive'
                    onClick={() => validate(notice) && handleOnClickUpdate({ index, item: notice })}
                    disabled={freeze}
                  >
                    Update
                  </Button>
                </React.Fragment>
              )}
              {index !== 'add' && (
                <React.Fragment>
                  <Button
                    variant='contained'
                    color='primary'
                    className='button-positive'
                    onClick={() =>
                      validate(notice) && handleOnClickUpdate({ index, item: { ...notice, complied: true } })
                    }
                    disabled={notice.complied}
                  >
                    Complied
                  </Button>
                </React.Fragment>
              )}
              {index !== 'add' && canPrintNotice && (
                <React.Fragment>
                  <Button
                    variant='contained'
                    color='primary'
                    className='button-positive'
                    onClick={() => handleOnClickPrintNotice(index)}
                  >
                    {notice.complied ? 'View Notice' : 'Print Notice'}
                  </Button>
                </React.Fragment>
              )}
            </React.Fragment>
          )}
        </div>
      </div>
    </div>
  )
}
