import React from 'react'
import { Switch, Route } from 'react-router-dom'

// Pages of Medical Consultant
import Dashboard from './../Components/ClaimsReconciliation/Dashboard/Dashboard'
import ClaimsViewer from './../Components/ClaimsViewer'

// Services
import { SideBarService } from 'SharedComponents/SideBarComponent/SideBarComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { generateSideBar, generateBreadCrumbs, generateRoutes } from './Reports'

// API Services
import ClaimsReconServices from 'Services/Api/Claims/ClaimsReconServices'

const sideBarItems = [
  {
    label: 'Dashboard',
    icon: 'home',
    to: '/'
  },
  {
    label: 'Claims',
    icon: 'file-alt',
    to: '/claims'
  }
]

const breadcrumbs = {
  dashboard: {
    text: 'Dashboard',
    to: '/'
  },
  claims: {
    text: 'Claims',
    to: '/claims'
  },
  specificClaim: {
    text: data => data,
    to: data => `/claims/${data}`
  },
  recon: {
    text: 'Recon',
    to: ''
  }
}

const permissions = {
  base: {
    create: false,
    read: true,
    update: false,
    delete: false,
    options: {}
  },
  diagnosis: {
    create: false,
    read: true,
    update: false,
    delete: false,
    options: {}
  },
  procedures: {
    create: false,
    read: true,
    update: false,
    delete: false,
    options: {}
  },
  hospital_items: {
    create: false,
    read: true,
    update: false,
    delete: false,
    options: {}
  },
  professional_fees: {
    create: false,
    read: true,
    update: false,
    delete: false,
    options: {}
  },
  tabs: {
    benefits: {
      create: false,
      read: true,
      update: false,
      delete: false,
      options: {}
    },
    partner: {
      create: false,
      read: true,
      update: false,
      delete: false,
      options: {}
    },
    exclusions: {
      create: false,
      read: true,
      update: false,
      delete: false,
      options: {}
    },
    utilization: {
      create: false,
      read: true,
      update: false,
      delete: false,
      options: {}
    },
    contact: {
      create: false,
      read: true,
      update: false,
      delete: false,
      options: {}
    },
    comments: {
      create: false,
      read: true,
      update: false,
      delete: false
    }
  },
  footer: {
    visible: true,
    enabled: true,
    reassign: {
      visible: true,
      enabled: true,
      options: { roles: ['CLAIMS_VERIFIER_SUPERVISOR'], validate: false }
    },
    analysis_sheet: { visible: true, enabled: true, options: {} },
    letter_of_advice: { visible: true, enabled: true, options: {} },
    remove: { visible: true }
  }
}

export default ({ userRole }) => {
  SideBarService.setItems([...sideBarItems, generateSideBar(userRole)])
  BreadcrumbsService.setItems({ ...breadcrumbs, ...generateBreadCrumbs(userRole) })

  return (
    <Switch>
      <Route path='/' exact component={Dashboard} />
      <Route
        path='/claims/recon/:claim_number'
        exact
        render={props => (
          <ClaimsViewer
            breadCrumbsList={['claims', `specificClaim:${props.match.params.claim_number}`, 'recon']}
            {...props}
            permissions={permissions}
            ApiService={ClaimsReconServices}
          />
        )}
      />

      {/* REPORTS */}
      {generateRoutes(userRole)}
    </Switch>
  )
}
