import React from 'react'
import Paper from '@material-ui/core/Paper'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'
import IconButton from '@material-ui/core/IconButton'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

// Shared Components
import Table from 'SharedComponents/Table/Table'

// Styles
import classes from './DynamicTable.module.scss'

export default function DynamicTable (props) {
  const { headers, items, fileName } = props.data
  const { onRemoveFile } = props
  const { onDetectDataRows } = props

  const [tableState, setTableState] = React.useState({
    rows: [],
    count: 0,
    limit: 10,
    page: 1
  })

  const [trimmedItems, setTrimmedItems] = React.useState([])

  React.useEffect(() => {
    // Trim the actual passed items
    const _items = []

    for (let index = 0; index < items.length; index++) {
      const item = items[index]
      if (!item) break

      const hasValue = Object.keys(item).find(key => {
        return item[key] && item[key].trim().length > 0
      })
      if (hasValue) _items.push({ ...item, __index__: index })
    }

    // Store trimmed items
    setTrimmedItems(_items)

    // Trigger the callback sending all trimmed items
    onDetectDataRows(_items)
  }, [])

  React.useEffect(() => {
    // Trigger set rows
    handleOnChangeItems({})
  }, [trimmedItems])

  const handleOnTableChange = params => {
    setTableState(state => ({ ...state, ...params }))

    handleOnChangeItems(params)
  }

  const handleOnChangeItems = ({ page = 1, limit = 10 }) => {
    const startAt = (page - 1) * limit
    const endAt = page * limit
    setTableState(state => ({ ...state, rows: trimmedItems.slice(startAt, endAt), count: items.length }))
  }

  return (
    <Paper className={classes.root}>
      <div className={classes['file-name-container']}>
        <Typography id='file-name' variant='subtitle2' className={classes['file-name']}>
          {fileName}
        </Typography>

        <IconButton size='small'>
          <FontAwesomeIcon icon='minus-circle' className={classes.icon} onClick={onRemoveFile} />
        </IconButton>
      </div>

      <div className={classes.table}>
        <Table
          TableContainerProps={{ style: { flex: 1, overflow: 'auto', position: 'relative' } }}
          TableProps={{ stickyHeader: true }}
          head={
            <TableRow>
              <TableCell id={`column-row-number`}>#</TableCell>

              {headers.map(item => (
                <TableCell key={item} id={`column-${item}`}>
                  {item}
                </TableCell>
              ))}
            </TableRow>
          }
          body={tableState.rows.map(item => (
            <TableRow key={`${JSON.stringify(item)}-${item.__index__}`}>
              <TableCell id={`row-${item.__index__}`}>{item.__index__ + 1}</TableCell>

              {headers.map(header => (
                <TableCell key={`item-${header}-${item.__index__}`}>{item[header]}</TableCell>
              ))}
            </TableRow>
          ))}
          count={tableState.count}
          page={tableState.page}
          limit={tableState.limit}
          rowsPerPageOptions={[10, 15, 20]}
          onChange={handleOnTableChange}
        />
      </div>
    </Paper>
  )
}
