import React from 'react'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'

import CustomerCareServices from 'Services/Api/Claims/CustomerCareServices'
import ClaimsAdjudicationServices from 'Services/Api/Claims/ClaimsAdjudicationServices'

import TextField from 'SharedComponents/TextField/CustomTextField'
import { MozillaPdfBlob, MozillaPdfURL } from '../../Components/PdfViewer2/PdfViewer2'

import classes from './PdfViewers.module.scss'

export default function PdfViewer (props) {
  const { storage, errors } = props
  const { setErrors, onChange } = props

  const [mct, setMct] = React.useState('5e494813213ab37f5f550b44')
  const [claimNumber, setClaimNumber] = React.useState('PF0820000002-001')

  return (
    <div className={classes.root}>
      {/* <Typography>BASE64 * File limited</Typography>
      <MozillaPdf name={'test'} service={Service.getMemberContract({ mct_id: mct })} enabled /> */}

      <div className={classes.header}>
        <Typography>Member Contract (Direct Url)</Typography>
        <TextField value={mct} onChange={event => setMct(event.target.value)} />
      </div>

      <MozillaPdfURL
        name={'Member Contract'}
        service={CustomerCareServices.getMemberContractUrl({ mct_id: mct })}
        enabled
      />

      <div className={classes.header}>
        <Typography>Claims Analysis Sheet (URL)</Typography>
        <TextField value={claimNumber} onChange={event => setClaimNumber(event.target.value)} />
      </div>

      <MozillaPdfURL
        name={`Analysis Sheet of ${claimNumber}`}
        service={ClaimsAdjudicationServices.getAnalysisSheetURL({ claimNo: claimNumber })}
        enabled
      />

      <div className={classes.header}>
        <Typography>Claims Analysis Sheet (Blob)</Typography>
        <TextField value={claimNumber} onChange={event => setClaimNumber(event.target.value)} />
      </div>

      <MozillaPdfBlob
        name={`Analysis Sheet of ${claimNumber}`}
        service={ClaimsAdjudicationServices.getAnalysisSheet({ claimNo: claimNumber })}
        enabled
      />
    </div>
  )
}
