import React from 'react'
import Paper from '@material-ui/core/Paper'
import Typography from '@material-ui/core/Typography'

// Components
import BaseDetails from '../BaseDetails/BaseDetails'
import DiagnosisList from '../DiagnosisList/DiagnosisList'
import ProcedureForm from '../ProcedureForm/ProcedureForm'
import HospitalItemForm from '../HospitalItemForm/HospitalItemForm'
import ProfessionalFeeForm from '../ProfessionalFeeForm/ProfessionalFeeForm'
import TextField from 'SharedComponents/TextField/CustomTextField'
import TextFieldCurrency from 'SharedComponents/TextFieldCurrency/TextFieldCurrency'
import DatePicker from 'SharedComponents/DatePicker2/DatePicker2'
import DropDown from 'SharedComponents/DropDown/DropDown'
import AutoComplete from 'SharedComponents/AutoComplete2/AutoComplete2'
import ProcedureList from '../ProcedureList/ProcedureList'
import HospitalItemList from '../HospitalItemList/HospitalItemList'
import ProfessionalFeeList from '../ProfessionalFeeList/ProfessionalFeeList'
import ClaimFooter from '../ClaimFooter/ClaimFooter'
import ReimbursementInfo from '../../../../ReimbursementInfo/ReimbursementInfo'
import DentalProcedureForm from '../DentalProcedureForm/DentalProcedureForm'
import ClaimsRemarks from '../ClaimsRemarks/ClaimsRemarks'
import UploadedList from './UploadedList/UploadedList'
import FileViewer from 'SharedComponents/FileViewer/FileViewer'

// Utilities
import { CurrencyFormatter } from 'Utilities/TextFormatter'
import lodash from 'lodash'

// Libraries
import moment from 'moment'

// Styles
import classes from './ClaimReimbursement.module.scss'

// Validator
import MainValidator from '../../Validators'
import AdditionalFields from './AdditionalFields'

// Store
import { useSelector, useDispatch } from 'react-redux'
import { ClaimsEncoding as actions } from 'Pages/Protected/Claims/Store/actions'

const {
  setClaimFields,
  setClaimBaseDetails,
  initDropDowns,
  initSuggestions,
  setProcedure,
  setDentalProcedure,
  setProfessionalFee,
  setHospitalItem
} = actions

let prevClaim = null

export default function ClaimReimbursement (props) {
  const dispatch = useDispatch()
  //  Variables
  const { _claimNumber } = props.match.params
  const { claim, initDefault, fetching, isAdd } = props
  // Events
  const {
    onChangeFacility,
    getClaimDetails,
    onChangeDoctor,
    onChangePro,
    doValidateAll,
    onSave,
    doValidateProcedure,
    doValidateHospitalItem,
    doValidateProfessionalFee,
    doViewClaimNotice = () => {},
    onChangeRoom
  } = props
  // Services
  const { apiService, getRoomsList } = props

  const suggestions = useSelector(({ ClaimsEncoding: state }) => state.suggestions)
  const dropDown = useSelector(({ ClaimsEncoding: state }) => state.dropDown)
  const grandTotal = useSelector(({ ClaimsEncoding: state }) => state.grandTotal)
  const errors = useSelector(({ ClaimsEncoding: state }) => state.error)

  const [batchNumber, setBatchNumber] = React.useState(null)

  const [openDialog, setOpenDialog] = React.useState(false)

  const [disableControls, setDisableControls] = React.useState(false)
  const [allowedFields, setAllowedFields] = React.useState({})
  const [initialAllowedFields, setInitialAllowedFields] = React.useState({})
  const [lineItemFields, setlineItemFields] = React.useState({})
  const [showReimbursementModal, setShowReimbursementModal] = React.useState(false)
  const [type, setType] = React.useState('')

  const [viewFile, setViewFile] = React.useState({
    show: false,
    title: 'File Preview',
    fileName: '',
    fileType: '',
    data: ''
  })

  React.useEffect(() => {
    const { _batchNumber } = props.match.params
    dispatch(initSuggestions())
    dispatch(initDropDowns(['rooms']))

    const _allowedFields = AdditionalFields('InPatient')

    const _lineItemFields = {
      procedures: [],
      hospital_items: [],
      professional_fees: [],
      reimbursement_info: []
    }

    setTimeout(() => {
      setAllowedFields(_allowedFields)
      setlineItemFields(_lineItemFields)
      dispatch(setClaimFields({ ..._allowedFields, ..._lineItemFields }))
    }, 10)

    setType(_allowedFields.reimbursement_type)
    setBatchNumber(_batchNumber)

    if (!props.isAdd) {
      getClaimDetails(data => {
        setType(data.reimbursement_details.type)
        if (data.reimbursement_details.type === 'InPatient') {
          getRoomsList({ field: 'facility_id', value: data.facility.id })
        }

        // Append All fields and values
        // Then Trim based on Type
        let _claim = AdditionalFields.trim(data.reimbursement_details.type, {
          approval_code: data.approval_code,
          contract_plan_type: data.contract_plan_type,
          loa_number: data.loa_number,
          reimbursement_type: data.reimbursement_details.type,
          member_account_number: data.member.account_number,
          member_last_name: data.member.last_name,
          member_first_name: data.member.first_name,
          member_middle_name: data.member.middle_name,
          facility_id: data.facility.id,
          facility_name: data.facility.name,
          date_from: data.date.from,
          medicine_category: data.medicine_category,
          reimbursement_category: data.reimbursement_details.category,
          official_receipt_number: data.reimbursement_details.official_receipt_number,
          diagnosis: data.diagnosis || [],
          procedures: data.procedures || [],
          professional_fees: data.professional_fees || [],
          hospital_items: data.hospital_items || [],
          reimbursement_info: data.reimbursement_info || [],
          claim_remarks: data.claim_remarks,
          date_to: data.date.to,
          room_type: data.room?.type,
          room_rate: data.room?.rate,
          pro_id: data.pro?.id,
          pro_name: data.pro?.name,
          doctor_id: data.doctor?.id,
          doctor_name: data.doctor?.name,
          department: data.department
        })
        dispatch(setClaimFields(_claim))
        prevClaim = _claim
        setAllowedFields(AdditionalFields(data.reimbursement_details.type))
      })
    } else initDefault({ facility: true, doctor: false, rooms: true })
  }, [])

  React.useEffect(() => {
    if (!type) return
    switch (type) {
      case 'InPatient':
        initDefault({ facility: true, doctor: false, rooms: true })
        break
      default:
        initDefault()
    }

    dispatch(setDentalProcedure({ show: false }))
    dispatch(setProcedure({ show: false }))
    dispatch(setHospitalItem({ show: false }))
    dispatch(setProfessionalFee({ show: false }))
  }, [type])

  const hasClaimChanges = () => {
    return lodash.isEqual(prevClaim, claim)
  }

  const onChangeReimbursementType = ({ value, field }) => {
    const _claim = AdditionalFields.trim(value, claim)
    dispatch(setClaimFields(_claim))
    setAllowedFields(AdditionalFields(value))
    dispatch(setClaimBaseDetails({ value, field }))
    setType(value)
  }

  const setFields = () => {
    const _claim = AdditionalFields.trim(type, claim)
    dispatch(setClaimFields(_claim))
    setAllowedFields(AdditionalFields(type))
  }

  return (
    <React.Fragment>
      {claim && (
        <div className={classes.root}>
          <Paper className={classes['first-container']}>
            <BaseDetails
              claim={claim}
              errors={errors}
              disableControls={disableControls}
              showReimbursement
              onChange={({ value, field }) => dispatch(setClaimBaseDetails({ value, field }))}
              onChangeReimbursementType={onChangeReimbursementType}
              getRoomsList={getRoomsList}
              onLoaPull={() => setFields()}
              // handleMemberAccountNumber={type === 'Dental'}
              // isDental={type === 'Dental'}
            />

            <div className={classes.wrap}>
              <div className={classes['input']}>
                <AutoComplete
                  id='medical-partner-id'
                  required
                  label='Medical Partner ID'
                  value={claim.facility_id}
                  disabled={disableControls}
                  suggestions={suggestions.facility}
                  field={'id'}
                  loading={fetching['facility_id']}
                  onChange={(value, search) =>
                    onChangeFacility({
                      field: 'facility_id',
                      value,
                      search,
                      rooms: type === 'InPatient'
                    })
                  }
                  error={errors.facility_id}
                />
              </div>

              <div className={classes['input']}>
                <AutoComplete
                  id='medical-partner-facility-name'
                  required
                  label='Medical Partner (Facility) Name'
                  value={claim.facility_name}
                  disabled={disableControls}
                  suggestions={suggestions.facility}
                  field={'name'}
                  loading={fetching['facility_name']}
                  onChange={(value, search) =>
                    onChangeFacility({
                      field: 'facility_name',
                      value,
                      search,
                      rooms: type === 'InPatient'
                    })
                  }
                  error={errors.facility_name}
                />
              </div>

              {AdditionalFields.exist(allowedFields, 'doctor_id') && (
                <div className={classes['input']}>
                  <AutoComplete
                    id='doctor-id'
                    label='Medical Partner ID'
                    value={claim.doctor_id}
                    disabled={disableControls}
                    suggestions={suggestions.doctor}
                    field={'id'}
                    loading={fetching['doctor_id']}
                    onChange={(value, search) =>
                      onChangeDoctor({
                        field: 'doctor_id',
                        value,
                        search
                      })
                    }
                    error={errors.doctor_id}
                  />
                </div>
              )}

              {AdditionalFields.exist(allowedFields, 'doctor_name') && (
                <div className={classes['input']}>
                  <AutoComplete
                    id='doctor-name'
                    label='Medical Partner (Doctor) Name'
                    value={claim.doctor_name}
                    disabled={disableControls}
                    suggestions={suggestions.doctor}
                    field={'full_name'}
                    loading={fetching['doctor_name']}
                    onChange={(value, search) =>
                      onChangeDoctor({
                        field: 'doctor_name',
                        value,
                        search
                      })
                    }
                    error={errors.doctor_name}
                  />
                </div>
              )}

              {type === 'InPatient' ? (
                <React.Fragment>
                  <div className={classes['input']}>
                    <DatePicker
                      id='admission-date-from'
                      required
                      label='Admission Date (From)'
                      value={claim.date_from}
                      disabled={disableControls}
                      onChange={date => {
                        dispatch(
                          setClaimBaseDetails({
                            value: moment(date)
                              .startOf('day')
                              .toISOString(),
                            field: 'date_from'
                          })
                        )
                        onChangeRoom({
                          room_type: claim.room_type,
                          availment_date: date,
                          facility_id: claim.facility_id
                        })
                      }}
                      error={errors.date_from}
                    />
                  </div>

                  <div className={classes['input']}>
                    <DatePicker
                      id='admission-date-to'
                      required
                      label='Admission Date (To)'
                      value={claim.date_to}
                      disabled={disableControls}
                      onChange={date =>
                        dispatch(
                          setClaimBaseDetails({
                            value: date.toISOString(),
                            field: 'date_to'
                          })
                        )
                      }
                      error={errors.date_to}
                    />
                  </div>
                </React.Fragment>
              ) : (
                <div className={classes['input']}>
                  <DatePicker
                    id='date-of-service'
                    required
                    label='Date of Service'
                    value={claim.date_from}
                    disabled={disableControls}
                    onChange={date => {
                      dispatch(
                        setClaimBaseDetails({
                          value: date.toISOString(),
                          field: 'date_from'
                        })
                      )
                      dispatch(
                        setClaimBaseDetails({
                          value: date.toISOString(),
                          field: 'date_to'
                        })
                      )
                    }}
                    error={errors.date_from}
                  />
                </div>
              )}

              {AdditionalFields.exist(allowedFields, 'department') && (
                <div className={classes['input']}>
                  <TextField
                    id='department'
                    label='Department'
                    value={claim.department}
                    disabled={disableControls}
                    onChange={event =>
                      dispatch(
                        setClaimBaseDetails({
                          value: event.target.value,
                          field: 'department'
                        })
                      )
                    }
                    error={errors.department}
                  />
                </div>
              )}

              {!AdditionalFields.exist(allowedFields, 'room_rate') && (
                <React.Fragment>
                  <div className={classes['input']}>
                    <TextField
                      id='medicine-category'
                      required
                      label='Medicine Category'
                      value={claim.medicine_category}
                      disabled={disableControls}
                      onChange={event =>
                        dispatch(
                          setClaimBaseDetails({
                            value: event.target.value,
                            field: 'medicine_category'
                          })
                        )
                      }
                      error={errors.medicine_category}
                    />
                  </div>

                  <div className={classes['input']}>
                    <TextField
                      id='reimbursement-category'
                      label='Reimbursement Category'
                      value={claim.reimbursement_category}
                      disabled={disableControls}
                      onChange={event =>
                        dispatch(
                          setClaimBaseDetails({
                            value: event.target.value,
                            field: 'reimbursement_category'
                          })
                        )
                      }
                      error={errors.reimbursement_category}
                    />
                  </div>
                </React.Fragment>
              )}

              {AdditionalFields.exist(allowedFields, 'room_type') && (
                <div className={classes['input']}>
                  <DropDown
                    id='room-type'
                    required
                    label='Room Type'
                    items={dropDown.rooms}
                    value={claim.room_type}
                    disabled={disableControls}
                    loading={fetching['room_type']}
                    onChange={event =>
                      onChangeRoom({
                        room_type: event.target.value,
                        availment_date: claim.date_from,
                        facility_id: claim.facility_id
                      })
                    }
                    error={errors.room_type}
                  />
                </div>
              )}

              {AdditionalFields.exist(allowedFields, 'room_rate') && (
                <div className={classes['input']}>
                  <TextFieldCurrency
                    id='room-rate'
                    required
                    label='Room Rate'
                    value={claim.room_rate}
                    disabled={disableControls}
                    loading={fetching['room_rate']}
                    onChange={event =>
                      dispatch(
                        setClaimBaseDetails({
                          value: event.target.value,
                          field: 'room_rate'
                        })
                      )
                    }
                    error={errors.room_rate}
                  />
                </div>
              )}

              {AdditionalFields.exist(allowedFields, 'pro_id') && (
                <div className={classes['input']}>
                  <AutoComplete
                    id='pro-id'
                    required
                    label='PRO ID'
                    value={claim.pro_id}
                    suggestions={suggestions.pro}
                    field={'employee_id'}
                    disabled={disableControls}
                    loading={fetching['pro_id']}
                    onChange={(value, search) =>
                      onChangePro({
                        field: 'pro_id',
                        value,
                        search
                      })
                    }
                    error={errors.pro_id}
                  />
                </div>
              )}

              {AdditionalFields.exist(allowedFields, 'pro_name') && (
                <div className={classes['input']}>
                  <AutoComplete
                    id='pro-name'
                    required
                    label='PRO Name'
                    value={claim.pro_name}
                    disabled={disableControls}
                    suggestions={suggestions.pro}
                    field={'name'}
                    loading={fetching['pro_name']}
                    onChange={(value, search) =>
                      onChangePro({
                        field: 'pro_name',
                        value,
                        search
                      })
                    }
                    error={errors.pro_name}
                  />
                </div>
              )}

              {AdditionalFields.exist(allowedFields, 'room_rate') && (
                <React.Fragment>
                  <div className={classes['input']}>
                    <TextField
                      id='medicine-category'
                      required
                      label='Medicine Category'
                      value={claim.medicine_category}
                      disabled={disableControls}
                      onChange={event =>
                        dispatch(
                          setClaimBaseDetails({
                            value: event.target.value,
                            field: 'medicine_category'
                          })
                        )
                      }
                      error={errors.medicine_category}
                    />
                  </div>

                  <div className={classes['input']}>
                    <TextField
                      id='reimbursement-category'
                      label='Reimbursement Category'
                      value={claim.reimbursement_category}
                      disabled={disableControls}
                      onChange={event =>
                        dispatch(
                          setClaimBaseDetails({
                            value: event.target.value,
                            field: 'reimbursement_category'
                          })
                        )
                      }
                      error={errors.reimbursement_category}
                    />
                  </div>
                </React.Fragment>
              )}

              <div className={classes['input']}>
                <TextField
                  id='official-receipt-number'
                  label='Official Receipt Number'
                  value={claim.official_receipt_number}
                  disabled={disableControls}
                  required
                  onChange={event =>
                    dispatch(
                      setClaimBaseDetails({
                        value: event.target.value,
                        field: 'official_receipt_number'
                      })
                    )
                  }
                  error={errors.official_receipt_number}
                />
              </div>
            </div>

            <div className={classes['child-container']}>
              <DiagnosisList items={claim.diagnosis} errors={errors.diagnosis} />
            </div>
          </Paper>

          <div className={classes['tables-container']}>
            <div className={classes['left-side']} id='left-side-panel'>
              <ProcedureList data={claim.procedures} isDental={type === 'Dental'} />
              <HospitalItemList data={claim.hospital_items} />
              <ProfessionalFeeList data={claim.professional_fees} />
              <ClaimsRemarks data={claim.claim_remarks} />
              <UploadedList
                field={{
                  index: 'tags.properties.claim_number',
                  value: props.match.params ? props.match.params._claimNumber : ''
                }}
                isAdd={props.isAdd}
                onItemClick={data => setViewFile(state => ({ ...state, ...data, show: true }))}
              />
              <FileViewer
                url={viewFile.data}
                show={viewFile.show}
                title={viewFile.title}
                fileName={viewFile.fileName}
                fileType={viewFile.fileType}
                onClose={() => setViewFile(state => ({ ...state, show: false }))}
              />
            </div>

            <div className={classes['right-side']} id='right-side-panel'>
              <div className={classes['wrapper']}>
                <div className={classes['sticky']}>
                  {/* temporary dental proc  dental_procedure */}
                  <DentalProcedureForm
                    diagnosis={claim.diagnosis}
                    errors={errors.procedure}
                    doValidate={doValidateProcedure}
                  />
                  <ProcedureForm
                    diagnosis={claim.diagnosis}
                    errors={errors.procedure}
                    doValidate={doValidateProcedure}
                  />
                  <HospitalItemForm
                    diagnosis={claim.diagnosis}
                    errors={errors.hospital_item}
                    doValidate={doValidateHospitalItem}
                  />
                  <ProfessionalFeeForm
                    diagnosis={claim.diagnosis}
                    errors={errors.professional_fee}
                    doValidate={doValidateProfessionalFee}
                  />
                </div>
              </div>
            </div>
          </div>

          <div className={classes['grand-total']}>
            <Typography variant='subtitle2'>TOTAL PHP </Typography>
            <Typography id={'grand-total'} variant='subtitle2'>
              {CurrencyFormatter(grandTotal)}
            </Typography>
          </div>

          <ClaimFooter
            isAdd={isAdd}
            handleOnSave={goBack => onSave({ allowedFields, lineItemFields, goBack })}
            doValidate={() => {
              const valid = doValidateAll({ validator: MainValidator, field: allowedFields })
              return valid
            }}
            setOpenDialog={setOpenDialog}
            openDialog={openDialog}
            setShowReimbursementModal={setShowReimbursementModal}
            onOk={() => {
              setOpenDialog(false)
              props.history.push(`/batches/${batchNumber}`)
            }}
            hasClaimChanges={hasClaimChanges}
          />
          <ReimbursementInfo
            claim={{ ...claim, claimNumber: _claimNumber, batch_number: batchNumber }}
            data={claim.reimbursement_info}
            show={showReimbursementModal}
            setShowModal={setShowReimbursementModal}
            service={apiService}
            doViewClaimNotice={doViewClaimNotice}
            handleOnSave={reimbursement_info =>
              dispatch(
                setClaimBaseDetails({
                  value: reimbursement_info,
                  field: 'reimbursement_info'
                })
              )
            }
            lookup={'return-reason'}
          />
        </div>
      )}
    </React.Fragment>
  )
}
