import React from 'react'
import Button from '@material-ui/core/Button'

// Components
import Alert from 'SharedComponents/Alert/Alert'

// Styles
import classes from './ClaimFooter.module.scss'

export default function ClaimFooter (props) {
  // Variables
  const { isAdd = false, setShowReimbursementModal } = props
  // Events
  const { handleOnSave, doValidate, setOpenDialog, openDialog, onOk, hasClaimChanges } = props

  const [saveAlert, setSaveAlert] = React.useState({ mode: 'list', show: false, next: () => {} })

  const [validateModal, setValidateModal] = React.useState({ show: false, focus: '' })

  const doValidateMiddleware = () => {
    const res = doValidate()
    if (res) {
      setValidateModal({ show: true, focus: res })
      return true
    }

    return false
  }

  const doSaveMiddleware = ({ mode, next = () => {} }) => {
    if (!hasClaimChanges()) {
      setSaveAlert(state => ({ ...state, mode, next, show: true }))
    } else {
      next()
    }
  }

  return (
    <div className={classes.root}>
      {setShowReimbursementModal && (
        <Button
          variant='contained'
          color='primary'
          id='return'
          className='button-positive'
          onClick={() =>
            !doValidateMiddleware() && doSaveMiddleware({ mode: 'stay', next: () => setShowReimbursementModal(true) })
          }
          disabled={isAdd}
        >
          Return
        </Button>
      )}

      <Button
        variant='contained'
        color='primary'
        id='save-for-now'
        className='button-positive'
        onClick={() => !doValidateMiddleware() && handleOnSave('list')}
      >
        Save for now
      </Button>
      <Button
        variant='contained'
        color='primary'
        id='save-and-close'
        className='button-positive'
        onClick={() => !doValidateMiddleware() && handleOnSave('dashboard')}
      >
        Save and Close
      </Button>
      <Button
        variant='contained'
        color='primary'
        id='cancel'
        className='button-negative'
        onClick={() => setOpenDialog(true)}
      >
        Cancel
      </Button>

      <Alert
        open={saveAlert.show}
        message='Encoded claim details are not yet saved. Do you want to save the claim details first?'
        cancelLabel='No'
        okLabel='Yes'
        onCancel={() => setSaveAlert(state => ({ ...state, show: false }))}
        onOk={async () => {
          setSaveAlert(state => ({ ...state, show: false }))
          const res = await handleOnSave(saveAlert.mode)
          if (res) saveAlert.next()
        }}
      />

      <Alert
        open={openDialog}
        message='Are you sure you want to cancel?'
        cancelLabel='No'
        okLabel='Yes'
        onCancel={() => setOpenDialog(false)}
        onOk={onOk}
      />

      <Alert
        open={validateModal.show}
        message='Please input all mandatory fields.'
        okLabel='Ok'
        onOk={() => {
          setTimeout(() => {
            const container = document.getElementById(validateModal.focus)
            container.focus()
            container.scrollIntoView()
          }, 500)
          setValidateModal({ show: false, focus: '' })
        }}
      />
    </div>
  )
}
