import React from 'react'
import Paper from '@material-ui/core/Paper'
import TextField from '@material-ui/core/TextField'
import Typography from '@material-ui/core/Typography'
import Autocomplete from '@material-ui/lab/Autocomplete'
import CircularProgress from '@material-ui/core/CircularProgress'

import classes from './AutoComplete2.module.scss'

export default function AutoComplete2 (props) {
  const { label = '', required, readOnly, ...other } = props
  const { value = '', field = 'id', loading, error, suggestions = [] } = props
  const { onChange } = props

  const [focus, setFocus] = React.useState()
  const [_error, setError] = React.useState()

  React.useEffect(() => {
    setError()
    if (typeof value === 'string') {
      if (!value.trim()) setError(error)
    }
  }, [error, value, loading])

  const handleOnChange = (value, search) => {
    setError()
    if (typeof onChange === 'function') onChange(value, search)
  }

  const handleBlur = e => {
    if (!e.currentTarget.contains(e.relatedTarget)) setFocus(false)
  }

  return (
    <div className={classes.root}>
      {label && (
        <Typography variant='subtitle2' className={classes.label}>
          {label}
          {!readOnly && required ? ' *' : ''}
        </Typography>
      )}

      <div className={classes.wrapper}>
        {readOnly ? (
          //  Read Only Value
          <Typography
            readOnly
            id={props.id}
            variant='subtitle2'
            className={`${classes.label} ${classes['read-only-value']}`}
          >
            {value}
          </Typography>
        ) : (
          <React.Fragment>
            <Paper
              className={`${classes['autocomplete-field']} ${
                _error ? classes['has-error'] : focus && classes.focus
              } box-shadow-field`}
              onBlur={handleBlur}
            >
              <Autocomplete
                className={classes.input}
                options={suggestions}
                noOptionsText={'Enter atleast 3 characters to search.'}
                loading={loading || false}
                renderInput={params => (
                  <TextField
                    {...params}
                    className={classes.input}
                    ref={params.InputProps.ref}
                    InputProps={{
                      ...params.InputProps,
                      disableUnderline: true,
                      endAdornment: (
                        <React.Fragment>
                          {loading ? <CircularProgress color='inherit' size={20} /> : null}
                        </React.Fragment>
                      )
                    }}
                  />
                )}
                getOptionLabel={option => (typeof option === 'string' ? option : `${option[field]}`)}
                // renderOption={option => <React.Fragment>{option[field]}</React.Fragment>}
                {...other}
                onChange={(event, value) => {
                  handleOnChange(value, false)
                }}
                onInputChange={(event, value) => {
                  handleOnChange(value, true)
                }}
                onFocus={() => setFocus(true)}
                inputValue={value}
                value={typeof value === 'object' ? value[field] : value}
                freeSolo
                autoSelect
                openOnFocus
                autoHighlight
                disableClearable
                // forcePopupIcon={false}
              />
            </Paper>
            {_error && <span className={classes.error}>{_error}</span>}
          </React.Fragment>
        )}
      </div>
    </div>
  )
}
