import React from 'react'
import Paper from '@material-ui/core/Paper'

// Components
import AutoComplete from 'SharedComponents/AutoComplete2/AutoComplete2'
import DropDown from 'SharedComponents/DropDown/DropDown'
import TextField from 'SharedComponents/TextField/CustomTextField'
import TextFieldCurrency from 'SharedComponents/TextFieldCurrency/TextFieldCurrency'
import Button from '@material-ui/core/Button'
import DiscrepancyBase from './../DiscrepancyBase/DiscrepancyBase'
import Alert from 'SharedComponents/Alert/Alert'

// Styles
import classes from './HospitalItemForm.module.scss'

// Services
import ClaimsAdjudicationServices from 'Services/Api/Claims/ClaimsAdjudicationServices'

// Store
import { useSelector, useDispatch } from 'react-redux'
import { ClaimsAnalysis as actions } from 'Pages/Protected/Claims/Store/actions'
const {
  setHospitalItem,
  resetSuggestion,
  setSuggestion,
  addClaimHospitalItem,
  updateClaimHospitalItem,
  removeClaimHospitalItem,
  resetError
} = actions

export default function HospitalItemForm (props) {
  const dispatch = useDispatch()
  const {
    index,
    discrepancy = {},
    error = {},
    doValidate,
    permissions,
    diagnosis,
    showAmountPaidByMember = true,
    showPhilhealthRemarks = true,
    showAmountNotCovered = true,
    showAmountDueFromMember = true
  } = props

  const hospitalItem = useSelector(({ ClaimsAnalysis: state }) => state.hospitalItem)
  const suggestions = useSelector(({ ClaimsAnalysis: state }) => state.suggestions)
  const dropDown = useSelector(({ ClaimsAnalysis: state }) => state.dropDown)

  const [debounce, setDebounce] = React.useState({
    hospitalItem: null
  })

  const [fetching, setFetching] = React.useState({
    description: false,
    code: false,
    name: false,
    item_code: false
  })

  const [openDeleteDialog, setOpenDeleteDialog] = React.useState({ show: false, index: null })
  const [_errors, setErrors] = React.useState({})

  // ***** REACTIVE

  React.useEffect(() => {
    for (var prop in error) {
      if (error.hasOwnProperty(prop)) {
        setErrors(error)
        break
      }
    }
  }, [error, index])

  React.useEffect(() => {
    if (diagnosis) {
      const { diagnosis_code, diagnosis_description } = hospitalItem.details
      if (!diagnosis.find(item => item.code === diagnosis_code && item.description === diagnosis_description)) {
        dispatch(setHospitalItem({ details: { diagnosis_code: '', diagnosis_description: '' } }))
      }
    }
  }, [diagnosis])

  // ***** EVENTS

  const handleOnChangeHospitalItem = ({ value, field, search }) => {
    if (search) {
      dispatch(setHospitalItem({ details: { [field]: value } }))
      dispatch(resetSuggestion({ field: 'hospitalItem' }))
      getHospitalItemSuggestions({ value, by: field })
    } else {
      const _value = value || {}
      dispatch(
        setHospitalItem({
          details: {
            id: _value.id,
            name: _value.name,
            item_code: _value.item_code
          }
        })
      )
      dispatch(resetSuggestion({ field: 'hospitalItem' }))
    }
  }

  const handleOnChangeDiagnosis = ({ value, field }) => {
    const fieldArr = field.split('_')
    const _field = fieldArr.length === 2 ? fieldArr[1] : `${fieldArr[1]}_${fieldArr[2]}`
    const { code, description, given_diagnosis } = diagnosis.find(d => d[_field] === value)
    dispatch(
      setHospitalItem({
        details: {
          diagnosis_code: code,
          diagnosis_description: description,
          diagnosis_given_diagnosis: given_diagnosis
        }
      })
    )
  }
  // ***** FETCHING

  const getHospitalItemSuggestions = ({ value = '', by }) => {
    if (value.trim().length < 3) return

    clearTimeout(debounce.hospitalItem)
    setFetching(state => ({ ...state, [by]: true }))
    setDebounce(state => ({
      ...state,
      hospitalItem: setTimeout(() => {
        if (value) {
          ClaimsAdjudicationServices.lookups({
            on: 'hospital-item',
            params: { query: value, by }
          })
            .then(res => {
              const { data } = res.data
              dispatch(
                setSuggestion({
                  field: 'hospitalItem',
                  suggestion: data
                })
              )
            })
            .finally(() => {
              setFetching(state => ({ ...state, [by]: false }))
            })
        }
      }, 500)
    }))
  }

  // ***** ACTIONS

  const handleOnChange = ({ value, field }) => {
    dispatch(setHospitalItem({ details: { [field]: value } }))
  }

  const handleOnSave = index => {
    dispatch(resetError({ hospital_item: { index: null, details: {} } }))
    const _error = doValidate({ data: hospitalItem.details, index })
    setErrors(_error)
    if (Object.keys(_error).length <= 0) {
      dispatch(addClaimHospitalItem(hospitalItem.details))
      dispatch(setHospitalItem({ show: false, mode: '', index: '' }))
    }
  }

  const handleOnUpdate = index => {
    dispatch(resetError({ hospital_item: { index: null, details: {} } }))
    const _error = doValidate({ data: hospitalItem.details, index })
    setErrors(_error)
    if (Object.keys(_error).length <= 0) {
      dispatch(updateClaimHospitalItem({ index, details: hospitalItem.details }))
      dispatch(setHospitalItem({ show: false, mode: '', index: '' }))
    }
  }

  const handleOnRemoveConfirm = index => {
    setOpenDeleteDialog({ show: true, index })
  }

  const handleOnRemove = index => {
    dispatch(resetError({ hospital_item: { index: null, details: {} } }))
    dispatch(setHospitalItem({ show: false, mode: '', index: '' }))
    dispatch(removeClaimHospitalItem({ index }))
    setOpenDeleteDialog({ show: false, index: null })
  }

  const handleOCancel = index => {
    dispatch(resetError({ index }))
    dispatch(setHospitalItem({ show: false, mode: '', index: '' }))
  }

  return (
    <React.Fragment>
      {/* {console.group('Hospital Item Form')} */}
      {/* {console.log('Data', hospitalItem)} */}
      {/* {console.log('Discrepancy', discrepancy)} */}
      {/* {console.log('Errors', error)} */}
      {/* {console.log('Suggestions ', suggestions)} */}
      {/* {console.log('Index ', hospitalItem.index, index)} */}
      {/* {console.groupEnd()} */}
      {hospitalItem.show && hospitalItem.index === index && (
        <Paper className={classes.root}>
          {diagnosis && (
            <React.Fragment>
              <div className={classes.groups}>
                <div className={classes.input}>
                  <DropDown
                    id='given-diagnosis'
                    label='Given Diagnosis'
                    required
                    items={diagnosis.map(({ given_diagnosis }) => ({
                      text: given_diagnosis,
                      value: given_diagnosis
                    }))}
                    value={hospitalItem.details.diagnosis_given_diagnosis}
                    onChange={event =>
                      handleOnChangeDiagnosis({
                        field: 'diagnosis_given_diagnosis',
                        value: event.target.value
                      })
                    }
                    error={_errors.diagnosis_given_diagnosis}
                    readOnly={!permissions.update}
                  />
                </div>
              </div>
              <div className={classes.groups}>
                <div className={classes.input}>
                  <DropDown
                    id='icd10-description'
                    required
                    label='ICD10 Description'
                    items={diagnosis.map(({ description }) => ({ text: description, value: description }))}
                    value={hospitalItem.details.diagnosis_description}
                    onChange={event =>
                      handleOnChangeDiagnosis({
                        field: 'diagnosis_description',
                        value: event.target.value
                      })
                    }
                    readOnly={!permissions.update}
                    error={_errors.diagnosis_description}
                  />
                </div>
                <div className={classes.input}>
                  <DropDown
                    id='icd10-code'
                    required
                    label='ICD10 Code'
                    items={diagnosis.map(({ code }) => ({ text: code, value: code }))}
                    value={hospitalItem.details.diagnosis_code}
                    onChange={event =>
                      handleOnChangeDiagnosis({
                        field: 'diagnosis_code',
                        value: event.target.value
                      })
                    }
                    readOnly={!permissions.update}
                    error={_errors.diagnosis_code}
                  />
                </div>
              </div>
            </React.Fragment>
          )}
          <div className={classes.groups}>
            <div className={classes.input}>
              <DiscrepancyBase
                id='dp-hospital-item'
                discrepancy={{
                  message: 'Failed',
                  result: { item_code: discrepancy.item_code, name: discrepancy.name }
                }}
              >
                <div className={classes.description}>
                  <AutoComplete
                    label={'Item Name'}
                    required
                    id={`hospital-item-name`}
                    value={hospitalItem.details.name}
                    suggestions={suggestions.hospitalItem}
                    field={'name'}
                    onChange={(value, search) => handleOnChangeHospitalItem({ field: 'name', value, search })}
                    error={_errors.name}
                    readOnly={!permissions.update}
                  />
                </div>
                <div className={classes.code}>
                  <AutoComplete
                    label={'Item Code'}
                    required
                    id={`hospital-item-code`}
                    value={hospitalItem.details.item_code}
                    suggestions={suggestions.hospitalItem}
                    field={'item_code'}
                    onChange={(value, search) => handleOnChangeHospitalItem({ field: 'item_code', value, search })}
                    error={_errors.item_code}
                    readOnly={!permissions.update}
                  />
                </div>
              </DiscrepancyBase>
            </div>
          </div>

          <div className={classes.groups}>
            <div className={classes.input}>
              <DiscrepancyBase
                id='dp-hospitalItem-gross-amount'
                discrepancy={{
                  message: 'Failed',
                  result: discrepancy.gross_amount
                }}
              >
                <TextFieldCurrency
                  id='hospital-item-gross-amount'
                  required
                  label='Gross Amount'
                  value={hospitalItem.details.gross_amount}
                  onChange={event =>
                    handleOnChange({
                      field: 'gross_amount',
                      value: event.target.value
                    })
                  }
                  error={_errors.gross_amount}
                  readOnly={!permissions.update}
                />
              </DiscrepancyBase>
            </div>

            <div className={classes.input}>
              <DiscrepancyBase
                id='dp-hospitalItem-vat-amount'
                discrepancy={{
                  message: 'Failed',
                  result: discrepancy.vat_amount
                }}
              >
                <TextFieldCurrency
                  id='hospital-item-vat-amount'
                  label='VAT Amount'
                  value={hospitalItem.details.vat_amount}
                  onChange={event =>
                    handleOnChange({
                      field: 'vat_amount',
                      value: event.target.value
                    })
                  }
                  error={_errors.vat_amount}
                  readOnly={!permissions.update}
                />
              </DiscrepancyBase>
            </div>
          </div>
          {showAmountPaidByMember && (
            <div className={classes.groups}>
              <div className={classes.input}>
                <DiscrepancyBase
                  id='dp-hospitalItem-amount-paid-by-member'
                  discrepancy={{
                    message: 'Failed',
                    result: discrepancy.amount_paid_by_member
                  }}
                >
                  <TextFieldCurrency
                    id='hospital-item-amount-paid-by-member'
                    label='Amount Paid by Member'
                    value={hospitalItem.details.amount_paid_by_member}
                    onChange={event =>
                      handleOnChange({
                        field: 'amount_paid_by_member',
                        value: event.target.value
                      })
                    }
                    error={_errors.amount_paid_by_member}
                    readOnly={!permissions.update}
                  />
                </DiscrepancyBase>
              </div>
            </div>
          )}
          <div className={classes.groups}>
            <div className={classes.input}>
              <DiscrepancyBase
                id='dp-philhealth-amount-paid'
                discrepancy={{
                  message: 'Failed',
                  result: discrepancy.philhealth_amount_paid
                }}
              >
                <TextFieldCurrency
                  id='philhealth-amount-paid'
                  label='PhilHealth Amount Paid'
                  value={hospitalItem.details.philhealth_amount_paid}
                  onChange={event =>
                    handleOnChange({
                      field: 'philhealth_amount_paid',
                      value: event.target.value
                    })
                  }
                  error={_errors.philhealth_amount_paid}
                  readOnly={!permissions.update}
                />
              </DiscrepancyBase>
            </div>
            {showPhilhealthRemarks && (
              <div className={classes.input}>
                <DiscrepancyBase
                  id='dp-philhealth-remarks'
                  discrepancy={{
                    message: 'Failed',
                    result: discrepancy.philhealth_remarks
                  }}
                >
                  <TextField
                    multiline
                    rows={3}
                    id='philhealth-remarks'
                    label='PhilHealth Remarks'
                    value={hospitalItem.details.philhealth_remarks}
                    onChange={event =>
                      handleOnChange({
                        field: 'philhealth_remarks',
                        value: event.target.value
                      })
                    }
                    error={_errors.philhealth_remarks}
                    readOnly={!permissions.update}
                  />
                </DiscrepancyBase>
              </div>
            )}
          </div>
          {showAmountNotCovered && (
            <React.Fragment>
              <div className={classes.groups}>
                <div className={classes.input}>
                  <DiscrepancyBase
                    id='dp-amount-not-covered'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.amount_not_covered
                    }}
                  >
                    <TextFieldCurrency
                      id='amount-not-covered'
                      label='Amount Not Covered'
                      value={hospitalItem.details.amount_not_covered}
                      onChange={event => {
                        if (event.target.value === 0) {
                          hospitalItem.details.reason = ''
                          setErrors(state => ({ ...state, reason: '' }))
                        }

                        handleOnChange({
                          field: 'amount_not_covered',
                          value: event.target.value
                        })
                      }}
                      error={_errors.amount_not_covered}
                      readOnly={!permissions.update}
                    />
                  </DiscrepancyBase>
                </div>
              </div>
              <div className={classes.groups}>
                <div className={classes.input}>
                  <DiscrepancyBase
                    id='dp-amount-not-covered-reason'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.reason
                    }}
                  >
                    <DropDown
                      id='amount-not-covered-reason'
                      label='Reason'
                      required={hospitalItem.details.amount_not_covered > 0}
                      disableComponents={hospitalItem.details.amount_not_covered === 0}
                      value={hospitalItem.details.reason}
                      items={dropDown.reason}
                      onChange={event =>
                        handleOnChange({
                          field: 'reason',
                          value: event.target.value
                        })
                      }
                      error={_errors.reason}
                      readOnly={!permissions.update}
                    />
                  </DiscrepancyBase>
                </div>
              </div>
            </React.Fragment>
          )}
          {showAmountDueFromMember && (
            <React.Fragment>
              <div className={classes.groups}>
                <div className={classes.input}>
                  <DiscrepancyBase
                    id='dp-amount-due-from-member'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.amount_due_from_member
                    }}
                  >
                    <TextFieldCurrency
                      id='amount-due-from-member'
                      label='Amount Due from Member'
                      value={hospitalItem.details.amount_due_from_member}
                      onChange={event => {
                        if (event.target.value === 0) {
                          hospitalItem.details.reason_for_charging_member = ''
                          setErrors(state => ({ ...state, amount_due_from_member: '' }))
                        }

                        handleOnChange({
                          field: 'amount_due_from_member',
                          value: event.target.value
                        })
                      }}
                      error={_errors.amount_due_from_member}
                      readOnly={!permissions.update}
                    />
                  </DiscrepancyBase>
                </div>
              </div>
              <div className={classes.groups}>
                <div className={classes.input}>
                  <DiscrepancyBase
                    id='dp-hospital-item'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.reason_for_charging_member
                    }}
                  >
                    <DropDown
                      id='reason-for-charging-member'
                      label='Reason for Charging Member'
                      required={hospitalItem.details.amount_due_from_member > 0}
                      disableComponents={hospitalItem.details.amount_due_from_member === 0}
                      value={hospitalItem.details.reason_for_charging_member}
                      items={dropDown.reason_charging}
                      onChange={event =>
                        handleOnChange({
                          field: 'reason_for_charging_member',
                          value: event.target.value
                        })
                      }
                      error={_errors.reason_for_charging_member}
                      readOnly={!permissions.update}
                    />
                  </DiscrepancyBase>
                </div>
              </div>
            </React.Fragment>
          )}
          {showAmountDueFromMember && (
            <React.Fragment>
              <div className={classes.groups}>
                <div className={classes.input}>
                  <DiscrepancyBase
                    id='dp-amount-due-from-client'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.amount_due_from_client
                    }}
                  >
                    <TextFieldCurrency
                      id='amount-due-from-client'
                      label='Amount Due from Client'
                      value={hospitalItem.details.amount_due_from_client}
                      onChange={event => {
                        if (event.target.value === 0) {
                          hospitalItem.details.reason_for_charging_client = ''
                          setErrors(state => ({ ...state, amount_due_from_client: '' }))
                        }

                        handleOnChange({
                          field: 'amount_due_from_client',
                          value: event.target.value
                        })
                      }}
                      error={_errors.amount_due_from_client}
                      readOnly={!permissions.update}
                    />
                  </DiscrepancyBase>
                </div>
              </div>
              <div className={classes.groups}>
                <div className={classes.input}>
                  <DiscrepancyBase
                    id='dp-amount-due-from-client'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.reason_for_charging_client
                    }}
                  >
                    <DropDown
                      id='reason-for-charging-client'
                      label='Reason for Charging Client'
                      required={hospitalItem.details.amount_due_from_client > 0}
                      disableComponents={hospitalItem.details.amount_due_from_client === 0}
                      value={hospitalItem.details.reason_for_charging_client}
                      items={dropDown.reason_charging}
                      onChange={event =>
                        handleOnChange({
                          field: 'reason_for_charging_client',
                          value: event.target.value
                        })
                      }
                      error={_errors.reason_for_charging_client}
                      readOnly={!permissions.update}
                    />
                  </DiscrepancyBase>
                </div>
              </div>
            </React.Fragment>
          )}
          <div className={`${classes.groups} ${classes.buttons}`}>
            {hospitalItem.mode === 'add' && permissions.create && (
              <React.Fragment>
                <Button
                  id='hospital-save-button'
                  variant='contained'
                  color='primary'
                  className='button-positive'
                  onClick={() => handleOnSave(hospitalItem.index)}
                >
                  Save
                </Button>
              </React.Fragment>
            )}
            {hospitalItem.mode !== 'add' && permissions.update && (
              <React.Fragment>
                <Button
                  id='hospital-update-button'
                  variant='contained'
                  color='primary'
                  className='button-positive'
                  onClick={() => handleOnUpdate(hospitalItem.index)}
                >
                  Update
                </Button>
                {permissions.delete && (
                  <Button
                    id='hospital-delete-button'
                    variant='contained'
                    color='secondary'
                    className='button-negative'
                    onClick={() => handleOnRemoveConfirm(hospitalItem.index)}
                  >
                    Delete
                  </Button>
                )}
              </React.Fragment>
            )}
            <Button
              id={`hospital-${permissions.create && permissions.update ? 'cancel' : 'close'}-button`}
              variant='contained'
              color='secondary'
              className='button-negative'
              onClick={() => handleOCancel(hospitalItem.index)}
            >
              {permissions.create && permissions.update ? 'Cancel' : 'Close'}
            </Button>
          </div>
        </Paper>
      )}
      <Alert
        open={openDeleteDialog.show}
        message='Are you sure you want to delete this hospital item?'
        cancelLabel='No'
        okLabel='Yes'
        onCancel={() => setOpenDeleteDialog({ show: false, index: null })}
        onOk={() => handleOnRemove(hospitalItem.index)}
      />
    </React.Fragment>
  )
}
