import React from 'react'
import Paper from '@material-ui/core/Paper'
import Typography from '@material-ui/core/Typography'

// Components
import BaseDetails from '../BaseDetails/BaseDetails'
import DiagnosisList from '../DiagnosisList/DiagnosisList'
import TextField from 'SharedComponents/TextField/CustomTextField'
import TextFieldCurrency from 'SharedComponents/TextFieldCurrency/TextFieldCurrency'
import DatePicker from 'SharedComponents/DatePicker2/DatePicker2'
import DropDown from 'SharedComponents/DropDown/DropDown'
import AutoComplete from 'SharedComponents/AutoComplete2/AutoComplete2'
import ProcedureList from '../ProcedureList/ProcedureList'
import HospitalItemList from '../HospitalItemList/HospitalItemList'
import ProfessionalFeeList from '../ProfessionalFeeList/ProfessionalFeeList'
import ClaimFooter from '../ClaimFooter/ClaimFooter'
import DiscrepancyBase from '../DiscrepancyBase/DiscrepancyBase'
import DisapprovedLabel from '../DisapprovedLabel/DisapprovedLabel'
import RightPanelTabs from '../RightPanelTabs/RightPanelTabs'
import ReimbursementInfo from '../../../ReimbursementInfo/ReimbursementInfo'
import ClaimsRemarks from '../ClaimsRemarks/ClaimsRemarks'
import UploadedList from './UploadedList/UploadedList'

// Services
import ClaimsAdjudicationServices from 'Services/Api/Claims/ClaimsAdjudicationServices'

// Utilities
import { CurrencyFormatter } from 'Utilities/TextFormatter'

// Libraries
import moment from 'moment'

// Styles
import classes from './ClaimReimbursement.module.scss'

// Validator
import MainValidator from '../../Validators/Claim'
import AdditionalFields from './AdditionalFields'

// Store
import { useSelector, useDispatch } from 'react-redux'
import { ClaimsAnalysis as actions } from 'Pages/Protected/Claims/Store/actions'

const { setClaimFields, setClaimBaseDetails, initDropDowns, initSuggestions } = actions

export default function ClaimReimbursement (props) {
  const dispatch = useDispatch()
  // Events
  const { onViewClaimNotice, onChangeDoctor = () => {}, onReject, onChangeFacility, onChangeRoom } = props
  // Variables
  const { claim_number } = props.match.params
  const { claim, permissions, _type, grandTotal } = props
  const { fetching } = props
  // Services
  const { getClaimDetails, onChangePro, getReason, getRoomsList } = props
  // Validation
  const { doValidateAll, doValidateProcedure, doValidateHospitalItem, doValidateProfessionalFee } = props

  const suggestions = useSelector(({ ClaimsAnalysis: state }) => state.suggestions)
  const dropDown = useSelector(({ ClaimsAnalysis: state }) => state.dropDown)
  const discrepancy = useSelector(({ ClaimsAnalysis: state }) => state.discrepancy)
  const error = useSelector(({ ClaimsAnalysis: state }) => state.error)
  const escalations = useSelector(({ ClaimsAnalysis: state }) => state.escalations)
  const inquiries = useSelector(({ ClaimsAnalysis: state }) => state.inquiries)
  const recon = useSelector(({ ClaimsAnalysis: state }) => state.recon)
  const [type, setType] = React.useState('InPatient')

  // const [openDialog, setOpenDialog] = React.useState(false)
  const [allowedFields, setAllowedFields] = React.useState({})
  const [initialAllowedFields, setInitialAllowedFields] = React.useState({})
  const [showReimbursementModal, setShowReimbursementModal] = React.useState(false)
  //

  React.useEffect(() => {
    dispatch(initSuggestions())
    dispatch(initDropDowns(['rooms']))
    getReason()

    const _allowedFields = AdditionalFields('InPatient')

    const _lineItemFields = {
      procedures: [],
      hospital_items: [],
      professional_fees: [],
      reimbursement_info: []
    }

    setTimeout(() => {
      setAllowedFields(_allowedFields)
      // setlineItemFields(_lineItemFields)
      dispatch(setClaimFields({ ..._allowedFields, ..._lineItemFields }))
    }, 10)

    setType(_allowedFields.reimbursement_type)
    getClaimDetails(data => {
      setType(data.reimbursement_details.type)
      if (data.reimbursement_details.type === 'InPatient') {
        getRoomsList({ field: 'facility_id', value: data.facility.id })
      }

      // Append All fields and values
      // Then Trim based on Type
      let _claim = AdditionalFields.trim(data.reimbursement_details.type, {
        approval_code: data.approval_code,
        contract_plan_type: data.contract_plan_type,
        loa_number: data.loa_number,
        reimbursement_type: data.reimbursement_details.type,
        member_account_number: data.member.account_number,
        member_last_name: data.member.last_name,
        member_first_name: data.member.first_name,
        member_middle_name: data.member.middle_name,
        facility_id: data.facility.id,
        facility_name: data.facility.name,
        date_from: data.date.from,
        medicine_category: data.medicine_category,
        reimbursement_category: data.reimbursement_details.category,
        official_receipt_number: data.reimbursement_details.official_receipt_number,
        diagnosis: data.diagnosis || [],
        procedures: data.procedures || [],
        professional_fees: data.professional_fees || [],
        hospital_items: data.hospital_items || [],
        reimbursement_info: data.reimbursement_info || [],
        reimbursement_info_analysts: data.reimbursement_info_analysts || [],
        claim_remarks: data.claim_remarks,
        date_to: data.date.to,
        room_type: data.room?.type,
        room_rate: data.room?.rate,
        pro_id: data.pro?.id,
        pro_name: data.pro?.name,
        doctor_id: data.doctor?.id,
        doctor_name: data.doctor?.name,
        department: data.department
      })

      dispatch(setClaimFields(_claim))
      // prevClaim = _claim
      setAllowedFields(AdditionalFields(data.reimbursement_details.type))
    })
  }, [])

  const onChangeReimbursementType = ({ value, field }) => {
    const _claim = AdditionalFields.trim(value, claim)
    dispatch(setClaimFields(_claim))
    setAllowedFields(AdditionalFields(value))
    dispatch(setClaimBaseDetails({ value, field }))
    setType(value)
    if (_claim.room_type !== undefined) {
      getRoomsList({ field: 'facility_id', value: _claim.facility_id })
    }
  }

  const setFields = () => {
    const _claim = AdditionalFields.trim(type, claim)
    dispatch(setClaimFields(_claim))
    setAllowedFields(AdditionalFields(type))
  }

  return (
    <div className={classes.root}>
      {claim && (
        <React.Fragment>
          <DisapprovedLabel
            id='dp-failed-adjudication'
            value='Failed Adjudication'
            reasons={discrepancy.disapproved ? discrepancy.disapproved : []}
          />

          <Paper className={classes['first-container']}>
            <BaseDetails
              claim={claim}
              onChange={({ value, field }) => dispatch(setClaimBaseDetails({ value, field }))}
              onChangeReimbursementType={onChangeReimbursementType}
              permissions={permissions.base}
              showReimbursement
              requiredFields={{ reimbursement_type: true, approval_code: true, loa_number: true }}
              getRoomsList={getRoomsList}
              onLoaPull={() => setFields()}
            />
            <div className={classes.wrap}>
              <div className={classes['input']}>
                <DiscrepancyBase
                  id='dp-medical-partner-id'
                  discrepancy={{
                    message: 'Failed',
                    result: discrepancy.facility_id
                  }}
                >
                  <AutoComplete
                    id='medical-partner-id'
                    required
                    label='Medical Partner ID'
                    value={claim.facility_id}
                    suggestions={suggestions.facility}
                    field={'id'}
                    loading={fetching['facility_id']}
                    onChange={(value, search) =>
                      onChangeFacility({
                        field: 'facility_id',
                        value,
                        search,
                        rooms: type === 'InPatient'
                      })
                    }
                    error={error.facility_id}
                    readOnly={!permissions.base.update}
                  />
                </DiscrepancyBase>
              </div>

              <div className={classes['input']}>
                <DiscrepancyBase
                  id='dp-medical-partner-facility-name'
                  discrepancy={{
                    message: 'Failed',
                    result: discrepancy.facility_name
                  }}
                >
                  <AutoComplete
                    id='medical-partner-facility-name'
                    required
                    label='Medical Partner (Facility) Name'
                    value={claim.facility_name}
                    suggestions={suggestions.facility}
                    field={'name'}
                    loading={fetching['facility_name']}
                    onChange={(value, search) =>
                      onChangeFacility({
                        field: 'facility_name',
                        value,
                        search,
                        rooms: type === 'InPatient'
                      })
                    }
                    error={error.facility_name}
                    readOnly={!permissions.base.update}
                  />
                </DiscrepancyBase>
              </div>

              {AdditionalFields.exist(allowedFields, 'doctor_id') && (
                <div className={classes['input']}>
                  <DiscrepancyBase
                    id='dp-medical-doctor-id'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.doctor_id
                    }}
                  >
                    <AutoComplete
                      id='dp-medical-partner-id'
                      required
                      label='Medical Partner ID'
                      value={claim.doctor_id}
                      suggestions={suggestions.doctor}
                      field={'id'}
                      loading={fetching['doctor_id']}
                      onChange={(value, search) =>
                        onChangeDoctor({
                          field: 'doctor_id',
                          value,
                          search
                        })
                      }
                      error={error.doctor_id}
                      readOnly={!permissions.base.update}
                    />
                  </DiscrepancyBase>
                </div>
              )}

              {AdditionalFields.exist(allowedFields, 'doctor_name') && (
                <div className={classes['input']}>
                  <DiscrepancyBase
                    id='dp-medical-doctor-name'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.doctor_name
                    }}
                  >
                    <AutoComplete
                      id='doctor-name'
                      required
                      label='Medical Partner (Doctor) Name'
                      value={claim.doctor_name}
                      suggestions={suggestions.doctor}
                      field={'full_name'}
                      loading={fetching['doctor_name']}
                      onChange={(value, search) =>
                        onChangeDoctor({
                          field: 'doctor_name',
                          value,
                          search
                        })
                      }
                      error={error.doctor_name}
                      readOnly={!permissions.base.update}
                    />
                  </DiscrepancyBase>
                </div>
              )}

              {type === 'InPatient' ? (
                <React.Fragment>
                  <div className={classes['input']}>
                    <DiscrepancyBase
                      id='dp-admission-date-from'
                      discrepancy={{
                        message: 'Failed',
                        result: discrepancy.date_from
                      }}
                    >
                      <DatePicker
                        id='admission-date-from'
                        required
                        label='Admission Date (From)'
                        value={claim.date_from}
                        onChange={date => {
                          dispatch(
                            setClaimBaseDetails({
                              value: moment(date)
                                .startOf('day')
                                .toISOString(),
                              field: 'date_from'
                            })
                          )
                          onChangeRoom({
                            room_type: claim.room_type,
                            availment_date: date,
                            facility_id: claim.facility_id
                          })
                        }}
                        error={error.date_from}
                        readOnly={!permissions.base.update}
                      />
                    </DiscrepancyBase>
                  </div>

                  <div className={classes['input']}>
                    <DiscrepancyBase
                      id='admission-date-to'
                      discrepancy={{
                        message: 'Failed',
                        result: discrepancy.date_to
                      }}
                    >
                      <DatePicker
                        id='admission-date-to'
                        required
                        label='Admission Date (To)'
                        value={claim.date_to}
                        onChange={date =>
                          dispatch(
                            setClaimBaseDetails({
                              value: date.toISOString(),
                              field: 'date_to'
                            })
                          )
                        }
                        error={error.date_to}
                        readOnly={!permissions.base.update}
                      />
                    </DiscrepancyBase>
                  </div>
                </React.Fragment>
              ) : (
                <div className={classes['input']}>
                  <DiscrepancyBase
                    id='admission-date-of-service'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.date_from
                    }}
                  >
                    <DatePicker
                      id='date-of-service'
                      required
                      label='Date of Service'
                      value={claim.date_from}
                      onChange={date => {
                        dispatch(
                          setClaimBaseDetails({
                            value: date.toISOString(),
                            field: 'date_from'
                          })
                        )
                        dispatch(
                          setClaimBaseDetails({
                            value: date.toISOString(),
                            field: 'date_to'
                          })
                        )
                      }}
                      error={error.date_from}
                      readOnly={!permissions.base.update}
                    />
                  </DiscrepancyBase>
                </div>
              )}

              {AdditionalFields.exist(allowedFields, 'department') && (
                <div className={classes['input']}>
                  <DiscrepancyBase
                    id='dp-department'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.department
                    }}
                  >
                    <TextField
                      id='department'
                      label='Department'
                      value={claim.department}
                      onChange={event =>
                        dispatch(
                          setClaimBaseDetails({
                            value: event.target.value,
                            field: 'department'
                          })
                        )
                      }
                      error={error.pro_id}
                      readOnly={!permissions.base.update}
                    />
                  </DiscrepancyBase>
                </div>
              )}

              {!AdditionalFields.exist(allowedFields, 'room_rate') && (
                <React.Fragment>
                  <div className={classes['input']}>
                    <DiscrepancyBase
                      id='dp-medicine-category'
                      discrepancy={{
                        message: 'Failed',
                        result: discrepancy.medicine_category
                      }}
                    >
                      <TextField
                        id='medicine-category'
                        label='Medicine Category'
                        value={claim.medicine_category}
                        onChange={event =>
                          dispatch(
                            setClaimBaseDetails({
                              value: event.target.value,
                              field: 'medicine_category'
                            })
                          )
                        }
                        readOnly={!permissions.base.update}
                        error={error.medicine_category}
                      />
                    </DiscrepancyBase>
                  </div>
                  <div className={classes['input']}>
                    <TextField
                      id='reimbursement-category'
                      label='Reimbursement Category'
                      value={claim.reimbursement_category}
                      onChange={event =>
                        dispatch(
                          setClaimBaseDetails({
                            value: event.target.value,
                            field: 'reimbursement_category'
                          })
                        )
                      }
                      readOnly={!permissions.base.update}
                      error={error.reimbursement_category}
                    />
                  </div>
                </React.Fragment>
              )}

              {AdditionalFields.exist(allowedFields, 'room_type') && (
                <div className={classes['input']}>
                  <DiscrepancyBase
                    id='dp-room-type'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.room_type
                    }}
                  >
                    <DropDown
                      id='room-type'
                      label='Room Type'
                      items={dropDown.rooms}
                      value={claim.room_type}
                      loading={fetching['room_type']}
                      onChange={event =>
                        onChangeRoom({
                          room_type: event.target.value,
                          availment_date: claim.date_from,
                          facility_id: claim.facility_id
                        })
                      }
                      error={error.room_type}
                      readOnly={!permissions.base.update}
                    />
                  </DiscrepancyBase>
                </div>
              )}

              {AdditionalFields.exist(allowedFields, 'room_rate') && (
                <div className={classes['input']}>
                  <DiscrepancyBase
                    id='dp-room-rate'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.room_rate
                    }}
                  >
                    <TextFieldCurrency
                      id='room-rate'
                      label='Room Rate'
                      value={claim.room_rate}
                      loading={fetching['room_rate']}
                      onChange={event =>
                        dispatch(
                          setClaimBaseDetails({
                            value: event.target.value,
                            field: 'room_rate'
                          })
                        )
                      }
                      error={error.room_rate}
                      readOnly={!permissions.base.update}
                    />
                  </DiscrepancyBase>
                </div>
              )}

              {AdditionalFields.exist(allowedFields, 'pro_id') && (
                <div className={classes['input']}>
                  <AutoComplete
                    id='pro-id'
                    required
                    label='PRO ID'
                    value={claim.pro_id}
                    suggestions={suggestions.pro}
                    field={'employee_id'}
                    loading={fetching['pro_id']}
                    onChange={(value, search) =>
                      onChangePro({
                        field: 'pro_id',
                        value,
                        search
                      })
                    }
                    error={error.pro_id}
                    readOnly={!permissions.base.update}
                  />
                </div>
              )}

              {AdditionalFields.exist(allowedFields, 'pro_name') && (
                <div className={classes['input']}>
                  <AutoComplete
                    id='pro-name'
                    label='PRO Name'
                    required
                    value={claim.pro_name}
                    suggestions={suggestions.pro}
                    field={'name'}
                    loading={fetching['pro_id']}
                    onChange={(value, search) =>
                      onChangePro({
                        field: 'pro_name',
                        value,
                        search
                      })
                    }
                    error={error.pro_name}
                    readOnly={!permissions.base.update}
                  />
                </div>
              )}

              {AdditionalFields.exist(allowedFields, 'room_rate') && (
                <React.Fragment>
                  <div className={classes['input']}>
                    <DiscrepancyBase
                      id='dp-medicine-category'
                      discrepancy={{
                        message: 'Failed',
                        result: discrepancy.medicine_category
                      }}
                    >
                      <TextField
                        id='medicine-category'
                        label='Medicine Category'
                        value={claim.medicine_category}
                        onChange={event =>
                          dispatch(
                            setClaimBaseDetails({
                              value: event.target.value,
                              field: 'medicine_category'
                            })
                          )
                        }
                        readOnly={!permissions.base.update}
                        error={error.medicine_category}
                      />
                    </DiscrepancyBase>
                  </div>

                  <div className={classes['input']}>
                    <DiscrepancyBase
                      id='dp-reimbursement-category'
                      discrepancy={{
                        message: 'Failed',
                        result: discrepancy.reimbursement_category
                      }}
                    >
                      <TextField
                        id='reimbursement-category'
                        label='Reimbursement Category'
                        value={claim.reimbursement_category}
                        onChange={event =>
                          dispatch(
                            setClaimBaseDetails({
                              value: event.target.value,
                              field: 'reimbursement_category'
                            })
                          )
                        }
                        readOnly={!permissions.base.update}
                        error={error.reimbursement_category}
                      />
                    </DiscrepancyBase>
                  </div>
                </React.Fragment>
              )}

              <div className={classes['input']}>
                <DiscrepancyBase
                  id='dp-official-receipt-number'
                  discrepancy={{
                    message: 'Failed',
                    result: discrepancy.official_receipt_number
                  }}
                >
                  <TextField
                    id='official-receipt-number'
                    label='Official Receipt Number'
                    value={claim.official_receipt_number}
                    required
                    onChange={event =>
                      dispatch(
                        setClaimBaseDetails({
                          value: event.target.value,
                          field: 'official_receipt_number'
                        })
                      )
                    }
                    readOnly={!permissions.base.update}
                    error={error.official_receipt_number}
                  />
                </DiscrepancyBase>
              </div>
            </div>

            <div className={classes['child-container']}>
              <div className={classes['left-side']}>
                <DiagnosisList
                  id='diagnosis-list'
                  permissions={permissions.diagnosis}
                  items={claim.diagnosis}
                  error={error.diagnosis}
                />
              </div>
            </div>
          </Paper>

          <div className={classes['tables-container']}>
            <div className={classes['left-side']} id='left-side-panel'>
              {permissions.procedures.read && (
                <ProcedureList
                  id='procedure'
                  data={claim.procedures}
                  doValidate={doValidateProcedure}
                  permissions={permissions.procedures}
                  diagnosis={claim.diagnosis}
                />
              )}
              {permissions.hospital_items.read && (
                <HospitalItemList
                  id='hospital-item'
                  data={claim.hospital_items}
                  doValidate={doValidateHospitalItem}
                  permissions={permissions.hospital_items}
                  diagnosis={claim.diagnosis}
                />
              )}
              {permissions.professional_fees.read && (
                <ProfessionalFeeList
                  id='professional-fee'
                  data={claim.professional_fees}
                  doValidate={doValidateProfessionalFee}
                  permissions={permissions.professional_fees}
                  diagnosis={claim.diagnosis}
                />
              )}
              <ClaimsRemarks
                data={claim.claim_remarks}
                permissions={!permissions.base.update}
                onChange={event =>
                  dispatch(
                    setClaimBaseDetails({
                      value: event.target.value,
                      field: 'claim_remarks'
                    })
                  )
                }
              />
              {/* <UploadedList /> */}
            </div>
            <div className={classes['right-side']} id='right-side-panel'>
              <div className={classes['wrapper']}>
                <div className={classes['sticky']}>
                  <RightPanelTabs inquiries={inquiries} escalations={escalations} recon={recon} {...props} />
                </div>
              </div>
            </div>
          </div>
          <div className={classes['grand-total']}>
            <Typography variant='subtitle2'>TOTAL PHP </Typography>
            <Typography id={'grand-total'} variant='subtitle2'>
              {CurrencyFormatter(grandTotal)}
            </Typography>
          </div>
          <ClaimFooter
            {...props}
            // userManagementService={userManagementService}
            // getUsers={getUsers}
            // doValidate={doValidateAll}
            // doEscalate={onEscalate}
            // doReassign={onReassign}
            // doViewAnalysisSheet={onViewAnalysisSheet}
            // doViewLetterOfAdvice={onViewLetterOfAdvice}
            // doViewClaimNotice={onViewClaimNotice}
            // doApprove={onApprove}
            // doSave={onSave}
            // doCancel={onCancel}
            // doRemove={onRemove}
            // doReturn={onReturn}
            onReject={() => setShowReimbursementModal(true)}
            doValidate={() => {
              const valid = doValidateAll({ validator: MainValidator, field: allowedFields })
              return valid
            }}
            permissions={permissions.footer}
            grandTotal={grandTotal}
            claimType={_type}
            isReimbursement
            reimbursement_info={claim.reimbursement_info}
          />
          <ReimbursementInfo
            claim={{ ...claim, claimNumber: claim_number }}
            data={claim.reimbursement_info_analysts}
            show={showReimbursementModal}
            setShowModal={setShowReimbursementModal}
            permission={permissions.reimbursement_info}
            service={ClaimsAdjudicationServices}
            // canPrintNotice={false}
            doViewClaimNotice={onViewClaimNotice}
            handleOnSave={reimbursement_info =>
              dispatch(
                setClaimBaseDetails({
                  value: reimbursement_info,
                  field: 'reimbursement_info_analysts'
                })
              )
            }
            doReject={() => onReject({ goBack: false })}
            lookup={'reject-reason'}
          />
        </React.Fragment>
      )}
    </div>
  )
}
