import React from 'react'

import moment from 'moment'
import lodash from 'lodash'

// Material Components
import Button from '@material-ui/core/Button'
import FormControl from '@material-ui/core/FormControl'
import GetAppIcon from '@material-ui/icons/GetApp'
import MenuItem from '@material-ui/core/MenuItem'
import Paper from '@material-ui/core/Paper'
import Radio from '@material-ui/core/Radio'
import RadioGroup from '@material-ui/core/RadioGroup'
import Select from '@material-ui/core/Select'
import Table from 'SharedComponents/Table/Table'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TableSortLabel from '@material-ui/core/TableSortLabel'
import Typography from '@material-ui/core/Typography'

// Shared Components
import DatePicker from 'SharedComponents/DatePicker2/DatePicker2'
import TextField from 'SharedComponents/TextField/CustomTextField'
import SearchTextComponent from 'SharedComponents/SearchTextComponent/SearchTextComponent'
import AutoComplete from 'SharedComponents/AutoComplete2/AutoComplete2'
import DropDown from 'SharedComponents/DropDown/DropDown'

// import ClaimsUtilLedgerReport from '../ClaimsUtilLedgerReport/ClaimsUtilLedgerReport'
import Utilization from './../../ClaimsViewer/Components/Utilization/Utilization'

// Services
// TODO
// Change ClaimsService when API for Treasury is available
import ClaimsServices from 'Services/Api/Claims/ClaimsServices'
import ClaimsReconServices from 'Services/Api/Claims/ClaimsReconServices'
import ClaimsAdjudicationServices from 'Services/Api/Claims/ClaimsAdjudicationServices'
import CustomerCareServices from 'Services/Api/Claims/CustomerCareServices'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'

// Utility
import { CurrencyFormatter } from 'Utilities/TextFormatter'
import { SafeAccess } from 'Utilities/SafeAccess'

// Styles
import classes from './style/UtilizationReport.module.scss'
import theme from './style/theme'

// Data
import legend from './data/legend'

// Config
import months from './config/months'
import getDefault from './config/types'
import Chart from './Chart/Chart'
import dropdown from './config/dropdown'

// Validator
import DateRangeValidator from '../TransmittalReport/validator/DateRangeValidator'
const inititalDate = { from: new Date().toISOString(), to: new Date().toISOString(), field: 'status_date' }

const clientColumns = [
  { label: 'Type', field: 'type', search: true, match: 'any', align: 'left' },
  { label: 'Total Amount', field: 'total_amount', search: true, match: 'number', align: 'center' },
  { label: 'Number of Cases', field: 'cases', search: true, match: 'number', align: 'center' },
  { label: 'Availed By Principals', field: 'availed_by_principals', search: true, match: 'number', align: 'center' },
  { label: 'Availed By Dependents', field: 'availed_by_dependents', search: true, match: 'number', align: 'center' }
]

const memberDataColumns = ['claim_no', 'diagnosis', 'procedures', 'provider']

export default function UtilizationReport (props) {
  const [reports, setReports] = React.useState({
    data: [],
    search: { client: { index: '', key: '' }, member: { index: '', key: '' } },
    range: { client_id: inititalDate, member_id: inititalDate },
    filter: {},
    orderBy: 'type',
    order: true,
    page: 1,
    limit: 10,
    count: 0,
    rowsPerPageOptions: [10, 15, 20]
  })
  const [debounce, setDebounce] = React.useState(null)
  const [reportType, setReportType] = React.useState('client')
  const [chartData, setChartData] = React.useState({})
  const [graph, setGraph] = React.useState({
    utilized: 0,
    ibnr: 0,
    total: 0
  })

  const [fetching, setFetching] = React.useState({
    main: false
  })

  const [totalAmount, setTotalAmount] = React.useState(0)
  const [limitType, setLimitType] = React.useState({ ABL: 'Annual Benefit Limit', MBL: 'Maximum Benefit Limit' })
  const [graphFilter, setGraphFilter] = React.useState('all')
  const [client, setClient] = React.useState({ name: '--', account_no: '', limit: { type: '', limit: 0 } })
  const [member, setMember] = React.useState({})
  const [utilizationLoa, setUtilizationLoa] = React.useState({})
  const [memberFetchStatus, setMemberFetchStatus] = React.useState('')
  const [dateRangeError, setDateRangeError] = React.useState({
    date_from: '',
    date_to: ''
  })
  const [suggestion, setSuggestion] = React.useState({
    client: [],
    member: []
  })
  const [memberUtilizations, setMemberUtilizations] = React.useState([])
  const [searchBy, setSearchBy] = React.useState({ value: '', field: '' })

  React.useEffect(() => {
    PageTitleService('Utilization Report')
    BreadcrumbsService.setSelectedItems(['utilizationReport'])
    setSearchBy({ value: 'Search by ID', field: getField({ value: 'Search by ID', type: reportType }) })
  }, [])

  React.useEffect(() => {
    fetchTable()
    // Watch filter items
  }, [reports.filter, reports.orderBy, reports.order, reports.limit, reports.page])

  const validate = () => {
    const field = reportType === 'client' ? 'client_id' : 'member_id'
    const { error } = DateRangeValidator({
      date_from: reports.range[field].from,
      date_to: reports.range[field].to
    })

    setDateRangeError({ ...error })
    return !!error
  }

  const getCoveredMonths = ({ data = [] }) => {
    const _field = reportType === 'client' ? 'client_id' : 'member_id'
    const from = new Date(reports.range[_field].from).getMonth()
    const to = new Date(reports.range[_field].to).getMonth()
    const covered = []
    const result = data.map(i => i.month - 1)

    for (let i = from; i <= to; i++) {
      covered.push(i)
    }

    return lodash.uniq([...covered, ...result]).map(i => months[i])
  }

  const fetchReport = () => {
    if (reportType === 'client' && reports.search.client.key === '') return
    if (reportType === 'member' && reports.search.member.key === '') return

    LoadingService.loading(true)
    if (reportType === 'member') {
      getMember(reports.search.member.key)
      // doRefreshMemberReport(refreshMemberReport + 1)
    } else {
      Promise.all([
        ClaimsServices.utilizationClient({
          search: reports.search.client.key,
          range: reports.range.client_id,
          filter: reports.filter,
          sort: `${reports.orderBy}|${reports.order ? 'asc' : 'desc'}`,
          page: reports.page,
          limit: reports.limit,
          type: graphFilter
        }),
        ClaimsReconServices.getClientUtilization({ client_account_no: reports.search.client.key }),
        CustomerCareServices.getClientUtilizationLoa({ client_account_no: reports.search.client.key })
      ])
        .then(res => {
          const [graphClientRes, clientUtilRes, clientUtilLoaRes] = res
          const { data: graph } = graphClientRes.data
          const { data: utilAmount } = clientUtilRes.data
          const { data: loaAmount } = clientUtilLoaRes.data
          // Total limit is just a placeholder, waiting for the endpoint // dirty fix
          setGraph(state => ({
            ...state,
            total: graph.client.limit.limit,
            utilized: utilAmount.total_utilization_amount || 0,
            ibnr: loaAmount.total_ibnr_amount || 0
          }))
          // setGraphData(p => ({ ...p, initial: graph.results }))
          setTotalAmount(graph.grand_total || 0)
          setClient(state => ({
            ...state,
            name: graph.client.name,
            account_no: graph.client.account_number,
            limit: graph.client.limit
          }))

          const covered_months = getCoveredMonths({ data: graph.results })
          setChartData({ ...graph, covered_months })
          fetchTable()
        })
        .catch(err => console.error(`Error Fetching Data: ${err}`))
    }
  }

  // Fetching table
  const fetchTable = () => {
    ClaimsServices.utilizationClientTable({
      search: reports.search.client.key,
      range: reports.range.client_id,
      filter: reports.filter,
      sort: `${reports.orderBy}|${reports.order ? 'asc' : 'desc'}`,
      page: reports.page,
      limit: reports.limit
    }).then(res => {
      const { results = [], page, count, limit } = res.data.data

      setReports(state => ({ ...state, data: results, count, page, limit }))
      LoadingService.loading(false)
    })
  }

  const handleOnFilter = args => {
    const { field, value, match } = args

    clearTimeout(debounce)
    setDebounce(
      setTimeout(() => {
        setReports(state => ({ ...state, filter: { ...state.filter, [field]: { value, match } } }))
      }, 500)
    )
  }

  const handleOnPaginate = args => {
    setReports(state => ({ ...state, ...args }))
  }

  const handleSearchChange = ({ value, field, search }) => {
    if (search) {
      setSuggestion(state => ({ ...state, [field]: [] }))
      getSuggestions({ value, field })
    } else {
      const _value = value || {}
      setReports(state => ({ ...state, search: { ...state.search, [field]: { index: searchBy.value.includes('ID') ? _value.id : _value.name, key: _value.id } } }))
      setSuggestion(state => ({ ...state, [field]: [] }))
    }
  }

  const getSuggestions = ({ value = '', field }) => {
    setReports(state => ({ ...state, search: { ...state.search, [field]: { ...state.search[field], index: value } } }))
    if (value.trim().length < 3) return

    setFetching(state => ({ ...state, main: true }))
    clearTimeout(debounce)
    setDebounce(
      setTimeout(() => {
        if (value && reportType === 'client') {
          const fields = {
            client_id: 'corporate_account_no',
            client_name: 'registered_name'
          }
          CustomerCareServices.getClientInfo({ value, field: fields[searchBy.field] }).then(res => {
            const { data = [] } = res.data
            const item = []

            for (const d of data) {
              item.push({ id: d[fields.client_id], name: d[fields.client_name] })
            }
            setSuggestion(state => ({ ...state, client: item }))
          }).finally(() => {
            setFetching(state => ({ ...state, main: false }))
          })
        } else if (value && reportType === 'member') {
          let query = {}

          if (searchBy.field === 'member_id') {
            query = { member_id: { like: value } }
          } else {
            const _value = value.split(' ')
            const p = _value.filter(i => i !== '').map(v => {
              if (v) {
                return { or: [
                  { first_name: { like: v, options: 'i' } },
                  { middle_name: { like: v, options: 'i' } },
                  { last_name: { like: v, options: 'i' } }
                ]
                }
              }
            })
            query = { and: p }
          }
          // http://18.138.94.224:4101/api/members?filter={"where":{"or":[{"first_name":{"like":"Ariana|Grande"}},{"last_name":{"like":"Ariana|Grande"}}]}}
          CustomerCareServices.getMembers2({
            where: { ...query },
            limit: 10,
            order: 'member_id desc'
          }).then(res => {
            const { data = [] } = res.data
            const item = []
            for (const d of data) {
              item.push({ id: d['member_id'], name: d['member_name'] })
            }
            setSuggestion(state => ({ ...state, member: item }))
          }).finally(() => {
            setFetching(state => ({ ...state, main: false }))
          })
        }
      }, 500)
    )
  }

  const handleReportType = args => {
    const type = args.target.value

    setReportType(type)
    setSearchBy(state => ({ ...state, field: getField({ value: state.value, type }) }))
  }

  const handleGraphFilter = args => {
    // Triggers the filter watch function
    setGraphFilter(args)
  }

  const getMember = account_number => {
    CustomerCareServices.getMemberUtilizationLoa({ account_number: account_number }).then(res => {
      const { data } = res.data
      setUtilizationLoa(data)
    })
    setMemberFetchStatus('fetching')
    CustomerCareServices.getMember({ account_number: account_number })
      .then(res => {
        const { data } = res.data
        setMemberFetchStatus('success')
        setMember(data)
      })
      .catch(err => {
        const { data } = err.response
        // status === 404
        setMemberFetchStatus(`${data.status}`)
      })
      .finally(() => LoadingService.loading(false))

    ClaimsReconServices.getMemberUtilizations({ member_account_no: account_number, range: reports.range.member_id }).then(res => {
      const { data = [] } = res.data
      setMemberUtilizations(data)
    }).catch(err => {
      if (err) { setMemberUtilizations([]) }
    })
  }

  const formatChart = data => {
    const { results = [], covered_months = [] } = data
    // const _data = getDefault({ month: covered_months, type: graphFilter })
    const _data = getDefault({ month: covered_months })
    let mergeResultsbyMonth = []

    for (const r of results) {
      mergeResultsbyMonth = [...mergeResultsbyMonth, ...r.claims]
    }

    const parsedData = mergeResultsbyMonth.map(_merge => {
      for (const i of _data) {
        if (_merge.claim_type === i.label) {
          const month = months[_merge.month - 1]
          i.data[covered_months.indexOf(month)] = _merge.total_amount
          return { ...i, data: i.data }
        }
      }
    })

    const dynamic = lodash.uniqBy(parsedData, j => j.label)
    const filteredData = lodash
      .orderBy(_data
        .map(i => dynamic
          .find(u => u.label === i.label) || i), ['label'], ['asc'])

    return {
      labels: covered_months,
      datasets: filteredData.filter(i => {
        if (i.label.replace(/ /g, '') === graphFilter) {
          return i
        } else if (graphFilter === 'all') {
          return filteredData
        }
      })
    }
  }

  const getField = ({ value, type }) => {
    const { field = '' } = (dropdown.filter(i => i.type === type && i.value === value)).pop()
    return field
  }

  const handleOnChangeDropdown = ({ value }) => {
    setSearchBy({ value, field: getField({ value, type: reportType }) })
  }

  const formatYAxis = n => {
    let value = null

    switch (true) {
      case n < 1e3:
        value = n
        break
      case n >= 1e3 && n < 1e6:
        value = +(n / 1e3).toFixed(1) + 'K'
        break
      case n >= 1e6 && n < 1e9:
        value = +(n / 1e6).toFixed(1) + 'M'
        break
      case n >= 1e9 && n < 1e12:
        value = +(n / 1e9).toFixed(1) + 'B'
        break
      case n >= 1e12 && n < 1e15:
        value = +(n / 1e12).toFixed(1) + 'T'
        break
      case n >= 1e15:
        value = +(n / 1e15).toFixed(1) + 'Q'
    }

    return value
  }

  return (
    <Paper className={classes.root}>
      <div className={classes.header}>
        <Typography variant='h6'>Report Type:</Typography>
        <div className={classes.content}>
          <div className={classes.filter}>
            <div className={classes.input}>
              <RadioGroup className={classes['radio-group']} value={reportType} onChange={handleReportType}>
                <div className={classes['radio-container']}>
                  <p>Corporate Client</p> <Radio color='primary' value='client' />
                </div>
                <div className={classes['radio-container']}>
                  <p> Member </p> <Radio color='primary' value='member' />
                </div>
              </RadioGroup>
            </div>
          </div>
        </div>
        <div className={classes.content}>
          <div className={classes.filter}>
            <div className={classes.dropdown}>
              <DropDown
                id='search-by'
                items={dropdown.filter(i => i.type === reportType)}
                value={searchBy.value}
                onChange={event =>
                  handleOnChangeDropdown({
                    value: event.target.value
                  })
                }
                // error={_errors.diagnosis_code}
                // readOnly={!permissions.update}
              />
            </div>
            <div className={classes.auto}>
              <AutoComplete
                id='account_number'
                placeholder={reportType === 'client' ? `Search for Client ID` : `Search for Member ID`}
                value={reportType === 'client' ? reports.search.client.index : reports.search.member.index}
                suggestions={reportType === 'client' ? suggestion.client : suggestion.member}
                field={searchBy.value.includes('ID') ? 'id' : 'name'}
                loading={fetching['main']}
                onChange={(value, search) =>
                  handleSearchChange({
                    field: reportType === 'client' ? 'client' : 'member',
                    value,
                    search
                  })
                }
              />
            </div>
          </div>
        </div>
      </div>
      <div className={classes.header}>
        <Typography variant='h6'>Utilization Date Range</Typography>
        <div className={classes.content}>
          <div className={classes.filter}>
            <div className={classes.input}>
              <p>From: </p>
              <DatePicker
                id='ledger-filter-date-from'
                value={reports.range[`${reportType === 'client' ? 'client_id' : 'member_id'}`].from}
                onChange={date => {
                  const field = reportType === 'client' ? 'client_id' : 'member_id'
                  setReports(state => ({
                    ...state,
                    range: {
                      ...state.range,
                      [field]: { ...state.range[field], from: date.toISOString() }
                    }
                  }))
                }}
                disableFuture
                error={dateRangeError.date_from}
              />
            </div>
            <div className={classes.input}>
              <p>To: </p>
              <DatePicker
                id='ledger-filter-date-to'
                value={reports.range[`${reportType === 'client' ? 'client_id' : 'member_id'}`].to}
                onChange={date => {
                  const field = reportType === 'client' ? 'client_id' : 'member_id'
                  setReports(state => ({
                    ...state,
                    range: {
                      ...state.range,
                      [field]: { ...state.range[field], to: date.toISOString() }
                    }
                  }))
                }}
                disableFuture
                error={dateRangeError.date_to}
              />
            </div>
          </div>
        </div>
        <div className={classes.content}>
          <div className={classes.filter}>
            <Button
              className={`button-positive ${classes.submit}`}
              onClick={() => {
                if (validate()) return
                fetchReport()
              }}
              variant='contained'
              color='primary'
            >
              Generate
            </Button>
          </div>
        </div>
      </div>
      <hr />
      {reportType && reportType === 'client' && (
        // TODO
        /**
         * Header-report:
         * -Waiting for data
         */
        <div className={classes.body}>
          <Typography variant='h6'>{client.name}</Typography>
          <div className={classes['header-report']}>
            <div className={classes['report-primary']}>
              <div className={classes.title}>{client.limit.type ? limitType[client.limit.type] : ''}</div>
              <Typography variant='h6'>{`PHP ${CurrencyFormatter(graph.total)}`}</Typography>
            </div>
            <div className={classes['line-graph']}>
              <div className={classes.container}>
                <div className={classes['line-container']}>
                  <span
                    className={classes.item}
                    style={{ width: `${(graph.utilized / graph.total) * 100}%`, background: '#4fb083' }}
                  />
                  <span
                    className={classes.item}
                    style={{ width: `${(graph.ibnr / graph.total) * 100}%`, background: '#2a2b6e' }}
                  />
                  <span
                    className={classes.item}
                    style={{
                      width: `${
                        ((graph.total - graph.ibnr - graph.utilized) / graph.total) * 100 < 0
                          ? 0
                          : ((graph.total - graph.ibnr - graph.utilized) / graph.total) * 100
                      }%`,
                      background: '#f8b439'
                    }}
                  />
                </div>
                <div className={classes['line-details']}>
                  <div className={classes.item}>
                    <div className={classes.label}>
                      <span className={classes.color} style={{ background: '#4fb083' }} />
                      <Typography variant='button'>UTILIZED</Typography>
                    </div>
                    <Typography id='utilization-graph-utilized' variant='button' style={{ color: '#4fb083' }}>
                      {`PHP ${CurrencyFormatter(graph.utilized)}`}
                    </Typography>
                  </div>
                  <div className={classes.item}>
                    <div className={classes.label}>
                      <span className={classes.color} style={{ background: '#2a2b6e' }} />
                      <Typography variant='button'>IBNR</Typography>
                    </div>
                    <Typography id='utilization-graph-ibnr' variant='button' style={{ color: '#2a2b6e' }}>
                      {`PHP ${CurrencyFormatter(graph.ibnr)}`}
                    </Typography>
                  </div>
                  <div className={classes.item}>
                    <div className={classes.label}>
                      <span className={classes.color} style={{ background: '#f8b439' }} />
                      <Typography variant='button'>AVAILABLE</Typography>
                    </div>
                    <Typography id='utilization-graph-available' variant='button' style={{ color: '#f8b439' }}>
                      {`PHP ${CurrencyFormatter(graph.total - graph.ibnr - graph.utilized)}`}
                    </Typography>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <Typography variant='h6'>Utilization Summary</Typography>
          <div className={classes.filter} style={{ textAlign: 'right' }}>
            <span>
              <FormControl>
                <Select
                  className={classes['select']}
                  value={graphFilter}
                  onChange={e => handleGraphFilter(e.target.value)}
                >
                  <MenuItem value={'all'}>
                    <Typography className={classes.input}>All Types</Typography>
                  </MenuItem>
                  {legend.map((data, index) => (
                    <MenuItem value={data.type} key={index}>
                      <Typography className={classes.input}>{data.description}</Typography>
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </span>
          </div>
          <div className={classes['graph-legend']}>
            <div className={classes['legend-wrapper']}>
              {legend.map((data, index) => (
                <div className={classes['legend-group']} key={index}>
                  <div className={classes['legend']} style={{ background: theme[data.type] }}>
                    {''}
                  </div>
                  <div className={classes['description']}>{data.description}</div>
                </div>
              ))}
            </div>
          </div>
          <div className={classes['chart']}>
            <Chart
              data={formatChart(chartData)}
              options={{
                scales: {
                  yAxes: [{
                    ticks: {
                      beginAtZero: true,
                      callback: function (label, index, labels) {
                        return formatYAxis(label)
                      }
                    }
                  }]
                },
                legend: {
                  display: false
                },
                // tooltips: {
                //   enabled: true,
                //   mode: 'single',
                //   callbacks: {
                //     label: (tooltipItems, data) => {
                //       tooltipItems.value = CurrencyFormatter(tooltipItems.value)
                //       return `${tooltipItems.yLabel}: ${CurrencyFormatter(tooltipItems.value)}`
                //     }
                //   }
                // },
                // animation: {
                //   show: {
                //     x: {
                //       from: 0
                //     },
                //     y: {
                //       from: 0
                //     }
                //   },
                //   hide: {
                //     x: {
                //       to: 0
                //     },
                //     y: {
                //       to: 0
                //     }
                //   }
                // },
                maintainAspectRatio: false
              }} />
          </div>
          <div className={classes.table}>
            <Table
              TableContainerProps={{
                style: { maxHeight: '570px', overflow: 'auto', position: 'relative', minHeight: '190px' }
              }}
              TableProps={{ stickyHeader: true }}
              head={
                <TableRow>
                  {clientColumns.map((headCell, index) => (
                    <TableCell key={`table-header-${index}`} align={headCell.align}>
                      <TableSortLabel
                        active={reports.orderBy === headCell.field}
                        direction={reports.order ? 'asc' : 'desc'}
                        onClick={event => handleOnPaginate({ orderBy: headCell.field, order: !reports.order })}
                      >
                        {headCell.label}
                      </TableSortLabel>
                      {headCell.search && (
                        <div className={classes.input}>
                          {headCell.type && headCell.type === 'date' ? (
                            <DatePicker
                              noMargin
                              id={`${headCell.field}`}
                              onChange={date =>
                                handleOnFilter({
                                  field: headCell.field,
                                  value: date.toISOString(),
                                  match: headCell.match
                                })
                              }
                            />
                          ) : (
                            <TextField
                              noMargin
                              onChange={event =>
                                handleOnFilter({
                                  field: headCell.field,
                                  value: event.target.value,
                                  match: headCell.match
                                })
                              }
                            />
                          )}
                        </div>
                      )}
                    </TableCell>
                  ))}
                </TableRow>
              }
              body={
                reports.data &&
                reports.data.map((claim, index) => (
                  <TableRow key={index}>
                    <TableCell>{claim.type}</TableCell>
                    <TableCell align='right'>{CurrencyFormatter(claim.total_amount)}</TableCell>
                    <TableCell align='center'>{claim.cases}</TableCell>
                    <TableCell align='center'>{claim.availed_by_principals}</TableCell>
                    <TableCell align='center'>{claim.availed_by_dependents}</TableCell>
                  </TableRow>
                ))
              }
              pagination
              count={reports.count}
              page={reports.page}
              limit={reports.limit}
              rowsPerPageOptions={reports.rowsPerPageOptions}
              onChange={args => handleOnPaginate(args)}
            />
          </div>
          <div className={classes.footer}>
            <div className={classes.label}>
              <Typography style={{ 'fontWeight': 'bold' }}>{`Total Amount: ${CurrencyFormatter(totalAmount)}`}</Typography>
            </div>
          </div>
        </div>
      )}

      {reportType && reportType === 'member' && (
        <div>
          <div className={classes.header}>
            <Typography variant='h6'>
              {SafeAccess(member, ['info', 'first_name'], '') +
                ' ' +
                SafeAccess(member, ['info', 'middle_name'], '') +
                ' ' +
                SafeAccess(member, ['info', 'last_name'], '')}
            </Typography>
            <Typography variant='h6'>{SafeAccess(member, ['info', 'brand_name'], '')}</Typography>
          </div>

          <div className={classes.content}>
            <table className={classes.memberinfo}>
              <tbody>
                <tr className={classes.memberinfoRow}>
                  <td className={classes.memberInfoLabel}>Type</td>
                  <td className={classes.memberInfoValue}>{SafeAccess(member, ['info', 'member_type'], '')}</td>

                  <td className={classes.memberInfoLabel}>Birthdate (Age)</td>
                  <td className={classes.memberInfoValue}>
                    {member &&
                      member.info &&
                      moment(SafeAccess(member, ['info', 'date_of_birth'], '')).format('MMMM DD, YYYY') +
                        ' (' +
                        moment().diff(SafeAccess(member, ['info', 'date_of_birth'], ''), 'years', false) +
                        ')'}
                  </td>

                  <td className={classes.memberInfoLabel}>Gender</td>
                  <td className={classes.memberInfoValue}>{SafeAccess(member, ['info', 'gender'], '')}</td>
                </tr>
                <tr className={classes.memberinfoRow}>
                  <td className={classes.memberInfoLabel}>Plant Type</td>
                  <td className={classes.memberInfoValue}>{SafeAccess(member, ['info', 'plan_type'], '')}</td>

                  <td className={classes.memberInfoLabel}>
                    {SafeAccess(member, ['benefit_limits', 1, 'type'], '')} Limit
                  </td>
                  <td className={classes.memberInfoValue}>
                    {SafeAccess(member, ['benefit_limits', 1, 'limit_unit'], '') +
                      ' ' +
                      CurrencyFormatter(SafeAccess(member, ['benefit_limits', 1, 'limit'], ''))}
                  </td>

                  <td className={classes.memberInfoLabel}>Civil Status</td>
                  <td className={classes.memberInfoValue}>{SafeAccess(member, ['info', 'civil_status'], '')}</td>
                </tr>
                <tr className={classes.memberinfoRow}>
                  <td className={classes.memberInfoLabel}>R&amp;B</td>
                  <td className={classes.memberInfoValue}>{SafeAccess(member, ['info', 'plan_type'], '')}</td>

                  <td className={classes.memberInfoLabel}>
                    {SafeAccess(member, ['benefit_limits', 0, 'type'], '')} Limit
                  </td>
                  <td className={classes.memberInfoValue}>
                    {SafeAccess(member, ['benefit_limits', 0, 'limit_unit'], '') +
                      ' ' +
                      CurrencyFormatter(SafeAccess(member, ['benefit_limits', 0, 'limit'], ''))}
                  </td>

                  <td className={classes.memberInfoLabel}>Effectivity Date</td>
                  <td className={classes.memberInfoValue}>
                    {moment(SafeAccess(member, ['info', 'effectivity_date'], '')).format('MMMM DD, YYYY')}
                  </td>
                </tr>
              </tbody>
            </table>
          </div>

          {/* TODO: replace with filler */}
          <br />

          <Utilization
            fetchService={ClaimsAdjudicationServices.getUtilizations}
            utilizationLoa={utilizationLoa}
            // isMember={isMember}
            member={member}
            // client={client}
            diagnosis={[]}
            dataColumns={memberDataColumns}
            hideSearchBar
            hideActionColumn
            claimUtilizations={memberUtilizations}
            fromUtilReport
          />
        </div>
      )}
    </Paper>
  )
}
