import React from 'react'
import Radio from '@material-ui/core/Radio'
import Tooltip from '@material-ui/core/Tooltip'
import IconButton from '@material-ui/core/IconButton'
import DeleteIcon from '@material-ui/icons/Delete'

// Compoments
import AutoComplete from 'SharedComponents/AutoComplete2/AutoComplete2'

// import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import Typography from '@material-ui/core/Typography'
// import Popover from '@material-ui/core/Popover'

import classes from './ICDTextField.module.scss'

const ICDTextFields = props => {
  const { children } = props
  return <div className={classes.root}>{children}</div>
}

const ICDTextField = props => {
  const {
    required,
    id = '',
    codeId = '',
    description = '',
    code = '',
    primary = false,
    suggestions = { description: [], code: [] },
    onChangeDescription,
    onChangeCode,
    onChangePrimary,
    onDeleteDiagnosis,
    error = { description: '', code: '' },
    permissions,
    fetching = {}
  } = props

  const [tooltipOpen, setTooltipOpen] = React.useState(false)
  const handleTooltipOpen = () => {
    setTooltipOpen(!!description.trim())
  }

  return (
    <div className={classes.wrapper}>
      <div className={classes.header}>
        <Typography variant='subtitle2' className={classes.delete}>
          {''}
        </Typography>

        <Typography variant='subtitle2' className={classes.description}>
          ICD10 Description {required && ' *'}
        </Typography>

        <Typography variant='subtitle2' className={classes.code}>
          ICD10 Code {required && ' *'}
        </Typography>

        <Typography variant='subtitle2' className={classes.primary}>
          Primary {required && ' *'}
        </Typography>
      </div>
      <div className={classes['sub-wrapper']}>
        <div className={classes.delete}>
          {permissions.delete && (
            <IconButton id='delete-icd' onClick={index => onDeleteDiagnosis({ index })}>
              <DeleteIcon fontSize='small' />
            </IconButton>
          )}
        </div>

        <Tooltip
          onClose={() => setTooltipOpen(false)}
          onOpen={handleTooltipOpen}
          open={tooltipOpen}
          title={description}
        >
          <div className={`${classes['description']}`}>
            <AutoComplete
              id={id}
              value={description}
              suggestions={suggestions.description}
              field={'description'}
              loading={fetching['description']}
              onChange={onChangeDescription}
              error={error.description}
              readOnly={!permissions.update}
            />
          </div>
        </Tooltip>
        <div className={`${classes['code']}`}>
          <AutoComplete
            id={codeId}
            value={code}
            suggestions={suggestions.code}
            field={'code'}
            loading={fetching['code']}
            onChange={onChangeCode}
            error={error.code}
            readOnly={!permissions.update}
          />
        </div>
        <div className={classes.primary}>
          <Radio color='primary' checked={primary} onClick={onChangePrimary} disabled={!permissions.update} />
        </div>
      </div>
    </div>
  )
}

const GivenDiagnosisField = props => {
  const {
    required,
    id = '',
    givenDiag = '',
    suggestions = { description: [], code: [] },
    onChangeGivenDiag,
    error = { description: '', code: '' },
    permissions,
    fetching = {}
  } = props
  return (
    <React.Fragment>
      <div className={classes.wrapper}>
        <div className={classes.header}>
          <Typography variant='subtitle2' className={classes.delete}>
            {''}
          </Typography>
          <Typography variant='subtitle2' className={classes.description}>
            Given Diagnosis {required && ' *'}
          </Typography>
        </div>
        <div className={classes['given-diagnosis']}>
          <AutoComplete
            id={`given-${id}`}
            value={givenDiag}
            suggestions={suggestions.given_diagnosis}
            field={'given_diagnosis'}
            loading={fetching['given_diagnosis']}
            onChange={onChangeGivenDiag}
            error={error.given_diagnosis}
            readOnly={!permissions.update}
          />
        </div>
      </div>
    </React.Fragment>
  )
}

export { ICDTextFields, ICDTextField, GivenDiagnosisField }
