import React from 'react'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'

// Components
import DashboardTile from 'SharedComponents/DashboardTileComponent/DashboardTileComponent'
import ViewList from '../ViewList2/ViewList'
import LoadingIndicator from 'SharedComponents/LoadingIndicator/LoadingIndicator'

const columns = [
  { id: 'approved-by', name: 'PR Processor', sortField: 'approved_by', match: 'any' },
  { id: 'total-bundles', name: 'Total Bundle/SOA', sortField: 'total_bundles', match: 'any' },
  { id: 'total-batches', name: 'Total Batches', sortField: 'total_batches', match: 'any' }
]

const SubmittedPrSupervisorTile = props => {
  const { services, sizes = { sm: 12 } } = props

  const [claims, setClaims] = React.useState({ count: 0, data: [] })

  const [fetching, setFetching] = React.useState(false)
  const [fetchingViewList, setFetchingViewList] = React.useState(false)

  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    filter: {},
    data: [],
    orderBy: 'claim.date.due',
    order: true
  }

  const [viewList, setViewList] = React.useState(initialValue)

  React.useEffect(() => {
    setFetching(true)
    Promise.all([services.getSubmittedPaymentRequest({ limit: 10, sort: '' }), services.getBundleCount()])
      .then(values => {
        const bundleResponse = values
        const submittedRequestClaims = bundleResponse[0].data.data.results
        const submittedRequestClaimCount = bundleResponse[1].data.data
        setClaims({ data: submittedRequestClaims, count: submittedRequestClaimCount })
      })
      .finally(values => {
        setFetching(false)
      })
  }, [])

  const handleOnShowViewList = () => {
    const title = 'PR Processor'
    const category = 'submitted'
    setViewList({ ...initialValue, show: true, title, category })

    setFetchingViewList(true)
    services
      .getSubmittedPaymentRequest({ limit: 10, sort: '' })
      .then(({ data: res }) => {
        const { results, count, limit, page } = res.data
        setViewList(state => ({ ...state, data: results, count, limit, page, category }))
      })
      .finally(values => {
        setFetchingViewList(false)
      })
  }

  const handleOnViewListChange = params => {
    const _params = {
      limit: viewList.limit,
      page: viewList.page,
      search: viewList.search,
      sort: `${viewList.orderBy}|${viewList.order ? 'desc' : 'asc'}`,
      filter: viewList.filter,
      ...params
    }

    setFetchingViewList(true)
    services
      .getSubmittedPaymentRequest(_params)
      .then(({ data: res }) => {
        const { results, count, limit, page } = res.data
        setViewList(state => ({ ...state, data: results, count, limit, page, category: 'submitted' }))
      })
      .finally(values => {
        setFetchingViewList(false)
      })
  }

  return (
    <React.Fragment>
      <DashboardTile
        id='pr-processor'
        count={{ color: 3, value: claims.count, id: 'submitted-claim-count' }}
        sizes={sizes}
        title='PR Processor'
        viewList={
          <Typography
            variant='caption'
            className='bold text-link'
            onClick={handleOnShowViewList}
            id='submitted-pr-view-list'
          >
            View List
          </Typography>
        }
        isFilterShown={false}
      >
        <LoadingIndicator loading={fetching}>
          <Table stickyHeader size='small'>
            <TableHead>
              <TableRow>
                <TableCell>PR Processor</TableCell>
                <TableCell>Total Bundle/SOA</TableCell>
                <TableCell>Total Batches</TableCell>
                <TableCell />
              </TableRow>
            </TableHead>
            <TableBody>
              {claims.data.map((item, index) => (
                <TableRow key={index}>
                  <TableCell id={`approved-by-${index}`}>{item.verifierName}</TableCell>
                  <TableCell id={`total-bundles-${index}`}>{item.total_bundles}</TableCell>
                  <TableCell id={`total-batches-${index}`}>{item.total_batches}</TableCell>
                  <TableCell>
                    <Link to={`/payment-requests/${item.id}/list`}>
                      <Typography variant='caption' className={`text-link underline`} id={`submmited-pr-${index}`}>
                        View
                      </Typography>
                    </Link>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </LoadingIndicator>
      </DashboardTile>

      <ViewList
        id='submitted-pr'
        columns={columns}
        searchTextPlaceHolder='Search for PR Processor'
        viewList={viewList}
        setViewList={setViewList}
        onViewListChange={handleOnViewListChange}
        fetching={fetchingViewList}
        body={_data =>
          _data.map((item, index) => (
            <TableRow key={index}>
              <TableCell id={`approved-by-${index}`}>{item.verifierName}</TableCell>
              <TableCell id={`total-bundles-${index}`}>{item.total_bundles}</TableCell>
              <TableCell id={`total-batches-${index}`}>{item.total_batches}</TableCell>
              <TableCell>
                <Link to={`/payment-requests/${item.id}/list`}>
                  <Typography variant='caption' className={`text-link underline`} id={`submmited-pr-${index}`}>
                    View
                  </Typography>
                </Link>
              </TableCell>
            </TableRow>
          ))
        }
      />
    </React.Fragment>
  )
}

SubmittedPrSupervisorTile.displayName = 'DashboardTileComponent'

export default SubmittedPrSupervisorTile
