import React from 'react'
import { Switch, Route } from 'react-router-dom'

// import StackTrace from 'stacktrace-js'

// Routes by role
import BatchEncoderRoutes from './Routes/BatchEncoder'
import BatchEncoderSupervisorRoutes from './Routes/BatchEncoderSupervisor'
import ClaimsDetailsEncoderRoutes from './Routes/ClaimsDetailsEncoder'
import ClaimsDetailsEncoderSupervisorRoutes from './Routes/ClaimsDetailsEncoderSupervisor'
import ClaimsAnalystRoutes from './Routes/ClaimsAnalyst'
import PaymentRequestRoutes from './Routes/PaymentRequest'
import PaymentRequestSupervisorRoutes from './Routes/PaymentRequestSupervisor'
import MedicalConsultantRoutes from './Routes/MedicalConsultant'
import ClaimsReconRoutes from './Routes/ClaimsRecon'
import ClaimsVerifierSupervisorRoutes from './Routes/ClaimsVerifierSupervisor'
import ClaimsManagerRoutes from './Routes/ClaimsManager'

// TEST Page
import Test from './Components/Test/Test'

import BatchEncodingServices from 'Services/Api/Claims/BatchEncodingServices'

// Constants
import { mapRoles } from './Constant/ClaimUsers'

// Store
import { Provider } from 'react-redux'
import Store from './Store'

// window.onerror = function (msg, file, line, col, error) {
//   StackTrace.fromError(error).then(err => {
//     StackTrace.report(err, `${process.env.REACT_APP_SERVER_API}/c/c/error`, {
//       type: 'window.onerror',
//       url: window.location.href,
//       userId: window.userId,
//       agent: window.navigator.userAgent,
//       date: new Date(),
//       msg: msg
//     })
//   })
// }

export default function ClaimsModule ({ history }) {
  const [userRole, setUserRole] = React.useState('')
  const [isLoading, setIsLoading] = React.useState(true)

  React.useEffect(() => {
    document.title = 'HIMS - Claims'
    const role = localStorage.getItem('role')
    setUserRole(mapRoles(role))
    setIsLoading(false)
  }, [])

  const batchEncoderLogic = props => {
    if (localStorage.getItem('from_login')) {
      BatchEncodingServices.createBundle().then(res => {
        const { data } = res.data
        localStorage.removeItem('from_login')
        history.push(`/bundles/${data}/create`)
      })
    } else return <Route path='/' render={() => BatchEncoderRoutes(props)} />
  }

  return (
    <Provider store={Store}>
      {!isLoading && (
        <Switch>
          <Route path='/test' exact component={Test} />

          {userRole === 'BATCH_ENCODER' && batchEncoderLogic({ userRole })}
          {userRole === 'BATCH_ENCODER_SUPERVISOR' && (
            <Route path='/' render={() => BatchEncoderSupervisorRoutes({ userRole })} />
          )}

          {userRole === 'CLAIMS_DETAILS_ENCODER' && (
            <Route path='/' render={() => ClaimsDetailsEncoderRoutes({ userRole })} />
          )}

          {userRole === 'CLAIMS_DETAILS_ENCODER_SUPERVISOR' && (
            <Route path='/' render={() => ClaimsDetailsEncoderSupervisorRoutes({ userRole })} />
          )}

          {userRole === 'CLAIMS_ANALYST' && <Route path='/' render={() => ClaimsAnalystRoutes({ userRole })} />}

          {userRole === 'PR_OFFICER' && <Route path='/' render={() => PaymentRequestRoutes({ userRole })} />}

          {userRole === 'PR_OFFICER_SUPERVISOR' && (
            <Route path='/' render={() => PaymentRequestSupervisorRoutes({ userRole })} />
          )}

          {userRole === 'MEDICAL_CONSULTANT' && <Route path='/' render={() => MedicalConsultantRoutes({ userRole })} />}

          {userRole === 'CLAIMS_RECON' && <Route path='/' render={() => ClaimsReconRoutes({ userRole })} />}

          {userRole === 'CLAIMS_VERIFIER_SUPERVISOR' && (
            <Route path='/' render={() => ClaimsVerifierSupervisorRoutes({ userRole })} />
          )}

          {userRole === 'CLAIMS_MANAGER' && <Route path='/' render={() => ClaimsManagerRoutes({ userRole })} />}
        </Switch>
      )}
    </Provider>
  )
}
