import constants from './../../contants'
import { dataValue } from 'Pages/Protected/Partner/AffiliationFormEncoder/Pages/HospitalAffiliation/components/Utilities/dataHeader'

const initialState = {
  doctor: [],
  client: []
}

const suggestionsReducer = (state = {}, action) => {
  // value of suggestion first render without calling action (temp) reason: action.for is undefined on first render if there's a selector on component
  // tried to solved by useEffect but failed since useEffect will render after component's rendered
  if (!action.for) return initialState
  // value of suggestion calling action (temp) if not equal to BATCH_ENCODING (when calling an action)
  else if (action.for !== constants.for.BATCH_ENCODING) return state

  switch (action.type) {
    case 'INIT_SUGGESTIONS':
      return initialState

    case 'SET_SUGGESTION_OBJECT':
      return (() => {
        let { field, suggestion } = action.payload
        return {
          ...state,
          [field]: suggestion
        }
      })()

    case 'RESET_SUGGESTION_OBJECT':
      return (() => {
        const { field } = action.payload
        return { ...state, [field]: initialState[field] }
      })()

    default:
      return state
  }
}

export default suggestionsReducer
