import Joi from '@hapi/joi'

// Services
import ExtractValidationResult from 'Services/ExtractValidationResult'

const schema = Joi.object({
  _id: Joi.string().allow('', null),
  complied: Joi.boolean().allow(true, false),
  date_complied: Joi.date().allow('', null),
  date_created: Joi.date(),
  date_encoded: Joi.date(),
  date_return: Joi.date().allow('', null),
  date_released: Joi.date()
    .label('Date Released')
    .required()
    .messages({ 'string.empty': `"Date Released" is required.` }),
  prepared_by: Joi.string()
    .trim()
    .allow('', null),
  reasons: Joi.array()
    .items(
      Joi.object({
        _id: Joi.string().allow('', null),
        error: Joi.string().allow('', null),
        value: Joi.string()
          .label('Reason')
          .trim()
          .required()
          .messages({ 'string.empty': `"Reason" is required.` }),
        other_details: Joi.string()
          .trim()
          .allow('', null)
      })
    )
    .allow(null),
  released_to: Joi.string()
    .trim()
    .label('Released To')
    .required()
    .messages({
      'string.empty': `"Released To" is required.`
    }),
  remarks: Joi.string()
    .trim()
    .allow('', null)
}).options({ abortEarly: false })

const validate = data => ExtractValidationResult(schema.validate(data))

export default validate
