import React from 'react'

// MUI Components
import Button from '@material-ui/core/Button'
import Paper from '@material-ui/core/Paper'
import Typography from '@material-ui/core/Typography'
import GetAppIcon from '@material-ui/icons/GetApp'
import PrintIcon from '@material-ui/icons/Print'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TableSortLabel from '@material-ui/core/TableSortLabel'
// Custom Components
import Table from 'SharedComponents/Table/Table'
import DatePicker from 'SharedComponents/DatePicker2/DatePicker2'
import AutoComplete from 'SharedComponents/AutoComplete/AutoComplete'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import ArrayToCSV from 'Services/ArrayToCSV'
import classes from './ClaimsSummaryReport.module.scss'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import ClaimsServices from 'Services/Api/Claims/ClaimsServices'

// Validator
import DateRangeValidator from './validator/DateRangeValidator'

// Utilities
import { DateFormatter, CurrencyFormatter } from 'Utilities/TextFormatter'
import moment from 'moment'

const columns = [
  { label: 'Month', align: 'center', type: 'string', data: 'month' },
  { label: 'Number of Claims Received', align: 'center', type: 'number', data: 'received' },
  { label: 'Number of Claims Processed', align: 'center', type: 'number', data: 'processed' },
  { label: 'Amount of Claims Processed', align: 'center', type: 'currency', data: 'total_amount' }
]
const initialClaims = {
  data: {
    claims: [
      { month: 'January', received: 0, processed: 0, total_amount: 0 },
      { month: 'Febuary', received: 0, processed: 0, total_amount: 0 },
      { month: 'March', received: 0, processed: 0, total_amount: 0 },
      { month: 'April', received: 0, processed: 0, total_amount: 0 },
      { month: 'May', received: 0, processed: 0, total_amount: 0 },
      { month: 'June', received: 0, processed: 0, total_amount: 0 },
      { month: 'July', received: 0, processed: 0, total_amount: 0 },
      { month: 'August', received: 0, processed: 0, total_amount: 0 },
      { month: 'September', received: 0, processed: 0, total_amount: 0 },
      { month: 'October', received: 0, processed: 0, total_amount: 0 },
      { month: 'November', received: 0, processed: 0, total_amount: 0 },
      { month: 'December', received: 0, processed: 0, total_amount: 0 }
    ],
    total_received: 0,
    total_processed: 0,
    total_amount: 0
  },
  range: {
    from: moment().format(),
    to: moment().format()
  },
  filter: {
    claim_type: 'ALL',
    claim_source: 'ALL',
    claim_sub_type: 'ALL',
    contract_plan: 'ALL',
    section: 'ALL',
    claim_status: 'ALL',
    user: 'ALL',
    geography: 'ALL'
  }
}
const initialValue = {
  claim_type: [],
  claim_source: [],
  claim_sub_type: [],
  contract_plan: [],
  section: [],
  claim_status: [],
  user: [],
  geography: []
}

/**
 * Claims Summary Report
 *
 * @export
 *
 * @param {*} props
 *   dataColumns - default []; if defined, will be the allowable data columns that
 *     will be shown on the claims utilization report
 *   hideReport - default false; if defined, will be used to hide the entire report
 *   hideheader - default false; if defined, will be used to hide the header section
 *   dateRange - default current date; if defined, will be used to define the date range in-effect
 *
 * @returns
 */
export default function ClaimsSummaryReport (props) {
  const { dataColumns = [], hideReport, hideHeader, dateRange } = props

  const [suggestion, setSuggestion] = React.useState(initialValue)
  const [debounce, setDebounce] = React.useState({
    claim_type: null,
    claim_source: null,
    claim_sub_type: null,
    section: null,
    user: null,
    geopgraphy: null
  })
  const [dateRangeError, setDateRangeError] = React.useState({
    date_from: '',
    date_to: ''
  })
  const [claims, setClaims] = React.useState(initialClaims)

  React.useEffect(() => {
    PageTitleService('Claims Summary Report')
    BreadcrumbsService.setSelectedItems(['claimsSummaryReport'])
    handleOnFilter()
  }, [])

  const fetchCSV = () => {
    LoadingService.loading(true)
    ClaimsServices.claimsSummaryReport({
      range: claims.range,
      filter: claims.filter
    }).then(({ data: res }) => {
      const { data } = res
      const { total_received = 0, total_processed = 0, total_amount } = data
      const results = data.claims
      const rows = results.map(claim => {
        return columns.map(col => formatData({ type: col.type, source: claim, field: col.data }))
      })

      const header = columns.map(c => c.label)

      ArrayToCSV({
        statics: [
          [
            'Claims Summary Report',
            '\r\n',
            `Date Range: ${DateFormatter(claims.range.from)} - ${DateFormatter(claims.range.to)}`,
            '\r\n',
            '\r\n',
            `Claim Type: ${claims.filter.claim_type}`,
            '\r\n',
            `Claim Source: ${claims.filter.claim_source}`,
            '\r\n',
            `Contract Plan Type: ${claims.filter.contract_plan}`,
            '\r\n',
            `Section: ${claims.filter.section}`,
            '\r\n',
            `User: ${claims.filter.user}`,
            '\r\n'
          ],
          [`Grand Total: `, `${total_received}`, `${total_processed}`, `${total_amount}`].join(',')
        ],
        fileName: 'Claims Summary Report',
        header,
        rows
      })
      LoadingService.loading(false)
    })
  }

  const formatData = ({ type = null, source, field }) => {
    let output = ''
    if (type === 'date') {
      output = DateFormatter(source[field])
    } else if (type === 'array') {
      const [root, property] = field.split('.')
      output = source[root] ? source[root].map(f => f[property]).join(`${'\r\n'}${'\r\n'}`) : ''
    } else if (type === 'number') {
      output = source[field]
    } else if (type === 'currency') {
      output = CurrencyFormatter(source[field])
    } else {
      output = source[field] ? source[field] : ''
    }

    return output !== '' && output !== 'undefined' && output !== `${'\r\n'}${'\r\n'}` ? output : 'N/A'
  }

  const handleOnChange = ({ value, category, field, isSuggestionClick }) => {
    const _category = category.split('-').join('_')
    setClaims(state => ({ ...state, filter: { ...state.filter, [_category]: value } }))
    if (!isSuggestionClick) {
      clearTimeout(debounce[category])
      setDebounce(state => ({
        ...state,
        [_category]: setTimeout(() => {
          if (value) {
            ClaimsServices.lookupsQuery({ category, field, value }).then(res => {
              const { data } = res.data
              setSuggestion(state => ({ ...state, [_category]: ['ALL', ...data.map(d => d[field])] }))
            })
          }
        }, 500)
      }))
    }
  }

  const handleOnFilter = () => {
    LoadingService.loading(true)
    const params = {
      filter: claims.filter,
      range: claims.range
    }
    ClaimsServices.claimsSummaryReport(params).then(({ data: res }) => {
      const { data } = res
      setClaims({ ...claims, data: data })
      LoadingService.loading(false)
    })
  }

  const validate = () => {
    const { error } = DateRangeValidator({
      date_from: claims.range.from,
      date_to: claims.range.to,
      date_max: `12-31-${moment(claims.range.from).year()}`
    })
    setDateRangeError({ ...error })
    return !!error
  }

  return (
    <Paper className={classes.root}>
      <div className={classes.header} style={{ display: hideHeader === 'true' ? 'none' : 'block' }}>
        <div className={classes.content}>
          <div className={classes['field-filter']}>
            <div className={classes.input}>
              <Typography variant='caption'>Claim type</Typography>
              <AutoComplete
                placeholder='Search by claim type'
                id='claim-type'
                value={claims.filter.claim_type}
                suggestions={suggestion.claim_type}
                onChange={(value, isSuggestionClick) =>
                  handleOnChange({
                    value,
                    isSuggestionClick,
                    category: 'claim-type',
                    field: 'name'
                  })
                }
              />
            </div>
            <div className={classes.input}>
              <Typography variant='caption'>Claim Source</Typography>
              <AutoComplete
                placeholder='Search by claim source'
                id='claim-source'
                value={claims.filter.claim_source}
                suggestions={suggestion.claim_source}
                onChange={(value, isSuggestionClick) =>
                  handleOnChange({
                    value,
                    isSuggestionClick,
                    category: 'claim-source',
                    field: 'source'
                  })
                }
              />
            </div>
            <div className={classes.input}>
              <Typography variant='caption'>Claim Sub-Type/Category</Typography>
              <AutoComplete
                placeholder='Search by claim sub-type/category'
                id='claim-sub-type'
                value={claims.filter.claim_sub_type}
                suggestions={suggestion.claim_sub_type}
                onChange={(value, isSuggestionClick) =>
                  handleOnChange({
                    value,
                    isSuggestionClick,
                    category: 'claim-sub-type',
                    field: 'sub_type'
                  })
                }
              />
            </div>
            <div className={classes.input}>
              <Typography variant='caption'>Section</Typography>
              <AutoComplete
                placeholder='Search by section'
                id='section'
                value={claims.filter.section}
                suggestions={[]}
                onChange={event => {
                  setClaims(state => ({
                    ...state,
                    filter: { ...state.filter, section: event }
                  }))
                }}
              />
            </div>
            <div className={classes.input}>
              <Typography variant='caption'>User</Typography>
              <AutoComplete
                placeholder='Search by user'
                id='user'
                value={claims.filter.user}
                suggestions={[]}
                onChange={event => {
                  setClaims(state => ({
                    ...state,
                    filter: { ...state.filter, user: event }
                  }))
                }}
              />
            </div>
            <div className={classes.input}>
              <Typography variant='caption'>Geography</Typography>
              <AutoComplete
                placeholder='Search by region'
                id='geography'
                value={claims.filter.geography}
                suggestions={[]}
                onChange={event => {
                  setClaims(state => ({
                    ...state,
                    filter: { ...state.filter, geography: event }
                  }))
                }}
              />
            </div>
          </div>
        </div>
        <Typography variant='h6'>Date Range</Typography>
        <div className={classes.content}>
          <div className={classes.filter}>
            <div className={classes.input}>
              <p>From: </p>
              <DatePicker
                id='filter-date-from'
                value={claims.range.from}
                onChange={date =>
                  setClaims(state => ({
                    ...state,
                    range: {
                      ...state.range,
                      from: date.toISOString()
                    }
                  }))
                }
                disableFuture
                error={dateRangeError.date_from}
              />
            </div>
            <div className={classes.input}>
              <p>To: </p>
              <DatePicker
                id='filter-date-to'
                value={claims.range.to}
                onChange={date =>
                  setClaims(state => ({
                    ...state,
                    range: {
                      ...state.range,
                      to: date.toISOString()
                    }
                  }))
                }
                disableFuture
                error={dateRangeError.date_to}
              />
            </div>
            <Button
              className={`button-positive ${classes.submit}`}
              onClick={() => !validate() && handleOnFilter()}
              variant='contained'
              color='primary'
            >
              Filter
            </Button>
          </div>
          <div className={classes.action}>
            <Button id='generate-report' onClick={() => fetchCSV()}>
              <GetAppIcon fontSize='small' />
              <Typography variant='caption' className='bold text-link'>
                Generate Report
              </Typography>
            </Button>
          </div>
        </div>
      </div>
      <div className={classes.body}>
        <div className={classes.table}>
          <Table
            TableContainerProps={{ style: { maxHeight: '60vh', overflow: 'auto', position: 'relative' } }}
            head={
              <TableRow>
                {columns.map((headCell, index) => (
                  <TableCell className={classes[headCell.class]} align={headCell.align} key={`table-header-${index}`}>
                    {headCell.label}
                  </TableCell>
                ))}
              </TableRow>
            }
            body={
              claims.data.claims.length ? (
                claims.data.claims.map((claim, claimIndex) => (
                  <TableRow className={classes['table-row']} key={`table-item-${claimIndex}`}>
                    {columns.map((col, colIndex) => (
                      <TableCell
                        className={col.remarks ? classes.remarks : classes['table-cell']}
                        key={`table-cell-${claimIndex}-${colIndex}`}
                        align='center'
                      >
                        {formatData({ type: col.type, source: claim, field: col.data })}
                      </TableCell>
                    ))}
                  </TableRow>
                ))
              ) : (
                <TableRow>
                  <TableCell className={classes.handling} colSpan={columns.length}>
                    No data found
                  </TableCell>
                </TableRow>
              )
            }
            pagination={false}
          />
        </div>
        <div className={classes.footer}>
          <div className={classes.label}>
            <Typography className='bold'>Grand Total:</Typography>
          </div>
          <div className={classes.received}>
            <Typography className='bold'>{claims.data.total_received}</Typography>
          </div>
          <div className={classes.processed}>
            <Typography className='bold'>{claims.data.total_processed}</Typography>
          </div>
          <div className={classes.total}>
            <Typography className='bold'>{claims.data.total_amount}</Typography>
          </div>
        </div>
      </div>
    </Paper>
  )
}
