import instance from './instance'
const axios = instance('/client')

export default class UserManagementServices {
  static getUsers (role_name) {
    return axios({
      url: `/api/users/list?filter={"where":{"main_role.name"%20:"${role_name}"}}`,
      method: 'GET'
    })
  }

  static getUsersByRole (role_name) {
    return axios({
      url: `/api/users/list?filter={"where":{"main_role.name"%20:"${role_name}"}}`,
      method: 'GET'
    })
  }

  static getUsersWithRole (role_name) {
    return axios({
      url: `/api/users/list?filter={"where":{"or":[{"roles.name":{"like":"${role_name}.*","options":"i"}}, {"main_role.name":"${role_name}"}]}}`,
      method: 'GET'
    })
  }

  static getClaimsVerifierSupervisors () {
    return this.getUsers('CLAIMS_VERIFIER_SUPERVISOR')
  }

  static getClaimsAnalyst () {
    return this.getUsers('CLAIMS_ANALYST')
  }

  static getUserById (id) {
    return axios({
      url: `/api/users/${id}`,
      method: 'GET'
    })
  }
}
