import React from 'react'
import Paper from '@material-ui/core/Paper'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TableSortLabel from '@material-ui/core/TableSortLabel'
import Button from '@material-ui/core/Button'
import Checkbox from '@material-ui/core/Checkbox'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'

// Shared Components
import Table from 'SharedComponents/Table/Table'
import SearchTextComponent from 'SharedComponents/SearchTextComponent/SearchTextComponent'
import TextField from 'SharedComponents/TextField/CustomTextField'
import DatePicker from 'SharedComponents/DatePicker2/DatePicker2'

// Components
import AssignDialog from './Components/AssignDialog/AssignDialog'
// Services
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import BatchEncodingSupervisoryServices from 'Services/Api/Claims/BatchEncodingSupervisoryServices'
import UserManagementServices from 'Services/Api/UserManagementServices'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'

// Styles
import classes from './CreatedBundlesPage.module.scss'

// Utilities
import { DateFormatter } from 'Utilities/TextFormatter'

const columnss = [
  { id: 'bundle-number', name: 'Bundle/SOA No.', sortField: 'bundle_number', match: 'any' },
  { id: 'date-received', name: 'Date Received', sortField: 'date_received', match: 'date' },
  { id: 'date-due', name: 'Due Date', sortField: 'date_due', match: 'date' },
  { id: 'facility-name', name: 'Medical Partner Name (Facility)', sortField: 'facility_name', match: 'any' }
]

export default function CreatedBundlesPage (props) {
  const [bundles, setBundles] = React.useState({
    // data: [],
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    orderBy: 'date_due',
    order: true,
    filter: {}
  })
  const [selected, setSelected] = React.useState({ bundle_numbers: [] })
  const [bundle, setBundle] = React.useState({})
  const [batchEncoderId, setBatchEncoderId] = React.useState()
  const [openModal, setOpenModal] = React.useState()
  const [activeAssigning, setActiveAssigning] = React.useState()
  const [debounce, setDebounce] = React.useState(null)

  React.useEffect(() => {
    const _batchEncoderId = props.match.params.user_id
    setBatchEncoderId(_batchEncoderId)
    LoadingService.loading(true)

    PageTitleService('Created Bundles')
    BreadcrumbsService.setSelectedItems(['dashboard', `createdBundles:${' '}`])
    getBundles(bundles)
  }, [props.match.params.user_id])

  const getBundles = params => {
    BatchEncodingSupervisoryServices.getBundles({
      user_id: props.match.params.user_id,
      search: params.search,
      sort: `${params.orderBy}|${params.order ? 'asc' : 'desc'}`,
      page: params.page,
      filter: params.filter,
      limit: params.limit
    }).then(({ data: response }) => {
      const { results } = response.data
      setBundles(state => ({ ...state, ...response.data }))
      const _data = []

      //* Trial
      for (const result of results) {
        const selectedBundleNumbers = selected.bundle_numbers
        if (selectedBundleNumbers.includes(result.bundle_number)) _data.push({ ...result, checked: true })
        else _data.push({ ...result, checked: false })
      }

      const checkedCount = _data.filter(i => i.checked).length
      const resultsCount = results.length

      setBundle({
        data: _data,
        loading: false,
        indeterminate: checkedCount > 0 && checkedCount < resultsCount ? true : undefined,
        checked: checkedCount === resultsCount ? true : undefined
      })
      LoadingService.loading(false)
    }).catch(err => {
      console.error(err)
    })
  }

  const handleSelectAll = event => {
    let _bundles = bundle

    if (_bundles.data) {
      _bundles.data.map(i => {
        i.checked = _bundles.indeterminate ? true : !_bundles.checked
        return i
      })
    }

    setBundle(_bundles)
    changeSelectAllState({ data: _bundles })
  }

  const handleItemSelect = ({ bundle, index }) => {
    const _bundles = {
      ...bundle,
      data: bundle.data.map((i, _index) => {
        if (_index === index) i.checked = !i.checked
        return i
      })
    }
    setBundle(_bundles)
    changeSelectAllState({ data: _bundles })
  }

  const changeSelectAllState = ({ data }) => {
    const unchecked = []
    let _bundles = data || bundle
    let selected_count = 0
    let bundleNumbers = []
    if (_bundles.data) {
      for (let b of _bundles.data) {
        if (b.checked) {
          selected_count++
          bundleNumbers.push(b.bundle_number)
        } else unchecked.push(b.bundle_number)
      }
    }

    let indeterminate = selected_count ? selected_count < _bundles.data.length : false
    let checked = selected_count ? selected_count === _bundles.data.length : false
    _bundles = { ..._bundles, indeterminate, checked }
    setBundle(_bundles)

    //* Trial
    const uniqueSelectedBundleNumber = [...new Set([ ...selected.bundle_numbers, ...bundleNumbers ])]
    const _data = uniqueSelectedBundleNumber.filter(i => !unchecked.includes(i))

    setSelected(state => ({ bundle_numbers: _data }))
  }

  const handleOnCancel = () => {
    let _data = bundle.data.map(i => ({ ...i, checked: false }))

    setBundle(state => ({ ...state, data: _data, checked: false, indeterminate: undefined }))
    setActiveAssigning(false)
    setSelected({ bundle_numbers: [] })
  }

  const handleOnSubmit = ({ note, assignee }) => {
    LoadingService.loading(true)
    BatchEncodingSupervisoryServices.reassignBundles({
      user_id: batchEncoderId,
      data: {
        note,
        assignee,
        bundle_numbers: selected.bundle_numbers
      }
    }).then(res => {
      setSelected({ bundle_numbers: [] })
      getBundles(bundles)
      LoadingService.loading(false)
    })
  }

  const handleOnSearchAllChange = args => {
    getBundles({ ...bundles, ...args })
  }

  const handleOnSearch = args => {
    const _params = {
      limit: bundles.limit,
      page: bundles.page,
      search: bundles.search,
      sort: `${bundles.orderBy}|${bundles.order ? 'desc' : 'asc'}`,
      filter: bundles.filter,
      ...args
    }
    getBundles(_params)
  }

  const handleOnPaginate = args => {
    const params = {
      ...bundles,
      ...args
    }
    setBundles(state => ({ ...state, ...args }))
    getBundles(params)
  }

  const handleSearchChange = e => {
    const search = e.target.value
    clearTimeout(debounce)
    setDebounce(
      setTimeout(() => {
        handleOnSearchAllChange({ search, page: 1 })
        setBundles(state => ({ ...state, search }))
      }, 500)
    )
  }

  return (
    <div className={classes.root}>
      <Paper className={classes.content}>
        <div className={classes['first-container']}>
          <div className={classes.action}>
            <Button
              id='cancelreassign-btn'
              variant='contained'
              color='primary'
              className='button-neutral'
              onClick={() => setActiveAssigning(true)}
              disabled={!!activeAssigning}
            >
              Re-Assign
            </Button>
          </div>

          <div className={classes.search}>
            <SearchTextComponent
              id='search'
              onChange={handleSearchChange}
              placeholder='Search for bundle number, date or facility name'
            />
          </div>
        </div>

        <div className={classes.table}>
          <Table
            TableContainerProps={{ style: { flex: 1, overflow: 'auto', position: 'relative' } }}
            TableProps={{ stickyHeader: true }}
            head={
              <TableRow>
                {activeAssigning && (
                  <TableCell id='checkbox-selecte-all' padding='checkbox' size='small'>
                    <Checkbox
                      color='primary'
                      style={{ marginTop: '30px' }}
                      disabled={!bundle.data.length}
                      checked={!!bundle.checked}
                      indeterminate={!!bundle.indeterminate}
                      onClick={event => handleSelectAll()}
                    />
                  </TableCell>
                )}

                {columnss.map(column => (
                  <TableCell key={column.sortField} id={column.id}>
                    <TableSortLabel
                      active={bundles.orderBy === column.sortField}
                      direction={bundles.order ? 'asc' : 'desc'}
                      onClick={event => handleOnPaginate({ orderBy: column.sortField, order: !bundles.order })}
                    >
                      {column.name}
                    </TableSortLabel>
                    {column.match === 'date' ? (
                      <DatePicker
                        noMargin
                        id={`column-filter-${column.id}`}
                        onChange={date => {
                          clearTimeout(debounce)
                          setDebounce(
                            setTimeout(() => {
                              setBundles(p => ({
                                ...p,
                                filter: { ...p.filter, [column.sortField]: { value: date.toISOString(), match: column.match } }
                              }))
                              handleOnSearch({
                                filter: { ...bundles.filter, [column.sortField]: { value: date.toISOString(), match: column.match } },
                                page: 1
                              })
                            }, 500)
                          )
                        }}
                        value={null}
                      />
                    ) : (
                      <TextField
                        id={`text-field-${column.id}`}
                        TextFieldStyle={{ margin: 0 }}
                        fullWidth
                        onChange={event => {
                          const search = event.target.value
                          clearTimeout(debounce)
                          setDebounce(
                            setTimeout(() => {
                              setBundles(p => ({
                                ...p,
                                filter: { ...p.filter, [column.sortField]: { value: search, match: column.match } }
                              }))
                              handleOnSearch({
                                filter: { ...bundles.filter, [column.sortField]: { value: search, match: column.match } },
                                page: 1
                              })
                            }, 500)
                          )
                        }}
                      />
                    ) }

                  </TableCell>
                ))}

                <TableCell align='center' />
              </TableRow>
            }
            body={bundle.data ? bundle.data.map((item, index) => (
              <TableRow key={item.bundle_number}>
                {activeAssigning && (
                  <TableCell id='checkbox-selecte-all' padding='checkbox' size='small'>
                    <Checkbox
                      color='primary'
                      onChange={() => handleItemSelect({ bundle, index })}
                      checked={item.checked || false}
                    />
                  </TableCell>
                )}

                <TableCell id={`bundle-number-${index}`}>{item.bundle_number}</TableCell>
                <TableCell id={`received-date-${index}`}>{DateFormatter(item.date_received)}</TableCell>
                <TableCell id={`due-date-${index}`}>{DateFormatter(item.date_due)}</TableCell>
                <TableCell id={`facility-name-${index}`}>{item.facility_name}</TableCell>
                <TableCell id={`view-work-${index}`}>
                  <Link to={`/batch-encoder-work/${batchEncoderId}/bundles/${item.bundle_number}`}>
                    <Typography variant='caption' className='text-link underline' id={`view-bundle-${index}`}>
                      View
                    </Typography>
                  </Link>
                </TableCell>
              </TableRow>
            )) : null}
            count={bundles.count}
            page={bundles.page}
            limit={bundles.limit}
            rowsPerPageOptions={[10, 15, 20]}
            onChange={args => handleOnPaginate(args)}
          />
        </div>

        <div className={classes.footer}>
          {activeAssigning && (
            <Typography variant='subtitle2' className={classes.title}>
              Total Bundle Selected: {selected.bundle_numbers.length ? selected.bundle_numbers.length : 0}
            </Typography>
          )}

          <div className={classes.actions}>
            {activeAssigning ? (
              <React.Fragment>
                <Button
                  variant='contained'
                  color='primary'
                  className='button-negative'
                  onClick={() => handleOnCancel()}
                  id='cancel'
                >
                  Cancel
                </Button>

                <Button
                  variant='contained'
                  color='primary'
                  className={`${classes.save} button-positive`}
                  onClick={() => setOpenModal(true)}
                  disabled={(selected.bundle_numbers.length ? selected.bundle_numbers.length : 0) < 1}
                  id='save'
                >
                  Assign
                </Button>
              </React.Fragment>
            ) : (
              <Button
                variant='contained'
                color='primary'
                className='button-negative'
                onClick={() => props.history.push('/')}
                id='cancel'
              >
                Close
              </Button>
            )}
          </div>
        </div>
      </Paper>
      <AssignDialog
        show={openModal}
        setShow={setOpenModal}
        services={UserManagementServices}
        onSubmit={handleOnSubmit}
        _id={batchEncoderId}
      />
    </div>
  )
}
