import React from 'react'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'

// Components
import DashboardTile from 'SharedComponents/DashboardTileComponent/DashboardTileComponent'
import ViewList from './../ViewList2/ViewList'
import LoadingIndicator from 'SharedComponents/LoadingIndicator/LoadingIndicator'

// Utilities
import { DateFormatter } from 'Utilities/TextFormatter'

const columns = [
  { name: 'Claim Number', sortField: 'claim_number', match: 'any' },
  { name: 'SOA No.', sortField: 'soa_number', match: 'any' },
  { name: 'Bundle', sortField: 'bundle_number', match: 'any' },
  { name: 'Due Date', sortField: 'date_due', match: 'date' },
  { name: 'Facility Name', sortField: 'facility_name', match: 'any' }
]

const ForReconciliationClaimsTile = props => {
  const { services, sizes = { sm: 12 } } = props
  const [claims, setClaims] = React.useState({
    for_recon: { count: 0, data: [] },
    ticket: { count: 0, data: [] }
  })

  const [fetching, setFetching] = React.useState(false)
  const [fetchingViewList, setFetchingViewList] = React.useState(false)

  const [viewList, setViewList] = React.useState({
    category: 'for-recon',
    show: false,
    title: '',
    data: [],
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    orderBy: 'date_due',
    order: true,
    filter: {}
  })

  React.useEffect(() => {
    setFetching(true)
    Promise.all([
      services.getClaims({
        category: 'for-recon',
        limit: viewList.limit,
        sort: `${viewList.orderBy}|${viewList.order ? 'asc' : 'desc'}`,
        range: 'this-week',
        filter: viewList.filter
      }),
      services.getClaimCount({ category: 'for-recon' })
    ])
      .then(values => {
        const [claimResponse, claimCountResponse] = values
        const allClaims = claimResponse.data.data.results
        const allClaimsCount = claimCountResponse.data.data
        setClaims(prevState => ({ ...prevState, for_recon: { data: allClaims, count: allClaimsCount } }))
      })
      .finally(values => {
        setFetching(false)
      })
  }, [])

  const handleFilterChange = value => {
    const category = 'for_recon'

    setFetching(true)
    services
      .getClaims({
        category,
        range: value,
        limit: viewList.limit,
        sort: `${viewList.orderBy}|${viewList.order ? 'asc' : 'desc'}`,
        filter: viewList.filter
      })
      .then(({ data: res }) => {
        const { data } = res
        setClaims(prevState => ({ ...prevState, [category]: { ...prevState[category], data: data.results } }))
      })
      .finally(values => {
        setFetching(false)
      })
  }

  const handleOnShowViewList = () => {
    let title = `Claims for Reconciliation`
    const category = 'for_recon'

    setViewList(p => ({ ...p, show: true, title, category }))

    setFetchingViewList(true)
    services
      .getClaims({ category, limit: viewList.limit, sort: 'date_due|desc' })
      .then(({ data: res }) => {
        const { results, count, limit, page } = res.data
        setViewList(state => ({ ...state, data: results, count, limit, page }))
      })
      .finally(values => {
        setFetchingViewList(false)
      })
  }

  const handleOnViewListChange = params => {
    const _params = {
      category: viewList.category,
      limit: viewList.limit,
      page: viewList.page,
      search: viewList.search,
      sort: `${viewList.orderBy}|${viewList.order ? 'desc' : 'asc'}`,
      ...params
    }

    setFetchingViewList(true)
    services
      .getClaims(_params)
      .then(({ data: res }) => {
        const { results, count, limit, page } = res.data
        setViewList(state => ({ ...state, data: results, count, limit, page }))
      })
      .finally(values => {
        setFetchingViewList(false)
      })
  }

  return (
    <React.Fragment>
      <DashboardTile
        id='claim-for-Reconciliation'
        count={{ color: 3, value: claims.for_recon.count, id: 'claim-for-Reconciliation-count' }}
        sizes={sizes}
        title='Claims for Reconciliation'
        viewList={
          <Typography id='fr-view-list' variant='caption' className='bold text-link' onClick={handleOnShowViewList}>
            View List
          </Typography>
        }
        onFilterChange={handleFilterChange}
      >
        <LoadingIndicator loading={fetching}>
          <Table stickyHeader size='small'>
            <TableHead>
              <TableRow>
                {columns.map((item, index) => (
                  <TableCell
                    key={index}
                    // id={item.id}
                  >
                    {item.name}
                  </TableCell>
                ))}

                <TableCell />
              </TableRow>
            </TableHead>
            <TableBody>
              {claims.for_recon.data.map((item, index) => (
                <TableRow key={`${item.claim_number}-${index}`}>
                  <TableCell>{item.claim_number}</TableCell>
                  <TableCell>{item.soa.number}</TableCell>
                  <TableCell>{item.bundle_number}</TableCell>
                  <TableCell>{DateFormatter(item.date.due)}</TableCell>
                  <TableCell>{item.facility.name}</TableCell>
                  <TableCell id={`view-review-${index}`}>
                    <Link to={`/claims/recon/${item.claim_number}`}>
                      <Typography variant='caption' className='text-link underline' id={`view-claim-${index}`}>
                        View Claim
                      </Typography>
                    </Link>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </LoadingIndicator>
      </DashboardTile>

      <ViewList
        id='claim-for-Reconciliation'
        columns={columns}
        searchTextPlaceHolder='Search for claim number, date or facility name'
        viewList={viewList}
        setViewList={setViewList}
        onViewListChange={handleOnViewListChange}
        fetching={fetchingViewList}
        body={_data =>
          _data.map((item, index) => (
            <TableRow key={`${item.claim_number}-${index}`}>
              <TableCell>{item.claim_number}</TableCell>
              <TableCell>{item.soa.number}</TableCell>
              <TableCell>{item.bundle_number}</TableCell>
              <TableCell>{DateFormatter(item.date.due)}</TableCell>
              <TableCell>{item.facility.name}</TableCell>
              <TableCell id={`view-review-${index}`}>
                <Link to={`/claims/recon/${item.claim_number}`}>
                  <Typography variant='caption' className='text-link underline' id={`update-bundle-${index}`}>
                    View Claim
                  </Typography>
                </Link>
              </TableCell>
            </TableRow>
          ))
        }
      />
    </React.Fragment>
  )
}

ForReconciliationClaimsTile.displayName = 'DashboardTileComponent'

export default ForReconciliationClaimsTile
