const roles = {
  BATCH_ENCODER: ['BATCH_ENCODER'],
  BATCH_ENCODER_SUPERVISOR: ['BATCH_ENCODER_SUPERVISOR'],
  CLAIMS_DETAILS_ENCODER: ['CLAIMS_ENCODER', 'CLAIMS_DETAILS_ENCODER'],
  CLAIMS_DETAILS_ENCODER_SUPERVISOR: ['CLAIMS_DETAILS_ENCODER_SUPERVISOR'],
  CLAIMS_ANALYST: ['CLAIMS_ANALYST', 'CLAIMS_VERIFIER'],
  PR_OFFICER: ['PR_OFFICER'],
  PR_OFFICER_SUPERVISOR: ['PR_OFFICER_SUPERVISOR'],
  MEDICAL_CONSULTANT: ['MEDICAL_CONSULTANT'],
  CLAIMS_RECON: ['CLAIMS_RECON'],
  CLAIMS_VERIFIER_SUPERVISOR: ['CLAIMS_VERIFIER_SUPERVISOR', 'CLAIMS_SUPERVISOR'],
  CLAIMS_MANAGER: ['CLAIMS_MANAGER']
}

const mapRoles = role => {
  for (const r in roles) {
    if (roles[r].includes(role)) return r
  }
}

export { mapRoles, roles }
