const includesAny = (s1 = '', s2 = '') => {
  return s1.toLowerCase().includes(s2.toLowerCase())
}

const compare = (list, find, comparer = includesAny, fields = []) => {
  if (Array.isArray(list)) {
    return list.some(item => {
      for (const field of fields) {
        if (comparer(item[field], find)) return true
      }
    })
  } else {
    if (comparer(list, find)) return true
  }
  return false
}

const removeDuplicates = (array) => {
  var a = array.concat();
  for(var i=0; i<a.length; ++i) {
      for(var j=i+1; j<a.length; ++j) {
          if(a[i] === a[j])
              a.splice(j--, 1);
      }
  }

  return a
}

export { compare, includesAny, removeDuplicates }
