import React from 'react'
import Typography from '@material-ui/core/Typography'

import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import startCase from 'lodash/startCase'

// Components
import DashboardTile from 'SharedComponents/DashboardTileComponent/DashboardTileComponent'
import ViewList from './../ViewList2/ViewList'
import LoadingIndicator from 'SharedComponents/LoadingIndicator/LoadingIndicator'

// Utilities
import { DateFormatter } from 'Utilities/TextFormatter'

const columns = [
  { id: 'claim-number', name: 'Claim Number', sortField: 'claim_number', match: 'any' },
  { id: 'due-date', name: 'Due Date', sortField: 'date_due', match: 'date' },
  { id: 'facility-name', name: 'Facility Name', sortField: 'facility.name', match: 'any' }
]

const ReconTIle = props => {
  const { services, sizes = { sm: 12 } } = props
  const [claims, setClaims] = React.useState({
    recon: { count: 0, data: [] }
  })

  const [fetching, setFetching] = React.useState(false)
  const [fetchingViewList, setFetchingViewList] = React.useState(false)

  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    data: [],
    orderBy: 'date_due',
    order: true,
    category: 'recon',
    filter: {}
  }

  const [viewList, setViewList] = React.useState(initialValue)

  React.useEffect(() => {
    setFetching(true)
    Promise.all([
      services.getItems({
        category: viewList.category,
        limit: viewList.limit,
        sort: 'date_due|desc',
        range: 'this-week',
        filter: viewList.filter
      }),
      services.getItemCount({ status: 'RECONCILED' })
    ])
      .then(values => {
        const [claimResponse, claimCountResponse] = values
        const allClaims = claimResponse.data.data.results
        const allClaimsCount = claimCountResponse.data.data
        setClaims(prevState => ({ ...prevState, recon: { data: allClaims, count: allClaimsCount } }))
      })
      .finally(values => {
        setFetching(false)
      })
  }, [])

  const handleFilterChange = category => value => {
    setFetching(true)
    services
      .getItems({
        category,
        limit: 10,
        sort: 'date_due|desc',
        filter: viewList.filter,
        range: value
      })
      .then(({ data: res }) => {
        const { data } = res

        setClaims(prevState => ({ ...prevState, [category]: { ...prevState[category], data: data.results } }))
      })
      .finally(values => {
        setFetching(false)
      })
  }

  const handleOnReview = claim_number => {
    props.history.push(`/claims/reconciled/${claim_number}`)
  }

  const handleOnShowViewList = category => () => {
    let title = startCase(`${category} Claims`)
    if (category === 'recon') title = 'Reconciled Claims'

    setViewList({ ...initialValue, show: true, title, category })

    setFetchingViewList(true)
    services
      .getItems({
        category,
        limit: 10,
        sort: 'date_due|desc'
      })
      .then(({ data: res }) => {
        const { results, count, limit, page } = res.data
        setViewList(state => ({ ...state, data: results, count, limit, page }))
      })
      .finally(values => {
        setFetchingViewList(false)
      })
  }

  const handleOnViewListChange = params => {
    const _params = {
      category: viewList.category,
      limit: viewList.limit,
      page: viewList.page,
      search: viewList.search,
      sort: `${viewList.orderBy}|${viewList.order ? 'desc' : 'asc'}`,
      filter: viewList.filter,
      ...params
    }

    setFetchingViewList(true)
    services
      .getItems(_params)
      .then(({ data: res }) => {
        const { results, count, limit, page } = res.data
        setViewList(state => ({ ...state, data: results, count, limit, page }))
      })
      .finally(values => {
        setFetchingViewList(false)
      })
  }

  return (
    <React.Fragment>
      <DashboardTile
        id='reconciled-claim'
        count={{ color: 3, value: claims.recon['count'], id: 'reconciled-claim-count' }}
        sizes={sizes}
        title='Reconciled Claims'
        viewList={
          <Typography
            id='rc-view-list'
            variant='caption'
            className='bold text-link'
            onClick={handleOnShowViewList('recon')}
          >
            View List
          </Typography>
        }
        onFilterChange={handleFilterChange('recon')}
      >
        <LoadingIndicator loading={fetching}>
          <Table stickyHeader size='small'>
            <TableHead>
              <TableRow>
                {columns.map(item => (
                  <TableCell key={`rc-${item.id}`} id={`rc-${item.id}`}>
                    {item.name}
                  </TableCell>
                ))}

                <TableCell />
              </TableRow>
            </TableHead>
            <TableBody>
              {claims.recon.data.map((item, index) => (
                <TableRow key={`${item.claim_number}-${index}`}>
                  <TableCell id={`rc-claim-no-${index}`}>{item.claim_number}</TableCell>
                  <TableCell id={`rc-due-date-${index}`}>{DateFormatter(item.date_due)}</TableCell>
                  <TableCell id={`rc-facility-name-${index}`}>{item.facility.name}</TableCell>
                  <TableCell id={`rc-view-claim-${index}`}>
                    <Typography
                      id={`view-claim-${index}`}
                      variant='caption'
                      className={`text-link underline`}
                      onClick={() => handleOnReview(item.claim_number)}
                    >
                      Analyze
                    </Typography>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </LoadingIndicator>
      </DashboardTile>

      <ViewList
        id='reconciled-claim'
        columns={columns}
        searchTextPlaceHolder='Search for claim number, date or facility name'
        viewList={viewList}
        setViewList={setViewList}
        onViewListChange={handleOnViewListChange}
        fetching={fetchingViewList}
        body={_data =>
          _data.map((item, index) => (
            <TableRow key={`${item.claim_number}-${index}`}>
              <TableCell id={`rc-claim-number-${index}`}>{item.claim_number}</TableCell>
              <TableCell id={`rc-due-date-${index}`}>{DateFormatter(item.date_due)}</TableCell>
              <TableCell id={`rc-facility-name-${index}`}>{item.facility.name}</TableCell>
              <TableCell id={`rc-view-claim-${index}`}>
                <Typography
                  id={`view-claim-${index}`}
                  variant='caption'
                  className={`text-link underline`}
                  onClick={() => handleOnReview(item.claim_number)}
                >
                  Analyze
                </Typography>
              </TableCell>
            </TableRow>
          ))
        }
      />
    </React.Fragment>
  )
}

ReconTIle.displayName = 'DashboardTileComponent'

export default ReconTIle
