import React from 'react'
import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography'
import Radio from '@material-ui/core/Radio'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TableSortLabel from '@material-ui/core/TableSortLabel'

// timefield lib
import TimeField from 'react-advanced-timefield'

// Components
import Table from 'SharedComponents/Table/Table'
import TextField from 'SharedComponents/TextField/CustomTextField'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import Alert from 'SharedComponents/Alert/Alert'
import DropDown from 'SharedComponents/DropDown/DropDown'
import LoadingIndicator from 'SharedComponents/LoadingIndicator/LoadingIndicator'

// Services
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'

// Services
import ClaimsMaintainerServices from 'Services/Api/Claims/ClaimsMaintainerServices'
import ClaimsServices from 'Services/Api/Claims/ClaimsServices'

// Constants
import ClaimTypes from 'Constant/claimTypes'

// Styles
import classes from './MaintenancePage.module.scss'

// Constants
const lookups = {
  hospital_items: { on: 'hospital-item-list', field: 'hospital-item-list' },
  reason_covered: { on: 'maintenance', field: 'covered-reason' },
  reason_charging: { on: 'maintenance', field: 'charging-reason' },
  reason_return: { on: 'maintenance', field: 'return-reason' },
  reason_reject: { on: 'maintenance', field: 'reject-reason' },
  claims_source: { on: 'maintenance', field: 'claims-source' },
  claim_sub_type: { on: 'maintenance', field: 'claims-sub-type' },
  claim_sub_sub_type: { on: 'maintenance', field: 'claims-sub-sub-type' },
  handling_timi_kpi: { on: 'handling-time-kpi', field: 'handling-time-kpi' }
}

const claim_types = ClaimTypes.dropdown()

const initialValue = {
  show: false,
  title: '',
  count: 0,
  search: '',
  limit: 10,
  page: 1,
  data: [],
  orderBy: '',
  order: true,
  header: 'Hospital Item List',
  filter: {
    description: { value: '' },
    status: { value: '' },
    section: { value: '' },
    claim_type: { value: '' },
    handling_time_formatted: { value: '' }
  }
}
const initialItem = {
  description: '',
  status: 'Active',
  code: '',
  active: true,
  deactivate: false,
  claim_type: '',
  section: '',
  handling_time: ''
}

let columns = [
  { name: 'Description', sortField: 'description', id: 'description', match: 'any' },
  { name: 'Status', sortField: 'status', id: 'status', match: 'start' }
]

let kpi_columns = [
  { name: 'Section', sortField: 'section', id: 'section', match: 'any' },
  { name: 'Claim Type', sortField: 'claim_type', id: 'description', match: 'any' },
  { name: 'Handling Time', sortField: 'handling_time_formatted', id: 'description', match: 'any' },
  { name: 'Status', sortField: 'status', id: 'status', match: 'start' }
]

export default function MaintenancePage (props) {
  const [data, setData] = React.useState(initialValue)
  const [lookup, setLookup] = React.useState(lookups.hospital_items)
  const [debounce, setDebounce] = React.useState(null)
  const [item, setItem] = React.useState(initialItem)
  const [showAddItem, setShowAddItem] = React.useState({
    show: false,
    mode: 'add'
  })
  const [section, setSection] = React.useState([])

  const [index, setindex] = React.useState()

  const [showAlert, setShowAlert] = React.useState({
    open: false,
    message: '',
    onOk: '',
    okLabel: 'Okay',
    cancelLabel: ''
  })

  const [fetching, setFetching] = React.useState(false)

  React.useEffect(() => {
    PageTitleService('Claims Maintenance')
    BreadcrumbsService.setSelectedItems(['maintenance'])
    ClaimsServices.lookupsAll({ category: 'section' }).then(res => {
      const { data = [] } = res.data
      const _data = data.map(c => ({ text: c.name, value: c.name }))
      setSection(_data)
    })
  }, [])

  React.useEffect(() => {
    ClaimsMaintainerServices.lookups({ on: lookup.on, params: { ...data, field: lookup.field } }).then(
      ({ data: res }) => {
        const { results, count } = res.data
        setData(state => ({ ...state, data: results, count }))
        setFetching(false)
      }
    )
  }, [lookup])

  const handleOnChange = params => {
    const _params = {
      limit: data.limit,
      page: data.page,
      search: data.search,
      sort: `${data.orderBy}|${data.order ? 'desc' : 'asc'}`,
      filter: data.filter,
      field: lookup.field,
      ...params
    }

    ClaimsMaintainerServices.lookups({ on: lookup.on, params: _params }).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setData(state => ({ ...state, data: results, count, limit, page }))
    })
  }

  const isActive = args => {
    return lookup && lookup.field && args === lookup.field ? 'active' : ''
  }

  const onClickHospitalItem = () => {
    setFetching(true)
    setLookup(lookups.hospital_items)
    setData({
      ...data,
      header: 'Hospital Item List',
      filter: { ...data.filter, description: { value: '' }, status: { value: '' } }
    })
  }
  const onClickNotCoveredReason = () => {
    setFetching(true)
    setLookup(lookups.reason_covered)
    setData({
      ...data,
      header: 'Not Covered Reasons',
      filter: { ...data.filter, description: { value: '' }, status: { value: '' } }
    })
  }

  const onClickChargingReason = () => {
    setFetching(true)
    setLookup(lookups.reason_charging)
    setData({
      ...data,
      header: 'Reasons For Charging Member',
      filter: { ...data.filter, description: { value: '' }, status: { value: '' } }
    })
  }

  const onClickClaimSource = () => {
    setFetching(true)
    setLookup(lookups.claims_source)
    setData({
      ...data,
      header: 'Claim Source',
      filter: { ...data.filter, description: { value: '' }, status: { value: '' } }
    })
  }

  const onClickReturnReason = () => {
    setFetching(true)
    setLookup(lookups.reason_return)
    setData({
      ...data,
      header: 'Reimbursement Return Reasons',
      filter: { ...data.filter, description: { value: '' }, status: { value: '' } }
    })
  }

  const onClickRejectReason = () => {
    setFetching(true)
    setLookup(lookups.reason_reject)
    setData({
      ...data,
      header: 'Reimbursement Reject Reasons',
      filter: { ...data.filter, description: { value: '' }, status: { value: '' } }
    })
  }

  const onClickClaimSubType = () => {
    setFetching(true)
    setLookup(lookups.claim_sub_type)
    setData({
      ...data,
      header: 'Claim Sub-Type',
      filter: { ...data.filter, description: { value: '' }, status: { value: '' } }
    })
  }

  const onClickClaimSubSubType = () => {
    setFetching(true)
    setLookup(lookups.claim_sub_sub_type)
    setData({
      ...data,
      header: 'Claim Sub Sub-Type',
      filter: { ...data.filter, description: { value: '' }, status: { value: '' } }
    })
  }

  const onClickHandlingTimeKpi = () => {
    setFetching(true)
    setLookup(lookups.handling_timi_kpi)
    setData({
      ...data,
      count: 0,
      header: 'Handling Time KPI',
      filter: {
        ...data.filter,
        section: { value: '' },
        claim_type: { value: '' },
        handling_time: { value: '' },
        status: { value: '' }
      }
    })
  }

  const handleOnSort = orderBy => e => {
    setData(p => ({ ...p, orderBy, order: !data.order }))
    handleOnChange({ sort: `${orderBy}|${data.order ? 'asc' : 'desc'}` })
  }
  const handleColumnFiltering = args => {
    const { field, value, match } = args
    setData(p => ({ ...p, filter: { ...p.filter, [field]: { value, match } } }))
    handleOnChange({ filter: { ...data.filter, [field]: { value, match } }, page: 1 })
  }

  const validate = () => {
    if (lookup.field !== 'handling-time-kpi') return !item.description.trim()
    else return !item.handling_time || item.handling_time === '00:00:00' || !item.section || !item.claim_type
  }

  const handleOnSaveItem = () => {
    LoadingService.loading(true)
    const initialLookup = lookup
    let _search = {}

    if (!(lookup.field === 'handling-time-kpi')) {
      _search = {
        description: item.description,
        code: item.code
      }
    } else {
      if (showAddItem.mode === 'add') {
        _search = {
          section: item.section,
          claim_type: item.claim_type
        }
      } else {
        const { section, claim_type, status, handling_time } = data.data[index]

        switch (true) {
          case section === item.section && claim_type === item.claim_type && status !== item.status:
            _search = {
              section: item.section,
              claim_type: item.claim_type,
              status: item.status
            }
            break
          case section === item.section && claim_type === item.claim_type && handling_time !== item.handling_time:
            _search = {
              section: item.section,
              claim_type: item.claim_type,
              status: item.handling_time
            }
            break
          default:
            _search = {
              section: item.section,
              claim_type: item.claim_type
            }
        }
      }
    }

    const _params = {
      limit: 1,
      page: 1,
      search: _search,
      field: lookup.field
    }
    const handleOnOk = () => {
      setShowAlert(state => ({ ...state, open: false }))
    }

    if (validate()) {
      setShowAlert(state => ({
        ...state,
        open: true,
        message: 'Description details is empty. Please input the description details.',
        onOk: handleOnOk,
        okLabel: 'Okay',
        cancelLabel: ''
      }))
      LoadingService.loading(false)
    } else {
      ClaimsMaintainerServices.lookups({ on: lookup.on, params: _params }).then(({ data: res }) => {
        const { results } = res.data
        if (results.length > 0) {
          setShowAlert(state => ({
            ...state,
            open: true,
            message: 'Description details already exists. Please change the description details encoded.',
            onOk: handleOnOk,
            okLabel: 'Okay',
            cancelLabel: ''
          }))
        } else {
          if (showAddItem.mode === 'add') {
            ClaimsMaintainerServices.storeLookups({ on: lookup.on, data: item, params: { field: lookup.field } }).then(
              res => {
                setItem(initialItem)
                setLookup(false)
                setLookup(initialLookup)
                setShowAddItem({ ...showAddItem, show: false })
                LoadingService.loading(false)
              }
            )
          } else if (showAddItem.mode === 'edit') {
            ClaimsMaintainerServices.updateLookups({
              on: lookup.on,
              data: item,
              code: item.code,
              params: { field: lookup.field }
            }).then(res => {
              setItem(initialItem)
              setLookup(false)
              setLookup(initialLookup)
              setShowAddItem({ ...showAddItem, show: false })
              LoadingService.loading(false)
            })
          }
        }
        LoadingService.loading(false)
      })
    }
  }

  const handleOnEditItem = (field = '', item) => {
    if (field === 'kpi') {
      setShowAddItem({ ...showAddItem, show: true, mode: 'edit' })
      setItem({
        section: item.section,
        claim_type: item.claim_type,
        handling_time: item.handling_time,
        code: item._id,
        status: item.status,
        active: item.status === 'Active',
        deactivate: item.status === 'Inactive'
      })
    } else {
      setShowAddItem({ ...showAddItem, show: true, mode: 'edit' })
      setItem({
        description: item.description,
        code: item.code,
        status: item.status,
        active: item.status === 'Active',
        deactivate: item.status === 'Inactive'
      })
    }
  }

  const handleOnCancelItem = () => {
    if (item.description || item.section || item.claim_type || item.handling_time) {
      setShowAlert(state => ({
        ...state,
        open: true,
        message: 'Are you sure you want to discard your changes?',
        onOk: () => {
          setShowAlert(alert => ({ ...alert, open: false }))
          setShowAddItem(show => ({ ...show, show: false }))
          setItem(item => ({ ...item, description: '', section: '', claim_type: '', handling_time: '' }))
        },
        okLabel: 'Yes',
        cancelLabel: 'No'
      }))
    } else {
      setShowAddItem({ ...showAddItem, show: false })
    }
  }

  return (
    <div className={classes.root}>
      <div className={classes['button-parent']}>
        <Button
          disabled={showAddItem.show}
          variant='contained'
          color='primary'
          id='hospital-item-list'
          className={`button-positive ${classes[isActive('hospital-item-list')]}`}
          onClick={onClickHospitalItem}
        >
          Hospital Item List
        </Button>

        <Button
          disabled={showAddItem.show}
          variant='contained'
          color='primary'
          id='not-covered-reason'
          className={`button-positive ${classes[isActive('covered-reason')]}`}
          onClick={onClickNotCoveredReason}
        >
          Not Covered Reason
        </Button>

        <Button
          disabled={showAddItem.show}
          variant='contained'
          color='primary'
          id='reason-for-charging'
          className={`button-positive ${classes[isActive('charging-reason')]}`}
          onClick={onClickChargingReason}
        >
          Reason for Charging Member
        </Button>

        <Button
          disabled={showAddItem.show}
          variant='contained'
          color='primary'
          id='claim-source'
          className={`button-positive ${classes[isActive('claims-source')]}`}
          onClick={onClickClaimSource}
        >
          Claim Source
        </Button>

        <Button
          disabled={showAddItem.show}
          variant='contained'
          color='primary'
          id='claim-source'
          className={`button-positive ${classes[isActive('handling-time-kpi')]}`}
          onClick={onClickHandlingTimeKpi}
        >
          Handling Time KPI
        </Button>
      </div>

      <div className={classes['button-parent']}>
        <Button
          disabled={showAddItem.show}
          variant='contained'
          color='primary'
          id='reimbursement-return-reason'
          className={`button-positive ${classes[isActive('return-reason')]}`}
          onClick={onClickReturnReason}
        >
          Reimbursement Return Reason
        </Button>

        <Button
          disabled={showAddItem.show}
          variant='contained'
          color='primary'
          id='reimbursement-reject-reason'
          className={`button-positive ${classes[isActive('reject-reason')]}`}
          onClick={onClickRejectReason}
        >
          Reimbursement Reject Reason
        </Button>

        <Button
          disabled={showAddItem.show}
          variant='contained'
          color='primary'
          id='claim-sub-type'
          className={`button-positive ${classes[isActive('claims-sub-type')]}`}
          onClick={onClickClaimSubType}
        >
          Claim Sub-Type
        </Button>

        <Button
          disabled={showAddItem.show}
          variant='contained'
          color='primary'
          id='claim-sub-sub-type'
          className={`button-positive ${classes[isActive('claims-sub-sub-type')]}`}
          onClick={onClickClaimSubSubType}
        >
          Claim Sub Sub-Type
        </Button>
      </div>

      <div className={classes.header}>
        <div className={classes['header-wrapper']}>
          <span className={classes['header-text']}> {data.header} </span>
          <Button
            disabled={showAddItem.show}
            variant='contained'
            color='primary'
            id='add-item'
            className='button-positive'
            onClick={() => setShowAddItem({ ...showAddItem, show: true, mode: 'add' })}
          >
            + Add
          </Button>
        </div>
      </div>

      {showAddItem.show && (
        <div className={classes['add-item-root']}>
          {!(lookup.field === 'handling-time-kpi') ? (
            <div className={classes.wrapper}>
              <span className={classes['header-text']}>Description:</span>
              <div className={classes['text-wrapper']}>
                <TextField
                  id={'description-field'}
                  value={item.description}
                  onChange={event => {
                    setItem({ ...item, description: event.target.value })
                  }}
                />
              </div>
              <div className={classes['radio-buttons']}>
                <div className={classes.activate}>
                  Active:
                  <Radio
                    color='primary'
                    id={'activate'}
                    checked={item.active}
                    onClick={() => setItem({ ...item, status: 'Active', active: true, deactivate: false })}
                  />
                </div>
                <div className={classes.deactivate}>
                  Deactivate:
                  <Radio
                    color='primary'
                    id={'deactivate'}
                    checked={item.deactivate}
                    onClick={() => setItem({ ...item, status: 'Inactive', active: false, deactivate: true })}
                  />
                </div>
              </div>
              <div className={classes['button-wrapper']}>
                <Button
                  variant='contained'
                  color='primary'
                  id='add-item'
                  className='button-positive'
                  onClick={handleOnSaveItem}
                >
                  Save
                </Button>
                <Button
                  variant='contained'
                  color='primary'
                  id='add-item'
                  className='button-negative'
                  onClick={handleOnCancelItem}
                >
                  Cancel
                </Button>
              </div>
            </div>
          ) : (
            <div>
              <div className={classes.wrapper}>
                <span className={classes['header-text']}>Section:</span>
                <div className={classes['text-wrapper']}>
                  <DropDown
                    id='section'
                    items={section}
                    value={item.section || ''}
                    onChange={event => setItem(state => ({ ...state, section: event.target.value }))}
                  />
                </div>
                <span className={classes['header-text']}>Claim Type:</span>
                <div className={classes['text-wrapper']}>
                  <DropDown
                    id='claim-type'
                    items={claim_types}
                    value={item.claim_type || ''}
                    onChange={event => setItem(state => ({ ...state, claim_type: event.target.value }))}
                  />
                </div>
                <span className={classes['header-text']}>Handling Time:</span>
                {/* Trial - feel free to change this time input */}
                <div className={classes['text-wrapper']}>
                  <TimeField
                    id={'description-field'}
                    value={item.handling_time}
                    input={<TextField TextFieldStyle={{ padding: '3.5px' }} />}
                    showSeconds
                    onChange={(event, time) => setItem(state => ({ ...state, handling_time: time }))}
                  />
                </div>
                <div className={classes['radio-buttons']}>
                  <div className={classes.activate}>
                    Active:
                    <Radio
                      color='primary'
                      id={'activate'}
                      checked={item.active}
                      onClick={() => setItem({ ...item, status: 'Active', active: true, deactivate: false })}
                    />
                  </div>
                  <div className={classes.deactivate}>
                    Deactivate:
                    <Radio
                      color='primary'
                      id={'deactivate'}
                      checked={item.deactivate}
                      onClick={() => setItem({ ...item, status: 'Inactive', active: false, deactivate: true })}
                    />
                  </div>
                </div>
              </div>
              <div className={classes['button-wrapper-kpi']}>
                <Button
                  variant='contained'
                  color='primary'
                  id='add-item'
                  className='button-positive'
                  onClick={handleOnSaveItem}
                >
                  Save
                </Button>
                <Button
                  variant='contained'
                  color='primary'
                  id='add-item'
                  className='button-negative'
                  onClick={handleOnCancelItem}
                >
                  Cancel
                </Button>
              </div>
            </div>
          )}
        </div>
      )}

      {!showAddItem.show && (
        <div className={classes.table}>
          <LoadingIndicator loading={fetching}>
            <Table
              TableContainerProps={{ style: { height: '100%', overflow: 'auto', position: 'relative' } }}
              TableProps={{ stickyHeader: true }}
              head={
                <React.Fragment>
                  <TableRow>
                    {!(lookup.field === 'handling-time-kpi')
                      ? columns.map(headCell => (
                          <TableCell key={headCell.sortField} id={`sort-${headCell.id}`}>
                            <TableSortLabel
                              active={data.orderBy === headCell.sortField}
                              direction={data.order ? 'asc' : 'desc'}
                              onClick={handleOnSort(headCell.sortField)}
                            >
                              {headCell.name}
                            </TableSortLabel>
                            <TextField
                              value={data.filter[headCell.sortField].value || ''}
                              onChange={event => {
                                const search = event.target.value
                                setData(p => ({
                                  ...p,
                                  filter: {
                                    ...p.filter,
                                    [headCell.sortField]: { value: search, match: headCell.match }
                                  }
                                }))
                                clearTimeout(debounce)
                                setDebounce(
                                  setTimeout(() => {
                                    handleColumnFiltering({
                                      field: headCell.sortField,
                                      value: search,
                                      match: headCell.match
                                    })
                                  }, 500)
                                )
                              }}
                            />
                          </TableCell>
                        ))
                      : kpi_columns.map(headCell => (
                          <TableCell key={headCell.sortField} id={`sort-${headCell.id}`}>
                            <TableSortLabel
                              active={data.orderBy === headCell.sortField}
                              direction={data.order ? 'asc' : 'desc'}
                              onClick={handleOnSort(headCell.sortField)}
                            >
                              {headCell.name}
                            </TableSortLabel>
                            <TextField
                              value={data.filter[headCell.sortField].value || ''}
                              onChange={event => {
                                const search = event.target.value
                                setData(p => ({
                                  ...p,
                                  filter: {
                                    ...p.filter,
                                    [headCell.sortField]: { value: search, match: headCell.match }
                                  }
                                }))
                                clearTimeout(debounce)
                                setDebounce(
                                  setTimeout(() => {
                                    handleColumnFiltering({
                                      field: headCell.sortField,
                                      value: search,
                                      match: headCell.match
                                    })
                                  }, 500)
                                )
                              }}
                            />
                          </TableCell>
                        ))}

                    <TableCell key={'action'} id={`sort-action`} />
                  </TableRow>
                </React.Fragment>
              }
              body={
                data &&
                data.data.map((item, index) =>
                  !(lookup.field === 'handling-time-kpi') ? (
                    <TableRow key={index}>
                      <TableCell id={`item-description-${index}`}>{item.description}</TableCell>
                      <TableCell id={`item-status-${index}`}>{item.status}</TableCell>
                      <TableCell align={'center'}>
                        <Typography
                          variant='caption'
                          className={`text-link underline`}
                          id={`submmited-pr-${index}`}
                          onClick={() => handleOnEditItem('desc', item)}
                        >
                          Edit
                        </Typography>
                      </TableCell>
                    </TableRow>
                  ) : (
                    <TableRow key={index}>
                      <TableCell id={`item-section-${index}`}>{item.section}</TableCell>
                      <TableCell id={`item-claim_type-${index}`}>{item.claim_type}</TableCell>
                      <TableCell id={`item-handling_time-${index}`}>{item.handling_time_formatted}</TableCell>
                      <TableCell id={`item-status-${index}`}>{item.status}</TableCell>
                      <TableCell align={'center'}>
                        <Typography
                          variant='caption'
                          className={`text-link underline`}
                          id={`submmited-pr-${index}`}
                          onClick={() => {
                            handleOnEditItem('kpi', item)
                            setindex(index)
                          }}
                        >
                          Edit
                        </Typography>
                      </TableCell>
                    </TableRow>
                  )
                )
              }
              count={data && data.count}
              page={data && data.page}
              limit={data && data.limit}
              rowsPerPageOptions={[10, 15, 20]}
              onChange={handleOnChange}
            />
          </LoadingIndicator>
        </div>
      )}
      <Alert
        open={showAlert.open}
        message={showAlert.message}
        okLabel={showAlert.okLabel}
        cancelLabel={showAlert.cancelLabel}
        onCancel={() => setShowAlert(alert => ({ ...alert, open: false }))}
        onOk={showAlert.onOk}
      />
    </div>
  )
}
