import React from 'react'

// Material Component
import { CurrencyFormatter } from 'Utilities/TextFormatter'

// Styles
import classes from './Footer.module.scss'

export default function ClaimsUtilLedgerReport (props) {
  const { total = {}, bundles = [], page } = props

  // TODO For refactoring :(( )
  return (
    <div className={classes.footer}>
      <hr />
      {bundles.length > 0 && (
        <div className={classes.bundle}>
          <p><span><b>Bundle Number: </b></span>{`${bundles[page - 1]}`}</p>
        </div>
      ) }

      <div className={classes.total}>
        <div className={classes['total-row']}>
          <div className={classes['total-cell']}>
            <p>{`TOTAL APPROVED: ${CurrencyFormatter(total.total_approved_amount)}`}</p>
          </div>

          <div className={classes['total-cell']}>
            <p>{`TOTAL TAX: ${CurrencyFormatter(total.total_tax_amount)}`}</p>
          </div>

        </div>
      </div>
    </div>
  )
}
