import React from 'react'

// Material Components
import Button from '@material-ui/core/Button'
import Paper from '@material-ui/core/Paper'
import Table from 'SharedComponents/Table/Table'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TableSortLabel from '@material-ui/core/TableSortLabel'
import Typography from '@material-ui/core/Typography'
import GetAppIcon from '@material-ui/icons/GetApp'

// Shared Components
import DatePicker from 'SharedComponents/DatePicker2/DatePicker2'
import TextField from 'SharedComponents/TextField/CustomTextField'
import AutoComplete from 'SharedComponents/AutoComplete/AutoComplete'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import ClaimsServices from 'Services/Api/Claims/ClaimsServices'
import ArrayToCSV from 'Services/ArrayToCSV'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'

// Utility
import { DateFormatter, CurrencyFormatter } from 'Utilities/TextFormatter'
import { SafeAccess } from 'Utilities/SafeAccess'

// Styles
import classes from './AgingPartialApprovedReport.module.scss'

// Validator
import DateRangeValidator from '../TransmittalReport/validator/DateRangeValidator'

const completeColumns = [
  { label: 'Month', data: 'month', align: 'left' },
  {
    label: 'Number of partially approved',
    label2: 'claims within grace period',
    type: 'number',
    data: 'total15Days',
    align: 'center'
  },
  {
    label: 'Number of partially approved',
    label2: 'claims beyond grace period',
    type: 'number',
    data: 'total1Week',
    align: 'center'
  },
  {
    label: 'Number of partially approved claims',
    label2: '1 month (30 Days) beyond grace period',
    type: 'number',
    data: 'total1Month',
    align: 'center'
  },
  {
    label: 'Percentage of partially approved',
    label2: 'claims beyond grace period (total)',
    type: 'number',
    data: 'totalAll',
    suffix: '%',
    align: 'center'
  }
]

/**
 * Aging of Partially Approved Claims
 *
 * @export
 *
 * @param {*} props
 *   dataColumns - default []; if defined, will be the allowable data columns that
 *     will be shown on the claims utilization report
 *   hideReport - default false; if defined, will be used to hide the entire report
 *   hideheader - default false; if defined, will be used to hide the header section
 *   dateRange - default current date; if defined, will be used to define the date range in-effect
 *
 * @returns
 */
export default props => {
  const { dataColumns = [], hideReport, hideHeader, dateRange } = props

  //* mirror of data for CSV fetching to minimize the time of generation
  //* also to avoid two way filter func
  const [rawData, setRawData] = React.useState({})

  const [claims, setClaims] = React.useState({
    data: [],
    range: {
      from: SafeAccess(dateRange, 'from', new Date().toISOString()),
      to: SafeAccess(dateRange, 'to', new Date().toISOString()),
      field: 'status_date'
    },
    filter: {
      medical_partner_id: 'ALL',
      medical_partner_name: 'ALL',
      claim_type: 'ALL',
      claim_source: 'ALL',
      facility_region: 'ALL'
    },
    total15Days: 0,
    total1Week: 0,
    total1Month: 0,
    totalAll: 0
  })

  const [suggestion, setSuggestion] = React.useState({
    medicalPartner: {
      id: [],
      name: [],
      data: []
    },
    claim_type: [],
    claim_source: [],
    facility_region: []
  })

  const [dateRangeError, setDateRangeError] = React.useState({
    date_from: '',
    date_to: ''
  })

  const [debounce, setDebounce] = React.useState({
    facility: null,
    claim_type: null,
    claim_source: null,
    facility_region: null
  })

  const [columns, setColumns] = React.useState(completeColumns)

  React.useEffect(() => {
    PageTitleService('Aging of Partially Approved Claim Report')
    BreadcrumbsService.setSelectedItems(['agingPartialApprovedReport'])
    fetchRecords()
  }, [])

  const handleOnFilter = args => {
    const { field, value, match } = args
    setClaims(state => ({ ...state, filter: { ...state.filter, [field]: { value, match } } }))
  }

  const fetchRecords = () => {
    LoadingService.loading(true)
    ClaimsServices.agingPartialApprovedClaimsReport({
      range: claims.range,
      filter: claims.filter
    }).then(res => {
      const { data } = res.data
      const { results = [], total15Days = 0, total1Week = 0, total1Month = 0, totalAll = 0 } = data
      setClaims(state => ({
        ...state,
        data: results,
        total15Days,
        total1Week,
        total1Month,
        totalAll
      }))

      setRawData({
        raw: results,
        total15Days,
        total1Week,
        total1Month,
        totalAll
      })
      LoadingService.loading(false)
    })
  }

  const validate = () => {
    const { error } = DateRangeValidator({
      date_from: claims.range.from,
      date_to: claims.range.to
    })

    setDateRangeError({ ...error })
    return !!error
  }

  const fetchCSV = () => {
    LoadingService.loading(true)
    const { raw = [], total15Days = 0, total1Week = 0, total1Month = 0, totalAll = 0 } = rawData
    const rows = raw.map(claim => {
      return columns.map(col => formatData({ type: col.type, source: claim, field: col.data }))
    })

    const header = columns.map(c => c.label)
    ArrayToCSV({
      statics: [
        [
          'Aging of Partially Approved Claim Report',
          '\r\n',
          '\r\n',
          `Medical Partner ID: ${claims.filter.medical_partner_id}`,
          '\r\n',
          `Medical Partner Name: ${claims.filter.medical_partner_name}`,
          '\r\n',
          `Claim Type: ${claims.filter.claim_type}`,
          '\r\n',
          `Claim Source: ${claims.filter.claim_source}`,
          '\r\n',
          `Geogpraphy: ${claims.filter.facility_region}`,
          '\r\n',
          `Date Range: ${DateFormatter(claims.range.from)} - ${DateFormatter(claims.range.to)}`,
          '\r\n',
          '\r\n'
        ],
        [[`Grand Total`, `${total15Days}`, `${total1Week}`, `${total1Month}`, `${totalAll}`]]
      ],
      fileName: 'Aging of Partially Approved Claim Report',
      header,
      rows
    })
    LoadingService.loading(false)
  }

  React.useEffect(() => {
    setClaims(state => ({
      ...state,
      range: {
        ...state.range,
        to: dateRange ? dateRange.to : new Date().toISOString(),
        from: dateRange ? dateRange.from : new Date().toISOString()
      }
    }))
  }, [dateRange])

  React.useEffect(() => {
    // Goal: Adjust columns to show based on data columns passed as props

    let filtered = null
    if (!dataColumns || dataColumns.length === 0) {
      filtered = completeColumns
    } else {
      filtered = completeColumns.filter(entry => dataColumns.indexOf(entry.data) > -1)
    }
    setColumns(filtered)
  }, [dataColumns])

  const formatData = ({ type = null, source, field }) => {
    let output = ''
    if (type === 'date') {
      output = DateFormatter(source[field])
    } else if (type === 'array') {
      const [root, property] = field.split('.')
      output = source[root] ? source[root].map(f => f[property]).join(`${'\r\n'}${'\r\n'}`) : ''
    } else if (type === 'number') {
      output = source[field]
    } else {
      output = source[field] ? source[field] : ''
    }

    return output !== '' && output !== 'undefined' && output !== `${'\r\n'}${'\r\n'}` ? output : 'N/A'
  }

  //* Generic
  const handleOnChange = ({ value, category, index = '', field, isSuggestionClick }) => {
    let _category = ''

    if (index) _category = `${category}_${index}`
    else _category = category.split('-').join('_')

    setClaims(state => ({ ...state, filter: { ...state.filter, [_category]: value } }))
    if (!isSuggestionClick) {
      clearTimeout(debounce[_category])
      setDebounce(state => ({
        ...state,
        [_category]: setTimeout(() => {
          if (value) {
            ClaimsServices.lookupsQuery({ category, field, value }).then(res => {
              const { data } = res.data
              if (data.body) {
                setSuggestion(state => ({
                  ...state,
                  [_category]: ['ALL', ...data.body.splice(0, 10).map(d => d[field])]
                }))
              } else {
                setSuggestion(state => ({ ...state, [_category]: ['ALL', ...data.splice(0, 10).map(d => d[field])] }))
              }
            })
          }
        }, 500)
      }))
    }
  }

  //* Facility
  const handleOnChangeFacility = ({ value, field, isSuggestionClick }) => {
    setClaims(state => ({ ...state, filter: { ...state.filter, [field]: value } }))
    if (isSuggestionClick) {
      for (let i of suggestion.medicalPartner.data) {
        if (value === i.id || value === i.name) {
          setClaims(state => ({
            ...state,
            filter: {
              ...state.filter,
              medical_partner_id: i.id,
              medical_partner_name: i.name,
              facility_region: i.geography
            }
          }))

          setSuggestion(state => ({
            ...state,
            medicalPartner: {
              id: ['ALL'],
              name: ['ALL'],
              data: []
            }
          }))
          return
        }
      }
    } else {
      setSuggestion(state => ({
        ...state,
        medicalPartner: {
          id: ['ALL'],
          name: ['ALL'],
          data: []
        }
      }))
      const { 2: by } = field.split('_')
      getFacilitySuggestions({ value, by })
    }
  }

  const getFacilitySuggestions = ({ value = '', by }) => {
    clearTimeout(debounce.facility)
    setDebounce(state => ({
      ...state,
      facility: setTimeout(() => {
        if (value) {
          ClaimsServices.lookups({
            on: 'medical-partner',
            params: { query: value, by }
          }).then(res => {
            const { data } = res.data
            let _items = []
            for (let d of data) _items.push(d[by])
            setSuggestion(state => ({
              ...state,
              medicalPartner: { ...state.medicalPartner, [by]: ['ALL', ..._items], data }
            }))
          })
        }
      }, 500)
    }))
  }

  return (
    <Paper className={classes.root} style={{ display: hideReport === 'true' ? 'none' : 'block' }}>
      <div className={classes.header} style={{ display: hideHeader === 'true' ? 'none' : 'block' }}>
        <div className={classes.content}>
          <div className={classes['field-filter']}>
            <div className={classes.input}>
              <Typography variant='caption'>Medical Partner ID</Typography>
              <AutoComplete
                placeholder='Search by medical partner id'
                id='facility-id'
                value={claims.filter.medical_partner_id}
                suggestions={suggestion.medicalPartner.id}
                onChange={(value, isSuggestionClick) =>
                  handleOnChangeFacility({
                    field: 'medical_partner_id',
                    value,
                    isSuggestionClick
                  })
                }
              />
            </div>
            <div className={classes.input}>
              <Typography variant='caption'>Medical Partner Name</Typography>
              <AutoComplete
                placeholder='Search by medical partner name'
                id='facility-name'
                value={claims.filter.medical_partner_name}
                suggestions={suggestion.medicalPartner.name}
                onChange={(value, isSuggestionClick) =>
                  handleOnChangeFacility({
                    field: 'medical_partner_name',
                    value,
                    isSuggestionClick
                  })
                }
              />
            </div>
            <div className={classes.input}>
              <Typography variant='caption'>Claim Type</Typography>
              <AutoComplete
                placeholder='Search by claim type'
                id='claim-type'
                value={claims.filter.claim_type}
                suggestions={suggestion.claim_type}
                onChange={(value, isSuggestionClick) =>
                  handleOnChange({
                    value,
                    isSuggestionClick,
                    category: 'claim-type',
                    field: 'name'
                  })
                }
              />
            </div>
            <div className={classes.input}>
              <Typography variant='caption'>Claim Source</Typography>
              <AutoComplete
                placeholder='Search by claim source'
                id='claim-source'
                value={claims.filter.claim_source}
                suggestions={suggestion.claim_source}
                onChange={(value, isSuggestionClick) =>
                  handleOnChange({
                    value,
                    isSuggestionClick,
                    category: 'claim-source',
                    field: 'source'
                  })
                }
              />
            </div>
            <div className={classes.input}>
              <Typography variant='caption'>Geography</Typography>
              <AutoComplete
                placeholder='Search by region'
                id='geography'
                value={claims.filter.facility_region}
                suggestions={suggestion.facility_region}
                onChange={(value, isSuggestionClick) =>
                  handleOnChange({
                    value,
                    isSuggestionClick,
                    category: 'facility_region',
                    field: 'region'
                  })
                }
              />
            </div>
          </div>
        </div>
        <Typography variant='h6'>Date Range</Typography>
        <div className={classes.content}>
          <div className={classes.filter}>
            <div className={classes.input}>
              <p>From: </p>
              <DatePicker
                id='filter-date-from'
                value={claims.range.from}
                onChange={date =>
                  setClaims(state => ({
                    ...state,
                    range: {
                      ...state.range,
                      from: date.toISOString()
                    }
                  }))
                }
                disableFuture
                error={dateRangeError.date_from}
              />
            </div>
            <div className={classes.input}>
              <p>To: </p>
              <DatePicker
                id='filter-date-to'
                value={claims.range.to}
                onChange={date =>
                  setClaims(state => ({
                    ...state,
                    range: {
                      ...state.range,
                      to: date.toISOString()
                    }
                  }))
                }
                disableFuture
                error={dateRangeError.date_to}
              />
            </div>
            <Button
              className={`button-positive ${classes.submit}`}
              onClick={() => !validate() && fetchRecords()}
              variant='contained'
              color='primary'
            >
              Filter
            </Button>
          </div>
          <div className={classes.action}>
            <Button id='generate-report' onClick={() => fetchCSV()} disabled={!claims.data.length}>
              <GetAppIcon fontSize='small' />
              <Typography variant='caption' className={`bold text-link ${!claims.data.length ? classes.disabled : classes.typo}`}>
                Generate Report
              </Typography>
            </Button>
          </div>
        </div>
      </div>
      <div className={classes.body}>
        <div className={classes.table}>
          <Table
            TableContainerProps={{ style: { maxHeight: '60vh', overflow: 'auto', position: 'relative' } }}
            TableProps={{ stickyHeader: true }}
            head={
              <TableRow>
                {columns.map((headCell, index) => (
                  <TableCell align={headCell.align} key={`table-header-${index}`}>
                    <div>{headCell.label}</div>
                    <div>{headCell.label2}</div>
                    {headCell.search && (
                      <div className={classes.input}>
                        {headCell.type && headCell.type === 'date' ? (
                          <DatePicker
                            noMargin
                            id={`${headCell.data}`}
                            value={null}
                            onChange={date =>
                              handleOnFilter({
                                field: headCell.data,
                                value: date.toISOString(),
                                match: headCell.match
                              })
                            }
                          />
                        ) : (
                          <TextField
                            noMargin
                            onChange={event =>
                              handleOnFilter({
                                field: headCell.data,
                                value: event.target.value,
                                match: headCell.match
                              })
                            }
                          />
                        )}
                      </div>
                    )}
                  </TableCell>
                ))}
              </TableRow>
            }
            body={
              claims.data && claims.data.length ? (
                claims.data.map((claim, claimIndex) => (
                  <TableRow className={classes['table-row']} key={`table-item-${claimIndex}`}>
                    {columns.map((col, colIndex) => (
                      <TableCell
                        className={col.remarks ? classes.remarks : classes['table-cell']}
                        key={`table-cell-${claimIndex}-${colIndex}`}
                        align={col.data === 'month' ? 'left' : 'center'}
                      >
                        {col.suffix
                          ? `${formatData({ type: col.type, source: claim, field: col.data })}${col.suffix}`
                          : formatData({ type: col.type, source: claim, field: col.data })}
                      </TableCell>
                    ))}
                  </TableRow>
                ))
              ) : (
                <TableRow>
                  <TableCell className={classes.handling} colSpan={completeColumns.length}>
                    No data found
                  </TableCell>
                </TableRow>
              )
            }
            pagination={false}
          />
        </div>
        {claims.data.length ? (
          <div className={classes.footer}>
            <div className={classes.label}>
              <Typography className='bold'>Grand Total: </Typography>
            </div>
            <div className={classes.total15Days}>
              <Typography className='bold'>{claims.total15Days}</Typography>
            </div>
            <div className={classes.total1Week}>
              <Typography className='bold'>{claims.total1Week}</Typography>
            </div>
            <div className={classes.total1Month}>
              <Typography className='bold'>{claims.total1Month}</Typography>
            </div>
            <div className={classes.totalAll}>
              <Typography className='bold'>{`${claims.totalAll}%`}</Typography>
            </div>
          </div>
        ) : '' }
      </div>
    </Paper>
  )
}
