import Button from '@material-ui/core/Button'
import ButtonFooter from './Components/ButtonFooter/ButtonFooter'
import Checkbox from '@material-ui/core/Checkbox'
import { Link } from 'react-router-dom'
import React from 'react'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TableSortLabel from '@material-ui/core/TableSortLabel'
import Typography from '@material-ui/core/Typography'
import Paper from '@material-ui/core/Paper'

// Utilities
import { DateFormatter } from 'Utilities/TextFormatter'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import ClaimsVerifierSupervisorServices from 'Services/Api/Claims/ClaimsVerifierSupervisorServices'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'

// Components
import Table from 'SharedComponents/Table/Table'
import SearchTextComponent from 'SharedComponents/SearchTextComponent/SearchTextComponent'
import DatePicker from 'SharedComponents/DatePicker2/DatePicker2'
import TextField from 'SharedComponents/TextField/CustomTextField'

// Styles
import classes from './ClaimsList.module.scss'

export default function ClaimsPage (props) {
  const headcell = [
    { id: 's-claim-number', name: ' Claim Number', sortField: 'claim_number', match: 'any' },
    { id: 's-date-received', name: ' Date Received', sortField: 'received_date', match: 'date' },
    { id: 's-due-date', name: ' Due Date', sortField: 'due_date', match: 'date' },
    { id: 's-facility-name', name: ' Medical Partner Name (Facility)', sortField: 'facility_name', match: 'any' }
  ]

  const [selectStatus, setSelectStatus] = React.useState(false)
  const [id, setId] = React.useState(null)
  const [debounce, setDebounce] = React.useState(null)
  const [selected, setSelected] = React.useState({ claim_numbers: [] })

  const [claims, setClaims] = React.useState({
    show: false,
    count: 0,
    search: '',
    filter: {},
    limit: 10,
    page: 1,
    // data: [],
    orderBy: 'due_date',
    order: true,
    rowsPerPageOptions: [10, 15, 20]
  })
  const [claim, setClaim] = React.useState({})

  React.useEffect(() => {
    PageTitleService('For Adjudication')
    BreadcrumbsService.setSelectedItems(['dashboard', 'analystAssigned'])
    LoadingService.loading(true)

    const _id = props.match.params.user_id
    getClaims(claims)
    setId(_id)
  }, [props.match.params.user_id])

  const getClaims = params => {
    ClaimsVerifierSupervisorServices.getAnalystClaims({
      category: params.category,
      search: params.search,
      sort: `${params.orderBy}|${params.order ? 'asc' : 'desc'}`,
      page: params.page,
      filter: params.filter,
      id: props.match.params.user_id,
      limit: params.limit
    })
      .then(res => {
        const { results = [], count, limit, page } = res.data.data
        setClaims(state => ({ ...state, count, limit, page }))
        const _data = []

        //* Trial
        for (const result of results) {
          const selectedClaimnumbers = selected.claim_numbers
          if (selectedClaimnumbers.includes(result.claim_number)) _data.push({ ...result, checked: true })
          else _data.push({ ...result, checked: false })
        }
        // const data = results.map(batch => ({ ...claims, checked: false }))

        const checkedCount = _data.filter(i => i.checked).length
        const resultsCount = results.length

        setClaim({
          data: _data,
          loading: false,
          indeterminate: checkedCount > 0 && checkedCount < resultsCount ? true : undefined,
          checked: checkedCount === resultsCount ? true : undefined
        })
        LoadingService.loading(false)
      })
      .catch(err => {
        console.error(err)
      })
  }

  const handleSelectAll = event => {
    let _claims = claim

    if (_claims.data) {
      _claims.data.map(i => {
        i.checked = _claims.indeterminate ? true : !_claims.checked
        return i
      })
    }

    setClaim(_claims)
    changeSelectAllState({ data: _claims })
  }

  const handleItemSelect = ({ claim, index }) => {
    const _claims = {
      ...claim,
      data: claim.data.map((i, _index) => {
        if (_index === index) i.checked = !i.checked
        return i
      })
    }
    setClaim(_claims)
    changeSelectAllState({ data: _claims })
  }

  const changeSelectAllState = ({ data }) => {
    const unchecked = []
    let _claims = data || claim
    let selected_count = 0
    let claimNumbers = []
    if (_claims.data) {
      for (let b of _claims.data) {
        if (b.checked) {
          selected_count++
          claimNumbers.push(b.claim_number)
        } else unchecked.push(b.claim_number)
      }
    }

    let indeterminate = selected_count ? selected_count < _claims.data.length : false
    let checked = selected_count ? selected_count === _claims.data.length : false
    _claims = { ..._claims, indeterminate, checked }
    setClaim(_claims)

    //* Trial
    const uniqueSelectedClaimNumber = [...new Set([ ...selected.claim_numbers, ...claimNumbers ])]
    const _data = uniqueSelectedClaimNumber.filter(i => !unchecked.includes(i))

    // setSelected(state => ({ batch_numbers: batchNumbers }))
    setSelected(state => ({ claim_numbers: _data }))
  }

  const handleOnCancel = () => {
    let _data = claim.data.map(i => ({ ...i, checked: false }))

    setClaim(state => ({ ...state, data: _data, checked: false, indeterminate: undefined }))
    setSelectStatus(false)
    setSelected({ claim_numbers: [] })
  }

  const handleOnSubmitReassign = params => {
    LoadingService.loading(true)
    const { user_id, message } = params
    ClaimsVerifierSupervisorServices.reassignMany({
      selected: selected.claim_numbers,
      id: user_id,
      notes: message,
      prev_user: id
    }).then(() => {
      setSelected({ claim_numbers: [] })
      getClaims(claims)
      LoadingService.loading(false)
    })
  }

  const handleOnSearchAllChange = args => {
    getClaims({ ...claims, ...args })
  }

  const handleOnSearch = args => {
    const _params = {
      limit: claims.limit,
      page: claims.page,
      search: claims.search,
      sort: `${claims.orderBy}|${claims.order ? 'desc' : 'asc'}`,
      filter: claims.filter,
      ...args
    }
    getClaims(_params)
  }

  const handleOnPaginate = args => {
    const params = {
      ...claims,
      ...args
    }
    setClaims(state => ({ ...state, ...args }))
    getClaims(params)
  }

  const handleTableRenderer = type => {
    if (type === 'headcell') {
      return (
        <TableRow>
          {selectStatus ? (
            <TableCell padding='checkbox'>
              <Checkbox
                color='primary'
                disabled={!claim.data.length}
                indeterminate={!!claim.indeterminate}
                checked={!!claim.checked}
                onClick={event => handleSelectAll()}
              />
            </TableCell>
          ) : null}
          {headcell.map(headCell => (
            <TableCell key={headCell.sortField} id={headCell.id}>
              <TableSortLabel
                active={claims.orderBy === headCell.sortField}
                direction={claims.order ? 'asc' : 'desc'}
                onClick={event => handleOnPaginate({ orderBy: headCell.sortField, order: !claims.order })}
              >
                {headCell.name}
              </TableSortLabel>
              { headCell.match === 'date' ? (
                <DatePicker
                  noMargin
                  id={`column-filter-${headCell.id}`}
                  onChange={date => {
                    clearTimeout(debounce)
                    setDebounce(
                      setTimeout(() => {
                        setClaims(p => ({
                          ...p,
                          filter: { ...p.filter, [headCell.sortField]: { value: date.toISOString(), match: headCell.match } }
                        }))
                        handleOnSearch({
                          filter: { ...claims.filter, [headCell.sortField]: { value: date.toISOString(), match: headCell.match } },
                          page: 1
                        })
                      }, 500)
                    )
                  }}
                  value={null}
                />
              ) : (
                <TextField
                  id={`column-filter-${headCell.id}`}
                  TextFieldStyle={{ margin: 0 }}
                  fullWidth
                  onChange={event => {
                    const search = event.target.value
                    clearTimeout(debounce)
                    setDebounce(
                      setTimeout(() => {
                        setClaims(p => ({
                          ...p,
                          filter: { ...p.filter, [headCell.sortField]: { value: search, match: headCell.match } }
                        }))
                        handleOnSearch({
                          filter: { ...claims.filter, [headCell.sortField]: { value: search, match: headCell.match } },
                          page: 1
                        })
                      }, 500)
                    )
                  }}
                />
              )}

            </TableCell>
          ))}

          <TableCell align='center' />
        </TableRow>
      )
    } else if (type === 'body') {
      return claim.data ? claim.data.map((row, index) => (
        <TableRow key={index}>
          {selectStatus ? (
            <TableCell padding='checkbox'>
              <Checkbox checked={row.checked} color='primary' onClick={event => handleItemSelect({ index, claim })} />
            </TableCell>
          ) : null}
          <TableCell id={`claim-number-${index}`}>{row.claim_number}</TableCell>
          <TableCell id={`date-received-${index}`}>{DateFormatter(row.received_date)}</TableCell>
          <TableCell id={`due-date-${index}`}>{DateFormatter(row.due_date)}</TableCell>
          <TableCell id={`facility-name-${index}`}>{row.facility_name}</TableCell>
          <TableCell id={`view-claim-${index}`}>
            <Link to={`/claims/view/${row.claim_number}`}>
              <Typography id={`analyze-claim-${index}`} variant='caption' className='text-link underline'>
                View
              </Typography>
            </Link>
          </TableCell>
        </TableRow>
      )) : null
    }
  }

  return (
    <div className={classes.root}>
      <Paper>
        <React.Fragment>
          <div className={classes['search-container']}>
            <div className={classes['reassign']}>
              <Button
                disabled={selectStatus}
                // style={{ height: '48px', marginBottom: '17px' }}
                variant='contained'
                color='primary'
                className='button-neutral'
                onClick={() => {
                  setSelectStatus(true)
                }}
              >
                Reassign
              </Button>
            </div>
            <div className={classes['search']}>
              <SearchTextComponent
                id='search'
                onChange={e => {
                  const search = e.target.value
                  clearTimeout(debounce)
                  setDebounce(
                    setTimeout(() => {
                      handleOnSearchAllChange({ search, page: 1 })
                      setClaims(state => ({ ...state, search }))
                    }, 500)
                  )
                }}
                placeholder='Search for claim number or facility name'
              />
            </div>
          </div>
          <div className={classes['table-container']}>
            <Table
              TableContainerProps={{ style: { height: '100%', overflow: 'auto', position: 'relative' } }}
              TableProps={{ stickyHeader: true }}
              head={handleTableRenderer('headcell')}
              body={handleTableRenderer('body')}
              count={claims.count}
              page={claims.page}
              limit={claims.limit}
              rowsPerPageOptions={claims.rowsPerPageOptions}
              pagination
              onChange={args => handleOnPaginate(args)}
              cesFooter
            />
          </div>

          <ButtonFooter
            selectStatus={selectStatus}
            claimsCount={selected.claim_numbers.length ? selected.claim_numbers.length : 0}
            handleOnCancel={() => handleOnCancel()}
            handleOnClose={() => props.history.push(`/`)}
            handleOnSubmit={handleOnSubmitReassign}
            _id={id}
          />
        </React.Fragment>
      </Paper>
    </div>
  )
}
