import React from 'react'

// Components
import Table from 'SharedComponents/Table/Table'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TableSortLabel from '@material-ui/core/TableSortLabel'
import TextField from 'SharedComponents/TextField/CustomTextField'
import SearchTextComponent from 'SharedComponents/SearchTextComponent/SearchTextComponent'
import Typography from '@material-ui/core/Typography'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'

// Utilities
import { compare, includesAny } from 'Utilities/ArrayCompare'

// Styles
import classes from './PartnerNetwork.module.scss'

const headCells = [
  { name: 'Code', sortField: 'code', search: true, match: 'start' },
  { name: 'Name', sortField: 'name', search: true, match: 'any' },
  { name: 'Location', sortField: 'location', search: true, match: 'any' },
  { name: '', sortField: 'affiliated_doctors', search: false }
]

const headCellsDoctors = [
  { name: 'Medical Partner ID', sortField: 'id', search: true, match: 'start' },
  { name: 'Doctor Name', sortField: 'full_name', search: true, match: 'any' },
  { name: 'Specialization', sortField: 'specialization', search: true, match: 'any' },
  { name: 'Schedule', sortField: 'schedule', search: true, match: 'any' },
  { name: 'Remarks', sortField: 'remarks', search: false, match: 'any' }
]

const doctorsInitState = {
  show: false,
  facility_id: '',
  data: [],
  filtered: [],
  filter: {},
  count: 0,
  page: 1,
  limit: 10,
  rowsPerPageOptions: [10, 15, 20],
  orderBy: 'full_name',
  order: true
}

const partnersInitState = {
  data: [],
  category: 'all',
  search: '',
  count: 0,
  page: 1,
  limit: 10,
  filter: {},
  rowsPerPageOptions: [10, 15, 20],
  orderBy: 'name',
  order: true
}

export default function PartnerNetwork (props) {
  const { partnerService, doctorService, memberAccountNumber } = props
  // const [partnerId, setPartnerId] = React.useState('')
  // const [partnerName, setPartnerName] = React.useState('')
  // const [partnerLocation, setPartnerLocation] = React.useState('')

  const [partners, setPartners] = React.useState(partnersInitState)

  const [doctors, setDoctors] = React.useState(doctorsInitState)

  const [debounce, setDebounce] = React.useState(null)

  const fetchPartner = args => {
    partnerService({
      member_account_no: memberAccountNumber,
      params: {
        search: args.search,
        sort: `${args.orderBy}|${args.order ? 'asc' : 'desc'}`,
        page: args.page,
        filter: args.filter,
        limit: args.limit
      }
    }).then(res => {
      const { results, count, limit, page } = res.data.data
      setPartners(state => ({
        ...state,
        data: results,
        count,
        page,
        limit
      }))
    })
  }

  const fetchDoctors = args => {
    doctorService({
      member_account_no: memberAccountNumber,
      params: {
        limit: args.limit,
        page: args.page,
        filter: args.filter,
        sort: `${args.orderBy}|${args.order ? 'asc' : 'desc'}`,
        search: args.search
      },
      facility_id: args.facility_id
    }).then(res => {
      const { results, count, limit, page } = res.data.data
      setDoctors(state => ({
        ...state,
        show: true,
        data: results,
        count,
        limit,
        page
        // filtered: data.results,
        // filters
      }))
    })
  }

  const onQueryColumn = (collection, params) => {
    if (collection === 'partners') {
      const _params = {
        ...partners,
        ...params
      }

      fetchPartner(_params)
    } else {
      const _params = {
        ...doctors,
        ...params
      }

      fetchDoctors(_params)
    }
  }

  const onQueryAll = params => {
    const _params = {
      ...partners,
      ...params
    }

    fetchPartner(_params)
  }

  const handleOnPaginate = (collection, params) => {
    if (collection === 'partners') {
      const _params = {
        ...partners,
        ...params
      }

      setPartners(state => ({ ...state, ...params }))
      fetchPartner(_params)
    } else {
      const _params = {
        ...doctors,
        ...params
      }

      setDoctors(state => ({ ...state, ...params }))
      fetchDoctors(_params)
    }
  }

  const handleOnClickAffiliated = params => {
    const _params = {
      ...doctors,
      ...params
    }

    setDoctors(state => ({ ...state, facility_id: params.facility_id }))
    fetchDoctors(_params)
  }

  React.useEffect(() => {
    if (memberAccountNumber) fetchPartner(partners)
  }, [memberAccountNumber])

  return (
    <div className={classes.root}>
      <div className={classes.header}>
        <div className={classes.search}>
          <SearchTextComponent
            id='search-partner'
            //  onChange={event => queryColumn('partners', { search: event.target.value, field: 'any' })}
            onChange={event => {
              const search = event.target.value
              clearTimeout(debounce)
              setDebounce(
                setTimeout(() => {
                  onQueryAll({ search, page: 1 })
                  setPartners(state => ({ ...state, search }))
                }, 500)
              )
            }}
            placeholder='Search for Partner Network Code, Name, and Location'
          />
        </div>
      </div>
      <div className={classes.table}>
        <Table
          TableContainerProps={{ style: { maxHeight: '500px', overflow: 'auto', position: 'relative' } }}
          TableProps={{ stickyHeader: true }}
          head={
            <TableRow>
              {headCells.map(headCell => (
                <TableCell key={headCell.sortField}>
                  <TableSortLabel
                    active={partners.orderBy === headCell.sortField}
                    direction={partners.order ? 'asc' : 'desc'}
                    // onClick={hanleOnPaginate('partners', headCell.sortField)}
                    onClick={() => handleOnPaginate('partners', { orderBy: headCell.sortField, order: !partners.order })
                    }
                  >
                    {headCell.name}
                  </TableSortLabel>
                  {headCell.search && (
                    <TextField
                      id={`pn-${headCell.sortField}-input`}
                      className={`${classes.search}`}
                      onChange={event => {
                        const search = event.target.value
                        clearTimeout(debounce)
                        setDebounce(
                          setTimeout(() => {
                            setPartners(p => ({ ...p, filter: { ...p.filter, [headCell.sortField]: { value: search, match: headCell.match } } }))
                            onQueryColumn('partners', { filter: { ...partners.filter, [headCell.sortField]: { value: search, match: headCell.match } }, page: 1 })
                          }, 500)
                        )
                      }
                      }
                    />
                  )}
                </TableCell>
              ))}

              <TableCell align='center' />
            </TableRow>
          }
          body={partners.data.map((row, index) => (
            <TableRow id={`pn-row-${index}`} key={`claim-${index}`} hover role='checkbox' tabIndex={-1}>
              <TableCell id={`pn-code-${index}`}>
                <div className={classes.cell}>
                  <span>{row.code}</span>
                </div>
              </TableCell>
              <TableCell id={`pn-name-${index}`}>
                <div className={classes.cell}>
                  <span>{row.name}</span>
                </div>
              </TableCell>
              <TableCell id={`pn-location-${index}`}>
                <div className={classes.cell}>
                  <span>{row.location}</span>
                </div>
              </TableCell>

              <TableCell id={`pn-view-link-${index}`}>
                <div className={classes.cell}>
                  <span>
                    <Typography
                      id={`pn-view-link-${index}-control`}
                      variant='caption'
                      className={`text-link `}
                      onClick={() => handleOnClickAffiliated({ facility_id: row.code })}
                      // onChange={() => {}}
                    >
                      View Affiliated Doctors
                    </Typography>
                  </span>
                </div>
              </TableCell>
            </TableRow>
          ))}
          pagination
          count={partners.count}
          page={partners.page}
          limit={partners.limit}
          rowsPerPageOptions={partners.rowsPerPageOptions}
          onChange={args => handleOnPaginate('partners', args)}
        />
      </div>

      <Dialog
        fullWidth
        maxWidth='lg'
        onClose={() => setDoctors(state => ({ ...state, show: false, filter: {}, count: 0, page: 1, limit: 10 }))}
        aria-labelledby='customized-dialog-title'
        open={doctors.show}
        className={classes.doctors}
        PaperProps={{ style: { height: 'auto', display: 'flex' } }}
      >
        <DialogTitle disableTypography className={classes['header-container']}>
          <div className={classes.header}>
            <Typography variant='h6' className={classes.title}>
              Affiliated Doctors
            </Typography>
            <IconButton
              aria-label='close'
              className={classes.button}
              onClick={() => setDoctors(state => ({ ...state, show: false, filter: {}, count: 0, page: 1, limit: 10 }))}
            >
              <CloseIcon />
            </IconButton>
          </div>
        </DialogTitle>

        <DialogContent dividers className={classes.content}>
          <div className={classes.table}>
            <Table
              TableContainerProps={{ style: { maxHeight: '500px', overflow: 'auto', position: 'relative' } }}
              TableProps={{ stickyHeader: true }}
              head={
                <TableRow>
                  {headCellsDoctors.map(cell => (
                    <TableCell key={cell.sortField}>
                      <TableSortLabel
                        active={doctors.orderBy === cell.sortField}
                        direction={doctors.order ? 'asc' : 'desc'}
                        // onClick={handleOnSort('doctors', cell.sortField)}
                        onClick={() => handleOnPaginate('doctors', { orderBy: cell.sortField, order: !doctors.order })}
                      >
                        {cell.name}
                      </TableSortLabel>

                      <TextField
                        readOnly={!cell.search}
                        className={`${classes.search}`}
                        onChange={event => {
                          const search = event.target.value
                          clearTimeout(debounce)
                          setDebounce(
                            setTimeout(() => {
                              setDoctors(p => ({ ...p, filter: { ...p.filter, [cell.sortField]: search } }))
                              onQueryColumn('doctors', { filter: { ...doctors.filter, [cell.sortField]: search }, page: 1 })
                            }, 500)
                          )
                        }
                        }
                        // onChange={event => {}}
                      />

                    </TableCell>
                  ))}

                  <TableCell align='center' />
                </TableRow>
              }
              body={
                doctors.data.length <= 0 ? (
                  <TableRow key={`claim-no-record`} hover role='checkbox' tabIndex={-1}>
                    <TableCell id={`no-record`} colSpan={5}>
                      <div className={`${classes.cell} ${classes.center}`}>
                        <span>No Records</span>
                      </div>
                    </TableCell>
                  </TableRow>
                ) : (
                  doctors.data.map((row, index) => (
                    <TableRow key={`claim-${index}`} hover role='checkbox' tabIndex={-1}>
                      <TableCell id={`doctor-id-${index}`}>
                        <div className={classes.cell}>
                          <span>{row.id}</span>
                        </div>
                      </TableCell>
                      <TableCell id={`doctor-name-${index}`}>
                        <div className={classes.cell}>
                          <span>{row.full_name}</span>
                        </div>
                      </TableCell>
                      <TableCell id={`doctor-specialization-${index}`}>
                        <div className={classes.cell}>
                          <span>
                            {row.specialization}
                          </span>
                        </div>
                      </TableCell>
                      <TableCell id={`doctor-schedule-${index}`}>
                        <div className={classes.cell}>
                          <span>{row.schedule}</span>
                        </div>
                      </TableCell>
                      <TableCell id={`doctor-remarks-${index}`}>
                        <div className={classes.cell}>
                          <span>{row.remarks}</span>
                        </div>
                      </TableCell>
                    </TableRow>
                  ))
                )
              }
              pagination
              count={doctors.count}
              page={doctors.page}
              limit={doctors.limit}
              rowsPerPageOptions={doctors.rowsPerPageOptions}
              onChange={args => handleOnPaginate('doctors', args)}
            />
          </div>
        </DialogContent>
      </Dialog>
    </div>
  )
}
