import React from 'react'

// Material Components
import Button from '@material-ui/core/Button'
import Paper from '@material-ui/core/Paper'
import Table from 'SharedComponents/Table/Table'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'
import GetAppIcon from '@material-ui/icons/GetApp'

// Shared Components
import DatePicker from 'SharedComponents/DatePicker2/DatePicker2'
import TextField from 'SharedComponents/TextField/CustomTextField'
import AutoComplete from 'SharedComponents/AutoComplete/AutoComplete'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import ClaimsServices from 'Services/Api/Claims/ClaimsServices'
import ArrayToCSV from 'Services/ArrayToCSV'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'

// Utility
import { DateFormatter } from 'Utilities/TextFormatter'
import { SafeAccess } from 'Utilities/SafeAccess'

// Constants
import ClaimTypes from 'Pages/Protected/Claims/Constant/ClaimTypes'

// Styles
import classes from './ReimbursementAgingReport.module.scss'

// Validator
import DateRangeValidator from '../TransmittalReport/validator/DateRangeValidator'

const completeColumns = [
  { label: '', data: 'item', align: 'left' },
  { label: 'Number of Reimbursement Claims', type: 'number', data: 'reimbursement_count', align: 'center' },
  {
    label: 'Number of Percentage of Reimbursement Claims',
    type: 'number',
    data: 'reimbursement_percent',
    align: 'center',
    suffix: '%'
  }
]

/**
 * Claims Utilization Report
 *
 * @export
 *
 * @param {*} props
 *   dataColumns - default []; if defined, will be the allowable data columns that
 *     will be shown on the claims utilization report
 *   hideReport - default false; if defined, will be used to hide the entire report
 *   hideheader - default false; if defined, will be used to hide the header section
 *   dateRange - default current date; if defined, will be used to define the date range in-effect
 *
 * @returns
 */
export default props => {
  const { dataColumns = [], hideReport, hideHeader, dateRange } = props

  const [claims, setClaims] = React.useState({
    data: [
      { item: 'Reimbursement within TAT', reimbursement_count: 0, reimbursement_percent: 0 },
      { item: 'Reimbusement above TAT', reimbursement_count: 0, reimbursement_percent: 0 }
    ],
    range: {
      from: SafeAccess(dateRange, 'from', new Date().toISOString()),
      to: SafeAccess(dateRange, 'to', new Date().toISOString()),
      field: 'created_at'
    },
    filter: { reimbursement_category: 'ALL' }
  })

  const [suggestion, setSuggestion] = React.useState({
    reimbursement_category: []
  })

  const [dateRangeError, setDateRangeError] = React.useState({
    date_from: '',
    date_to: ''
  })

  const [debounce, setDebounce] = React.useState({
    reimbursement_category: null
  })

  const [columns, setColumns] = React.useState(completeColumns)

  React.useEffect(() => {
    PageTitleService('Reimbursement Aging Report')
    BreadcrumbsService.setSelectedItems(['agingReimbursement'])
    fetchRecords()
  }, [])

  const handleOnFilter = args => {
    const { field, value, match } = args
    setClaims(state => ({ ...state, filter: { ...state.filter, [field]: { value, match } } }))
  }

  const fetchRecords = () => {
    LoadingService.loading(true)
    ClaimsServices.reimbursmentAgingReport({
      range: claims.range,
      filter: claims.filter
    }).then(res => {
      const { data } = res.data
      const { results = [] } = data
      setClaims(state => ({
        ...state,
        data: results
      }))
      LoadingService.loading(false)
    })
  }

  const validate = () => {
    const { error } = DateRangeValidator({
      date_from: claims.range.from,
      date_to: claims.range.to
    })

    setDateRangeError({ ...error })
    return !!error
  }

  const fetchCSV = () => {
    LoadingService.loading(true)
    ClaimsServices.reimbursmentAgingReport({
      range: claims.range,
      filter: claims.filter
    }).then(res => {
      const { data } = res.data
      const { results = [] } = data
      const rows = results.map(claim => {
        return columns.map(col => formatData({ type: col.type, source: claim, field: col.data }))
      })

      const header = columns.map(c => c.label)

      ArrayToCSV({
        statics: [
          [
            'Reimbursement Aging Report',
            '\r\n',
            '\r\n',
            `Reimbursement Category: ${claims.filter.reimbursement_category}`,
            '\r\n',
            `Date Range: ${DateFormatter(claims.range.from)} - ${DateFormatter(claims.range.to)}`,
            '\r\n',
            '\r\n'
          ]
        ],
        fileName: 'Reimbursement Aging Report',
        header,
        rows
      })
      LoadingService.loading(false)
    })
  }

  // React.useEffect(() => {
  //   fetchRecords()
  //   // claim.range is triggered manually via Filter button
  // }, [props.refresh])

  React.useEffect(() => {
    setClaims(state => ({
      ...state,
      range: {
        ...state.range,
        to: dateRange ? dateRange.to : new Date().toISOString(),
        from: dateRange ? dateRange.from : new Date().toISOString()
      }
    }))
  }, [dateRange])

  React.useEffect(() => {
    // Goal: Adjust columns to show based on data columns passed as props

    let filtered = null
    if (!dataColumns || dataColumns.length === 0) {
      filtered = completeColumns
    } else {
      filtered = completeColumns.filter(entry => dataColumns.indexOf(entry.data) > -1)
    }
    setColumns(filtered)
  }, [dataColumns])

  const formatData = ({ type = null, source, field }) => {
    let output = ''
    if (type === 'date') {
      output = DateFormatter(source[field])
    } else if (type === 'array') {
      const [root, property] = field.split('.')
      output = source[root] ? source[root].map(f => f[property]).join(`${'\r\n'}${'\r\n'}`) : ''
    } else if (type === 'number') {
      output = source[field]
    } else {
      output = source[field] ? source[field] : ''
    }

    return output !== '' && output !== 'undefined' && output !== `${'\r\n'}${'\r\n'}` ? output : 'N/A'
  }

  const handleOnChange = ({ value, category, field, isSuggestionClick }) => {
    setClaims(state => ({ ...state, filter: { ...state.filter, [category]: value } }))
    if (!isSuggestionClick) {
      clearTimeout(debounce[category])
      setDebounce(state => ({
        ...state,
        [category]: setTimeout(() => {
          // use constant (this also used in Reimbursement Claim page)
          const const_types = ClaimTypes.Reimbursement.subType

          if (value) {
            const _value = value.toLowerCase()
            const filtered = const_types
              .map(i => i.text)
              .filter(j => {
                const lower_case = j.toLowerCase()

                return lower_case.includes(_value)
              })

            setSuggestion(state => ({ ...state, [category]: ['ALL', ...filtered] }))
          } else {
            setSuggestion(state => ({ ...state, [category]: ['ALL', ...const_types.map(i => i.text)] }))
          }
        }, 500)
      }))
    }
  }

  return (
    <Paper className={classes.root} style={{ display: hideReport === 'true' ? 'none' : 'block' }}>
      <div className={classes.header} style={{ display: hideHeader === 'true' ? 'none' : 'block' }}>
        <div className={classes.content}>
          <div className={classes['field-filter']}>
            <div className={classes.input}>
              <Typography variant='caption'>Reimbursment Category</Typography>
              <AutoComplete
                placeholder='Search by reimbursement Category'
                id='reimbursment-category'
                value={claims.filter.reimbursement_category}
                suggestions={suggestion.reimbursement_category}
                onChange={(value, isSuggestionClick) =>
                  handleOnChange({
                    value,
                    isSuggestionClick,
                    category: 'reimbursement_category',
                    field: 'name'
                  })
                }
              />
            </div>
          </div>
        </div>
        <Typography variant='h6'>Date Range</Typography>
        <div className={classes.content}>
          <div className={classes.filter}>
            <div className={classes.input}>
              <p>From: </p>
              <DatePicker
                id='filter-date-from'
                value={claims.range.from}
                onChange={date =>
                  setClaims(state => ({
                    ...state,
                    range: {
                      ...state.range,
                      from: date.toISOString()
                    }
                  }))
                }
                disableFuture
                error={dateRangeError.date_from}
              />
            </div>
            <div className={classes.input}>
              <p>To: </p>
              <DatePicker
                id='filter-date-to'
                value={claims.range.to}
                onChange={date =>
                  setClaims(state => ({
                    ...state,
                    range: {
                      ...state.range,
                      to: date.toISOString()
                    }
                  }))
                }
                disableFuture
                error={dateRangeError.date_to}
              />
            </div>
            <Button
              className={`button-positive ${classes.submit}`}
              onClick={() => !validate() && fetchRecords()}
              variant='contained'
              color='primary'
            >
              Filter
            </Button>
          </div>
          <div className={classes.action}>
            <Button id='generate-report' onClick={() => fetchCSV()}>
              <GetAppIcon fontSize='small' />
              <Typography variant='caption' className='bold text-link'>
                Generate Report
              </Typography>
            </Button>
          </div>
        </div>
      </div>
      <div className={classes.body}>
        <div className={classes.table}>
          <Table
            TableContainerProps={{ style: { maxHeight: '60vh', overflow: 'auto', position: 'relative' } }}
            TableProps={{ stickyHeader: true }}
            head={
              <TableRow>
                {columns.map((headCell, index) => (
                  <TableCell key={`table-header-${index}`} align={headCell.align}>
                    {headCell.label}
                    {headCell.search && (
                      <div className={classes.input}>
                        {headCell.type && headCell.type === 'date' ? (
                          <DatePicker
                            noMargin
                            id={`${headCell.data}`}
                            value={null}
                            onChange={date =>
                              handleOnFilter({
                                field: headCell.data,
                                value: date.toISOString(),
                                match: headCell.match
                              })
                            }
                          />
                        ) : (
                          <TextField
                            noMargin
                            onChange={event =>
                              handleOnFilter({
                                field: headCell.data,
                                value: event.target.value,
                                match: headCell.match
                              })
                            }
                          />
                        )}
                      </div>
                    )}
                  </TableCell>
                ))}
              </TableRow>
            }
            body={
              claims.data &&
              claims.data.map((claim, claimIndex) => (
                <TableRow className={classes['table-row']} key={`table-item-${claimIndex}`}>
                  {columns.map((col, colIndex) => (
                    <TableCell
                      className={col.remarks ? classes.remarks : classes['table-cell']}
                      key={`table-cell-${claimIndex}-${colIndex}`}
                      align={col.align}
                    >
                      {col.suffix
                        ? `${formatData({ type: col.type, source: claim, field: col.data })}${col.suffix}`
                        : formatData({ type: col.type, source: claim, field: col.data })}
                    </TableCell>
                  ))}
                </TableRow>
              ))
            }
            pagination={false}
          />
        </div>
      </div>
    </Paper>
  )
}
