import React from 'react'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'

// Components
import DashboardTile from 'SharedComponents/DashboardTileComponent/DashboardTileComponent'
import ViewList from './../ViewList2/ViewList'
import LoadingIndicator from 'SharedComponents/LoadingIndicator/LoadingIndicator'

const columns = [
  { id: 'encoder-name', name: 'Encoder Name', sortField: 'name', match: 'any' },
  { id: 'total-bundle', name: 'Total Bundles', sortField: 'total_bundle', match: 'any' },
  { id: 'total-batch', name: 'Total Batches', sortField: 'total_batch', match: 'any' }
]

const BatchAndEncoderTile = props => {
  const { services, sizes = { sm: 12 } } = props
  const [items, setItems] = React.useState({ count: 0, data: [] })
  const [fetching, setFetching] = React.useState(false)
  const [fetchingViewList, setFetchingViewList] = React.useState(false)

  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    data: [],
    filter: {},
    orderBy: 'name',
    order: true
  }

  const [viewList, setViewList] = React.useState(initialValue)

  React.useEffect(() => {
    setFetching(true)
    Promise.all([
      services.getBatchEncoderWork({ category: 'created', limit: 10, sort: 'name|asc' }),
      services.getBatchEncoderWorkCount()
    ])
      .then(values => {
        const [batchEncoders, count] = values
        setItems({ data: batchEncoders.data.data.results, count: count.data.data })
      })
      .finally(values => {
        setFetching(false)
      })
  }, [])

  const handleOnShowViewList = () => {
    setViewList({ ...initialValue, show: true, title: 'Bundle and Batch Encoder' })
    setFetchingViewList(true)
    services
      .getBatchEncoderWork({ limit: 10, sort: 'name|desc' })
      .then(({ data: res }) => {
        const { results, count, limit, page } = res.data
        setViewList(state => ({ ...state, data: results, count, limit, page }))
      })
      .finally(values => {
        setFetchingViewList(false)
      })
  }

  const handleOnViewListChange = params => {
    const _params = {
      limit: viewList.limit,
      page: viewList.page,
      search: viewList.search,
      sort: `${viewList.orderBy}|${viewList.order ? 'desc' : 'asc'}`,
      filter: viewList.filter,
      ...params
    }

    setFetchingViewList(true)
    services
      .getBatchEncoderWork(_params)
      .then(({ data: res }) => {
        const { results, count, limit, page } = res.data
        setViewList(state => ({ ...state, data: results, count, limit, page }))
      })
      .finally(values => {
        setFetchingViewList(false)
      })
  }

  return (
    <React.Fragment>
      <DashboardTile
        count={{ color: 3, value: items.count, id: 'bundle-and-batch-encoder-count' }}
        sizes={sizes}
        title='Bundle and Batch Encoder'
        id='bundle-and-batch-encoder'
        isFilterShown={false}
        viewList={
          <Typography
            variant='caption'
            className='bold text-link'
            onClick={handleOnShowViewList}
            id='bundle-and-batch-encoder-view-list'
          >
            View List
          </Typography>
        }
      >
        <LoadingIndicator loading={fetching}>
          <Table stickyHeader size='small'>
            <TableHead>
              <TableRow>
                {columns.map(item => (
                  <TableCell key={item.id} id={item.id}>
                    {item.name}
                  </TableCell>
                ))}

                <TableCell />
              </TableRow>
            </TableHead>
            <TableBody>
              {items.data.map((item, index) => (
                <TableRow key={item.user_id}>
                  <TableCell id={`batch-encoder-name-${index}`}>{item.name}</TableCell>
                  <TableCell id={`total-bundle-${index}`}>{item.total_bundle}</TableCell>
                  <TableCell id={`total-batch-${index}`}>{item.total_batch}</TableCell>
                  <TableCell id={`view-work-${index}`}>
                    <Link to={`/batch-encoder-work/${item.user_id}`}>
                      <Typography variant='caption' className='text-link underline' id={`update-bundle-${index}`}>
                        View
                      </Typography>
                    </Link>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </LoadingIndicator>
      </DashboardTile>
      <ViewList
        id='bundle-and-batch-encoder'
        columns={columns}
        searchTextPlaceHolder='Search for Encoder Name, Total Bundles or Total Batches'
        viewList={viewList}
        setViewList={setViewList}
        onViewListChange={handleOnViewListChange}
        fetching={fetchingViewList}
        body={_data =>
          _data.map((item, index) => (
            <TableRow key={item.user_id}>
              <TableCell id={`batch-encoder-name-${index}`}>{item.name}</TableCell>
              <TableCell id={`total-bundle-${index}`}>{item.total_bundle}</TableCell>
              <TableCell id={`total-batch-${index}`}>{item.total_batch}</TableCell>
              <TableCell id={`view-work-${index}`}>
                <Link to={`/batch-encoder-work/${item.user_id}`}>
                  <Typography variant='caption' className='text-link underline' id={`update-bundle-${index}`}>
                    View
                  </Typography>
                </Link>
              </TableCell>
            </TableRow>
          ))
        }
      />
    </React.Fragment>
  )
}

BatchAndEncoderTile.displayName = 'DashboardTileComponent'

export default BatchAndEncoderTile
