import React from 'react'

// Components
import Alert from 'SharedComponents/Alert/Alert'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import Typography from '@material-ui/core/Typography'
import TextField from 'SharedComponents/TextField/CustomTextField'
import DropDown from 'SharedComponents/DropDown/DropDown'
import Button from '@material-ui/core/Button'

// Styles
import classes from './ClaimFooter.module.scss'

// Validations
import EscalateValidator from './EscalateValidator'
import ReconValidator from './ReconValidator'
import ReturnValidator from './ReturnValidator'

// lodash
import { sortBy, uniqBy } from 'lodash'

// Utilities
import { SafeAccess } from 'Utilities/SafeAccess'
import { removeDuplicates } from 'Utilities/ArrayCompare'

// Store
import { useSelector, useDispatch } from 'react-redux'
import { ClaimsAnalysis as actions } from 'Pages/Protected/Claims/Store/actions'
const {
  resetEscalation,
  setEscalationMessage,
  setEscalationAssignee,
  resetReassign,
  setReassignMessage,
  setReassignAssignee,
  resetReturn,
  setReturnNote
} = actions

export default function ClaimFooter (props) {
  // Events
  const {
    doValidate,
    onReject,
    onViewAnalysisSheet,
    onViewLetterOfAdvice,
    onReassign,
    onSave,
    onEscalate,
    onApprove,
    onCancel,
    isReimbursement,
    onRemove,
    onViewClaimNotice,
    onReturn
  } = props
  // Variables
  const { disabled, permissions, reimbursement_info = [], reimbursement_info_analyst = [] } = props
  // Services
  const { grandTotal, claimType, userManagementService = () => {} } = props

  const dispatch = useDispatch()
  const discrepancy = useSelector(({ ClaimsAnalysis: state }) => state.discrepancy)
  const escalations = useSelector(({ ClaimsAnalysis: state }) => state.escalations)
  const reassign = useSelector(({ ClaimsAnalysis: state }) => state.reassign)
  const c_return = useSelector(({ ClaimsAnalysis: state }) => state.return)
  const rejectStatus = useSelector(({ ClaimsAnalysis: state }) => state.rejectStatus)
  const claim = useSelector(({ ClaimsAnalysis: state }) => state.claim)

  const [escalateModal, setEscalateModal] = React.useState({
    dialog: false,
    confirm: false
  })
  const [reassignModal, setReassignModal] = React.useState({
    dialog: false,
    confirm: false
  })
  const [returnModal, setReturnModal] = React.useState({
    dialog: false,
    confirm: false
  })
  const [rejectModal, setRejectModal] = React.useState(false)
  const [discrepancyModal, setDiscrepancyModal] = React.useState(false)
  const [approveModal, setApproveModal] = React.useState({ message: '', show: false })
  const [saveModal, setSaveModal] = React.useState(false)
  const [cancelModal, setCancelModal] = React.useState(false)
  const [removeModal, setRemoveModal] = React.useState(false)
  const [validateModal, setValidateModal] = React.useState({ show: false, focus: '' })

  const [dropDownUsers, setDropDownUsers] = React.useState([])
  const [hasNotCovered, setHasNotCovered] = React.useState(false)

  const [escalationErrors, setEscalationErrors] = React.useState({
    message: '',
    assignee: ''
  })

  const [reassignErrors, setReassignErrors] = React.useState({
    message: '',
    assignee: ''
  })

  const [returnError, setReturnErrors] = React.useState({
    message: '',
    assignee: ''
  })
  // Init functions
  const initDropdownUsers = async ({ roles }) => {
    userManagementService.getUsersWithRole(roles[0]).then(({ data }) => {
      const users = data.map(user => ({ text: `${user.first_name} ${user.last_name}`, value: user._id }))
      setDropDownUsers(sortBy(users, ['text']))
    })
  }

  const hasAmountNotCovered = ({
    dental_procedures = [],
    procedures = [],
    hospital_items = [],
    professional_fees = []
  }) => {
    dental_procedures = dental_procedures || []
    procedures = procedures || []
    hospital_items = hospital_items || []
    professional_fees = professional_fees || []

    for (const item of [...dental_procedures, ...procedures, ...hospital_items, ...professional_fees]) {
      if (item.reason || item.amount_not_covered) return true
    }
    return false
  }

  // Reactive
  React.useEffect(() => {
    const notCovered = hasAmountNotCovered(claim)
    setHasNotCovered(notCovered)
  }, [claim])

  // Validators
  const doValidateEscalation = () => {
    const { error } = EscalateValidator({
      message: escalations.message,
      assignee: escalations.assignee.id
    })

    setEscalationErrors({ ...error })
    return !!error
  }

  const doValidateReturn = () => {
    const { error } = ReturnValidator({
      message: c_return.note
    })

    setReturnErrors({ ...error })
    return !!error
  }

  const doValidateReassign = () => {
    const { error } = ReconValidator({
      message: reassign.message,
      assignee: reassign.assignee.id
    })

    setReassignErrors({ ...error })
    return !!error
  }

  // UI Functions
  const handleEscalateDialog = () => {
    dispatch(resetEscalation())
    initDropdownUsers({ roles: permissions.escalate.options.roles })
    setEscalationErrors({ message: '', assignee: '' })
    dispatch(setEscalationMessage(''))
    setEscalateModal(state => ({ ...state, dialog: true }))
  }

  const handleReassignDialog = () => {
    dispatch(resetReassign())
    initDropdownUsers({ roles: permissions.reassign.options.roles })
    setReassignErrors({ message: '', assignee: '' })
    dispatch(setReassignMessage(''))
    setReassignModal(state => ({ ...state, dialog: true }))
  }

  const handleOnShowReturnDialog = () => {
    dispatch(resetReturn())
    setReassignErrors({ message: '' })
    dispatch(setReturnNote(''))
    setReturnModal(state => ({ ...state, dialog: true }))
  }

  const handleOnShowApprovalMatrix = ({ skip = false }) => {
    const { AuthorityMatrix } = permissions.approve.options

    if (AuthorityMatrix) {
      const { other_types } = AuthorityMatrix

      const selected_amount = AuthorityMatrix[claimType] === undefined ? other_types : AuthorityMatrix[claimType]

      if (grandTotal > selected_amount) {
        setApproveModal({
          show: true,
          message:
            'The billed amount to be approved is higher than the threshold. Are you sure you want to approve this claim?'
        })
      } else {
        if (!skip) {
          setApproveModal({ show: true, message: 'Are you sure you want to approve the claim?' })
        } else {
          onApprove({ goBack: false })
        }
      }
    } else {
      onApprove({ goBack: false })
    }

    return true
  }

  const handleOnApproval = () => {
    const { diagnosis, procedure, hospital_item, professional_fee, ...others } = discrepancy

    if (
      Object.keys(diagnosis).length > 0 ||
      Object.keys(procedure).length > 0 ||
      Object.keys(hospital_item).length > 0 ||
      Object.keys(professional_fee).length > 0 ||
      Object.entries(others).find(([key, value]) => value.length > 0)
    ) {
      setDiscrepancyModal(true)
      return false
    }

    handleOnShowApprovalMatrix({ skip: false })
    return true
  }

  const doValidateMiddleware = () => {
    const res = doValidate()
    if (res) {
      setValidateModal({ show: true, focus: res })
      return true
    }

    return false
  }

  const rmLogicReturn = infos => {
    // no reimbursement_info
    if (infos.length < 0) return false
    // multiple reimbursement_info
    if (infos.length > 1) return true

    // check if reimbursement_info [0] is filler
    if (infos.length === 1) {
      const info = infos[0]
      return !!(info.date_released && info.date_return)
    }
  }

  return (
    <div className={classes.root}>
      <div className={classes.left}>
        {SafeAccess(permissions.return_advice, ['visible']) && rmLogicReturn(reimbursement_info) && (
          <Button
            id='btn-return-advice'
            variant='contained'
            color='secondary'
            className='button-positive'
            onClick={() => onViewClaimNotice({})}
            disabled={disabled && permissions.analysis_sheet.enabled}
          >
            Return Advice
          </Button>
        )}
      </div>
      <div className={classes.center} />
      <div className={classes.right}>
        {permissions.visible && (
          <React.Fragment>
            {SafeAccess(permissions.analysis_sheet, ['visible']) && (
              <Button
                id='btn-view-analysis'
                variant='contained'
                color='secondary'
                className='button-positive'
                // onClick={() => !doValidateMiddleware() && onViewAnalysisSheet({ goBack: false })}
                onClick={() => onViewAnalysisSheet({ goBack: false })}
                disabled={disabled && permissions.analysis_sheet.enabled}
              >
                View Analysis Sheet
              </Button>
            )}
            {SafeAccess(permissions.letter_of_advice, ['visible']) && (
              <Button
                id='btn-view-loa'
                variant='contained'
                color='secondary'
                className='button-positive'
                // onClick={() => !doValidateMiddleware() && onViewLetterOfAdvice({ goBack: false })}
                onClick={() => onViewLetterOfAdvice({ goBack: false })}
                disabled={(disabled && permissions.letter_of_advice.enabled) || !hasNotCovered}
              >
                View Letter of Advice
              </Button>
            )}
            {SafeAccess(permissions.escalate, ['visible']) && (
              <Button
                id='btn-escalate'
                variant='contained'
                color='secondary'
                className='button-positive'
                onClick={() => !doValidateMiddleware() && handleEscalateDialog()}
                disabled={disabled && permissions.escalate.enabled}
              >
                Escalate
              </Button>
            )}
            {SafeAccess(permissions.reject, ['visible']) && (
              <Button
                id='btn-reject'
                variant='contained'
                color='secondary'
                onClick={() =>
                  !doValidateMiddleware() && (isReimbursement ? onReject({ goBack: false }) : setRejectModal(true))
                }
                disabled={rejectStatus && !isReimbursement}
              >
                Reject
              </Button>
            )}
            {SafeAccess(permissions.return, ['visible']) && (
              <Button
                id='btn-return'
                variant='contained'
                color='primary'
                className='button-positive'
                onClick={() => handleOnShowReturnDialog()}
                disabled={disabled && permissions.return.enabled}
              >
                Return
              </Button>
            )}
            {SafeAccess(permissions.approve, ['visible']) && (
              <Button
                id='approve'
                variant='contained'
                color='primary'
                className='button-positive'
                onClick={() => !doValidateMiddleware() && handleOnApproval()}
                disabled={disabled && permissions.approve.enabled}
              >
                Approve
              </Button>
            )}
            {SafeAccess(permissions.save, ['visible']) && (
              <Button
                id='save-for-now'
                variant='contained'
                color='primary'
                className='button-positive'
                onClick={() => !doValidateMiddleware() && setSaveModal(true)}
                disabled={disabled && permissions.save.enabled}
              >
                Save for now
              </Button>
            )}
            {SafeAccess(permissions.remove, ['visible']) && (
              <Button
                id='remove'
                variant='contained'
                color='secondary'
                onClick={() => setRemoveModal(true)}
                disabled={disabled && permissions.remove.enabled}
              >
                Remove
              </Button>
            )}
            {SafeAccess(permissions, ['close']) ? (
              <Button id='cancel' variant='contained' color='secondary' onClick={() => onCancel({ goBack: true })}>
                Close
              </Button>
            ) : (
              <Button id='cancel' variant='contained' color='secondary' onClick={() => setCancelModal(true)}>
                Cancel
              </Button>
            )}
            {SafeAccess(permissions.reassign, ['visible']) && (
              <Button
                id='btn-reassign'
                variant='contained'
                color='secondary'
                className='button-positive'
                onClick={() => handleReassignDialog()}
                disabled={disabled && permissions.reassign.enabled}
              >
                Reassign
              </Button>
            )}
          </React.Fragment>
        )}
      </div>
      <div className={classes.modals}>
        {SafeAccess(permissions.escalate, ['enabled']) && (
          <React.Fragment>
            <Dialog
              fullWidth
              maxWidth='md'
              onClose={() => setEscalateModal(state => ({ ...state, dialog: false }))}
              aria-labelledby='customized-dialog-title'
              open={escalateModal.dialog}
              className={classes.escalate}
              PaperProps={{ style: { height: 'auto', display: 'flex' } }}
            >
              <DialogTitle disableTypography className={classes['header-container']}>
                <div className={classes.header}>
                  <Typography variant='h6' className={classes.title}>
                    Reason For Escalation
                  </Typography>
                  <IconButton
                    aria-label='close'
                    className={classes.button}
                    onClick={() => setEscalateModal(state => ({ ...state, dialog: false }))}
                  >
                    <CloseIcon />
                  </IconButton>
                </div>
              </DialogTitle>

              <DialogContent dividers className={classes.content}>
                <div className={classes.input}>
                  <TextField
                    className={classes.comment}
                    multiline
                    rows={5}
                    id='escalation-text'
                    value={escalations.message}
                    autoFocus
                    fullWidth
                    placeholder='Compose a Message'
                    onChange={event => dispatch(setEscalationMessage(event.target.value))}
                    error={escalationErrors.message}
                  />
                </div>
                <div className={classes.footer}>
                  <div className={classes.assign}>
                    <Typography variant='subtitle2' className={classes.to}>
                      {dropDownUsers && dropDownUsers.length > 0 ? 'Assign To' : 'No Users'}
                    </Typography>
                    <DropDown
                      id='escalate-to'
                      items={dropDownUsers}
                      value={escalations.assignee.id}
                      onChange={event =>
                        dispatch(
                          setEscalationAssignee({
                            id: event.target.value,
                            name: event.target.value
                          })
                        )
                      }
                      error={escalationErrors.assignee}
                      disableComponents={!!(dropDownUsers && dropDownUsers.length === 0)}
                    />
                  </div>

                  <div className={classes.action}>
                    <Button
                      id='escalate-cancel'
                      onClick={() => setEscalateModal(state => ({ ...state, dialog: false }))}
                      variant='contained'
                      color='primary'
                      className='button-negative'
                    >
                      Cancel
                    </Button>
                    <Button
                      onClick={() =>
                        !doValidateEscalation() && setEscalateModal(state => ({ ...state, confirm: true }))
                      }
                      id='escalate-submit'
                      variant='contained'
                      color='primary'
                      className='button-positive'
                    >
                      Submit
                    </Button>
                  </div>
                </div>
              </DialogContent>
            </Dialog>
            <Alert
              open={escalateModal.confirm}
              message='Are you sure you want to escalate claim?'
              cancelLabel='No'
              okLabel='Yes'
              onCancel={() => setEscalateModal(state => ({ ...state, confirm: false }))}
              onOk={() => {
                setEscalateModal(state => ({ ...state, dialog: false, confirm: false }))
                onEscalate({ goBack: true })
              }}
            />
          </React.Fragment>
        )}

        {SafeAccess(permissions.reassign, ['enabled']) && (
          <React.Fragment>
            <Dialog
              fullWidth
              maxWidth='md'
              onClose={() => setReassignModal(state => ({ ...state, dialog: false }))}
              aria-labelledby='customized-dialog-title'
              open={reassignModal.dialog}
              className={classes.reassign}
              PaperProps={{ style: { height: 'auto', display: 'flex' } }}
            >
              <DialogTitle disableTypography className={classes['header-container']}>
                <div className={classes.header}>
                  <Typography variant='h6' className={classes.title}>
                    Reassign
                  </Typography>
                  <IconButton
                    aria-label='close'
                    className={classes.button}
                    onClick={() => setReassignModal(state => ({ ...state, dialog: false }))}
                  >
                    <CloseIcon />
                  </IconButton>
                </div>
              </DialogTitle>

              <DialogContent dividers className={classes.content}>
                <div className={classes.input}>
                  <TextField
                    className={classes.comment}
                    multiline
                    rows={5}
                    id=''
                    value={reassign.message}
                    autoFocus
                    fullWidth
                    placeholder='Compose a Message'
                    onChange={event => dispatch(setReassignMessage(event.target.value))}
                    error={reassignErrors.message}
                  />
                </div>
                <div className={classes.footer}>
                  <div className={classes.assign}>
                    <Typography variant='subtitle2' className={classes.to}>
                      Assign To:
                    </Typography>
                    <DropDown
                      id='reassign-to'
                      items={dropDownUsers}
                      value={reassign.assignee.id}
                      onChange={event =>
                        dispatch(
                          setReassignAssignee({
                            id: event.target.value,
                            name: event.target.value
                          })
                        )
                      }
                      error={reassignErrors.assignee}
                    />
                  </div>

                  <div className={classes.action}>
                    <Button
                      onClick={() => setReassignModal(state => ({ ...state, dialog: false }))}
                      variant='contained'
                      color='primary'
                      className='button-negative'
                    >
                      Cancel
                    </Button>
                    <Button
                      onClick={() => !doValidateReassign() && setReassignModal(state => ({ ...state, confirm: true }))}
                      variant='contained'
                      color='primary'
                      className='button-positive'
                    >
                      Submit
                    </Button>
                  </div>
                </div>
              </DialogContent>
            </Dialog>
            <Alert
              open={reassignModal.confirm}
              message={
                permissions.reassign && permissions.reassign.message
                  ? permissions.reassign.message
                  : 'Are you sure you want to re-assign claim?'
              }
              cancelLabel='No'
              okLabel='Yes'
              onCancel={() => setReassignModal(state => ({ ...state, confirm: false }))}
              onOk={() => {
                setReassignModal(state => ({ ...state, dialog: false, confirm: false }))
                onReassign({ goBack: true })
              }}
            />
          </React.Fragment>
        )}

        {SafeAccess(permissions.return, ['enabled']) && (
          <React.Fragment>
            <Dialog
              fullWidth
              maxWidth='md'
              onClose={() => setReturnModal(state => ({ ...state, dialog: false }))}
              aria-labelledby='customized-dialog-title'
              open={returnModal.dialog}
              className={classes.return}
              PaperProps={{ style: { height: 'auto', display: 'flex' } }}
            >
              <DialogTitle disableTypography className={classes['header-container']}>
                <div className={classes.header}>
                  <Typography variant='h6' className={classes.title}>
                    Notes
                  </Typography>
                  <IconButton
                    aria-label='close'
                    className={classes.button}
                    onClick={() => setReturnModal(state => ({ ...state, dialog: false }))}
                  >
                    <CloseIcon />
                  </IconButton>
                </div>
              </DialogTitle>

              <DialogContent dividers className={classes.content}>
                <div className={classes.input}>
                  <TextField
                    className={classes.comment}
                    multiline
                    rows={5}
                    id=''
                    value={c_return.note}
                    autoFocus
                    fullWidth
                    placeholder='Compose a Message'
                    onChange={event => dispatch(setReturnNote(event.target.value))}
                    error={returnError.message}
                  />
                </div>
                <div className={classes.return}>
                  <div className={classes.action}>
                    <Button
                      onClick={() => setReturnModal(state => ({ ...state, dialog: false }))}
                      variant='contained'
                      color='primary'
                      className='button-negative'
                    >
                      Cancel
                    </Button>
                    <Button
                      onClick={() => !doValidateReturn() && onReturn({ goBack: true })}
                      variant='contained'
                      color='primary'
                      className='button-positive'
                    >
                      Ok
                    </Button>
                  </div>
                </div>
              </DialogContent>
            </Dialog>
          </React.Fragment>
        )}

        {SafeAccess(permissions.reject, ['enabled']) && (
          <Alert
            open={rejectModal}
            message='Are you sure you want to reject this claim?'
            cancelLabel='No'
            okLabel='Yes'
            onCancel={() => setRejectModal(false)}
            onOk={() => {
              setRejectModal(false)
              onReject({ goBack: false })
            }}
          />
        )}

        {SafeAccess(permissions.approve, ['enabled']) && (
          <Alert
            open={discrepancyModal}
            message={'There is a highlighted claim item for checking. Are you sure you want to approve the claim?'}
            cancelLabel='No'
            okLabel='Yes'
            onCancel={() => setDiscrepancyModal(false)}
            onOk={() => {
              setDiscrepancyModal(false)
              handleOnShowApprovalMatrix({ skip: true })
            }}
          />
        )}

        {SafeAccess(permissions.approve, ['enabled']) && (
          <Alert
            open={approveModal.show}
            message={approveModal.message}
            cancelLabel='No'
            okLabel='Yes'
            onCancel={() => setApproveModal({ show: false })}
            onOk={() => {
              setApproveModal({ show: false })
              onApprove({ goBack: false })
            }}
          />
        )}

        <Alert
          open={validateModal.show}
          message='Please input all mandatory fields.'
          okLabel='Ok'
          onOk={() => {
            setTimeout(() => {
              const container = document.getElementById(validateModal.focus)
              container.focus()
              container.scrollIntoView()
            }, 500)
            setValidateModal({ show: false, focus: '' })
          }}
        />

        <Alert
          open={saveModal}
          message='Are you sure you want to save the changes?'
          cancelLabel='No'
          okLabel='Yes'
          onCancel={() => setSaveModal(false)}
          onOk={() => {
            setSaveModal(false)
            onSave({ goBack: 'dashboard' })
          }}
        />

        <Alert
          open={cancelModal}
          message='Are you sure you want to cancel?'
          cancelLabel='No'
          okLabel='Yes'
          onCancel={() => setCancelModal(false)}
          onOk={() => {
            setCancelModal(false)
            onCancel({ goBack: true })
          }}
        />

        <Alert
          open={removeModal}
          message='Are you sure you want to remove this claim?'
          cancelLabel='No'
          okLabel='Yes'
          onCancel={() => setRemoveModal(false)}
          onOk={() => {
            setRemoveModal(false)
            onRemove({ goBack: true })
          }}
        />
      </div>
    </div>
  )
}
