import constants from './../../contants'

export const resetReturn = payload => {
  return {
    for: constants.for.CLAIMS_ANALYSIS,
    type: 'RESET_RETURN_NOTES',
    payload
  }
}

export const setReturnNote = payload => {
  return {
    for: constants.for.CLAIMS_ANALYSIS,
    type: 'SET_RETURN_NOTES',
    payload
  }
}
