import instance from '../instance'
import { ExcludeErrorConstants } from 'Services/ErrorHandler/exclude'
const axios = instance('/c/c')

export default class ClaimsAdjudicationServices {
  static utilizationLegder (params) {
    return axios({
      url: `/utilization-ledger`,
      method: 'GET',
      params
    })
  }

  static transmittalReport (params, client) {
    return axios({
      url: '/transmittal-report',
      method: 'GET',
      params
    })
  }

  static getTransmittalReportPdf (params) {
    return axios({
      url: '/transmittal-report/reports',
      method: 'GET',
      // responseType: 'blob',
      // headers: { Accept: 'application/pdf' },
      params
    })
  }

  static utilizationClient (params) {
    return axios({
      url: '/utilization-ledger/client',
      method: 'GET',
      params
    })
  }

  static utilizationClientTable (params) {
    return axios({
      url: '/utilization-ledger/client/table',
      method: 'GET',
      params
    })
  }

  static loadingHIMSData (params) {
    return axios({
      url: `/loading-hims-data`,
      method: 'GET',
      params
    })
  }

  static confinementReport (params) {
    return axios({
      url: `/confinement-report`,
      method: 'GET',
      params
    })
  }

  static paymentRequestReport (params) {
    return axios({
      url: '/payment-request-report',
      method: 'GET',
      params
    })
  }

  static paymentRequestClaims (params, bundle_number) {
    return axios({
      url: `/payment-request-report/${bundle_number}/claims`,
      method: 'GET',
      params
    })
  }

  static reimbursementClaimReport (params) {
    return axios({
      url: '/reimbursement-report',
      method: 'GET',
      params
    })
  }

  static productivityReport (params) {
    return axios({
      url: '/productivity-report',
      method: 'GET',
      params
    })
  }

  static lookups ({ on, params }) {
    return axios({
      url: `/lookups/${on}`,
      method: 'GET',
      params: { limit: 10, ...params }
      // headers: { [ExcludeErrorConstants['all']]: 1 }
    })
  }

  static claimsSummaryReport (params) {
    return axios({
      url: '/claims-summary-report',
      method: 'GET',
      params
    })
  }

  static lookupsQuery ({ category, field, value }) {
    return axios({
      url: `/lookups/auto-suggest`,
      method: 'GET',
      params: { category, field, value },
      headers: { [ExcludeErrorConstants['all']]: 1 }
    })
  }

  static lookupsAll ({ category }) {
    return axios({
      url: `/lookups/auto-suggest/${category}`,
      method: 'GET',
      params: { category },
      headers: { [ExcludeErrorConstants['all']]: 1 }
    })
  }

  static returnedClaimsReport (params) {
    return axios({
      url: `/returned-claims-report`,
      method: 'GET',
      params
    })
  }

  static agingPartialApprovedClaimsReport (params) {
    return axios({
      url: `/aging-partial-approved-claims`,
      method: 'GET',
      params
    })
  }

  static agingReturnClaimsReport (params) {
    return axios({
      url: `/aging-return-claims`,
      method: 'GET',
      params
    })
  }

  static claimsBeyond3Months (params) {
    return axios({
      url: `/claims-beyond-3-months`,
      method: 'GET',
      params
    })
  }

  static reimbursmentAgingReport (params) {
    return axios({
      url: `/reimbursement-aging`,
      method: 'GET',
      params
    })
  }

  static performanceReport (params) {
    return axios({
      url: `/performance-report`,
      method: 'GET',
      params
    })
  }

  static detailedPerformanceReport (params) {
    return axios({
      url: `/detailed-performance-report`,
      method: 'GET',
      params
    })
  }

  static detailedMonitoringReport (params) {
    return axios({
      url: `/detailed-monitoring-report`,
      method: 'GET',
      params
    })
  }

  static detailedClaimsReport (params) {
    return axios({
      url: `/detailed-claims-report`,
      method: 'GET',
      params
    })
  }

  static genericURL (params) {
    const { url, method = 'GET', ...others } = params
    return axios({
      url: url,
      method: method,
      params: others
    })
  }

  static getUploads (params) {
    return axios({
      url: '/file',
      method: 'GET',
      params
    })
  }
}
