import instance from '../instance'
import { ExcludeErrorConstants } from 'Services/ErrorHandler/exclude'
const axios = instance('/x/cc')

export default class CustomerCareServices {
  static getMember (params) {
    return axios({
      url: `/member`,
      method: 'GET',
      params
    })
  }

  static getClient (params) {
    return axios({
      url: `/client`,
      method: 'GET',
      params
    })
  }

  static getMemberUtilizationLoa ({ account_number }) {
    return axios({
      url: `/members/${account_number}/utilization-loa`,
      method: 'GET',
      headers: { [ExcludeErrorConstants['all']]: 1 }
    })
  }

  // PRO User lookup
  static proUsersLookup ({ value, field }) {
    return axios({
      url: '/users/pro',
      method: 'GET',
      params: { [field]: value }
    })
  }

  static getMemberContract ({ mct_id }) {
    return axios({
      url: `/member/contract`,
      method: 'GET',
      headers: { [ExcludeErrorConstants['all']]: 1, Accept: 'application/pdf' },
      responseType: 'blob',
      params: { mct_id }
    })
  }

  static getClientContract ({ mct_id }) {
    return axios({
      url: `/client/contract`,
      method: 'GET',
      headers: { [ExcludeErrorConstants['all']]: 1, Accept: 'application/pdf' },
      responseType: 'blob',
      params: { mct_id }
    })
  }

  static getMemberContractUrl ({ mct_id }) {
    return new Promise((resolve, reject) => {
      const token = window.localStorage.getItem('token') || window.localStorage.getItem('access_token')
      const url = `/x/cc/member/contract?mct_id=${mct_id}&token=${token}`
      const baseURL = window.localStorage.CLAIMS_URL
        ? window.localStorage.CLAIMS_URL
        : `${process.env.REACT_APP_SERVER_API}`
      resolve(`${baseURL}${url}`)
    })
  }

  static getClientContractUrl ({ mct_id }) {
    return new Promise((resolve, reject) => {
      const token = window.localStorage.getItem('token') || window.localStorage.getItem('access_token')
      const url = `/x/cc/client/contract?mct_id=${mct_id}&token=${token}`
      const baseURL = window.localStorage.CLAIMS_URL
        ? window.localStorage.CLAIMS_URL.replace(/\/$/, '')
        : `${process.env.REACT_APP_SERVER_API}`
      resolve(`${baseURL}${url}`)
    })
  }

  static getClientInfo ({ value, field }) {
    return axios({
      url: `/client-info`,
      method: 'GET',
      params: { [field]: value }
    })
  }

  static getClients ({ where, fields, order, limit, skip, offset }) {
    return axios({
      url: `/clients`,
      method: 'GET',
      headers: { [ExcludeErrorConstants['all']]: 1 },
      params: { where, fields, order, limit, skip, offset }
    })
  }

  static getMembers ({ where, fields, order, limit, skip, offset }) {
    return axios({
      url: `/members`,
      method: 'GET',
      headers: { [ExcludeErrorConstants['all']]: 1 },
      params: { where, fields, order, limit, skip, offset }
    })
  }

  static getClientUtilizationLoa ({ client_account_no }) {
    return axios({
      url: `/client/${client_account_no}/utilization-loa`,
      method: 'GET'
    })
  }
  static getMemberContractPlanType ({ account_number }) {
    return axios({
      url: `/member/contract-plan-type`,
      method: 'GET',
      headers: { [ExcludeErrorConstants['all']]: 1 },
      params: { account_number }
    })
  }

  static getClientrContractPlanType ({ account_number }) {
    return axios({
      url: `/client/contract-plan-type`,
      method: 'GET',
      headers: { [ExcludeErrorConstants['all']]: 1 },
      params: { account_number }
    })
  }

  static getDoctorSpecilizations ({ data }) {
    return axios({
      url: '/doctor-specializations',
      method: 'GET',
      headers: { [ExcludeErrorConstants['all']]: 1 },
      params: { data }
    })
  }

  static getMembers2 (query) {
    return axios({
      url: `/members-lookup`,
      method: 'GET',
      headers: { [ExcludeErrorConstants['all']]: 1 },
      params: { args: query }
    })
  }
}
