import { fromBlob } from 'Services/Download'

export default function ArrayToCSV (props) {
  const {
    fileName = 'CSV',
    header = [],
    rows = [],
    extension = 'csv',
    statics = [],
    data = [],
    mimeType = 'data:text/csv;charset=utf-8'
  } = props
  const valueSeperator = ','
  const newLine = '\r\n'
  const [c_header = [], c_footer = []] = statics

  let _rows = [
    ...c_header,
    header.reduce((joined, currentValue) => [joined, `"${currentValue}"`].join(valueSeperator), '').slice(1),
    newLine,
    ...rows.map(row =>
      [
        row.reduce((joined, currentValue) => [joined, `"${currentValue}"`].join(valueSeperator), '').slice(1),
        newLine
      ].join('')
    ),
    ...c_footer
  ]

  if (data && data.length) _rows = data

  fromBlob({ fileName, extension, content: _rows, mimeType })
}
