import React from 'react'
import Typography from '@material-ui/core/Typography'

import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import IconButton from '@material-ui/core/IconButton'
import Menu from '@material-ui/core/Menu'
import MenuItem from '@material-ui/core/MenuItem'
import Print from '@material-ui/icons/Print'
import startCase from 'lodash/startCase'
import { Link } from 'react-router-dom'

// Components
import DashboardTile from 'SharedComponents/DashboardTileComponent/DashboardTileComponent'
import ViewList from './../ViewList2/ViewList'
import { PdfViewer2Service } from 'SharedComponents/PdfViewer2/PdfViewer2'
import LoadingIndicator from 'SharedComponents/LoadingIndicator/LoadingIndicator'

// Utilities
import { DateFormatter } from 'Utilities/TextFormatter'

const columns = [
  { id: 'claim-number', name: ' Claim Number', sortField: 'claim_number', match: 'start' },
  { id: 'due-date', name: ' Due Date', sortField: 'date_due', match: 'date' },
  { id: 'facility-name', name: ' Medical Partner Name (Facility)', sortField: 'facility.name', match: 'any' }
]

const SubmittedClaimsTile = props => {
  const { services, sizes = { sm: 12 } } = props
  const [claimNumber, setClaimNumber] = React.useState(false)
  const [claimType, setClaimType] = React.useState(null)
  const [disableLoaButton, setDisableLoaButton] = React.useState(false)
  const ITEM_HEIGHT = 48

  const [anchorEl, setAnchorEl] = React.useState(null)

  const [fetching, setFetching] = React.useState(false)
  const [fetchingViewList, setFetchingViewList] = React.useState(false)

  const open = Boolean(anchorEl)

  function handleClickSubmitted (event, claim) {
    setAnchorEl(event.currentTarget)
    setClaimNumber(claim.claim_number)
    setClaimType(claim._type)
    setDisableLoaButton(!claim.has_amount_concerns)
  }

  function handleClose () {
    setAnchorEl(null)
  }

  const [claims, setClaims] = React.useState({
    submitted: { count: 0, data: [] }
  })

  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    data: [],
    orderBy: 'date_due',
    order: true
  }

  const [viewList, setViewList] = React.useState(initialValue)

  React.useEffect(() => {
    setFetching(true)
    Promise.all([
      services.getClaims({
        category: 'submitted',
        limit: 10,
        sort: 'date_due|desc',
        range: 'this-week'
      }),
      services.getClaimCount({ category: 'submitted' })
    ])
      .then(values => {
        const [batchResponse, batchCountResponse] = values
        const openBatches = batchResponse.data.data.results
        const openBatchCount = batchCountResponse.data.data
        setClaims(prevState => ({ ...prevState, submitted: { data: openBatches, count: openBatchCount } }))
      })
      .finally(values => {
        setFetching(false)
      })
  }, [])

  const handleFilterChange = category => value => {
    // Not yet working, but change of filter will not show error.

    setFetching(true)
    services
      .getClaims({ category, range: value, limit: 10, sort: 'date_due|desc' })
      .then(({ data: res }) => {
        const { data } = res
        setClaims(prevState => ({ ...prevState, [category]: { ...prevState[category], data: data.results } }))
      })
      .finally(values => {
        setFetching(false)
      })
  }

  const handleOnShowViewList = category => () => {
    let title = startCase(`${category} Claims`)
    if (category === 'open') title = 'For Adjudication'

    setViewList({ ...initialValue, show: true, title, category })
    setFetchingViewList(true)
    services
      .getClaims({ category, limit: 10, sort: 'date_due|desc' })
      .then(({ data: res }) => {
        const { results, count, limit, page } = res.data
        setViewList(state => ({ ...state, data: results, count, limit, page }))
      })
      .finally(values => {
        setFetchingViewList(false)
      })
  }

  const handleOnViewListChange = params => {
    const _params = {
      category: viewList.category,
      limit: viewList.limit,
      page: viewList.page,
      search: viewList.search,
      sort: `${viewList.orderBy}|${viewList.order ? 'desc' : 'asc'}`,
      ...(!!viewList.filter && { filter: viewList.filter }),
      ...params
    }

    setFetchingViewList(true)
    services
      .getClaims(_params)
      .then(({ data: res }) => {
        const { results, count, limit, page } = res.data
        setViewList(state => ({ ...state, data: results, count, limit, page }))
      })
      .finally(values => {
        setFetchingViewList(false)
      })
  }

  // const handleOnClickItemViewList = ({ category, claim_number, claim, event }) => {
  //   handleClickSubmitted(event, claim)
  // }

  const handleOnShowPdf = ({ claim_number, type }) => {
    setAnchorEl()

    let pdf = {}

    switch (type) {
      case 'letter_of_advice':
        pdf = {
          name: 'Letter of Advice',
          service: services.getLetterOfAdvice({ claimNo: claim_number })
        }
        break

      case 'analysis_sheet':
        pdf = {
          name: 'Analysis Sheet',
          service: services.getAnalysisSheet({ claimNo: claim_number })
        }

        break

      case 'notice':
        pdf = {
          name: 'Claim Notice',
          service: services.getLetterNotice({ claimNo: claim_number })
        }
        break
    }

    PdfViewer2Service({ ...pdf, open: true, fileName: type })
  }

  return (
    <React.Fragment>
      <DashboardTile
        id='submitted-claims'
        count={{ color: 5, value: claims.submitted.count, id: 'submitted-claims-count' }}
        sizes={sizes}
        title='Submitted Claims'
        viewList={
          <Typography
            id='sc-view-list'
            variant='caption'
            className='bold text-link'
            onClick={handleOnShowViewList('submitted')}
          >
            View List
          </Typography>
        }
        onFilterChange={handleFilterChange('submitted')}
      >
        <LoadingIndicator loading={fetching}>
          <Table stickyHeader size='small'>
            <TableHead>
              <TableRow>
                <TableCell id='sc-claim-no'>Claim No.</TableCell>
                <TableCell id='sc-due-date'>Due Date</TableCell>
                <TableCell id='sc-facility-name'>Facility Name</TableCell>
                <TableCell />
              </TableRow>
            </TableHead>
            <TableBody>
              {claims.submitted.data.map((item, index) => (
                <TableRow key={index}>
                  <TableCell id={`sc-claim-no-${index}`}>{item.claim_number}</TableCell>
                  <TableCell id={`sc-due-date-${index}`}>{DateFormatter(item.date_due)}</TableCell>
                  <TableCell id={`sc-facility-name-${index}`}>{item.facility.name}</TableCell>
                  <TableCell>
                    <IconButton
                      id={`print${index}`}
                      aria-label='more'
                      aria-controls='long-menu'
                      aria-haspopup='true'
                      onClick={e => handleClickSubmitted(e, item)}
                    >
                      <Print />
                    </IconButton>
                  </TableCell>
                </TableRow>
              ))}
              <Menu
                id='long-menu'
                anchorEl={anchorEl}
                keepMounted
                open={open}
                onClose={handleClose}
                style={{
                  zIndex: 1310
                }}
                PaperProps={{
                  style: {
                    maxHeight: ITEM_HEIGHT * 4.5,
                    width: claimType === 'Reimbursement' ? 280 : 200
                  }
                }}
              >
                <MenuItem onClick={() => handleOnShowPdf({ claim_number: claimNumber, type: 'analysis_sheet' })}>
                  {claimType === 'Reimbursement' ? 'Approved Reimbursement Letter' : 'Analysis Sheet'}
                </MenuItem>
                {/* {disableLoaButton ? ( */}
                <MenuItem
                  disabled={disableLoaButton}
                  onClick={() => handleOnShowPdf({ claim_number: claimNumber, type: 'letter_of_advice' })}
                >
                  {claimType === 'Reimbursement' ? 'Rejected Reimbursement Letter' : 'Letter Of Advice'}
                </MenuItem>
              </Menu>
            </TableBody>
          </Table>
        </LoadingIndicator>
      </DashboardTile>

      <ViewList
        id='submitted-claims'
        columns={columns}
        searchTextPlaceHolder='Search for Claim Number, Due Date or Facility Name'
        viewList={viewList}
        setViewList={setViewList}
        onViewListChange={handleOnViewListChange}
        fetching={fetchingViewList}
        body={_data =>
          _data.map((item, index) => (
            <TableRow key={item.claim_number}>
              <TableCell id={`claim-number-${index}`}>{item.claim_number}</TableCell>
              <TableCell id={`due-date-${index}`}>{DateFormatter(item.date_due)}</TableCell>
              <TableCell id={`facility-name=${index}`}>{item.facility.name}</TableCell>
              <TableCell id={`print-cell-${index}`}>
                <IconButton
                  id={`print${index}`}
                  aria-label='more'
                  aria-controls='long-menu'
                  aria-haspopup='true'
                  onClick={e => handleClickSubmitted(e, item)}
                >
                  <Print />
                </IconButton>
              </TableCell>
              <Menu
                id='long-menu'
                anchorEl={anchorEl}
                keepMounted
                open={open}
                onClose={handleClose}
                style={{
                  zIndex: 1310
                }}
                PaperProps={{
                  style: {
                    maxHeight: ITEM_HEIGHT * 4.5,
                    width: claimType === 'Reimbursement' ? 280 : 200
                  }
                }}
              >
                <MenuItem onClick={() => handleOnShowPdf({ claim_number: claimNumber, type: 'analysis_sheet' })}>
                  {claimType === 'Reimbursement' ? 'Approved Reimbursement Letter' : 'Analysis Sheet'}
                </MenuItem>
                {/* {disableLoaButton ? ( */}
                <MenuItem
                  disabled={disableLoaButton}
                  onClick={() => handleOnShowPdf({ claim_number: claimNumber, type: 'letter_of_advice' })}
                >
                  {claimType === 'Reimbursement' ? 'Rejected Reimbursement Letter' : 'Letter Of Advice'}
                </MenuItem>
              </Menu>
            </TableRow>
          ))
        }
      />
    </React.Fragment>
  )
}

SubmittedClaimsTile.displayName = 'DashboardTileComponent'
export default SubmittedClaimsTile
