import React from 'react'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'

// Components
import DashboardTile from 'SharedComponents/DashboardTileComponent/DashboardTileComponent'
import ViewList from './../ViewList2/ViewList'
import LoadingIndicator from 'SharedComponents/LoadingIndicator/LoadingIndicator'

// Utilities
import { DateFormatter } from 'Utilities/TextFormatter'

const columns = [
  { id: 'bundle-number', name: ' Bundle Number', sortField: 'bundle_number', match: 'start' },
  { id: 'due-date', name: ' Due Date', sortField: 'date_due', match: 'date' },
  { id: 'facility-name', name: ' Medical Partner Name (Facility)', sortField: 'facility_name', match: 'any' }
]

const SubmittedBundlesTile = props => {
  const { services, sizes = { sm: 12 } } = props
  const [items, setItems] = React.useState({ count: 0, data: [] })

  const [fetching, setFetching] = React.useState(false)
  const [fetchingViewList, setFetchingViewList] = React.useState(false)

  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    filter: {},
    data: [],
    orderBy: 'date_due',
    order: true
  }

  const [viewList, setViewList] = React.useState(initialValue)

  React.useEffect(() => {
    setFetching(true)
    Promise.all([
      services.getBundles({
        category: 'submitted',
        limit: 10,
        sort: 'date_due|desc',
        range: 'this-week'
      }),
      services.getBundleCount({ category: 'submitted' })
    ])
      .then(values => {
        const [batchResponse, batchCountResponse] = values
        setItems({ data: batchResponse.data.data.results, count: batchCountResponse.data.data })
      })
      .finally(values => {
        setFetching(false)
      })
  }, [])

  const handleFilterChange = value => {
    setFetching(true)
    services
      .getBundles({
        category: 'submitted',
        range: value,
        limit: 10,
        sort: 'date_due|desc'
      })
      .then(({ data: res }) => {
        const { data } = res
        setItems(prevState => ({ ...prevState, data: data.results }))
      })
      .finally(values => {
        setFetching(false)
      })
  }

  const handleOnShowViewList = () => {
    setViewList({ ...initialValue, show: true, title: 'Submitted Bundles' })
    setFetchingViewList(true)
    services
      .getBundles({ category: 'submitted', limit: 10, sort: 'date_due|desc' })
      .then(({ data: res }) => {
        const { results, count, limit, page } = res.data
        setViewList(state => ({ ...state, data: results, count, limit, page }))
      })
      .finally(values => {
        setFetchingViewList(false)
      })
  }

  const handleOnViewListChange = params => {
    const _params = {
      category: 'submitted',
      limit: viewList.limit,
      page: viewList.page,
      search: viewList.search,
      sort: `${viewList.orderBy}|${viewList.order ? 'desc' : 'asc'}`,
      filter: viewList.filter,
      ...params
    }

    setFetchingViewList(true)
    services
      .getBundles(_params)
      .then(({ data: res }) => {
        const { results, count, limit, page } = res.data
        setViewList(state => ({ ...state, data: results, count, limit, page }))
      })
      .finally(values => {
        setFetchingViewList(false)
      })
  }

  return (
    <React.Fragment>
      <DashboardTile
        count={{ color: 3, value: items.count, id: 'submitted-count' }}
        sizes={sizes}
        title='Submitted Bundles'
        id='submitted-bundles'
        viewList={
          <Typography
            variant='caption'
            className='bold text-link'
            onClick={handleOnShowViewList}
            id='submitted-view-list'
          >
            View List
          </Typography>
        }
        onFilterChange={handleFilterChange}
      >
        <LoadingIndicator loading={fetching}>
          <Table stickyHeader size='small'>
            <TableHead>
              <TableRow>
                {columns.map(item => (
                  <TableCell key={`created-${item.id}`} id={`created-${item.id}`}>
                    {item.name}
                  </TableCell>
                ))}
                <TableCell />
              </TableRow>
            </TableHead>
            <TableBody>
              {items.data.map((item, index) => (
                <TableRow key={item.bundle_number}>
                  <TableCell id={`submitted-bundle-no-${index}`}>{item.bundle_number}</TableCell>
                  <TableCell id={`submitted-due-date-${index}`}>{DateFormatter(item.date_due)}</TableCell>
                  <TableCell id={`submitted-facility-name-${index}`}>{item.facility_name}</TableCell>
                  <TableCell>
                    <Link to={`/bundles/${item.bundle_number}`}>
                      <Typography variant='caption' className='text-link underline' id={`update-bundle-${index}`}>
                        View
                      </Typography>
                    </Link>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </LoadingIndicator>
      </DashboardTile>

      <ViewList
        id='submitted-bundles'
        columns={columns}
        searchTextPlaceHolder='Search for bundle number, date or facility name'
        viewList={viewList}
        setViewList={setViewList}
        onViewListChange={handleOnViewListChange}
        fetching={fetchingViewList}
        body={_data =>
          _data.map((row, index) => (
            <TableRow key={row.bundle_number}>
              <TableCell id={`bundle-number-${index}`}>{row.bundle_number}</TableCell>
              <TableCell id={`due-date-${index}`}>{DateFormatter(row.date_due)}</TableCell>
              <TableCell id={`facility-name-${index}`}>{row.facility_name}</TableCell>
              <TableCell id={`update-bundle-${index}`}>
                <Link to={`/bundles/${row.bundle_number}`}>
                  <Typography variant='caption' className='text-link underline'>
                    View
                  </Typography>
                </Link>
              </TableCell>
            </TableRow>
          ))
        }
      />
    </React.Fragment>
  )
}

SubmittedBundlesTile.displayName = 'DashboardTileComponent'

export default SubmittedBundlesTile
