import React from 'react'
import Grid from '@material-ui/core/Grid'
// import Button from '@material-ui/core/Button'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'

// Components
import ClaimsForApprovalTile from './../../../Components/Tiles/ClaimsForApprovalTile'
import BatchAndEncoderTile from './../../../Components/Tiles/BatchAndEncoderTile'
import ForReconciliationClaimsTile from '../../../Components/Tiles/ForReconciliationClaimsTile'
import MedicalCollectiblesTile from '../../../Components/Tiles/MedicalCollectiblesTile'
import ReimbursementClaimTile from '../../../Components/Tiles/ReimbursementClaimTile'
import SubmittedPrSupervisorTile from './../../../Components/Tiles/SubmittedPrSupervisorTile'
import ClaimsEncoderTile from './../../../Components/Tiles/ClaimsEncoderTile'
import ClaimsVerifierTile from './../../../Components/Tiles/ClaimsVerifierTile'
import ForPaymentRequestTile from './../../../Components/Tiles/ForPaymentRequestTile'
import TaskTray from 'SharedComponents/TaskTrayComponent/TaskTrayComponent'
import TicketList from 'SharedComponents/TicketListComponent/TicketListComponent'

// Services
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import BatchEncodingSupervisoryServices from 'Services/Api/Claims/BatchEncodingSupervisoryServices'
import ClaimsReconServices from 'Services/Api/Claims/ClaimsReconServices'
import ClaimsVerifierSupervisorServices from 'Services/Api/Claims/ClaimsVerifierSupervisorServices'
import ClaimsEncoderSupervisorServices from 'Services/Api/Claims/ClaimsEncoderSupervisorServices/ClaimsEncoderSupervisorServices'
import PaymentRequestServices from 'Services/Api/Claims/PaymentRequestServices'
import ClaimsManagerServices from 'Services/Api/Claims/ClaimsManagerServices/ClaimsManagerServices'

// Styles
import classes from './Dashboard.module.scss'

export default function Dashboard (props) {
  React.useEffect(() => {
    PageTitleService('Dashboard')
    BreadcrumbsService.setSelectedItems(['dashboard'])
  }, [])

  return (
    <div className={classes.root}>
      <Grid container className={classes.tiles} spacing={3} direction='row'>
        <Grid item className={classes['task-tray']}>
          <TaskTray sizes={{ lg: 12 }} title='Items to Monitor'>
            <ClaimsForApprovalTile services={ClaimsVerifierSupervisorServices} {...props} />
            <ForPaymentRequestTile services={PaymentRequestServices} mTitle='Payment Request for Approval' {...props} />
          </TaskTray>
          <TaskTray sizes={{ lg: 12 }} showTitle={false}>
            <MedicalCollectiblesTile
              services={ClaimsVerifierSupervisorServices.MedicalCollectiblesServices}
              {...props}
            />
            <ForReconciliationClaimsTile services={ClaimsReconServices} {...props} />
          </TaskTray>
          <TaskTray sizes={{ lg: 12 }} showTitle={false}>
            <ReimbursementClaimTile services={ClaimsVerifierSupervisorServices} {...props} />
            <SubmittedPrSupervisorTile services={ClaimsManagerServices} {...props} />
          </TaskTray>
          <TaskTray sizes={{ lg: 12 }} title='Claims Processor'>
            <BatchAndEncoderTile services={BatchEncodingSupervisoryServices} {...props} />
            <ClaimsEncoderTile services={ClaimsEncoderSupervisorServices} {...props} />
            <ClaimsVerifierTile services={ClaimsVerifierSupervisorServices} {...props} />
          </TaskTray>
        </Grid>
        <Grid item className={classes['ticket-list']}>
          <div className={classes.wrapper}>
            <div className={classes.float}>
              <TicketList count={0} title='Open Tickets' viewAllLink={{ to: '/batches?category=open', caption: '' }}>
                <Table size='small'>
                  <TableHead>
                    <TableRow>
                      <TableCell>Ticket #</TableCell>
                      <TableCell>Date</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {[].map((item, index) => (
                      <TableRow key={index}>
                        <TableCell>2334324343</TableCell>
                        <TableCell>08/12/2019</TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </TicketList>
            </div>
          </div>
        </Grid>
      </Grid>
    </div>
  )
}
