import React from 'react'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'

// Components
import DashboardTile from 'SharedComponents/DashboardTileComponent/DashboardTileComponent'
import ViewList from '../ViewList2/ViewList'
import LoadingIndicator from 'SharedComponents/LoadingIndicator/LoadingIndicator'

// Utilities
import { DateFormatter } from 'Utilities/TextFormatter'

const columns = [
  { id: 'claim-number', name: 'Claim No.', sortField: 'claim_number', match: 'any' },
  { id: 'due-date', name: 'Due Date ', sortField: 'date_due', match: 'date' },
  { id: 'account-name', name: 'Account No.', sortField: 'member.account_number', match: 'any' },
  { id: 'member-name', name: 'Member Name', sortField: 'member_name', match: 'any' }
]

const ReimbursementClaimTile = props => {
  const { services, sizes = { sm: 12 } } = props
  const [claims, setClaims] = React.useState({
    count: 0,
    data: []
  })

  const [fetching, setFetching] = React.useState(false)
  const [fetchingViewList, setFetchingViewList] = React.useState(false)

  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    data: [],
    orderBy: 'date_due',
    order: true,
    category: 'reimbursement',
    filter: {}
  }

  const [viewList, setViewList] = React.useState(initialValue)

  React.useEffect(() => {
    setFetching(true)
    Promise.all([
      services.getItems({
        category: viewList.category,
        limit: viewList.limit,
        sort: 'date_due|desc',
        range: 'this-week',
        filter: viewList.filter
      }),
      services.getItemCount({ claim_type: 'Reimbursement' })
    ])
      .then(values => {
        const [claimResponse, claimCountResponse] = values
        const { results } = claimResponse.data.data
        setClaims(prevState => ({
          ...prevState,
          data: results,
          count: claimCountResponse.data.data
        }))
      })
      .finally(values => {
        setFetching(false)
      })
  }, [])

  const handleOnViewListChange = params => {
    const _params = {
      category: viewList.category,
      limit: viewList.limit,
      page: viewList.page,
      search: viewList.search,
      sort: `${viewList.orderBy}|${viewList.order ? 'desc' : 'asc'}`,
      filter: viewList.filter,
      ...params
    }

    setFetchingViewList(true)
    services
      .getItems(_params)
      .then(({ data: res }) => {
        const { results, count, limit, page } = res.data
        setViewList(state => ({ ...state, data: results, count, limit, page }))
      })
      .finally(values => {
        setFetchingViewList(false)
      })
  }

  const handleOnShowViewList = () => {
    let title = 'Reimbursement Claim'
    // const _params = {
    //   category: viewList.category,
    //   limit: viewList.limit,
    //   page: viewList.page,
    //   search: viewList.search,
    //   sort: `${viewList.orderBy}|${viewList.order ? 'desc' : 'asc'}`
    // }
    setViewList({ ...initialValue, show: true, title })
    setFetchingViewList(true)
    services
      .getItems({ category: viewList.category, limit: 10, sort: 'date_due|desc' })
      .then(({ data: res }) => {
        const { results, count, limit, page } = res.data
        setViewList(state => ({ ...state, data: results, count, limit, page }))
      })
      .finally(values => {
        setFetchingViewList(false)
      })

    setViewList({ ...initialValue, show: true, title })
  }

  return (
    <React.Fragment>
      <DashboardTile
        id='reimbursement-claims'
        count={{ color: 3, value: claims.count, id: 'reimbursement-claims-count' }}
        sizes={sizes}
        title='Reimbursement Claim'
        isFilterShown={false}
        viewList={
          <Typography id='rmc-view-list' variant='caption' className='bold text-link' onClick={handleOnShowViewList}>
            View List
          </Typography>
        }
      >
        <LoadingIndicator loading={fetching}>
          <Table stickyHeader size='small'>
            <TableHead>
              <TableRow>
                {columns.map(item => (
                  <TableCell key={item.id} id={`rmc-${item.id}`}>
                    {item.name}
                  </TableCell>
                ))}
                <TableCell />
              </TableRow>
            </TableHead>
            <TableBody>
              {claims.data.map((item, index) => (
                <TableRow key={`rmc-claim-list-${index}`}>
                  <TableCell id={`rmc-claim-no-${index}`}>{item.claim_number}</TableCell>
                  <TableCell id={`rmc-due-date-${index}`}>{DateFormatter(item.date_due)}</TableCell>
                  <TableCell id={`rmc-account-no-${index}`}>{item.member.account_number}</TableCell>
                  <TableCell id={`rmc-member-name-${index}`}>{item.member_name}</TableCell>
                  <TableCell id={`rmc-view-claim-${index}`}>
                    <Link to={`/claims/reimbursement/${item.claim_number}`}>
                      <Typography variant='caption' className='text-link underline' id={`view-claim-${index}`}>
                        View
                      </Typography>
                    </Link>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </LoadingIndicator>
      </DashboardTile>

      <ViewList
        id='reimbursement-claims'
        columns={columns}
        searchTextPlaceHolder='Search for claim number, date or facility name'
        viewList={viewList}
        setViewList={setViewList}
        onViewListChange={handleOnViewListChange}
        fetching={fetchingViewList}
        body={_data =>
          _data.map((item, index) => (
            <TableRow key={`rmc-claim-list-${index}`}>
              <TableCell id={`rmc-claim-no-${index}`}>{item.claim_number}</TableCell>
              <TableCell id={`rmc-due-date-${index}`}>{DateFormatter(item.date_due)}</TableCell>
              <TableCell id={`rmc-account-no-${index}`}>{item.member.account_number}</TableCell>
              <TableCell id={`rmc-member-name-${index}`}>{item.member_name}</TableCell>
              <TableCell id={`rmc-view-claim-${index}`}>
                <Link to={`/claims/reimbursement/${item.claim_number}`}>
                  <Typography variant='caption' className='text-link underline' id={`view-claim-${index}`}>
                    View
                  </Typography>
                </Link>
              </TableCell>
            </TableRow>
          ))
        }
      />
    </React.Fragment>
  )
}

ReimbursementClaimTile.displayName = 'DashboardTileComponent'

export default ReimbursementClaimTile
