import React from 'react'

// Material Components
import Button from '@material-ui/core/Button'
import Paper from '@material-ui/core/Paper'
import Table from 'SharedComponents/Table/Table'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TableSortLabel from '@material-ui/core/TableSortLabel'
import Typography from '@material-ui/core/Typography'
import GetAppIcon from '@material-ui/icons/GetApp'

// Shared Components
import DatePicker from 'SharedComponents/DatePicker2/DatePicker2'
import TextField from 'SharedComponents/TextField/CustomTextField'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import ClaimsServices from 'Services/Api/Claims/ClaimsServices'
import ArrayToCSV from 'Services/ArrayToCSV'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'

// Utility
import { DateFormatter, CurrencyFormatter } from 'Utilities/TextFormatter'
import { SafeAccess } from 'Utilities/SafeAccess'

// Libraries
// import moment from 'moment'

// Styles
import classes from './ReimbursementReport.module.scss'

// Validator
import DateRangeValidator from '../TransmittalReport/validator/DateRangeValidator'

const completeColumns = [
  { label: 'No.', data: 'no', search: false },
  { label: 'Claim Number', data: 'claim_number', search: true, match: 'any' },
  { label: 'Reimbursement Claim Amount', type: 'number', data: 'grand_total', search: false },
  { label: 'Reimbursement Category', data: 'reimbursement_category', search: true, match: 'any' },
  { label: 'Claim Status', data: 'claim_status', type: 'sentence', search: true, match: 'any' },
  { label: 'Contract Plan Type', data: 'contract_plan_type', search: true, match: 'any' },
  { label: 'Geography', data: 'geography', search: true, match: 'any' }
]

/**
 * Claims Utilization Report
 *
 * @export
 *
 * @param {*} props
 *   dataColumns - default []; if defined, will be the allowable data columns that
 *     will be shown on the claims utilization report
 *   hideReport - default false; if defined, will be used to hide the entire report
 *   hideheader - default false; if defined, will be used to hide the header section
 *   dateRange - default current date; if defined, will be used to define the date range in-effect
 *
 * @returns
 */
export default function ReimbursementReport (props) {
  const { dataColumns = [], hideReport, hideHeader, dateRange } = props

  const [claims, setClaims] = React.useState({
    data: [],
    search: '',
    range: {
      from: SafeAccess(dateRange, 'from', new Date().toISOString()),
      to: SafeAccess(dateRange, 'to', new Date().toISOString()),
      field: 'latest.date.from'
    },
    filter: {},
    orderBy: 'claim_number',
    order: true,
    page: 1,
    limit: 10,
    count: 0,
    rowsPerPageOptions: [10, 15, 20],
    grandTotal: 0
  })

  const [dateRangeError, setDateRangeError] = React.useState({
    date_from: '',
    date_to: ''
  })

  const [columns, setColumns] = React.useState(completeColumns)

  React.useEffect(() => {
    PageTitleService('Reimbursement Claims Report')
    BreadcrumbsService.setSelectedItems(['reimbursementReport'])
  }, [])

  const handleOnPaginate = args => {
    setClaims(state => ({ ...state, ...args }))
  }

  const handleOnFilter = args => {
    const { field, value, match } = args
    setClaims(state => ({ ...state, filter: { ...state.filter, [field]: { value, match } } }))
  }

  const fetchRecords = () => {
    LoadingService.loading(true)
    ClaimsServices.reimbursementClaimReport({
      search: claims.search,
      range: claims.range,
      filter: claims.filter,
      sort: `${claims.orderBy}|${claims.order ? 'asc' : 'desc'}`,
      page: claims.page,
      limit: claims.limit
    }).then(res => {
      const { data } = res.data
      const { results = [], count = 0, grand_total = 0 } = data
      setClaims(state => ({ ...state, data: results, count, grandTotal: grand_total }))
      LoadingService.loading(false)
    })
  }

  const validate = () => {
    const { error } = DateRangeValidator({
      date_from: claims.range.from,
      date_to: claims.range.to
    })

    setDateRangeError({ ...error })
    return !!error
  }

  const fetchCSV = () => {
    LoadingService.loading(true)
    ClaimsServices.reimbursementClaimReport({
      search: claims.search,
      range: claims.range,
      filter: claims.filter,
      sort: `${claims.orderBy}|${claims.order ? 'asc' : 'desc'}`,
      page: claims.page,
      limit: claims.count > 0 ? claims.count : claims.rowsPerPageOptions[0]
    }).then(res => {
      const { data } = res.data
      const { results = [], grand_total = 0 } = data

      const rows = results.map(claim => {
        return columns.map(col => formatData({ type: col.type, source: claim, field: col.data }))
      })

      const header = columns.map(c => c.label)

      ArrayToCSV({
        statics: [
          [
            'Reimbursement Claims Report',
            '\r\n',
            `Date Range: ${DateFormatter(claims.range.from)} - ${DateFormatter(claims.range.to)}`,
            '\r\n',
            '\r\n'
          ],
          [`Total Amount: ${grand_total}`]
        ],
        fileName: 'Reimbursement Claims Report',
        header,
        rows
      })
      LoadingService.loading(false)
    })
  }

  React.useEffect(() => {
    fetchRecords()
    // claim.range is triggered manually via Filter button
  }, [props.refresh, claims.search, claims.filter, claims.orderBy, claims.order, claims.limit, claims.page])

  React.useEffect(() => {
    setClaims(state => ({
      ...state,
      range: {
        ...state.range,
        to: dateRange ? dateRange.to : new Date().toISOString(),
        from: dateRange ? dateRange.from : new Date().toISOString()
      }
    }))
  }, [dateRange])

  React.useEffect(() => {
    // Goal: Adjust columns to show based on data columns passed as props

    let filtered = null
    if (!dataColumns || dataColumns.length === 0) {
      filtered = completeColumns
    } else {
      filtered = completeColumns.filter(entry => dataColumns.indexOf(entry.data) > -1)
    }
    setColumns(filtered)
  }, [dataColumns])

  const formatData = ({ type = null, source, field }) => {
    let output = ''
    if (type === 'date') {
      output = DateFormatter(source[field])
    } else if (type === 'array') {
      const [root, property] = field.split('.')
      output = source[root] ? source[root].map(f => f[property]).join(`${'\r\n'}${'\r\n'}`) : ''
    } else if (type === 'number') {
      output = CurrencyFormatter(source[field])
    } else if (type === 'sentence') {
      output = source[field]
        .toLowerCase()
        .split('_')
        .map(word => word.charAt(0).toUpperCase() + word.substring(1))
        .join(' ')
    } else {
      output = source[field] ? source[field] : ''
    }

    return output !== '' && output !== 'undefined' && output !== `${'\r\n'}${'\r\n'}` ? output : 'N/A'
  }

  return (
    <Paper className={classes.root} style={{ display: hideReport === 'true' ? 'none' : 'block' }}>
      <div className={classes.header} style={{ display: hideHeader === 'true' ? 'none' : 'block' }}>
        <Typography variant='h6'>Date Range</Typography>
        <div className={classes.content}>
          <div className={classes.filter}>
            <div className={classes.input}>
              <p>From: </p>
              <DatePicker
                id='filter-date-from'
                value={claims.range.from}
                onChange={date =>
                  setClaims(state => ({
                    ...state,
                    range: {
                      ...state.range,
                      from: date.toISOString()
                    }
                  }))
                }
                disableFuture
                error={dateRangeError.date_from}
              />
            </div>
            <div className={classes.input}>
              <p>To: </p>
              <DatePicker
                id='filter-date-to'
                value={claims.range.to}
                onChange={date =>
                  setClaims(state => ({
                    ...state,
                    range: {
                      ...state.range,
                      to: date.toISOString()
                    }
                  }))
                }
                disableFuture
                error={dateRangeError.date_to}
              />
            </div>
            <Button
              className={`button-positive ${classes.submit}`}
              onClick={() => !validate() && fetchRecords()}
              variant='contained'
              color='primary'
            >
              Filter
            </Button>
          </div>
          <div className={classes.action}>
            <Button id='generate-report' disabled={claims.data.length <= 0} onClick={() => fetchCSV()}>
              <GetAppIcon fontSize='small' />
              <Typography variant='caption' className={`bold ${!claims.data.length ? classes.disabled : classes.typo}`}>
                Generate Report
              </Typography>
            </Button>
          </div>
        </div>
      </div>
      <div className={classes.body}>
        <div className={classes.table}>
          <Table
            TableContainerProps={{ style: { maxHeight: '60vh', overflow: 'auto', position: 'relative' } }}
            TableProps={{ stickyHeader: true }}
            head={
              <TableRow>
                {columns.map((headCell, index) => (
                  <TableCell key={`table-header-${index}`}>
                    <TableSortLabel
                      onClick={event => handleOnPaginate({ orderBy: headCell.data, order: !claims.order })}
                    >
                      {headCell.label}
                    </TableSortLabel>
                    <div className={`${classes.input} ${!headCell.search ? classes.hidden : ''}`}>
                      {headCell.type && headCell.type === 'date' ? (
                        <DatePicker
                          noMargin
                          id={`${headCell.data}`}
                          value={null}
                          onChange={date =>
                            handleOnFilter({
                              field: headCell.data,
                              value: date.toISOString(),
                              match: headCell.match
                            })
                          }
                        />
                      ) : (
                        <TextField
                          noMargin
                          onChange={event =>
                            handleOnFilter({
                              field: headCell.data,
                              value: event.target.value,
                              match: headCell.match
                            })
                          }
                        />
                      )}
                    </div>
                  </TableCell>
                ))}
              </TableRow>
            }
            body={
              claims.data &&
              claims.data.map((claim, claimIndex) => (
                <TableRow className={classes['table-row']} key={`table-item-${claimIndex}`}>
                  {columns.map((col, colIndex) => (
                    <TableCell
                      className={col.remarks ? classes.remarks : classes['table-cell']}
                      key={`table-cell-${claimIndex}-${colIndex}`}
                    >
                      {formatData({ type: col.type, source: claim, field: col.data })}
                    </TableCell>
                  ))}
                </TableRow>
              ))
            }
            pagination
            count={claims.count}
            page={claims.page}
            limit={claims.limit}
            rowsPerPageOptions={claims.rowsPerPageOptions}
            onChange={args => handleOnPaginate(args)}
            footerComponent={
              <Typography variant='caption' className='bold'>
                <span>Total Amount: </span>
                <span>{CurrencyFormatter(claims.grandTotal)}</span>
              </Typography>
            }
          />
        </div>
      </div>
    </Paper>
  )
}
