import React, { Suspense } from 'react'
import Paper from '@material-ui/core/Paper'
import Typography from '@material-ui/core/Typography'

// Services
import ClaimsServices from 'Services/Api/Claims/ClaimsServices'
import BaseUrlFetcher from 'Services/BaseUrlFetcher'

// Components
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import IconButton from '@material-ui/core/IconButton'
import DescriptionIcon from '@material-ui/icons/Description'

// Styles
import classes from './UploadedList.module.scss'

const getUrl = () => BaseUrlFetcher.getClaimsBaseUrl()
// const UPLOADS = 'uploads/test'
// const mock_data = [
//   { fileName: 'Sample Contract', fileType: 'PDF', data: `${URL}/${UPLOADS}/Sample Contract.pdf` },
//   { fileName: 'PDF', fileType: 'PDF', data: `${URL}/${UPLOADS}/pdf.pdf` },
//   { fileName: 'PNG', fileType: 'PNG', data: `${URL}/${UPLOADS}/png.png` },
//   { fileName: 'JPG', fileType: 'JPG', data: `${URL}/${UPLOADS}/jpg.jpg` },
//   { fileName: 'DOC', fileType: 'DOC', data: `${URL}/${UPLOADS}/doc.doc` },
//   { fileName: 'DOCX', fileType: 'DOCX', data: `${URL}/${UPLOADS}/docx.docx` },
//   { fileName: 'XLSX', fileType: 'XLSX', data: `${URL}/${UPLOADS}/xlsx.xlsx` },
//   { fileName: 'MP4', fileType: 'MP4', data: `${URL}/${UPLOADS}/mp4.mp4` }
// ]

export default function UploadedList (props) {
  const { onItemClick = () => {}, field = {}, isAdd = false } = props
  const [uploads, setUploads] = React.useState([])

  React.useEffect(() => {
    const { index, value } = field
    if (!isAdd) {
      ClaimsServices.getUploads({ where: { [index]: value } }).then(res => {
        const { data } = res.data
        const _data = data.map(i => ({
          fileName: i.file_name,
          fileType: i.file_type,
          data: getUrl() + `/uploads/${i.path}`
          // data: `${URL}/uploads/${i.path}?token=${localStorage.getItem('token') || ''}`
        }))
        setUploads(_data)
      })
    }
  }, [])

  return (
    <Paper className={classes.root}>
      <div className={classes['header']}>
        <Typography variant='subtitle2' className={classes['header-text']}>
          Supporting Documents
        </Typography>
      </div>
      <div className={classes['table']}>
        <Table stickyHeader size='small'>
          <TableHead>
            <TableRow>
              <TableCell id='sc-claim-no'>File Name</TableCell>
              <TableCell id='sc-due-date'>File Type</TableCell>
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody>
            {uploads.length ? uploads.map((item, index) => (
              <TableRow key={item.user_id}>
                <TableCell id={`batch-encoder-name-${index}`}>{item.fileName}</TableCell>
                <TableCell id={`total-bundle-${index}`}>{item.fileType}</TableCell>
                <TableCell align='center' id={`view-work-${index}`}>
                  <IconButton
                    id={`print${index}`}
                    size='small'
                    aria-label='more'
                    aria-controls='long-menu'
                    aria-haspopup='true'
                    onClick={() =>
                      onItemClick({
                        title: item.fileName,
                        fileName: item.fileName,
                        fileType: item.fileType.toLowerCase(),
                        data: item.data
                      })
                    }
                  >
                    <DescriptionIcon />
                  </IconButton>
                </TableCell>
              </TableRow>
            )) : (
              <TableRow>
                <TableCell className={classes.handling} colSpan={3}>
                No files found
                </TableCell>
              </TableRow>)}
          </TableBody>
        </Table>
      </div>
    </Paper>
  )
}
