import instance from '../instance'
import { ExcludeErrorConstants } from 'Services/ErrorHandler/exclude'
const axios = instance('/c/ce')

export default class ClaimsEncodingServices {
  static getBatches (params) {
    return axios({
      url: `/batches`,
      method: 'GET',
      params
    })
  }

  static getBatchCount (params) {
    return axios({
      url: `/batches/count`,
      method: 'GET',
      params
    })
  }

  static getBatch ({ batch_number }) {
    return axios({
      url: `/batches/${batch_number}`,
      method: 'GET'
    })
  }

  static assignBatch ({ batch_number }) {
    return axios({
      url: `/batches/${batch_number}/assign`,
      method: 'PUT'
    })
  }

  static submitBatch ({ batch_number }) {
    return axios({
      url: `/batches/${batch_number}/submit`,
      method: 'PUT'
    })
  }

  static getClaims ({ batch_number, params }) {
    return axios({
      url: `/batches/${batch_number}/claims`,
      method: 'GET',
      params
    })
  }

  static getClaimCount ({ batch_number }) {
    return axios({
      url: `/batches/${batch_number}/claims/count`,
      method: 'GET'
    })
  }

  static getClaim ({ batch_number, claim_number }) {
    return axios({
      url: `/batches/${batch_number}/claims/${claim_number}`,
      method: 'GET'
    })
  }

  static getLetterNotice ({ claim_number }) {
    return axios({
      url: `/claims/${claim_number}/reports/notice`,
      method: 'GET',
      responseType: 'blob',
      headers: { Accept: 'application/pdf' }
    })
  }

  static getReturnAdvice ({ claim_number, sub_document_id }) {
    return axios({
      url: `/claims/${claim_number}/reports/return`,
      method: 'GET',
      // responseType: 'blob',
      // headers: { Accept: 'application/pdf' },
      params: { sub_document_id }
    })
  }

  static getLetterOfAdvice ({ claimNo }) {
    return axios({
      url: `/claims/${claimNo}/reports/letter-of-advice`,
      method: 'GET',
      responseType: 'blob',
      headers: { Accept: 'application/pdf' }
    })
  }

  static addClaim ({ batch_number, data }) {
    return axios({
      url: `/batches/${batch_number}/claims`,
      method: 'POST',
      data
    })
  }

  static updateClaim ({ batch_number, claim_number, data }) {
    return axios({
      url: `/batches/${batch_number}/claims/${claim_number}`,
      method: 'PUT',
      data
    })
  }

  static deleteClaim ({ batch_number, claim_number }) {
    return axios({
      url: `/batches/${batch_number}/claims/${claim_number}`,
      method: 'DELETE'
    })
  }

  static deleteMultipleClaim ({ batch_number, data }) {
    return axios({
      url: `/batches/${batch_number}/claims`,
      method: 'DELETE',
      data
    })
  }

  static lookups ({ on, params }) {
    return axios({
      url: `/lookups/${on}`,
      method: 'GET',
      params: { limit: 10, ...params },
      headers: { [ExcludeErrorConstants['all']]: 1 }
    })
  }

  static isFacilityExists (params) {
    return axios({
      url: `/lookups/medical-partner/exists`,
      method: 'GET',
      params
    })
  }

  static isDoctorExists (params) {
    return axios({
      url: `/lookups/doctor-partner/exists`,
      method: 'GET',
      params
    })
  }

  static getICDSynonyms (params) {
    return axios({
      url: `/lookups/icd-10-synonym`,
      method: 'GET',
      params
    })
  }

  static addReimburesmentInfo ({ claim_number, data }) {
    return axios({
      url: `/claims/${claim_number}/reimbursement-info/add`,
      method: 'PUT',
      data
    })
  }

  static updateReimburesmentInfo ({ claim_number, info_id, data }) {
    return axios({
      url: `/claims/${claim_number}/reimbursement-info/${info_id}/update`,
      method: 'PUT',
      data
    })
  }
}
