// Libraries
import moment from 'moment'

// all common fields
const Common = {
  approval_code: '',
  contract_plan_type: '',
  loa_number: '',
  reimbursement_type: 'InPatient',
  member_account_number: '',
  member_last_name: '',
  member_first_name: '',
  member_middle_name: '',
  date_from: moment().toISOString(),
  date_to: moment().toISOString(),
  facility_id: '',
  facility_name: '',
  medicine_category: '',
  reimbursement_category: '',
  official_receipt_number: '',
  claim_remarks: '',
  diagnosis: [],
  procedures: [],
  hospital_items: [],
  professional_fees: [],
  reimbursement_info: [],
  reimbursement_info_analysts: []
}
// special case
const InPatient = {
  room_type: '',
  room_rate: 0,
  pro_id: '',
  pro_name: ''
}
// other fields breakdown if need
const OtherType = {
  doctor_id: '',
  doctor_name: '',
  department: ''
}

// default export of this function
const AdditionalFields = type => {
  let fields = {}

  switch (type) {
    case undefined:
    case 'InPatient':
      fields = InPatient
      break
    default:
      fields = OtherType
      break
  }

  return { ...Common, ...fields }
}

// faster thaan allowed because no loops
// however relies on saved fields in the usage
AdditionalFields.exist = (fields, field) => fields[field] !== undefined

// slow because of loops
// however ensure field was checked against defined
AdditionalFields.allowed = (type, field) => {
  return AdditionalFields(type).find(allowed => allowed === field)
}

// trim object base on fields above
AdditionalFields.trim = (type, data) => {
  const allowed = AdditionalFields(type)
  let result = {}

  for (const [key, value] of Object.entries(allowed)) {
    result[key] = data[key] || value
  }

  return result
}

export default AdditionalFields
