import Joi from '@hapi/joi'

// Services
import ExtractValidationResult from 'Services/ExtractValidationResult'

export default function ({ data }) {
  const available_fields = {
    description: Joi.string()
      .trim()
      .label('Description')
      .required(),
    code: Joi.string()
      .trim()
      .label('Code')
      .required(),
    tooth_number: Joi.string()
      .label('Tooth Number')
      .required(),
    gross_amount: Joi.number()
      .label('Gross Amount')
      .required(),
    vat_amount: Joi.number()
      .label('Vat Amount')
      .max(Joi.ref('gross_amount'))
      .message('"VAT Amount" must be equal or less than "Gross Amount"')
      .required(),
    amount_paid_by_member: Joi.number()
      .label('Amount Paid By Member')
      .required(),
    philhealth_amount_paid: Joi.number()
      .label('PhilHealth Amount Paid')
      .required(),
    philhealth_remarks: Joi.string()
      .trim()
      .label('PhilHealth Remarks')
      .allow('', null),
    amount_not_covered: Joi.number()
      .label('Amount Not Covered')
      .required(),
    reason: Joi.string()
      .trim()
      .label('Reason')
      .when('amount_not_covered', {
        not: 0,
        then: Joi.required(),
        otherwise: Joi.allow('', null)
      }),
    amount_due_from_member: Joi.number()
      .label('Amount Due from Member')
      .required(),
    reason_for_charging_member: Joi.string()
      .trim()
      .label('Reason For Charging Member')
      .when('amount_due_from_member', {
        not: 0,
        then: Joi.required(),
        otherwise: Joi.allow('', null)
      }),
    amount_due_from_client: Joi.number()
      .label('Amount Due from Client')
      .required(),
    reason_for_charging_client: Joi.string()
      .trim()
      .label('Reason For Charging Client')
      .when('amount_due_from_client', {
        not: 0,
        then: Joi.required(),
        otherwise: Joi.allow('', null)
      }),
    flag: Joi.boolean().default(false)
  }

  const result = {}
  const _data = {}
  for (const field of Object.keys(data)) {
    if (available_fields[field]) {
      result[field] = available_fields[field]
      _data[field] = data[field]
    }
  }
  const schema = Joi.object(result).options({ abortEarly: false })
  return ExtractValidationResult(schema.validate(_data))
}
