import React from 'react'
import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography'

// Components

import AutoCompletes from './Sections/AutoCompletes/AutoCompletes'
import DatePickers from './Sections/DatePickers/DatePickers'
import PdfViewers from './Sections/PdfViewers/PdfViewers'
import ErrorProducers from './Sections/ErrorProducers/ErrorProducers'

// Services
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
// import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import ClaimsAdjudicationServices from 'Services/Api/Claims/ClaimsAdjudicationServices'

import classes from './Test.module.scss'

// https://github.com/mui-org/material-ui-pickers/issues/1388
// const WrappedTextField = props => <ClaimsInputBase {...props} />

export default function Test (props) {
  const [storage, setStorage] = React.useState({
    facility_id: '',
    facility_name: '',
    date_from: new Date()
  })

  const [suggestion, setSuggestion] = React.useState({
    facilities: []
  })

  const [debounce, setDebounce] = React.useState({
    facility: null,
    doctor: null,
    icd10: null,
    cpt: null,
    hospital_items: null,
    pro: null
  })

  const [errors, setErrors] = React.useState({ date_from: '' })
  const [toggle, setToggle] = React.useState('')

  React.useEffect(() => {
    PageTitleService(`Test Page ${toggle.toUpperCase()}`)
    // BreadcrumbsService.setSelectedItems(['test'])
  }, [toggle])

  const onChange = ({ value, field }) => {
    setStorage(state => ({ ...state, [field]: value }))
  }

  return (
    <div className={classes.root}>
      <div className={classes.toolbar}>
        <Button
          variant='contained'
          color='primary'
          className='button-positive'
          onClick={() => setToggle('auto-complete')}
        >
          Auto-Complete
        </Button>
        <Button
          variant='contained'
          color='primary'
          className='button-positive'
          onClick={() => setToggle('date-picker')}
        >
          Date-Pickers
        </Button>
        <Button variant='contained' color='primary' className='button-positive' onClick={() => setToggle('pdf-viewer')}>
          Pdf-Viewer
        </Button>

        <Button
          variant='contained'
          color='primary'
          className='button-positive'
          onClick={() => setToggle('error-producer')}
        >
          Error-Producer
        </Button>
      </div>
      <div className={classes.content}>
        {'-------'}
        <div className={classes.wrapper}>
          {toggle === 'auto-complete' && (
            <AutoCompletes
              service={ClaimsAdjudicationServices}
              errors={errors}
              storage={storage}
              debounce={debounce}
              suggestion={suggestion}
              setErrors={setErrors}
              onChange={onChange}
              setStorage={setStorage}
              setDebounce={setDebounce}
              setSuggestion={setSuggestion}
            />
          )}

          {toggle === 'date-picker' && (
            <DatePickers errors={errors} storage={storage} setErrors={setErrors} onChange={onChange} />
          )}

          {toggle === 'pdf-viewer' && (
            <PdfViewers errors={errors} storage={storage} setErrors={setErrors} onChange={onChange} />
          )}

          {toggle === 'error-producer' && <ErrorProducers errors={errors} storage={storage} setErrors={setErrors} />}
        </div>
      </div>
    </div>
  )
}
